import {UrlService} from "./../utils/UrlService";

interface VhsSelectAllAttributes extends angular.IAttributes {
}

class VhsSelectAllController {
    private $timeout: angular.ITimeoutService;
    private targetElement: JQuery;
    private thisElement: JQuery;
    private $scope: angular.IScope;
    private static TIMER_MILLISECONDS = 1000;

    public static $inject = [
        "$timeout"
    ];

    constructor($timeout: angular.ITimeoutService) {
        this.$timeout = $timeout;
    }

    public isDisabled(): boolean {
        var result = !this.targetElement || $(this.targetElement).prop('disabled');
        $(this.thisElement).prop('disabled', result);
        return result;
    }

    public getCurrentState(): boolean {
        var total = this.targetElement.find('option').length;
        var selected = this.targetElement.find('option:selected').length;
        return (selected == total && total > 0);
    }

    public refreshState(): void {
        try {
            $(this.thisElement).prop('checked', this.getCurrentState());
            this.isDisabled();
        } catch (e) {
        }
    }

    public makeSelectAll(element: any, $scope: angular.IScope) {
        this.$scope = $scope;
        $scope["refreshState"] = (() => {
            this.refreshState;
        });
        this.thisElement = $(element).find('input');
        this.targetElement = $(element).find('select');
        this.isDisabled();

        $(document).ready(() => {

            $scope.$watch('option', () => {
                this.refreshState();
                this.$timeout(() => {
                    this.refreshState();
                }, VhsSelectAllController.TIMER_MILLISECONDS);
            });

            $(document).ready(() => {
                this.refreshState();
            });

            $(document).on('click', 'button', null, () => {
                setTimeout(() => {
                    this.refreshState();
                }, VhsSelectAllController.TIMER_MILLISECONDS);
            });

            $(this.targetElement).change(() => {
                this.refreshState();
            });

            $(this.targetElement).on('change', 'option', [], () => {
                this.refreshState();
            });

            $(this.thisElement).change((event: JQueryEventObject) => {
                if (this.isDisabled() || event.isDefaultPrevented()) {
                    event.preventDefault();
                    this.refreshState();
                    return;
                }
                var options: JQuery = this.targetElement.find('option');
                var previousState: boolean = this.getCurrentState();
                for (var i = 0; i < options.length; i++) {
                    if ($(options[i]).prop('selected') != !previousState) {
                        $(options[i]).prop('selected', !previousState);
                        $(options[i]).trigger('change');
                    }
                }
                if (!this.isDisabled()) {
                    this.thisElement.blur();
                    this.targetElement.focus();
                }
            });
        });
    }
}

export function VhsSelectAllDirectiveFactory(url: UrlService): angular.IDirective {
    function link($scope: angular.IScope, element: any,
                  attributes: VhsSelectAllAttributes, controller: VhsSelectAllController,
                  transclude: angular.ITranscludeFunction) {
        controller.makeSelectAll(element, $scope);
    }

    var directive: any = {};
    directive.restrict = 'A';
    directive.link = link;
    directive.controller = ["$timeout", VhsSelectAllController];
    directive.scope = {};
    directive.templateUrl = url.template('components/vhs-select-all.html');
    directive.transclude = true;
    return directive;
}

VhsSelectAllDirectiveFactory.$inject = [
    UrlService.NAME
];
