import {IStoragePlace} from "./models/IStoragePlace";
import {SettingsService} from "./SettingsService";
import {SystemSettingsRouter} from "./../urls/SystemSettingsRouter";
import {IGridModel, GridModel} from "./../IGridModel";
import {HttpGridHelper} from "./../IHttpGridService";
import {PermissionsService} from "./../security/PermissionsService";
import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";
import {UrlService} from "./../utils/UrlService";
import {MenuHelper} from "./../utils/MenuHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {notifier} from "./../INotifier";

export class StoragePlacesRegistryController {
    public static NAME = "settings.StoragePlacesRegistryController";

    private systemPermissions: ISystemPermissions;

    public storagePlacesGridModel: IGridModel<IStoragePlace>;

    public static $inject = [
        "$http",
        SettingsService.NAME,
        PermissionsService.NAME,
        SystemSettingsRouter.NAME,
        UrlService.NAME
    ];

    constructor($http: angular.IHttpService,
                private settingsApi: SettingsService,
                private permissionsApi: PermissionsService,
                public systemSettingsRouter: SystemSettingsRouter,
                private url: UrlService) {
        MenuHelper.setCurrentMenuItem(MenuHelper.ADMINISTRATION_STORAGE_PLACES_MENU_KEY);
        PageHeader.set().header("Storages").update();

        permissionsApi.getSystemPermissions().then(systemPermissions => {
            PageHeader.set().preserveHeader()
                .addActionLinkIf(systemPermissions.editStoragePlaces,
                '+&nbsp;New',
                this.systemSettingsRouter.storagePlaceCreateUrl())
                .update();
            this.systemPermissions = systemPermissions;
        });

        this.storagePlacesGridModel = new GridModel(HttpGridHelper.createLoader<IStoragePlace>($http,
            this.url.service("/api/settings/storagePlaces")
        ));
    }

    public getIsActiveGridStyle(isActive: boolean): any {
        return isActive
            ? {}
            : {"color": "#999999"};
    }

    public convertIsActive(isActive: boolean): string {
        return isActive != undefined
            ? isActive ? 'Yes' : 'No'
            : undefined;
    }

    public activate(index: number, storagePlaceId: number) {
        var promise = this.settingsApi.setStoragePlaceActive(storagePlaceId, true);
        promise.then(storagePlace => {
            this.storagePlacesGridModel.entries[index] = storagePlace;
            notifier.success(
                "Storage " +
                storagePlace.name +
                " has been activated");
        });
    }
}
