import {UrlService} from "./../utils/UrlService";
import {ReferenceService} from "./../reference/ReferenceService";
import {ISelectable} from "../ISelectable";
import {ReferenceEntitiesParams} from "./../reference/models/IReferenceEntitiesParams";

export interface IEntitiesSelect2SelectorDirectiveScope extends angular.IScope {
    urlString?: string;
    referenceKey?: string;
    filter?: any;
    model?: any;
    modelProperty?: string;
    multiple: boolean;
    disabled?: boolean;
    nullable: boolean;
    activeEntities?: boolean;
    placeholder?: string;
    onSelectorChange?: (object?: any) => {};

    vm?: EntitiesSelect2SelectorController;
}

export class EntitiesSelect2SelectorController {
    private static DEFAULT_PLACEHOLDER: string = "";

    private modelIds: Array<number>;
    private modelId: number = null;

    public items: Array<ISelectable>;
    public selectedItems: any = null;

    public placeholder: string;

    public static $inject = [
        "$scope",
        "$http",
        UrlService.NAME,
        ReferenceService.NAME
    ];

    constructor(private $scope: IEntitiesSelect2SelectorDirectiveScope,
                private $http: ng.IHttpService,
                private url: UrlService,
                private referenceApi: ReferenceService) {
        $scope.vm = this;

        this.placeholder = $scope.placeholder ? $scope.placeholder : EntitiesSelect2SelectorController.DEFAULT_PLACEHOLDER;

        if ($scope.multiple) {
            this.modelIds = $scope.model[$scope.modelProperty];
        } else {
            this.modelId = $scope.model[$scope.modelProperty];
        }

        this.loadEntitiesForSelector();
        this.$scope.$watch("filter", (newValue, oldValue) => {
            if (angular.equals(newValue, oldValue)) return;
            this.loadEntitiesForSelector();
        });
        this.$scope.$watch("activeEntities", (newValue, oldValue) => {
            if (newValue == oldValue) return;
            this.loadEntitiesForSelector();
        });
        this.$scope.$watch("model[modelProperty]", (newValue, oldValue) => {
            if (angular.equals(newValue, oldValue) ||
                angular.equals(newValue, this.$scope.multiple ? this.modelIds : this.modelId)) return;
            this.setValues();
        });
    }

    private loadEntitiesForSelector(): void {
        this.selectedItems = this.$scope.multiple ? [] : null;
        if (this.$scope.referenceKey != null) {
            this.referenceApi.getActiveReferenceEntitiesForSelector(this.$scope.referenceKey, new ReferenceEntitiesParams(
                null, null, undefined, undefined), null)
                .then(result => {
                    this.items = result;
                    this.items.forEach(item => {
                        if (this.$scope.multiple && this.modelIds.indexOf(item.id) > -1) {
                            if (this.selectedItems.filter(i => i.id == item.id).length == 0) {
                                this.selectedItems.push(item);
                            }
                        } else if (!this.$scope.multiple && this.modelId == item.id) {
                            this.selectedItems = item;
                        }
                    })
                });
        } else {
            this.$http.get(this.url.service(this.$scope.urlString), {
                params: this.$scope.filter
            }).then((result: any) => {
                this.items = result.data;
                this.items.forEach(item => {
                    if (this.$scope.multiple && this.modelIds.indexOf(item.id) > -1) {
                        if (this.selectedItems.filter(i => i.id == item.id).length == 0) {
                            this.selectedItems.push(item);
                        }

                    } else if (!this.$scope.multiple && this.modelId == item.id) {
                        this.selectedItems = item;
                    }
                })
            });
        }
    }

    private setValues() {
        if (this.$scope.multiple) {
            this.modelIds = this.$scope.model[this.$scope.modelProperty];
        } else {
            this.modelId = this.$scope.model[this.$scope.modelProperty];
        }
        this.selectedItems = this.$scope.multiple ? [] : null;
        this.items.forEach(item => {
            if (this.$scope.multiple && this.modelIds.indexOf(item.id) > -1) {
                if (this.selectedItems.filter(i => i.id == item.id).length == 0) {
                    this.selectedItems.push(item);
                }

            } else if (!this.$scope.multiple && this.modelId == item.id) {
                this.selectedItems = item;
            }
        });
    }

    public onSelectCallback(item: ISelectable, select: any): void {
        if (this.$scope.multiple) {
            this.modelIds.push(item.id);
        } else {
            this.selectedItems = item;
            this.$scope.model[this.$scope.modelProperty] = item.id;
            this.modelId = item.id;
        }
        let result: any = angular.copy(item);
        result.selected = true;
        this.$scope.onSelectorChange(result);
    }

    public onRemoveCallback(item: ISelectable, select: any): void {
        if (this.$scope.multiple) {
            var index = this.modelIds.indexOf(item.id, 0);
            if (index > -1) {
                this.modelIds.splice(index, 1);
            }
        } else {
            this.selectedItems = null;
            this.$scope.model[this.$scope.modelProperty] = null;
            this.modelId = null;
        }
        let result: any = angular.copy(item);
        result.selected = false;
        this.$scope.onSelectorChange(result);
    }
}

export function EntitiesSelect2SelectorDirectiveFactory(url: UrlService): ng.IDirective {

    var directive: ng.IDirective = {};
    directive.restrict = "A";
    directive.controller = EntitiesSelect2SelectorController;
    directive.scope = {
        urlString: "=?vhsEntitiesUrl",
        referenceKey: "=?vhsReferenceKey",
        filter: "=?vhsFilter",
        model: "=vhsModel",
        modelProperty: "=vhsModelProperty",
        multiple: "=vhsMultiple",
        disabled: "=?vhsDisabled",
        nullable: "=vhsNullable",
        activeEntities: "=?vhsActiveEntities",
        placeholder: "=?vhsPlaceholder",
        onSelectorChange: "&vhsOnChange"
    };
    directive.templateUrl = url.template("components/entities-select2-selector.html");
    directive.transclude = true;
    return directive;
}

EntitiesSelect2SelectorDirectiveFactory.$inject = [
    UrlService.NAME
];