export class SecuredObjectType {
    public key: number;
    public name: string;

    public static MATERIAL = new SecuredObjectType(1, 'material');
    public static COURSE = new SecuredObjectType(2, 'course');
    public static QUIZ = new SecuredObjectType(3, 'quiz');
    public static PLAYLIST = new SecuredObjectType(4, 'playlist');
    public static NEWS = new SecuredObjectType(5, 'news');

    constructor(key: number, name: string) {
        this.key = key;
        this.name = name;
    }
}