import {UrlService} from "../../utils/UrlService"

export interface IRichTextEditorDirectiveScope extends angular.IScope {
    vm?: RichTextEditorController;
    allowEmbedMaterials?: boolean;
    allowBundles?: boolean;
    allowSandbox?: boolean;
    bundleId?: number;
}

export class RichTextEditorController {

    public static TA_EMBEDDED_MATERIAL_BUTTON = "embeddedMaterial";
    public static TA_EMBEDDED_BUNDLE_BUTTON = "embeddedBundle";
    public static TA_SANDBOX_BUTTON = "sandbox";

    private element: angular.IAugmentedJQuery;

    public static $inject = [
        "$scope",
        "$timeout"
    ];

    constructor(private $scope: IRichTextEditorDirectiveScope,
                private $timeout: angular.ITimeoutService) {
        $scope.vm = this;
    }

    public init($scope: IRichTextEditorDirectiveScope,
                element: angular.IAugmentedJQuery) {
        this.element = element;
        if (!this.$scope.allowBundles || !this.$scope.allowEmbedMaterials || !this.$scope.allowSandbox || !this.$scope.bundleId) {
            this.$timeout(() => {
                this.hideEditorButtons();
            }, 0)
        }
    }

    private hideEditorButtons() {
        if (!this.$scope.allowBundles || !this.$scope.bundleId) {
            this.element.find("button[name='" + RichTextEditorController.TA_EMBEDDED_BUNDLE_BUTTON + "']").hide();
        }
        if (!this.$scope.allowEmbedMaterials) {
            this.element.find("button[name='" + RichTextEditorController.TA_EMBEDDED_MATERIAL_BUTTON + "']").hide();
        }
        if (!this.$scope.allowSandbox || !this.$scope.bundleId) {
            this.element.find("button[name='" + RichTextEditorController.TA_SANDBOX_BUTTON + "']").hide();
        }
    }
}

export function RichTextEditorDirectiveFactory(url: UrlService): angular.IDirective {
    function link($scope: IRichTextEditorDirectiveScope,
                  element: angular.IAugmentedJQuery,
                  attributes: angular.IAttributes,
                  controller: RichTextEditorController) {
        controller.init($scope, element);
    }

    let directive: angular.IDirective = {
        restrict: "A",
        link: link,
        controller: RichTextEditorController,
        scope: {
            allowEmbedMaterials: "@?vhsEmbedMaterials",
            allowBundles: "@?vhsEmbedBundles",
            allowSandbox: "@?vhsEmbedSandbox",
            bundleId: "@?vhsBundleId"
        },
        transclude: true,
        templateUrl: url.template("components/rich-text-editor.html")
    };
    return directive;
}

RichTextEditorDirectiveFactory.$inject = [
    UrlService.NAME
];
