import {MenuHelper} from "./../utils/MenuHelper";
import {Moment} from "./../utils/Moment";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {IGridModel, GridModel, IGridLoadingListener} from "./../IGridModel";
import {ISecurityLog, SecurityLogStatusEnum, SecurityLogActionEnum, SECURITY_LOG_ACTION_ENUM_VALUES, SECURITY_LOG_STATUS_ENUM_VALUES} from "./ISecurityLog";
import {UsersRouter} from "./../urls/UsersRouter";
import {UrlService} from "./../utils/UrlService";
import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";
import {PermissionsService} from "./../security/PermissionsService";
import {HttpGridHelper} from "./../IHttpGridService";
import {ISelectable} from "./../ISelectable";
import {PageMode} from "../PageMode";

export class SecurityLogController implements IGridLoadingListener{

    public static NAME = "logs.SecurityLogController";

    public securityLogsGridModel: GridModel<ISecurityLog>;
    public forDateFilter: string;
    public securityActionTypes: Array<ISelectable> = [];
    public securityLogStatuses: Array<ISelectable> = [];
    public actionsFilter: Array<number> = [];
    public statusesFilter: Array<number> = [];
    private systemPermissions: ISystemPermissions;

    public static $inject = [
        "$http",
        PermissionsService.NAME,
        UrlService.NAME,
        UsersRouter.NAME
    ];

    constructor($http: angular.IHttpService,
                private permissionsApi: PermissionsService,
                private url: UrlService,
                public usersRouter: UsersRouter
    ) {
        MenuHelper.setCurrentMenuItem(MenuHelper.ADMINISTRATION_SECURITY_LOGS_KEY);
        PageHeader.set().header("Security logs").update();


        this.securityLogsGridModel = new GridModel(HttpGridHelper.createLoader<ISecurityLog>($http, this.url.service("/api/logs/security")));
        this.securityLogsGridModel.addLoadingListener(this);

        this.permissionsApi.getSystemPermissions().then(p => {
            this.systemPermissions = p;
        });
        this.fillSecurityActionTypes();
        this.fillSecurityLogStatuses();
    }

    public startLoading() {
        var dateValue = null;
        if (this.forDateFilter) {
            dateValue = Moment.textToJavaDate(this.forDateFilter);
        }
        this.securityLogsGridModel.filter["forDate"] = dateValue;

        var statuses: Array<string> = [];
        for (var i = 0; i < this.statusesFilter.length; i++) {
            statuses.push(SecurityLogStatusEnum[this.statusesFilter[i]]);
        }
        this.securityLogsGridModel.filter["statuses"] = statuses;

        var actionTypes: Array<string> = [];
        for (var i = 0; i < this.actionsFilter.length; i++) {
            actionTypes.push(SecurityLogActionEnum[this.actionsFilter[i]]);
        }
        this.securityLogsGridModel.filter["actions"] = actionTypes;
    }

    public endLoading() {
        //do nothing
    }

    private fillSecurityActionTypes() {
        for (var i = 0; i < SECURITY_LOG_ACTION_ENUM_VALUES.length; i++) {
            this.securityActionTypes.push({
                id: i,
                name: SecurityLogActionEnum[SECURITY_LOG_ACTION_ENUM_VALUES[i]]
            });
        }
    }

    private fillSecurityLogStatuses() {
        for (var i = 0; i < SECURITY_LOG_STATUS_ENUM_VALUES.length; i++) {
            this.securityLogStatuses.push({
                id: i,
                name: SecurityLogStatusEnum[SECURITY_LOG_STATUS_ENUM_VALUES[i]]
            });
        }
    }
}