import {PermissionsRepository} from "./PermissionsRepository";
import {ISystemPermissions} from "./permissions/ISystemPermissions";
import {ICoursePermissions} from "./permissions/ICoursePermissions";
import {IMaterialPermissions} from "./permissions/IMaterialPermissions";
import {IPlaylistPermissions} from "./permissions/IPlaylistPermissions";
import {IQuizPermissions} from "./permissions/IQuizPermissions";
import {INewsPermissions} from "./permissions/INewsPermissions";
import {SecuredObjectType} from "./permissions/SecuredObjectType";
import {IPermissionWithUsersAndGroups} from "./permissions/IPermissionWithUsersAndGroups";
import {AuthService} from "./../auth/AuthService";
import {UrlService, IUrlBuilder} from "./../utils/UrlService";

export class PermissionsService {
    public static NAME = "security.PermissionsService";

    public static $inject = [
        AuthService.NAME,
        PermissionsRepository.NAME,
        "$http",
        UrlService.NAME
    ];

    constructor(private authApi: AuthService,
                private permissionsRepository: PermissionsRepository,
                private $http: angular.IHttpService,
                private url: UrlService) {
    }

    public getSystemPermissions(): angular.IPromise<ISystemPermissions> {
        return this.authApi.getCurrentUser()
            .then(user => user.permissions);
    }

    public getSecuredObjectPermissions(objectType: SecuredObjectType, entityId: string): angular.IPromise<any> {
        return this.permissionsRepository.getPermissionsForObject(objectType, entityId);
    }

    public getMaterialPermissions(materialId: string): angular.IPromise<IMaterialPermissions> {
        return this.permissionsRepository.getPermissionsForObject(SecuredObjectType.MATERIAL, materialId);
    }

    public getPlaylistPermissions(playlistId: string): angular.IPromise<IPlaylistPermissions> {
        return this.permissionsRepository.getPermissionsForObject(SecuredObjectType.PLAYLIST, playlistId);
    }

    public getCoursePermissions(courseId: string): angular.IPromise<ICoursePermissions> {
        return this.permissionsRepository.getPermissionsForObject(SecuredObjectType.COURSE, courseId);
    }

    public getQuizPermissions(quizId: string): angular.IPromise<IQuizPermissions> {
        return this.permissionsRepository.getPermissionsForObject(SecuredObjectType.QUIZ, quizId);
    }

    public getNewsPermissions(newsId: string): angular.IPromise<INewsPermissions> {
        return this.permissionsRepository.getPermissionsForObject(SecuredObjectType.NEWS, newsId);
    }

    public getAllObjectPermissionsWithUsersAndGroups(securedObjectType: SecuredObjectType, objectId?: string): angular.IPromise<Array<IPermissionWithUsersAndGroups>> {
        let url: string = "/api/permissions/usersAndGroups/:objectType";
        if (objectId != null) {
            url += "?id=:id";
        }
        let urlBuilder: IUrlBuilder = this.url.base(url)
            .param(":objectType", securedObjectType.name);
        if (objectId != null) {
            urlBuilder.param(":id", objectId);
        }
        return this.$http.get(urlBuilder.service())
            .then(result => {
                return result.data;
            });
    }
}