import {GridController} from "./GridDirective";
import {UrlService} from "./../utils/UrlService";

interface GridConfigurationAttributes extends ng.IAttributes {
}

export function GridConfigurationDirectiveFactory(url: UrlService): ng.IDirective {

    function link($scope: ng.IScope, element: ng.IAugmentedJQuery,
                  attributes: GridConfigurationAttributes, controller: GridController<any>,
                  transclude: ng.ITranscludeFunction) {
        controller.setIsConfigurable(true);
        $scope["_showPageSizeSelector"] = true;
    }

    var directive: any = {};
    directive.restrict = "A";
    directive.templateUrl = url.template("components/grid-configuration.html");
    directive.link = link;
    directive.require = "^vhsGrid";
    return directive;
}

GridConfigurationDirectiveFactory.$inject = [
    UrlService.NAME
];
