import {BalanceController} from "./BalanceController";
import {PurchaseHistoryController} from "./PurchaseHistoryController";
import {AccountManagementController} from "./AccountManagementController";
import {PurchaseController} from "./PurchaseController";
import {PurchaseService} from "./PurchaseService";

import {CommonModule} from "./../common/CommonModule";
import {SecurityModule} from "./../security/SecurityModule";
import {UrlsModule} from "./../urls/UrlsModule";
import {ContentModule} from "./../content/ContentModule";

export var FinancesModule: angular.IModule = angular
        .module("finances", [
            CommonModule.name,
            SecurityModule.name,
            UrlsModule.name,
            ContentModule.name
        ])
        .controller(BalanceController.NAME, BalanceController)
        .controller(PurchaseHistoryController.NAME, PurchaseHistoryController)
        .controller(AccountManagementController.NAME, AccountManagementController)
        .controller(PurchaseController.NAME, PurchaseController)
        .service(PurchaseService.NAME, PurchaseService)
    ;
