import {StringBuilder} from "./../utils/StringBuilder";
import {BaseRouter} from "./BaseRouter";

export class QuizzesRouter extends BaseRouter {
    public static NAME = "urls.QuizzesRouter";
    public static QUIZ_ID_PARAM = "quizId";

    public static indexRoute = "/quizzes";
    public static detailsRoute = `/quizzes/:${QuizzesRouter.QUIZ_ID_PARAM}`;
    public static passingRoute = `/quizzes/passing/:${QuizzesRouter.QUIZ_ID_PARAM}`;
    public static updateRoute = `/quizzes/update/:${QuizzesRouter.QUIZ_ID_PARAM}`;
    public static createRoute = "/quizzes/create";

    public static $inject = [
        "$location"
    ];

    constructor($location: ng.ILocationService) {
        super($location);
    }

    public indexUrl(): string {
        return this.url(QuizzesRouter.indexRoute);
    }

    public createUrl(): string {
        return this.url(QuizzesRouter.createRoute);
    }

    public detailsUrl(quizId: number): string {
        return this.url(StringBuilder
            .interpolate(QuizzesRouter.detailsRoute)
            .with(':' + QuizzesRouter.QUIZ_ID_PARAM, quizId.toString())
            .build());
    }

    public updateUrl(quizId: number): string {
        return this.url(StringBuilder.interpolate(QuizzesRouter.updateRoute)
            .with(':' + QuizzesRouter.QUIZ_ID_PARAM, quizId.toString())
            .build());
    }

    public passingUrl(quizId: number): string {
        return this.url(StringBuilder
            .interpolate(QuizzesRouter.passingRoute)
            .with(':' + QuizzesRouter.QUIZ_ID_PARAM, quizId.toString())
            .build());
    }
}