import {PersonalService} from "./PersonalService";

import {IUser} from "./../users/models/IUser";
import {UserService} from "./../users/UserService";
import {SecurityService} from "./../security/SecurityService";
import {MenuHelper} from "./../utils/MenuHelper";
import {ReferenceService} from "./../reference/ReferenceService";
import {notifier} from "./../INotifier";
import {ISelectable} from "./../ISelectable";

export class SubscriptionsController {
    public static NAME = "personal.SubscriptionsController";

    public userProfile: IUser;
    public subscriptions: any;
    public users: Array<ISelectable> = [];
    public notificationsFrequency: Array<ISelectable>;
    public disableSaveButton: boolean = false;

    public static $inject = [
        SecurityService.NAME,
        UserService.NAME,
        PersonalService.NAME,
        ReferenceService.NAME
    ];

    constructor(private securityService: SecurityService,
                private userService: UserService,
                private personalService: PersonalService,
                private referenceApi: ReferenceService) {
        this.securityService.demandAuthentication();
        MenuHelper.setCurrentMenuItem(MenuHelper.PERSONAL_ACCOUNT_SUBSCRIPTIONS_MENU_KEY);

        this.subscriptions = {
            authorsIds: [],
            subjectsIds: [],
            allNewsSubscription: false,
            studentsNewsSubscription: false,
            commentsSubscription: false
        };

        this.loadUserProfile();
        this.loadSubscriptions();
        this.loadNotificationsFrequency();
    }

    public changeSubscriptionsSettings(): void {
        this.personalService.changeSubscriptions(this.subscriptions).then(result => {
            this.userService.changeUserNotificationFrequency(this.userProfile.notificationFrequencyId).then(result => {
                notifier.success("Subscriptions are successfully saved");
            });
        });
    }

    private loadUserProfile(): void {
        this.userService.getCurrentUserProfile().then(u => {
            this.userProfile = u;
        });
    }

    public loadSubscriptions(): void {
        this.personalService.loadSubscriptions().then(result => {
            angular.copy(result, this.subscriptions);
            this.loadUsers();
        });
    }

    public cancelSubscriptionsSettings(): void {
        this.loadSubscriptions();
        this.loadUserProfile();
    }

    private loadNotificationsFrequency(): void {
        this.referenceApi.getActiveReferenceEntitiesForSelectorWithoutParams("calendarPeriods", [])
            .then(frequency => {
                this.notificationsFrequency = frequency;
            })
    }

    private loadUsers(): void {
        this.userService.getUsersWithoutExcludedUsersForSelector().then(result => {
            angular.copy(result, this.users);
        });
    }
}