import {notifier} from "./../INotifier";
import {StringBuilder} from "./../utils/StringBuilder";

export interface CurrentlyUploadableFile extends File {
    uploaded?: number;
    guid?: number;
    key?: string;
}

export class UploadsService {
    public static NAME = "storage.UploadsService";

    public static MATERIAL_KEY = "material";
    public static COURSE_KEY = "course";
    public static PREVIEW_UPLOAD_URL_KEY = "previewUploadUrl";
    public static PREVIEW_UPLOAD_METHOD_KEY = "previewUploadMethod";
    public static FILE_UPLOAD_URL_KEY = "fileUploadUrl";
    public static FILE_UPLOAD_METHOD_KEY = "fileUploadMethod";

    private static UPLOAD_PARALLEL_FILES = 3;

    private queue: Array<File> = [];
    private currentlyUploadableFiles: Array<File> = Array.apply(null, Array(UploadsService.UPLOAD_PARALLEL_FILES)).map(() => {return null});
    private currentlyUploadableFilesMap: any = {};

    constructor() {
        FileAPI.cors = true;
    }

    public getCurrentlyUploadableFiles(): Array<CurrentlyUploadableFile> {
        let result: Array<CurrentlyUploadableFile> = [];
        this.currentlyUploadableFiles.forEach((file: CurrentlyUploadableFile) => {
            if (file != null) {
                result.push(file);
            }
        });
        this.queue.forEach((file: CurrentlyUploadableFile) => {
            result.push(file);
        });
        return result;
    }

    public cancel(file: CurrentlyUploadableFile): void {
        let xhr: XmlHttpRequest = this.currentlyUploadableFilesMap[file.name + file.guid].xhr;
        xhr.abort();
    }

    public cancelByKey(key: string): void {
        let fileKey: string;
        this.currentlyUploadableFiles.forEach((file: CurrentlyUploadableFile) => {
            if (file != null) {
                fileKey = this.currentlyUploadableFilesMap[file.name + file.guid].key;
                if (fileKey == key) {
                    this.cancel(file);
                }
            }
        });
    }

    public upload(file: File, fileKey: string, url: string, method: string, successFunc: (file: File) => void, abortFunc: (file: File) => void,
                  errorFunc: (file: File) => void): void {
        FileAPI.filterFiles([file], () => {
            return true;
        }, (files: Array<File>) => {
            if (files.length) {
                files.forEach((file: CurrentlyUploadableFile) => {
                    file.guid = moment().valueOf();
                    this.queue.push(file);
                    this.currentlyUploadableFilesMap[file.name + file.guid] = {
                        index: null,
                        url: url,
                        method: method,
                        successFunc: successFunc,
                        abortFunc: abortFunc,
                        errorFunc: errorFunc,
                        xhr: null,
                        key: fileKey
                    };
                });
                this.uploadParallelFiles();
            }
        });
    }

    private uploadParallelFiles(): void {
        this.currentlyUploadableFiles.forEach((f: CurrentlyUploadableFile, index: number) => {
            if (this.queue.length > 0 && f == null) {
                let file: CurrentlyUploadableFile = this.queue.shift();
                this.currentlyUploadableFiles[index] = file;
                this.currentlyUploadableFilesMap[file.name + file.guid].index = index;
                this.uploadOneFile(file);
            }
        });
    }

    private uploadOneFile(file: CurrentlyUploadableFile): void {
        let cuf: any = this.currentlyUploadableFilesMap[file.name + file.guid];
        let xhr: XmlHttpRequest = FileAPI.upload({
            url: cuf.url,
            method: cuf.method,
            data: {
                guid: file.guid
            },
            files: {
                file: file
            },
            chunkSize: 100 * FileAPI.KB,
            chunkUploadRetry: 3,
            fileprogress: this.fileProgressFunc,
            filecomplete: this.fileCompleteFunc
        });
        cuf.xhr = xhr;
    }

    private fileProgressFunc: (evt: any, file: CurrentlyUploadableFile, xhr: XmlHttpRequest, options: any) => void
            = (evt: any, file: CurrentlyUploadableFile, xhr: XmlHttpRequest, options: any) => {
        file.uploaded = evt.loaded;
        file.guid = options.data.guid;
        let cup: any = this.currentlyUploadableFilesMap[file.name + file.guid];
        if (cup) {
            let index: number = cup.index;
            this.currentlyUploadableFiles[index] = file;
        }
    };

    private fileCompleteFunc: (err: any, xhr: XmlHttpRequest, file: CurrentlyUploadableFile, options: any) => void
            = (err: any, xhr: XmlHttpRequest, file: CurrentlyUploadableFile, options: any) => {
        file.guid = options.data.guid;
        if (err == "abort") {
            this.currentlyUploadableFilesMap[file.name + file.guid].abortFunc(file);
        } else if (err === false) {
            this.currentlyUploadableFilesMap[file.name + file.guid].successFunc(file);
        } else {
            this.currentlyUploadableFilesMap[file.name + file.guid].errorFunc(file);
        }
        let index: number = this.currentlyUploadableFilesMap[file.name + file.guid].index;
        this.currentlyUploadableFiles[index] = null;
        this.currentlyUploadableFilesMap[file.name + file.guid] = null;
        this.uploadParallelFiles();
    };
}