import {ISystemCommonSettings} from "./models/ISystemCommonSettings";
import {ISystemLocale} from "./models/ISystemLocale";
import {UrlService} from "./../utils/UrlService";
import {IStoragePlace} from "./models/IStoragePlace";
import IQService = angular.IQService;
import ITimeoutService = angular.ITimeoutService;

export class SettingsService {
    public static NAME = "settings.SettingsService";

    private static RESET_CURRENT_DATE_TIMEOUT_MS = 5 * 60 * 1000;

    private currentDate: number;

    public static $inject = [
        "$http",
        "$q",
        "$timeout",
        UrlService.NAME
    ];

    constructor(private $http: angular.IHttpService,
                private $q: IQService,
                private $timeout: ITimeoutService,
                private url: UrlService) {
    }

    public getSystemCommonSettings(): angular.IPromise<ISystemCommonSettings> {
        return this.$http.get(
            this.url.service("/api/settings/common"))
            .then(result => result.data);
    }

    public getAvailableLocales(): angular.IPromise<Array<ISystemLocale>> {
        return this.$http.get(
            this.url.service("/api/settings/common/locales"))
            .then(result => result.data);
    }

    public update(settings: ISystemCommonSettings): angular.IPromise<ISystemCommonSettings> {
        return this.$http.put(this.url.service("/api/settings/common"), settings)
            .then(result => result.data);
    }

    public getStoragePlaces(): angular.IPromise<Array<IStoragePlace>> {
        return this.$http.get(this.url.service("/api/settings/storagePlaces"))
            .then(result => result.data);
    }

    public getStoragePlace(id: number): angular.IPromise<IStoragePlace> {
        return this.$http.get(this.url.base("/api/settings/storagePlaces/:id").param(":id", id).service())
            .then(result => result.data);
    }

    public createStoragePlace(storagePlace: IStoragePlace): angular.IPromise<IStoragePlace> {
        return this.$http.post(this.url.service("/api/settings/storagePlaces"), storagePlace)
            .then(result => result.data);
    }

    public updateStoragePlace(storagePlace: IStoragePlace): angular.IPromise<IStoragePlace> {
        return this.$http.put(this.url.service("/api/settings/storagePlaces"), storagePlace)
            .then(result => result.data);
    }

    public setStoragePlaceActive(id: number, isActive: boolean): angular.IPromise<IStoragePlace> {
        return this.$http.put(this.url.base("/api/settings/storagePlaces/:id/setActive/:isActive")
            .param(":id", id)
            .param(":isActive", isActive).service(), undefined)
            .then(result => result.data);
    }

    public getCurrentDate(): angular.IPromise<number> {
        if (!this.currentDate) {
            return this.$http.get(this.url.service("/api/settings/common/currentDate"))
                .then(result => {
                    this.currentDate = <number> result.data;
                    this.$timeout(
                        () => {
                            this.currentDate = null
                        }, SettingsService.RESET_CURRENT_DATE_TIMEOUT_MS
                    );
                    return result.data
                });
        } else {
            return this.$q.when(this.currentDate);
        }
    }
}
