import {StatisticReportResult} from "./../models/StatisticReportResult";
import {IUser} from "./../../users/models/IUser";
import {IMaterialType} from "./../../materials/models/MaterialType";
import {ISubject} from "./../../ISubject";
import {IPair} from "./../../IPair";

export interface IReportTargetNamesMaker {
    make(report: StatisticReportResult<any, Array<any>, any>, func: (name: string) => any): Array<any>;
}

export class OwnerNamesMaker implements IReportTargetNamesMaker {

    public make(report: StatisticReportResult<any, Array<any>, any>, func: (name: string) => any): Array<any> {
        let result: Array<any> = [];
        report.data.forEach((d: IPair<IUser, Array<any>>) => {
            result.push(func(d.key.fullName));
        });
        return result;
    }
}

export class MaterialTypeNamesMaker implements IReportTargetNamesMaker {

    public make(report: StatisticReportResult<any, Array<any>, any>, func: (name: string) => any): Array<any> {
        let result: Array<any> = [];
        report.data.forEach((d: IPair<IMaterialType, Array<any>>) => {
            result.push(func(d.key.name));
        });
        return result;
    }
}

export class SubjectNamesMaker implements IReportTargetNamesMaker {

    public make(report: StatisticReportResult<any, Array<any>, any>, func: (name: string) => any): Array<any> {
        let result: Array<any> = [];
        report.data.forEach((d: IPair<ISubject, Array<any>>) => {
            result.push(func(d.key.name));
        });
        return result;
    }
}