import {IUser} from "./models/IUser";
import {IUserLogon} from "./models/IUserLogon";
import {IGroup} from "./models/IGroup";
import {UrlService} from "./../utils/UrlService";
import {ISelectable} from "./../ISelectable";
import {IUserWithProgress} from "./models/IUserWithProgress";

export class UserService {
    public static NAME = "users.UserService";

    public static $inject = [
        "$http",
        UrlService.NAME
    ];

    constructor(private $http: angular.IHttpService,
                private url: UrlService) {
    }

    public get(id: number): angular.IPromise<IUser> {
        return this.$http.get(this.url.base("/api/users/:id").param(":id", id).service())
            .then(result => result.data);
    }

    public create(user: IUser): angular.IPromise<IUser> {
        return this.$http.post(this.url.service("/api/users"), user)
            .then(result => result.data);
    }

    public selfSignup(user: IUser): angular.IPromise<IUser> {
        return this.$http.post(this.url.service("/api/users/selfSignup"), user)
            .then(result => result.data);
    }

    public update(user: IUser): angular.IPromise<IUser> {
        return this.$http.put(this.url.service("/api/users"), user)
            .then(result => result.data);
    }

    public setActive(id: number, isActive: boolean): angular.IPromise<IUserWithProgress> {
        return this.$http.put(this.url.base("/api/users/:id/setActive/:isActive")
            .param(":id", id)
            .param(":isActive", isActive).service(), undefined)
            .then(result => result.data);
    }

    public requestPasswordRestoreForUser(userId: number): ng.IPromise<string> {
        return this.$http.post(this.url.service(`/api/users/${userId}/localLogon/restorePassword`),{})
            .then(result => (<any> result.data).link);
    }

    public getGroups(): angular.IPromise<Array<IGroup>> {
        return this.$http.get(this.url.service("/api/users/groups"))
            .then(result => result.data);
    }

    public getUserLogons(userId: number): ng.IPromise<Array<IUserLogon>> {
        return this.$http.get(
            this.url.base("/api/users/:userId/logons").param(":userId", userId).service()
        )
            .then(result => result.data);
    }

    public getCurrentUserProfile(): angular.IPromise<IUser> {
        return this.$http.get(this.url.service("/api/users/currentProfile"))
            .then(result => result.data);
    }
    public getCurrentUserLocalLogon(): ng.IPromise<IUserLogon> {
        return this.$http.get(this.url.service("/api/users/current/localLogon"))
                        .then(result => result.data);
    }

    public updateCurrentUserProfile(user: IUser, password: string): ng.IPromise<IUser> {
        var userUpdateInput: any = angular.copy(user);
        userUpdateInput.password = password;
        return this.$http.put(this.url.service("/api/users/currentProfile"),userUpdateInput)
            .then(result => result.data);
    }

    public getUsersWithoutExcludedUsersForSelector(excludedUserIds?: Array<number>): angular.IPromise<Array<ISelectable>> {
        let params = {
            excludedUserIds: excludedUserIds
        };
        return this.$http.get(this.url.service("/api/userSelector/users"), {params: params})
            .then(result => result.data);
    }

    public getGroupsWithoutExcludedGroupsForSelector(excludedGroupIds?: Array<number>): angular.IPromise<Array<ISelectable>> {
        let params = {
            excludedGroupIds: excludedGroupIds
        };
        return this.$http.get(this.url.service("/api/userSelector/groups"), {params: params})
            .then(result => result.data);
    }

    public setActiveGroupWithChildren(isActive: boolean, parentId: number): ng.IPromise<any> {
        return this.$http.put(this.url.base("/api/users/groups/setActive/:isActive/with/children/:parentId")
            .param(":isActive", isActive)
            .param(":parentId", parentId).service(), undefined)
            .then(result => result.data);
    }

    public getGroup(groupId: number): angular.IPromise<IGroup> {
        return this.$http.get(this.url.base("/api/users/groups/:groupId").param(":groupId", groupId).service())
            .then(result => result.data);
    }

    public createGroup(group: IGroup): angular.IPromise<IGroup> {
        return this.$http.post(this.url.service("/api/users/groups"), group)
            .then(result => result.data);
    }

    public updateGroup(group: IGroup): angular.IPromise<IGroup> {
        return this.$http.put(this.url.service("/api/users/groups"), group)
            .then(result => result.data);
    }

    public getActiveGroupsNoChildren(parentId: number): ng.IPromise<Array<IGroup>> {
        return this.$http.get(this.url.base("/api/users/groups/active/no/children/:parentId")
            .param(":parentId", parentId).service())
            .then(result => result.data);
    }

    public getActiveGroups(): ng.IPromise<Array<IGroup>> {
        return this.$http.get(this.url.service("/api/users/groups/active"))
            .then(result => result.data);
    }

    public changeUserNotificationFrequency(frequencyId: number) {
        return this.$http.post(this.url.service("/api/users/notifications/frequency"), frequencyId ? JSON.stringify(frequencyId) : null)
            .then(result => result.data);
    }

    public downloadCertificate(parameters: any) {
        let reportType = 'COURSE_CERTIFICATE';
        return this.$http.post(
            this.url.base("/api/reports/downloadToken/:reportType")
                .param(":reportType", reportType)
                .service(), {
                parameters: JSON.stringify(parameters)
            }
        ).then((result: any) =>  this.url.base("/api/reports/download/:token").param(":token", result.data.token).service());
    }
}
