import {ReferenceUtils} from "./ReferenceUtils";
import {ReferenceService} from "./ReferenceService";
import {ReferenceRedirect} from "./ReferenceRedirect";
import {IReferenceEntityMetadata} from "./models/ReferenceMetadata";
import {ReferenceIsRequiredValidator} from "./validators/ReferenceIsRequiredValidator";
import {ReferenceMaxLengthValidator} from "./validators/ReferenceMaxLengthValidator";
import {ReferenceRequiredAttributesGroupValidator} from "./validators/ReferenceRequiredAttributesGroupValidator";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {MenuHelper} from "./../utils/MenuHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {ReferenceRouter} from "./../urls/ReferenceRouter";
import {PermissionsService} from "./../security/PermissionsService";
import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";
import {SecurityService} from "./../security/SecurityService";

export class ReferenceEditController {
    public static NAME = "reference.ReferenceEditController";

    public referenceEntity: Map<string, any>;
    public referenceMetadata: IReferenceEntityMetadata;
    public referenceAssociationFiltersMap: Map<string, string>;
    public referenceValidationErrorsMap: Map<string, string>;
    public systemPermissions: ISystemPermissions;
    public disableSaveButton: boolean = false;

    protected referenceDefaultValues: string;
    protected referenceFilter: string;
    protected referenceAssociationFilters: string;
    protected referenceDefaultValuesMap: Map<string, string>;

    private redirectStorageUrl: string;

    public static $inject = [
        "$routeParams",
        "$location",
        ReferenceRouter.NAME,
        ReferenceUtils.NAME,
        ReferenceService.NAME,
        ReferenceRedirect.NAME,
        PermissionsService.NAME,
        SecurityService.NAME
    ];

    constructor(protected $routeParams: ng.route.IRouteParamsService,
                protected $location: angular.ILocationService,
                protected referenceRouter: ReferenceRouter,
                public referenceUtils: ReferenceUtils,
                protected referenceApi: ReferenceService,
                protected referenceRedirect: ReferenceRedirect,
                protected permissionsApi: PermissionsService,
                private securityService: SecurityService) {
        let referenceId: number = parseInt(this.$routeParams[ReferenceRouter.REFERENCE_ID_PARAM], 10);
        let referenceName: string = this.$routeParams[ReferenceRouter.REFERENCE_NAME_PARAM];
        let hasReferenceId: boolean = !isNaN(referenceId);

        this.securityService.demandAuthentication()
            .demandSystemPermissionsIf(hasReferenceId, systemPermissions => systemPermissions.editReferenceData)
            .demandSystemPermissionsIf(!hasReferenceId, systemPermissions => systemPermissions.addReferenceData);
        MenuHelper.setCurrentMenuItem("administration_" + referenceName);

        this.permissionsApi.getSystemPermissions().then(p => {
            this.systemPermissions = p;
        });

        this.referenceDefaultValues = this.$location.search()[ReferenceRouter.REFERENCE_DEFAULT_VALUES_PARAM];
        this.referenceFilter = this.$location.search()[ReferenceRouter.REFERENCE_FILTER_PARAM];
        this.referenceAssociationFilters = this.$location.search()[ReferenceRouter.REFERENCE_ASSOCIATION_FILTERS_PARAM];
        this.referenceDefaultValuesMap = this.getReferenceDefaultValuesMap(this.referenceDefaultValues);
        this.referenceAssociationFiltersMap = this.getReferenceAssociationFiltersMap(this.referenceAssociationFilters);
        this.referenceValidationErrorsMap = new Map<string, string>();

        this.redirectStorageUrl = this.referenceRedirect.getAndRemoveStorageUrl();

        if (hasReferenceId) {
            this.referenceApi.getReferenceEntity(referenceName, referenceId).then(result => {
                this.referenceEntity = result.entity;
                this.referenceMetadata = result.metadata;
                this.referenceUtils.fixInputReferenceEntityAttributes(this.referenceEntity, this.referenceMetadata);
                PageHeader.set().header(this.referenceEntity[this.referenceMetadata.selectorAttribute.systemName])
                    .update();
            });
        } else {
            this.referenceApi.getEmptyReferenceEntity(referenceName).then(result => {
                this.referenceEntity = result.entity;
                this.referenceMetadata = result.metadata;
                this.referenceUtils.fixInputReferenceEntityAttributes(this.referenceEntity, this.referenceMetadata);
                this.setDefaultReferenceEntityAttributesValues();
                PageHeader.set().header('New referenced data entry')
                    .update();
            });
        }
    }

    public redirectToBack(): void {
        if (this.redirectStorageUrl) {
            this.$location.url(this.redirectStorageUrl);
        } else {
            let referenceName: string = this.referenceMetadata.key;
            this.$location.url(this.referenceRouter.indexUrl(referenceName))
                .search(ReferenceRouter.REFERENCE_DEFAULT_VALUES_PARAM, this.referenceUtils.getStringInBrowserFormat(this.referenceDefaultValues))
                .search(ReferenceRouter.REFERENCE_FILTER_PARAM, this.referenceUtils.getStringInBrowserFormat(this.referenceFilter))
                .search(ReferenceRouter.REFERENCE_ASSOCIATION_FILTERS_PARAM, this.referenceUtils.getStringInBrowserFormat(this.referenceAssociationFilters));
        }
    }

    public saveEntity(): void {
        this.disableSaveButton = true;
        let referenceName: string = this.referenceMetadata.key;
        let id: number = this.referenceEntity[ReferenceUtils.entityIdProperty];
        let call: ng.IPromise<any> = id != null
            ? this.referenceApi.updateReferenceEntity(referenceName, id, this.referenceEntity)
            : this.referenceApi.createReferenceEntity(referenceName, this.referenceEntity);

        call.then(result => {
            this.redirectToBack();
        }).finally(() => {
            this.disableSaveButton = false;
        });
    }

    public deactivateEntity(): void {
        let referenceName: string = this.referenceMetadata.key;
        let id: number = this.referenceEntity[ReferenceUtils.entityIdProperty];
        let version: number = this.referenceEntity[ReferenceUtils.entityVersionProperty];
        this.referenceApi.setActiveReferenceEntity(referenceName, id, version, false).then(result => {
            this.redirectToBack();
        });
    }

    public entityValid(): boolean {
        this.referenceValidationErrorsMap.clear();
        return new ReferenceMaxLengthValidator(this.referenceUtils, this.referenceEntity, this.referenceMetadata,
                this.referenceValidationErrorsMap)
                .valid()
            && (this.referenceMetadata.requiredAttributesGroup
                    ? new ReferenceRequiredAttributesGroupValidator(this.referenceUtils, this.referenceEntity, this.referenceMetadata,
                        this.referenceValidationErrorsMap)
                        .valid()
                    : new ReferenceIsRequiredValidator(this.referenceUtils, this.referenceEntity, this.referenceMetadata,
                        this.referenceValidationErrorsMap)
                        .valid());
    }

    private getReferenceDefaultValuesMap(referenceDefaultValues: string): Map<string, string> {
        let result: Map<string, string> = new Map<string, string>();
        if (referenceDefaultValues && referenceDefaultValues.length > 0) {
            let defaultValues: string[] = referenceDefaultValues.split(";");
            defaultValues.forEach(defaultValue => {
                let defaultValueParts: string[] = defaultValue.split("=");
                let property: string = defaultValueParts[0];
                let value: string = defaultValueParts[1];
                result.set(property, value);
            });
        }
        return result;
    }

    private getReferenceAssociationFiltersMap(referenceAssociationFilters: string): Map<string, string> {
        let result: Map<string, string> = new Map<string, string>();
        if (referenceAssociationFilters && referenceAssociationFilters.length > 0) {
            let associationFilters: string[] = referenceAssociationFilters.split("|");
            associationFilters.forEach(associationFilter => {
                let associationFilterParts: string[] = associationFilter.split("=>");
                let association: string = associationFilterParts[0];
                let filter: string = associationFilterParts[1];
                result.set(association, filter);
            });
        }
        return result;
    }

    private setDefaultReferenceEntityAttributesValues() {
        this.referenceMetadata.defaultAttributes.forEach(attr => {
            let attrType: string = attr.type;
            let attrSysName: string = attr.systemName;
            let defaultValue: string = this.referenceDefaultValuesMap.get(attrSysName);

            if (this.referenceUtils.isTextAttributeType(attrType)) {
                this.referenceEntity[attrSysName] = this.referenceUtils.convertStringDefaultValue(defaultValue);
            } else if (this.referenceUtils.isBooleanAttributeType(attrType)) {
                this.referenceEntity[attrSysName] = this.referenceUtils.convertBooleanDefaultValue(defaultValue);
            } else if (this.referenceUtils.isDateAttributeType(attrType)) {
                this.referenceEntity[attrSysName] = this.referenceUtils.convertDateDefaultValue(defaultValue);
            } else if (this.referenceUtils.isMoneyAttributeType(attrType)) {
                this.referenceEntity[attrSysName] = this.referenceUtils.convertMoneyDefaultValue(defaultValue);
            } else if (this.referenceUtils.isOneAssociationAttributeType(attrType)) {
                this.referenceEntity[attrSysName] = this.referenceUtils.convertOneAssociationDefaultValue(defaultValue);
            } else if (this.referenceUtils.isManyAssociationAttributeType(attrType)) {
                this.referenceEntity[attrSysName] = this.referenceUtils.convertManyAssociationDefaultValue(defaultValue);
            }
        });
    }
}
