export interface IUrlBuilder {
    param(param: string, replacement: any): IUrlBuilder;
    cacheable(): IUrlBuilder;
    template(): string;
    service(): string;
}

export class UrlService {
    public static NAME = "urls.UrlService";

    private serviceRoot = "/";
    private appVersion = "/";
    private defaultLocale = "ru_RU";
    private id = new Date();

    public constructor(serviceRoot: string, appVersion: string, defaultLocale: string) {
        this.serviceRoot = serviceRoot[serviceRoot.length - 1] != "/"
            ? serviceRoot + "/"
            : serviceRoot;
        this.appVersion = appVersion;
        this.defaultLocale = defaultLocale;
    }

    public service(baseUrl: string): string {
        return serviceUrl(baseUrl, this.serviceRoot, false /* cache */);
    }

    public template(baseUrl: string): string {
        return templateUrl(baseUrl, this.appVersion, this.defaultLocale);
    }

    public base(baseUrl: string): IUrlBuilder {
        return new UrlBuilder(baseUrl, this.serviceRoot, this.appVersion, this.defaultLocale);
    }

    public staticResourceUrl(baseUrl: string): string {
        return staticResourceUrl(baseUrl, this.appVersion);
    }
}

class UrlBuilder implements IUrlBuilder {
    private base: string;
    private serviceRoot: string;
    private appVersion: string;
    private cache: boolean = false;
    private defaultLocale: string;

    public constructor(base: string, serviceRoot: string, appVersion: string, defaultLocale: string) {
        this.base = base;
        this.appVersion = appVersion;
        this.serviceRoot = serviceRoot;
        this.defaultLocale = defaultLocale;
    }

    public param(param: string, replacement: any): IUrlBuilder {
        this.base = this.base.replace(param, replacement);
        return this;
    }

    public cacheable(): IUrlBuilder {
        this.cache = true;
        return this;
    }

    public template(): string {
        return templateUrl(this.base, this.appVersion, this.defaultLocale);
    }

    public service(): string {
        return serviceUrl(this.base, this.serviceRoot, this.cache);
    }
}

export var VIEWS_URL_PREFIX = "views/";

function appendQueryParam(url: string, param: string, value: string) {
    return url.indexOf("?") != -1
        ? url + "&" + param + "=" + value
        : url + "?" + param + "=" + value;
}

function templateUrl(base: string, version: string, defaultLocale: string) {
    var path = base.replace(".html", "-" + defaultLocale + ".html");
    return VIEWS_URL_PREFIX + appendQueryParam(path, "v", version);
}

function staticResourceUrl(base: string, version: string) {
    var path = base;
    return appendQueryParam(path, "v", version);
}

function serviceUrl(base: string, serviceRoot: string, cache: boolean) {
    if (!cache) {
        base = appendQueryParam(base, "no-cache", new Date().getTime().toString());
    }
    return serviceRoot + (base[0] === "/" ? base.substring(1) : base);
}