export interface IReferenceEntitiesParams {
    referenceFilter?: string;
    referenceSorting?: string;
    referenceSearchString?: string;
    referenceSplitParam?: boolean;
}

export class ReferenceEntitiesParams implements IReferenceEntitiesParams {
    public referenceFilter: string;
    public referenceSorting: string;
    public referenceSearchString: string;
    public referenceSplitParam: boolean;

    constructor(referenceFilter: string,
                referenceSorting: string,
                referenceSearchString: string,
                referenceSplitParam: boolean) {
        this.referenceFilter = referenceFilter;
        this.referenceSorting = referenceSorting;
        this.referenceSearchString = referenceSearchString;
        this.referenceSplitParam = referenceSplitParam;
    }
}