import {PersonalService} from "./PersonalService";

import {IUser} from "./../users/models/IUser";
import {UserService} from "./../users/UserService";
import {SecurityService} from "./../security/SecurityService";
import {MenuHelper} from "./../utils/MenuHelper";
import {UrlService} from "./../utils/UrlService";
import {ICourse} from "./../courses/models/ICourse";
import {CoursesRouter} from "./../urls/CoursesRouter";
import {IGridModel, GridModel} from "./../IGridModel";
import {HttpGridHelper} from "./../IHttpGridService";
import {ReferencedObjectType} from "./../ReferencedObjectType";
import {notifier} from "./../INotifier";

export class CoursesController {
    public static NAME = "personal.CoursesController";

    public userProfile: IUser;
    public coursesGridModel: IGridModel<ICourse>;
    public rowShow: Map<number, boolean> = new Map<number, boolean>();
    public viewType: number;

    public static $inject = [
        "$http",
        "$location",
        UrlService.NAME,
        SecurityService.NAME,
        UserService.NAME,
        PersonalService.NAME,
        CoursesRouter.NAME
    ];

    constructor($http: angular.IHttpService,
                private $location: angular.ILocationService,
                private url: UrlService,
                private securityService: SecurityService,
                private userService: UserService,
                private personalService: PersonalService,
                public coursesRouter: CoursesRouter) {
        this.securityService.demandAuthentication();
        MenuHelper.setCurrentMenuItem(MenuHelper.PERSONAL_ACCOUNT_COURSES_MENU_KEY);

        this.viewType = 0;

        this.loadUserProfile();

        this.coursesGridModel = new GridModel(HttpGridHelper.createLoader<ICourse>($http, this.url.service("/api/courses/my")));
        this.coursesGridModel.entries.forEach(e => {
            this.rowShow.set(e.id, false);
        });
    }

    public showDetails(index: number): void {
        this.rowShow.set(index, !this.rowShow.get(index));
    }

    public loadCourse(courseId: number): void {
        this.$location.url(this.coursesRouter.detailsUrl(courseId));
    }

    public addCourseToFavorites(course: ICourse) {
        this.addToFavorites(course.id, ReferencedObjectType.COURSE).then(result => {
            notifier.success("The course has been added to your favorites");
            course.favorite = true;
        }).catch(response => {
            if(response.data.error == "object.already.in.favorite") {
                notifier.error("This course is already in your favorites");
            }
        });
    }

    public removeCourseFromFavorites(course: ICourse) {
        this.removeFromFavorites(course.id, ReferencedObjectType.COURSE).then(result => {
            notifier.success("The course has been removed from your favorites");
            course.favorite = false;
        }).catch(response => {
            if(response.data.error == "object.not.in.favorite") {
                notifier.error("Failed to remove from your favorites");
            }
        });
    }

    private addToFavorites(id: number, type: ReferencedObjectType) {
        this.rowShow.set(id, !this.rowShow.get(id));
        return this.personalService.addToFavorites(id, type);
    }

    private removeFromFavorites(id: number, type: ReferencedObjectType) {
        this.rowShow.set(id, !this.rowShow.get(id));
        return this.personalService.removeFromFavorites(id, type);
    }

    private loadUserProfile(): void {
        this.userService.getCurrentUserProfile().then(u => {
            this.userProfile = u;
        })
    }
}