import {SecuredObjectType} from "./permissions/SecuredObjectType";
import {UrlService} from "./../utils/UrlService";
import {Event} from "./../authcore/Event";

export class PermissionsRepository {
    public static NAME = "security.PermissionsRepository";

    private static CACHE_EXPIRATION_TIME = 1000 * 60 * 5;
    private static CACHE_CLEAR_INTERVAL = 1000 * 5;

    private timer: angular.IPromise<any>;

    private permissionsCache: Map<string, any> = new Map<string, any>();
    private expirationCache: Map<string, number> = new Map<string, number>();

    public static $inject = [
        "$rootScope",
        "$interval",
        "$http",
        "$q",
        UrlService.NAME
    ];

    constructor($rootScope: angular.IRootScopeService,
                private $interval: angular.IIntervalService,
                private $http: angular.IHttpService,
                private $q: angular.IQService,
                private url: UrlService) {
        $rootScope.$on(Event.LOGGED_ON, () => {
            this.clearCache(true);
        });
        $rootScope.$on(Event.LOGGED_OFF, () => {
            this.clearCache(true);
        });
        this.timer = this.$interval(() => {
            this.clearCache(false)
        }, PermissionsRepository.CACHE_CLEAR_INTERVAL, 0, false);
        $rootScope.$on('$destroy', () => {
            this.$interval.cancel(this.timer);
        });
    }

    public getPermissionsForObject(objectType: SecuredObjectType, objectId: string): angular.IPromise<any> {
        var key = objectType.key + '_' + objectId;
        var value = this.permissionsCache.get(key);
        if (value) {
            var promise: ng.IDeferred<any> = this.$q.defer();
            promise.resolve(value);

            return promise.promise;
        }

        return this.$http.get(this.url.base("/api/permissions/:objectType/:id")
            .param(":objectType", objectType.name)
            .param(":id", objectId)
            .service())
            .then(result => {
                this.permissionsCache.set(key, result.data);
                this.expirationCache.set(key, new Date().getTime());
                return result.data;
            });
    }

    private clearCache(force: boolean) {
        if (force) {
            this.permissionsCache.clear();
            this.expirationCache.clear();
        } else {
            var expired = [];
            var expiredFromTime = new Date().getTime() - PermissionsRepository.CACHE_EXPIRATION_TIME;
            this.expirationCache.forEach((value, key, map) => {
                if (value < expiredFromTime) {
                    expired.push(key);
                }
            });
            for (var i = 0; i < expired.length; i++) {
                var key = expired[i];
                this.permissionsCache.delete(key);
                this.expirationCache.delete(key);
            }
        }
    }
}