import {MenuHelper} from "./../utils/MenuHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {MaterialsRouter} from "./../urls/MaterialsRouter";
import {PermissionsService} from "./../security/PermissionsService";
import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";
import {IPlaylist} from "./models/IPlaylist";
import {IGridModel, GridModel} from "./../IGridModel";
import {HttpGridHelper} from "./../IHttpGridService";
import {UrlService} from "./../utils/UrlService";
import {ObjectTypeUtils} from "./../utils/ObjectTypeUtils";
import {PersonalService} from "./../personal/PersonalService";
import {notifier} from "./../INotifier";
import {AuthManager} from "./../authcore/AuthManager";
import {ReferencedObjectType} from "./../ReferencedObjectType";

export class PlaylistsController {
    public static NAME = "materials.PlaylistsController";

    public viewType: number;
    public rowShow: Map<number, boolean> = new Map<number, boolean>();
    public isAuthenticated: boolean;
    public systemPermissions: ISystemPermissions;
    public playlistsGridModel: IGridModel<IPlaylist>;

    public static $inject = [
        "$http",
        "$location",
        MaterialsRouter.NAME,
        PermissionsService.NAME,
        UrlService.NAME,
        PersonalService.NAME,
        AuthManager.NAME
    ];

    constructor($http: angular.IHttpService,
                private $location: ng.ILocationService,
                public materialsRouter: MaterialsRouter,
                private permissionsApi: PermissionsService,
                private url: UrlService,
                private personalService: PersonalService,
                private authManager: AuthManager) {
        MenuHelper.setCurrentMenuItem(MenuHelper.EDUCATION_PLAYLISTS_MENU_KEY);
        PageHeader.set().header("Playlists").update();

        this.isAuthenticated = this.authManager.isAuthenticated();
        if (this.isAuthenticated) {
            this.permissionsApi.getSystemPermissions().then(systemPermissions => {
                this.systemPermissions = systemPermissions;
            });
        }

        this.playlistsGridModel = new GridModel(HttpGridHelper.createLoader<IPlaylist>($http, this.url.service("/api/playlists")));
        this.playlistsGridModel.pageSize = 8;
        this.viewType = 1;
    }

    public getLanguages(languages: Array<string>): string {
        var languagesStr: string = "";
        languages.forEach(l => {
            languagesStr +=  l + ", ";
        });
        return languagesStr.substring(0, languagesStr.length-2);
    }

    public searchPlaylists(): void {
        this.playlistsGridModel.loadCurrentPage();
    }

    public loadPlaylist(playlistId: number): void {
        this.$location.url(this.materialsRouter.playlistDetailsUrl(playlistId));
    }

    public showDetails(index: number) {
        this.rowShow.set(index, !this.rowShow.get(index));
    }

    public addToFavorites(playlist: IPlaylist) {
        this.rowShow.set(playlist.id, !this.rowShow.get(playlist.id));
        this.personalService.addToFavorites(playlist.id, ReferencedObjectType.PLAYLIST).then(result => {
            notifier.success("The playlist has been added to your favorites");
            playlist.favorite = true;
        }).catch(response => {
            if(response.data.error == "object.already.in.favorite") {
                notifier.error("This playlist is already in your favorites");
            }
        });
    }

    public removeFromFavorites(playlist: IPlaylist) {
        this.rowShow.set(playlist.id, !this.rowShow.get(playlist.id));
        this.personalService.removeFromFavorites(playlist.id, ReferencedObjectType.PLAYLIST).then(result => {
            notifier.success("The playlist has been removed from your favorites");
            playlist.favorite = false;
        }).catch(response => {
            if(response.data.error == "object.not.in.favorite") {
                notifier.error("Failed to remove from your favorites");
            }
        });
    }
}
