import {MenuHelper} from "./../utils/MenuHelper";
import {Moment} from "./../utils/Moment";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {IGridModel, GridModel, IGridLoadingListener} from "./../IGridModel";
import {ISystemLog, SystemLogLevelEnum, SystemLogSourceEnum, SYSTEM_LOG_LEVEL_ENUM_VALUES, SYSTEM_LOG_SOURCE_ENUM_VALUES} from "./ISystemLog";
import {UsersRouter} from "./../urls/UsersRouter";
import {UrlService} from "./../utils/UrlService";
import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";
import {PermissionsService} from "./../security/PermissionsService";
import {HttpGridHelper} from "./../IHttpGridService";
import {ISelectable} from "./../ISelectable";
import {PageMode} from "../PageMode";

export class SystemLogController implements IGridLoadingListener {

    public static NAME = "logs.SystemLogController";

    public systemLogsGridModel: GridModel<ISystemLog>;
    public forDateFilter: string;
    public systemLogLevels: Array<ISelectable> = [];
    public systemLogSources: Array<ISelectable> = [];
    public levelsFilter: Array<number> = [];
    public sourcesFilter: Array<number> = [];
    private systemPermissions: ISystemPermissions;


    public static $inject = [
        "$http",
        PermissionsService.NAME,
        UrlService.NAME,
        UsersRouter.NAME
    ];

    constructor($http: angular.IHttpService,
                private permissionsApi: PermissionsService,
                private url: UrlService,
                public usersRouter: UsersRouter) {
        MenuHelper.setCurrentMenuItem(MenuHelper.ADMINISTRATION_SYSTEM_LOGS_KEY);
        PageHeader.set().header("System logs").update();
        this.url = url;
        this.permissionsApi = permissionsApi;
        this.usersRouter = usersRouter;

        this.systemLogsGridModel = new GridModel(HttpGridHelper.createLoader<ISystemLog>($http, this.url.service("/api/logs/system")));
        this.systemLogsGridModel.addLoadingListener(this);

        this.permissionsApi.getSystemPermissions().then(p => {
            this.systemPermissions = p;
        });
        this.fillSystemActionTypes();
        this.fillSystemLogStatuses();
    }

    public startLoading() {
        var dateValue = null;
        if (this.forDateFilter) {
            dateValue = Moment.textToJavaDate(this.forDateFilter);
        }
        this.systemLogsGridModel.filter["forDate"] = dateValue;

        var sources: Array<string> = [];
        for (var i = 0; i < this.sourcesFilter.length; i++) {
            sources.push(SystemLogSourceEnum[this.sourcesFilter[i]]);
        }
        this.systemLogsGridModel.filter["sources"] = sources;

        var levels: Array<string> = [];
        for (var i = 0; i < this.levelsFilter.length; i++) {
            levels.push(SystemLogLevelEnum[this.levelsFilter[i]]);
        }
        this.systemLogsGridModel.filter["levels"] = levels;
    }

    public endLoading() {
        //do nothing
    }

    private fillSystemActionTypes() {
        for (var i = 0; i < SYSTEM_LOG_LEVEL_ENUM_VALUES.length; i++) {
            this.systemLogLevels.push({
                id: i,
                name: SystemLogLevelEnum[SYSTEM_LOG_LEVEL_ENUM_VALUES[i]]
            });
        }
    }

    private fillSystemLogStatuses() {
        for (var i = 0; i < SYSTEM_LOG_SOURCE_ENUM_VALUES.length; i++) {
            this.systemLogSources.push({
                id: i,
                name: SystemLogSourceEnum[SYSTEM_LOG_SOURCE_ENUM_VALUES[i]]
            });
        }
    }
}
