import {SecurityLogController} from "./SecurityLogController";
import {SystemLogController} from "./SystemLogController";

import {UrlsModule} from "./../urls/UrlsModule";

export var LogsModule: angular.IModule = angular
    .module("logs", [
        UrlsModule.name
    ])
    .controller(SecurityLogController.NAME, SecurityLogController)
    .controller(SystemLogController.NAME, SystemLogController);