import {MaterialType} from "./../materials/models/MaterialType";
import {MaterialTypeKeyEnum} from "./../materials/models/MaterialType";
import {ReferencedObjectType} from "./../ReferencedObjectType";

export class ObjectTypeUtils {
    public static getMaterialTypeTitle(typeId: number): string {
        switch (typeId) {
            case MaterialType.AUDIO: return 'Audio';
            case MaterialType.VIDEO: return 'Video';
            case MaterialType.LINK: return 'Link';
            case MaterialType.PHOTO: return 'Image';
            case MaterialType.DOCUMENTS: return 'Other documents';
        }
    }

    public static getMaterialGlyphiconClass(typeId: number): string {
        switch (typeId) {
            case MaterialType.AUDIO: return "glyphicon glyphicon-headphones";
            case MaterialType.VIDEO: return "glyphicon glyphicon-play";
            case MaterialType.LINK: return "glyphicon glyphicon-link";
            case MaterialType.PHOTO: return "glyphicon glyphicon-picture";
            case MaterialType.DOCUMENTS: return "glyphicon glyphicon-file";
        }
    }

    public static getMaterialGlyphiconClassFromKey(typeKey: string): string {
        switch (typeKey) {
            case MaterialTypeKeyEnum[MaterialTypeKeyEnum.AUDIO]: return "glyphicon glyphicon-headphones";
            case MaterialTypeKeyEnum[MaterialTypeKeyEnum.VIDEO]: return "glyphicon glyphicon-play";
            case MaterialTypeKeyEnum[MaterialTypeKeyEnum.LINK]: return "glyphicon glyphicon-link";
            case MaterialTypeKeyEnum[MaterialTypeKeyEnum.PHOTO]: return "glyphicon glyphicon-picture";
            case MaterialTypeKeyEnum[MaterialTypeKeyEnum.DOCUMENTS]: return "glyphicon glyphicon-file";
        }
    }

    public static getNewsGlyphiconClass(): string {
        return "glyphicon glyphicon-info-sign";
    }

    public static getReferenceObjectTypeTitle(objectType: string): string {
        switch (objectType) {
            case ReferencedObjectType[ReferencedObjectType.MATERIAL]: return 'Material';
            case ReferencedObjectType[ReferencedObjectType.COURSE]: return 'Course';
            case ReferencedObjectType[ReferencedObjectType.PLAYLIST]: return 'Playlist';
            case ReferencedObjectType[ReferencedObjectType.NEWS]: return 'News';
        }
    }

    public static getReferenceObjectTypeFromString(objectType: string): ReferencedObjectType {
        switch (objectType) {
            case ReferencedObjectType[ReferencedObjectType.MATERIAL]: return ReferencedObjectType.MATERIAL;
            case ReferencedObjectType[ReferencedObjectType.COURSE]: return ReferencedObjectType.COURSE;
            case ReferencedObjectType[ReferencedObjectType.PLAYLIST]: return ReferencedObjectType.PLAYLIST;
            case ReferencedObjectType[ReferencedObjectType.NEWS]: return ReferencedObjectType.NEWS;
        }
    }

    public static getContentTypeTitle(contentTypeKey: string): string {
        switch (contentTypeKey) {
            case MaterialTypeKeyEnum[MaterialTypeKeyEnum.AUDIO]: return 'Audio';
            case MaterialTypeKeyEnum[MaterialTypeKeyEnum.VIDEO]: return 'Video';
            case MaterialTypeKeyEnum[MaterialTypeKeyEnum.LINK]: return 'Link';
            case MaterialTypeKeyEnum[MaterialTypeKeyEnum.PHOTO]: return 'Image';
            case MaterialTypeKeyEnum[MaterialTypeKeyEnum.DOCUMENTS]: return 'Other documents';
            case ReferencedObjectType[ReferencedObjectType.PLAYLIST]: return 'Playlist';
            case ReferencedObjectType[ReferencedObjectType.COURSE]: return 'Course';
        }
    }

    public static isMaterialObject(objectType: string): boolean {
        return objectType == ReferencedObjectType[ReferencedObjectType.MATERIAL];
    }

    public static isPlaylistObject(objectType: string): boolean {
        return objectType == ReferencedObjectType[ReferencedObjectType.PLAYLIST];
    }

    public static isCourseObject(objectType: string): boolean {
        return objectType == ReferencedObjectType[ReferencedObjectType.COURSE];
    }

    public static isNewObject(objectType: string): boolean {
        return objectType == ReferencedObjectType[ReferencedObjectType.NEWS];
    }

    public static getReferencedObjectTypeAsString(value: any): string {
        if (value in ReferencedObjectType) {
            return (typeof value === 'string') ? value :
                (typeof value === 'number') ? ReferencedObjectType[value] : undefined;
        } else {
            return undefined;
        }
    }

    public static getReferencedObjectTypeAsNumber(value: any): ReferencedObjectType {
        if (value in ReferencedObjectType) {
            return (typeof value === 'number') ? value :
                (typeof value === 'string') ? ReferencedObjectType[value] : undefined;
        } else {
            return undefined;
        }
    }

    public static isYoutubeUrl(url: string) {
        return url && (
                url.indexOf("https://www.youtube.com") == 0 ||
                url.indexOf("http://www.youtube.com") == 0 ||
                url.indexOf("https://youtu.be") == 0 ||
                url.indexOf("http://youtu.be") == 0
        );
    }
}