import {IQuiz} from "./models/IQuiz";
import {IQuizQuestion} from "./models/IQuizQuestion";
import {IQuizAnswer} from "./models/IQuizAnswer";
import {QuizzesRouter} from "./../urls/QuizzesRouter";
import {QuizService} from "./QuizService";

import {IPermissionWithUsersAndGroups} from "./../security/permissions/IPermissionWithUsersAndGroups";
import {SecuredObjectType} from "./../security/permissions/SecuredObjectType";
import {MenuHelper} from "./../utils/MenuHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {UrlService} from "./../utils/UrlService";
import {SecurityService} from "./../security/SecurityService";
import {PermissionsService} from "./../security/PermissionsService";
import {AuthService} from "./../auth/AuthService";
import {notifier} from "./../INotifier";
import {ISelectable} from "./../ISelectable";
import {UserService} from "./../users/UserService";

export interface IFile extends File {
    title?: string;
}

export class QuizEditController {
    public static NAME = "quizzes.QuizEditController";

    public static PERCENTAGE_TYPE = "PERCENTAGE";

    public quiz: IQuiz;
    public quizQuestions: Array<IQuizQuestion> = [];
    public currentQuestion: IQuizQuestion = {};
    public currentAnswers: Array<IQuizAnswer> = [];
    public isEditQuestion: boolean = false;
    public editQuestionIndex: number;
    public percentageNumber: number;
    public pointsNumber: number;

    public quizId: number;
    public preview: IFile;
    public disabledSaveButton: boolean = false;
    public owners: Array<ISelectable> = [];
    public permissionsWithUsersAndGroups: Array<IPermissionWithUsersAndGroups> = [];

    public static $inject = [
        "$http",
        "$scope",
        "$location",
        "$routeParams",
        "Upload",
        UrlService.NAME,
        SecurityService.NAME,
        PermissionsService.NAME,
        AuthService.NAME,
        QuizService.NAME,
        UserService.NAME,
        QuizzesRouter.NAME
    ];

    constructor(private $http: angular.IHttpService,
                private $scope,
                private $location: angular.ILocationService,
                private $routeParams: angular.route.IRouteParamsService,
                private $uploader: ng.angularFileUpload.IUploadService,
                private url: UrlService,
                private securityService: SecurityService,
                private permissionsService: PermissionsService,
                private authService: AuthService,
                private quizService: QuizService,
                private userService: UserService,
                private quizzesRouter: QuizzesRouter) {
        this.quizId = $routeParams[QuizzesRouter.QUIZ_ID_PARAM];
        MenuHelper.setCurrentMenuItem(MenuHelper.EDUCATION_QUIZZES_MENU_KEY);

        let quizIdStr: string = String(this.quizId);
        let hasQuizId: boolean = this.quizId != null;

        this.securityService.demandAuthentication()
            .demandSystemPermissionsIf(!hasQuizId, systemPermissions => systemPermissions.addQuizzes)
            .demandQuizPermissionsIf(hasQuizId, quizIdStr, quizPermissions => quizPermissions.edit);

        this.quiz = {
            questions: [],
            correctAnswersType: 'PERCENTAGE'
        };
        this.currentQuestion = {
            text: "",
            isMultiple: false,
            answers: []
        };

        this.userService.getUsersWithoutExcludedUsersForSelector().then(result =>{
            this.owners = angular.copy(result);
        });
        this.authService.getCurrentUser().then(user => {
            this.quiz.ownerId = user.id;
            this.quiz.ownerName = user.fullName;
        });

        if(hasQuizId) {
            PageHeader.set().header("Edit a quiz").update();
            this.loadQuiz();
            this.disabledSaveButton = false;
            this.loadPermissionsWithUsersAndGroups(SecuredObjectType.QUIZ, quizIdStr);
        } else {
            PageHeader.set().header("New quiz").update();
            this.loadPermissionsWithUsersAndGroups(SecuredObjectType.QUIZ);
        }

        this.$scope.$on('$locationChangeStart', (event) => {
            if(this.$scope.quiz_form.$dirty && this.disabledSaveButton == false) {
                if(!confirm("Unsaved data will be lost if you leave this page")) {
                    event.preventDefault();
                }
            }
        });
    }

    private loadQuiz() {
        var promise: angular.IPromise<IQuiz> = this.quizService.get(this.quizId);
        promise.then(result => {
            angular.copy(result, this.quiz);
            if (result.duration != null) {
                this.quiz.duration = result.duration / 60;
            }
            angular.copy(result.questions, this.quizQuestions);
            if (result.correctAnswersType == QuizEditController.PERCENTAGE_TYPE) {
                this.percentageNumber = result.correctAnswersNumber;
            } else {
                this.pointsNumber = result.correctAnswersNumber;
            }
        });
    }

    private loadPermissionsWithUsersAndGroups(securedObjectType: SecuredObjectType, objectId?: string): void {
        this.permissionsService.getAllObjectPermissionsWithUsersAndGroups(securedObjectType, objectId).then(result => {
            this.permissionsWithUsersAndGroups = result;
        });
    }

    public save() {
        if (this.quiz.hasProgress) {
            notifier.warning("Your changes were saved, but some users have already completed this quiz. Results for completed quizzes were not modified and can be inconsistent with new version of quiz.");
        }
        this.disabledSaveButton = true;
        this.writeIndexes(this.quizQuestions);
        angular.copy(this.quizQuestions, this.quiz.questions);
        this.quiz.correctAnswersNumber = this.quiz.correctAnswersType == QuizEditController.PERCENTAGE_TYPE ? this.percentageNumber : this.pointsNumber;
        var promise = this.quizId
            ? this.quizService.update(this.quiz, this.permissionsWithUsersAndGroups)
            : this.quizService.create(this.quiz, this.permissionsWithUsersAndGroups);
        promise.then(quiz => {
            if(this.preview != null) {
                this.upload(this.preview, quiz.id, "preview").then(r => {
                    this.processSuccessCallback(quiz.id);
                }).catch(response => {
                    if (response.data.error == "error.storage.not.available") {
                        notifier.error("No available storage. Thumbnail has not been saved");
                    }
                    this.processSuccessCallback(quiz.id);
                });
            } else {
                this.processSuccessCallback(quiz.id);
            }
        }).catch(err => {
            notifier.error(err);
        });
    }

    private processSuccessCallback(quizId: number): void {
        notifier.success("The quiz is successfully saved");
        this.$location.url(this.quizzesRouter.detailsUrl(quizId));
    }

    private upload(file: File, quizId: number, fileType: string): angular.angularFileUpload.IUploadPromise<File> {
        var urlBuilder = this.url.base("/api/quizzes/upload?quizId=:quizId&fileType=:fileType")
            .param(":quizId", quizId)
            .param(":fileType", fileType);
        return this.$uploader.upload({
                method: "POST",
                url: angular.copy(urlBuilder)
                    .service(),
                file: file}
        );
    }

    public redirectOrClearFields(): void {
        if(this.quizId) {
            this.$location.url(this.quizzesRouter.detailsUrl(this.quizId));
        } else {
            this.cleanFields();
        }
    }

    private cleanFields() : void {
        this.quiz = {};
        this.loadPermissionsWithUsersAndGroups(SecuredObjectType.QUIZ);
    }

    public clearCurrentQuestionFields(): void {
        this.currentQuestion = {
            text: "",
            isMultiple: false,
            answers: []
        };
        this.currentAnswers = [];
        this.isEditQuestion = false;
        this.editQuestionIndex = null;
    }

    public currentQuestionValid(): boolean {
        return this.currentQuestion.text != "" && this.currentAnswers.every(a => a.text != "")
            && this.currentAnswers.filter((a: IQuizAnswer) => a.isCorrect).length > 0;
    }

    public checkCurrentCorrectAnswer(answer: IQuizAnswer): void {
        this.currentAnswers.forEach((a: IQuizAnswer) => {
            a.isCorrect = false;
        });
        answer.isCorrect = true;
    }

    public toggleQuestionMultiple(): void {
        if (!this.currentQuestion.isMultiple && this.currentAnswers.filter((a: IQuizAnswer) => a.isCorrect).length > 1) {
            this.currentAnswers.forEach((a: IQuizAnswer) => {
                a.isCorrect = false;
            });
        }
    }

    public editQuestion(index: number, question: IQuizQuestion) {
        angular.copy(question, this.currentQuestion);
        angular.copy(question.answers, this.currentAnswers);
        this.currentQuestion.answers = [];
        this.isEditQuestion = true;
        this.editQuestionIndex = index;
    }

    public addAnswerToQuestion() {
        var answer = {
            text: "",
            isCorrect: false
        };
        this.currentAnswers.push(answer);
    }

    public removeAnswerFromQuestion(index: number) {
        this.currentAnswers.splice(index, 1);
    }

    public addQuestionToQuiz() {
        angular.copy(this.currentAnswers, this.currentQuestion.answers);
        if (this.currentQuestion.points == null) {
            this.currentQuestion.points = 1;
        }
        this.isEditQuestion
            ? this.quizQuestions.splice(this.editQuestionIndex, 1, this.currentQuestion)
            : this.quizQuestions.push(this.currentQuestion);
        this.clearCurrentQuestionFields();
    }

    public removeQuestionFromQuiz(index: number) {
        this.quizQuestions.splice(index, 1);
    }

    private writeIndexes(questions: Array<IQuizQuestion>): void {
        questions.forEach((question: IQuizQuestion) => {
            question.indexNumber = questions.indexOf(question);
            question.answers.forEach((answer: IQuizAnswer) => answer.indexNumber = question.answers.indexOf(answer));
        });
    }

    public getQuizPointsSum(): number {
        var sum = 0;
            this.quizQuestions.forEach((q: IQuizQuestion) => {
                sum += q.points;
            });
        return sum;
    }

    public moveUp(index: number, objectArray: any): void {
        var placeholder = {};
        var objectToMove = objectArray.splice(index, 1, placeholder)[0];
        objectArray.splice(index - 1, 0, objectToMove);
        objectArray.splice(objectArray.indexOf(placeholder), 1);
    }

    public moveDown(index: number, objectArray: any): void {
        var placeholder = {};
        var objectToMove = objectArray.splice(index + 1, 1, placeholder)[0];
        objectArray.splice(index, 0, objectToMove);
        objectArray.splice(objectArray.indexOf(placeholder), 1);
    }

    public isMoveUp(index: number): boolean {
        return index > 0;
    }

    public isMoveDown(index: number, objectArray: any): boolean {
        return index < objectArray.length - 1;
    }
}