import {UrlService} from "./../utils/UrlService";
import {ISelectableReferenceEntity} from "./../reference/models/ISelectableReferenceEntity";
import {ReferenceService} from "./../reference/ReferenceService";
import {ReferenceUtils} from "./../reference/ReferenceUtils";
import {ReferenceEntitiesParams} from "./../reference/models/IReferenceEntitiesParams";

export interface IReferenceCheckboxSelectorDirectiveScope extends ng.IScope {
    referenceName?: string;
    referenceFilter?: string;
    referenceSorting?: string;
    model?: any;
    modelProperty?: string;
    disabled?: boolean;

    vm?: ReferenceCheckboxSelectorController;
}

export class ReferenceCheckboxSelectorController {

    public referenceEntities: Array<ISelectableReferenceEntity>;
    public selectAll: boolean;

    private modelIds: Array<number>;

    public static $inject = [
        "$scope",
        ReferenceService.NAME,
        ReferenceUtils.NAME
    ];

    constructor(private $scope: IReferenceCheckboxSelectorDirectiveScope,
                private referenceApi: ReferenceService,
                private referenceUtils: ReferenceUtils) {
        $scope.vm = this;
        this.referenceApi.getActiveReferenceEntitiesForSelector(this.$scope.referenceName, new ReferenceEntitiesParams(
            this.$scope.referenceFilter, this.$scope.referenceSorting, undefined, undefined), null)
            .then(result => {
                this.referenceEntities = result;

                this.$scope.$watch(() => {
                    return $scope.model[$scope.modelProperty]
                }, (newValue) => {
                    this.modelIds = $scope.model[$scope.modelProperty];
                    this.pushDataModel();
                });
            });
    }

    protected pushDataModel(): void {
        for (var i = 0; i < this.referenceEntities.length; ++i) {
            var entity: ISelectableReferenceEntity = this.referenceEntities[i];
            entity["selected"] = false;
            for (var j = 0; j < this.modelIds.length; ++j) {
                var id = this.modelIds[j];
                if (entity.id == id) {
                    entity["selected"] = true;
                    break;
                }
            }
        }
        this.initSelectAll();
    }

    public itemSelection(entity: ISelectableReferenceEntity): void {
        for (var i = 0; i < this.modelIds.length; ++i) {
            var id = this.modelIds[i];
            if (id == entity.id) {
                this.modelIds.splice(i, 1);
                return;
            }
        }
        this.modelIds.push(entity.id);
    }

    public initSelectAll(): boolean {
        var countSelected = 0;
        for (var i = 0; i < this.referenceEntities.length; i++) {
            if (this.referenceEntities[i]["selected"]) {
                countSelected++;
            }
        }
        return this.selectAll = this.referenceEntities && countSelected == this.referenceEntities.length;
    }

    public clickSelectAll(): void {
        if (!this.initSelectAll()) {
            for (var i = 0; i < this.referenceEntities.length; ++i) {
                var entity: ISelectableReferenceEntity = this.referenceEntities[i];
                if (this.modelIds.indexOf(entity.id) < 0) {
                    this.itemSelection(entity);
                }
            }
        } else {
            for (var i = 0; i < this.referenceEntities.length; ++i) {
                var entity: ISelectableReferenceEntity = this.referenceEntities[i];
                if (this.modelIds.indexOf(entity.id) >= 0) {
                    this.itemSelection(entity);
                }
            }
        }
        this.pushDataModel();
        this.initSelectAll();
    }
}

export function ReferenceCheckboxSelectorDirectiveFactory(url: UrlService): ng.IDirective {
    var directive: ng.IDirective = {};
    directive.restrict = "A";
    directive.controller = ReferenceCheckboxSelectorController;
    directive.scope = {
        referenceName: "=vhsReferenceName",
        referenceFilter: "=?vhsReferenceFilter",
        referenceSorting: "=?vhsReferenceSorting",
        model: "=vhsModel",
        modelProperty: "=vhsModelProperty",
        disabled: "=?vhsDisabled",
    };
    directive.templateUrl = url.template("components/reference-checkbox-selector.html");
    directive.transclude = true;
    return directive;
}

ReferenceCheckboxSelectorDirectiveFactory.$inject = [
    UrlService.NAME
];
