import {UrlService} from "../../utils/UrlService"

import {IGridModel, GridModel, IGridLoadingListener} from "../../IGridModel";
import {HttpGridHelper} from "../../IHttpGridService";

import {IMaterial} from "../../materials/models/IMaterial"
import {Event} from "../models/Event"
import {ITextAngularEventData} from "../models/ITextAngularEventData"
import {StringBuilder} from "../../utils/StringBuilder"
import {RandomUtils} from "../../utils/RandomUtils";
import {DomUtils} from "../../utils/DomUtils";

export interface IVhsSelectMaterialDirectiveScope extends angular.IScope {
    vm?: VhsSelectMaterialController;
}

export class VhsSelectMaterialController {

    public static EMBEDDED_MATERIAL_TA_HTML: string = '<p class="ta-vhs-embedded-material" id=":materialId">:materialName</p>';

    public materialsGridModel: IGridModel<IMaterial>;
    public materialId: number;
    private lastEventData: ITextAngularEventData;
    private element: angular.IAugmentedJQuery;
    private richEditorParent: angular.IAugmentedJQuery;
    private loadPopupContent: boolean = undefined;

    public static $inject = [
        "$scope",
        "$timeout",
        "$http",
        UrlService.NAME
    ];

    constructor(private $scope: IVhsSelectMaterialDirectiveScope,
                private $timeout: angular.ITimeoutService,
                private $http: angular.IHttpService,
                private url: UrlService) {
        $scope.vm = this;
        $scope.$on(Event.TA_SELECT_EMBEDDED_MATERIAL, (event: ng.IAngularEvent, data: ITextAngularEventData) => {
            if (this.isEventProcessable(data)) {
                this.loadPopupContent = true;
                this.lastEventData = data;
                this.element.find("div.modal").modal({
                    backdrop: 'static',
                    keyboard: false,
                    show: true
                });
            }
        });

        this.materialsGridModel = new GridModel(HttpGridHelper.createLoader<IMaterial>($http, this.url.service("/api/materials")));
    }

    public init($scope: IVhsSelectMaterialDirectiveScope,
                element: angular.IAugmentedJQuery) {
        this.element = element;
        this.richEditorParent = DomUtils.findParentWithClass(this.element, "vhs-rich-editor-content");
    }

    public onMaterialSelect() {
        if (this.materialId) {
            let materialName = "&nbsp;";
            for (let i = 0; i < this.materialsGridModel.entries.length; i++) {
                let material = this.materialsGridModel.entries[i];
                if (material.id == this.materialId) {
                    materialName = material.topic;
                    break;
                }
            }
            this.lastEventData.callbackFunction(
                StringBuilder.interpolate(VhsSelectMaterialController.EMBEDDED_MATERIAL_TA_HTML)
                    .with(":materialId", RandomUtils.generateRandomId("" + this.materialId + "__"))
                    .with(":materialName", materialName)
                    .build()
            );
        }
    }

    private isEventProcessable(data: ITextAngularEventData) {
        if (data.eventSourceButton != null) {
            var parent = DomUtils.findParentWithClass(data.eventSourceButton, "vhs-rich-editor-content");
            if (parent != null && parent.is(this.richEditorParent)) {
                return true;
            }
        }
        return false;
    }
}

export function VhsSelectMaterialDirectiveFactory(url: UrlService): angular.IDirective {
    function link($scope: IVhsSelectMaterialDirectiveScope,
                  element: angular.IAugmentedJQuery,
                  attributes: angular.IAttributes,
                  controller: VhsSelectMaterialController) {
        controller.init($scope, element);
    }

    let directive: angular.IDirective = {
        restrict: "A",
        link: link,
        controller: VhsSelectMaterialController,
        scope: {},
        templateUrl: url.template("components/vhs-select-material.html"),
    };
    return directive;
}

VhsSelectMaterialDirectiveFactory.$inject = [
    UrlService.NAME
];
