import {ReferencedObjectType} from "./../ReferencedObjectType";
import {MaterialsRouter} from "./../urls/MaterialsRouter";
import {CoursesRouter} from "./../urls/CoursesRouter";
import {CommonRouter} from "./../urls/CommonRouter";
import {BaseRouter} from "./../urls/BaseRouter";

export class ContentRouter extends BaseRouter {
    public static NAME = "urls.ContentRouter";

    public static $inject = [
        "$location",
        MaterialsRouter.NAME,
        CoursesRouter.NAME,
        CommonRouter.NAME
    ];

    constructor($location: ng.ILocationService,
                public materialsRouter: MaterialsRouter,
                public courseRouter: CoursesRouter,
                public commonRouter: CommonRouter) {
        super($location);
    }

    public detailsUrl(objectId: number, objectType: any) {
        let myObjectType: ReferencedObjectType =
            (typeof objectType === 'string') ? ReferencedObjectType[objectType] :
                (typeof objectType === 'number') ? objectType : undefined;
        switch (myObjectType) {
            case ReferencedObjectType.MATERIAL:
                return this.materialsRouter.watchUrl(objectId);
            case ReferencedObjectType.COURSE:
                return this.courseRouter.detailsUrl(objectId);
            case ReferencedObjectType.PLAYLIST:
                return this.materialsRouter.playlistDetailsUrl(objectId);
            default:
                return this.commonRouter.notFoundUrl();
        }
    }
}