import {AuthService} from "./AuthService";
import {AuthRouter} from "./../urls/AuthRouter";
import {notifier} from "../INotifier";
import {MainRouter} from "./../urls/MainRouter";

export class LogOutController {
    public static NAME = "auth.LogOutController";

    public static $inject = [
        AuthService.NAME,
        MainRouter.NAME,
        AuthRouter.NAME,
        "$location"
    ];

    constructor(private authApi: AuthService,
                private mainRouter: MainRouter,
                private authRouter: AuthRouter,
                private $location: angular.ILocationService) {
        this.authApi.logOff()
            .then(() => {
                notifier.success("Logged Out");
                //this.$location.url(this.mainRouter.mainUrl());
                this.$location.url(this.authRouter.logonUrl());
            });
    }
}
