import {BaseRouter} from "./BaseRouter";

export class StatisticsRouter extends BaseRouter {
    public static NAME = "urls.StatisticsRouter";
    public static materialViewReportRoute = "/statistics/analytics/material/view/report";
    public static materialDownloadReportRoute = "/statistics/analytics/material/download/report";
    public static materialUploadReportRoute = "/statistics/analytics/material/upload/report";

    public static $inject = [
        "$location"
    ];

    constructor($location: ng.ILocationService) {
        super($location);
    }

    public materialViewReportUrl(): string {
        return this.url(StatisticsRouter.materialViewReportRoute);
    }

    public materialDownloadReportUrl(): string {
        return this.url(StatisticsRouter.materialDownloadReportRoute);
    }

    public materialUploadReportUrl(): string {
        return this.url(StatisticsRouter.materialUploadReportRoute);
    }
}
