import {RedirectReason} from "./RedirectReason";

import {StorageWrapper, Storages} from "./../infrastructure/Storage";
import {AuthRouter} from "./../urls/AuthRouter";
import {MainRouter} from "./../urls/MainRouter";
import {UrlService} from "./../utils/UrlService";
import {LocationUtils} from "./../utils/LocationUtils";

export class AuthRedirect {
    public static NAME = "authcore.AuthRedirect";

    private static KEY = "auth-redirect";

    public static $inject = [
        "$location",
        "$window",
        Storages.SESSION_NAME,
        AuthRouter.NAME,
        MainRouter.NAME,
        UrlService.NAME
    ];

    constructor(private $location: angular.ILocationService,
                private $window: angular.IWindowService,
                private sessionStorage: StorageWrapper,
                private authRouter: AuthRouter,
                private mainRouter: MainRouter,
                private url: UrlService) {
        // this.clearRedirection();
    }

    public redirectToLogOn(reason?: RedirectReason) {
        console.log("Redirecting to Log On...");
        this.storeUrlBeforeAuthRedirect();
        this.forwardToLogOn(false /*native*/, reason);
    }

    private storeUrlBeforeAuthRedirect() {
        var currentUrl = this.$location.url();
        if (!this.sessionStorage.contains(AuthRedirect.KEY) && currentUrl !== this.authRouter.logonUrl()) {
            console.log("Storing URL: " + currentUrl);
            this.sessionStorage.put(AuthRedirect.KEY, currentUrl);
        }
    }

    public forwardToLogOn(native: boolean, reason?: RedirectReason) {
        console.log("Forwarding to Log On...");
        if (!native) {
            let isFullScreen: boolean = LocationUtils.isFullScreen(this.$location);
            switch (reason) {
                case RedirectReason.ACCESS_DENIED:
                    this.$location.url(this.authRouter.logonUrl()).search(AuthRouter.ACCESS_DENIED_PARAM_NAME, true); break;
                case RedirectReason.TOKEN_EXPIRED:
                    this.$location.url(this.authRouter.logonUrl()).search(AuthRouter.TOKEN_EXPIRED_PARAM_NAME, true); break;
                case RedirectReason.LOGON_REQUIRED:
                    this.$location.url(this.authRouter.logonUrl()).search(AuthRouter.LOGON_REQUIRED_PARAM_NAME, true); break;
                default:
                    this.$location.url(this.authRouter.logonUrl());
            }
        } else {
            var baseUrl = this.$window.location.href.split("?")[0],
                url = baseUrl + "#" + this.authRouter.logonUrl();
            console.log("URL: " + url);
            this.$window.location.replace(url);
        }
    }

    public forwardToSpnego() {
        console.log("Redirecting to SPNEGO...");
        this.$window.location.href = this.url.service("/api/spnego/exchange");
    }

    public forwardToStartPersonifySSO() {
        console.log("Redirecting to PersonifySSO...");
        this.storeUrlBeforeAuthRedirect();
        this.$window.location.href = this.url.service("/api/sso/start?ssoProvider=PERSONIFY_SSO");
    }


    public forwardToAccessDenied() {
        console.log("Redirecting to Access Denied...");
        this.$location.url(this.authRouter.accessDeniedUrl());
    }

    public returnAfterLogOn(native: boolean) {
        console.log("Returning after Log On...");
        var redirect = this.sessionStorage.get<string>(AuthRedirect.KEY);
        this.clearRedirection();
        redirect = redirect ? redirect : "/";
        if (!native) {
            console.log("$location.path('" + redirect + "')");
            this.$location.url(redirect ? redirect : this.mainRouter.mainUrl());
        } else {
            var baseUrl = this.$window.location.href.split("?")[0],
                url = baseUrl + "#" + redirect;
            console.log("$window.location.replace('" + url + "')");
            this.$window.location.replace(url);
        }
    }

    public clearRedirection(): void {
        this.sessionStorage.remove(AuthRedirect.KEY);
    }
}
