import {UrlService} from "./../utils/UrlService"

import {CourseService} from "./../courses/CourseService"
import {ICourseMaterial} from "./../courses/models/ICourseMaterial"
import {IMultimediaSource} from "./../components/models/IMultimedia";
import {RandomUtils} from "../utils/RandomUtils";
import {StringBuilder} from "../utils/StringBuilder";
import {MaterialTypeKeyEnum} from "./../materials/models/MaterialType";
import {ObjectTypeUtils} from "./../utils/ObjectTypeUtils";

export interface IVhsEmbeddedMaterialDirectiveScope extends angular.IScope {
    id?: string;

    vm?: VhsEmbeddedMaterialController;
}

export class VhsEmbeddedMaterialController {
    public static TA_ELEMENT_SELECTOR = "p.ta-vhs-embedded-material";
    public static TA_DIRECTIVE_HTML = "<div vhs-embedded-material id=':materialId'></div>";
    public static TA_RENDER_FUNCTION = (element: angular.IAugmentedJQuery,
                                        $compile: angular.ICompileService,
                                        $rootScope: angular.IRootScopeService,
                                        $timeout: angular.ITimeoutService) => {
        var id: string = RandomUtils.generateRandomId('vhsEmbeddedMaterial_');
        var materialId: string = element.attr('id');
        var endIdIndex: number = materialId.indexOf("__");
        if (endIdIndex >= 0) {
            materialId = materialId.substring(0, endIdIndex);
        }
        element.replaceWith("<div id='" + id + "'></div>");
        $timeout(() => {
            VhsEmbeddedMaterialController.TA_COMPILE_AND_REPLACE_ELEMENT_FUNCTION('#' + id, materialId, $compile, $rootScope);
        }, 0);
    };

    public static TA_COMPILE_AND_REPLACE_ELEMENT_FUNCTION = (elementSelector: any,
                                         materialId: string,
                                         $compile: angular.ICompileService,
                                         $scope: angular.IRootScopeService) => {
        $(elementSelector).replaceWith($compile(
            StringBuilder.interpolate(VhsEmbeddedMaterialController.TA_DIRECTIVE_HTML).with(":materialId", materialId).build()
        )($scope));
    };

    public materialId: number;
    public material: ICourseMaterial;
    public multimediaSources: IMultimediaSource[];
    public isViewAllowed: boolean;
    public isInlineView: boolean;
    public materialType: string;
    public isMaterialEmpty: boolean;
    public isMaterialNotReady: boolean;

    public static $inject = [
        "$scope",
        "$timeout",
        CourseService.NAME
    ];

    constructor(private $scope: IVhsEmbeddedMaterialDirectiveScope,
                private $timeout: angular.ITimeoutService,
                private courseApi: CourseService) {
        $scope.vm = this;
    }

    public init($scope: IVhsEmbeddedMaterialDirectiveScope,
                element: any) {
        this.materialId = parseInt($scope.id);
        this.loadMaterial(this.materialId);
    }

    public multimediaErrorCallback(code: number, message: string): angular.IPromise<ICourseMaterial> {
        return this.loadMaterial(this.materialId);
    }

    public loadMaterial(materialId: number): angular.IPromise<ICourseMaterial> {
        return this.courseApi.getEmbeddedMaterial(this.materialId)
            .then(result => {
                this.material = angular.copy(result);
                this.multimediaSources = [{
                    src: this.isMaterialYoutubeLink() ? this.material.link : (this.material.downloadUrl || ''),
                    type: this.isMaterialYoutubeLink() ? "video/youtube" : this.material.contentType,
                    dataRes: ''
                }];
                this.isInlineView = true;
                if (this.isMaterialYoutubeLink()) {
                    this.materialType = 'YOUTUBE';
                    this.isInlineView = false;
                } else {
                    this.materialType = this.material.typeKey;
                    if (this.materialType ==  MaterialTypeKeyEnum[MaterialTypeKeyEnum.VIDEO] ||
                        this.materialType ==  MaterialTypeKeyEnum[MaterialTypeKeyEnum.AUDIO]) {
                        this.isInlineView = false;
                    }
                }
                this.isMaterialEmpty = this.material.id && this.material.typeKey !== MaterialTypeKeyEnum[MaterialTypeKeyEnum.LINK] &&
                    (!this.material.fileStatus || this.material.fileStatus === 'EMPTY');
                this.isMaterialNotReady = !this.isMaterialEmpty && this.material.id && this.material.typeKey !== MaterialTypeKeyEnum[MaterialTypeKeyEnum.LINK] &&
                    this.material.fileStatus && this.material.fileStatus !== 'EMPTY' && this.material.fileStatus !== 'READY';
                this.isViewAllowed = this.material.permissions.view && !this.isMaterialEmpty && !this.isMaterialNotReady;
                return this.material;
            })
            .catch(response => {
                this.material = null;
                return this.material;
            });
    }

    public isMaterialYoutubeLink(): boolean {
        if (!this.material) {
            return undefined;
        }
        return this.material && this.material.typeKey == MaterialTypeKeyEnum[MaterialTypeKeyEnum.LINK] && ObjectTypeUtils.isYoutubeUrl(this.material.link);
    }
}

export function VhsEmbeddedMaterialDirectiveFactory(url: UrlService): angular.IDirective {
    function link($scope: IVhsEmbeddedMaterialDirectiveScope,
                  element: angular.IAugmentedJQuery,
                  attributes: angular.IAttributes,
                  controller: VhsEmbeddedMaterialController) {
        controller.init($scope, element);
    }

    let directive: angular.IDirective = {
        restrict: "AE",
        link: link,
        controller: VhsEmbeddedMaterialController,
        scope: {
            id: "@?",
        },
        templateUrl: url.template("components/vhs-embedded-material.html"),
    };
    return directive;
}

VhsEmbeddedMaterialDirectiveFactory.$inject = [
    UrlService.NAME
];
