import {IObjectWithComments} from "./models/IObjectWithComments";
import {PersonalService} from "./PersonalService";

import {IUser} from "./../users/models/IUser";
import {UserService} from "./../users/UserService";
import {SecurityService} from "./../security/SecurityService";
import {MenuHelper} from "./../utils/MenuHelper";
import {UrlService} from "./../utils/UrlService";
import {ObjectTypeUtils} from "./../utils/ObjectTypeUtils";
import {IComment} from "./../social/models/IComment";
import {MaterialsRouter} from "./../urls/MaterialsRouter";
import {NewsRouter} from "./../urls/NewsRouter";
import {IGridModel, GridModel} from "./../IGridModel";
import {HttpGridHelper} from "./../IHttpGridService";
import {ReferencedObjectType} from "./../ReferencedObjectType";
import {notifier} from "./../INotifier";


export class NewCommentsController {
    public static NAME = "personal.NewCommentsController";

    public userProfile: IUser;
    public objectsWithCommentsGridModel: IGridModel<IObjectWithComments>;
    public rowShow: Map<number, boolean> = new Map<number, boolean>();
    public objectComments: Map<number, Array<IComment>> = new Map<number, Array<IComment>>();
    public disableGetCommentsButton: boolean;

    public static $inject = [
        "$http",
        "$location",
        UrlService.NAME,
        SecurityService.NAME,
        UserService.NAME,
        PersonalService.NAME,
        MaterialsRouter.NAME,
        NewsRouter.NAME
    ];

    constructor($http: angular.IHttpService,
                private $location: angular.ILocationService,
                private url: UrlService,
                private securityService: SecurityService,
                private userService: UserService,
                private personalService: PersonalService,
                public materialsRouter: MaterialsRouter,
                public newsRouter: NewsRouter) {
        this.securityService.demandAuthentication();
        MenuHelper.setCurrentMenuItem(MenuHelper.PERSONAL_ACCOUNT_NEW_COMMENTS_MENU_KEY);

        this.disableGetCommentsButton = false;

        this.loadUserProfile();

        this.objectsWithCommentsGridModel = new GridModel(HttpGridHelper.createLoader<IObjectWithComments>($http,
            this.url.service("/api/personal/comments/objects")));
        this.objectsWithCommentsGridModel.entries.forEach(e => {
            this.rowShow.set(e.objectId, false);
        });
    }

    public showComments(objectId: number, objectType: string): void {
        if (!this.rowShow.get(objectId) && !this.disableGetCommentsButton) {
            if (!this.objectComments.has(objectId)) {
                this.disableGetCommentsButton = true;
                this.personalService.getUserCommentsForObject(objectId, ObjectTypeUtils.getReferenceObjectTypeFromString(objectType)).then(result => {
                    this.objectComments.set(objectId, result);
                    this.rowShow.set(objectId, !this.rowShow.get(objectId));
                    this.disableGetCommentsButton = false;
                });
            }
        } else {
            this.rowShow.set(objectId, !this.rowShow.get(objectId));
        }
    }

    public getObjectRoute(object: IObjectWithComments) {
        if (object.objectType == ReferencedObjectType[ReferencedObjectType.MATERIAL]) {
            if (!object.available) {
                notifier.error("Access to the material is restricted");
            } else {
                this.$location.url(this.materialsRouter.watchUrl(object.objectId));
            }
        } else if (object.objectType == ReferencedObjectType[ReferencedObjectType.NEWS]) {
            this.$location.url(this.newsRouter.oneNewsUrl(object.objectId));
        }
    }

    public getGlyphiconClass(objectType: string, materialTypeId?: number): string {
        switch (objectType) {
            case ReferencedObjectType[ReferencedObjectType.MATERIAL]: return ObjectTypeUtils.getMaterialGlyphiconClass(materialTypeId);
            case ReferencedObjectType[ReferencedObjectType.NEWS]: return ObjectTypeUtils.getNewsGlyphiconClass();
        }
    }

    public getTypeTitle(objectType: string, materialTypeId: number): string {
        switch (objectType) {
            case ReferencedObjectType[ReferencedObjectType.MATERIAL]: return ObjectTypeUtils.getMaterialTypeTitle(materialTypeId);
            default: return ObjectTypeUtils.getReferenceObjectTypeTitle(objectType);
        }
    }

    public getObjectRouteForComment(object:IObjectWithComments, commentId: number) {
        if (object.permissions.view) {
            if (object.objectType == ReferencedObjectType[ReferencedObjectType.MATERIAL]) {
                this.$location.url(this.materialsRouter.watchUrl(object.objectId)).hash("comment" + commentId);
            } else if (object.objectType == ReferencedObjectType[ReferencedObjectType.NEWS]) {
                this.$location.url(this.newsRouter.oneNewsUrl(object.objectId)).hash("comment" + commentId);
            }
        }
    }

    private loadUserProfile(): void {
        this.userService.getCurrentUserProfile().then(u => {
            this.userProfile = u;
        })
    }
}