export class TimeoutUtils {
    private semaphores: any = {};

    private checkNew(uid: any, handler: Function) {
        var semaphore = this.getByUid(uid);
        if (undefined == semaphore) {
            this.setByUid(uid,
                {
                    handler: handler,
                    hash: null,
                    active: false
                }
            );
        }
    }

    private getByUid(uid: any) {
        return this.semaphores[uid];
    }

    private setByUid(uid, semaphore) {
        this.semaphores[uid] = semaphore;
    }

    public processWithDelay(uid: any, handler: Function, delay: number) {
        this.checkNew(uid, handler);
        var hash = new Date().getTime();
        this.setByUid(uid,
            {
                handler: handler,
                hash: hash,
                active: true
            }
        );
        this.executeWithTimeout(handler, delay, uid, hash);
    }

    private executeWithTimeout(handler: Function, timeout: number, uid: any, hash: any) {
        var $self = this;
        var timer = setTimeout(function () {
            if ($self.getByUid(uid).hash === hash) {
                handler();
            }
        }, timeout);
    }
}