import {ReferenceUtils} from "./ReferenceUtils";
import {ReferenceService} from "./ReferenceService";
import {ReferenceRedirect} from "./ReferenceRedirect";
import {ReferenceController} from "./ReferenceController";
import {ReferenceEditController} from "./ReferenceEditController";

import {SecurityModule} from "./../security/SecurityModule";
import {UrlsModule} from "./../urls/UrlsModule";
import {InfrastructureModule} from "./../infrastructure/InfrastructureModule";

export var ReferenceModule: angular.IModule = angular
    .module("reference", [
        SecurityModule.name,
        UrlsModule.name,
        InfrastructureModule.name
    ])
    .service(ReferenceUtils.NAME, ReferenceUtils)
    .service(ReferenceService.NAME, ReferenceService)
    .service(ReferenceRedirect.NAME, ReferenceRedirect)
    .controller(ReferenceController.NAME, ReferenceController)
    .controller(ReferenceEditController.NAME, ReferenceEditController);