export enum SortOrder {
    ASC,
    DESC
}

export class SortEntry {
    public fieldName: string;
    public sortOrder: SortOrder;

    constructor(fieldName: string, sortOrder: SortOrder) {
        this.fieldName = fieldName;
        this.sortOrder = sortOrder;
    }

    public reverseOrder() {
        this.sortOrder = this.sortOrder == SortOrder.ASC ? SortOrder.DESC : SortOrder.ASC;
    }

    public static asc(fieldName: string): SortEntry {
        return new SortEntry(fieldName, SortOrder.ASC);
    }

    public static desc(fieldName: string): SortEntry {
        return new SortEntry(fieldName, SortOrder.DESC);
    }
}