import {UrlService} from "./../utils/UrlService";

import {ISectionInHierarchyExtended} from "./models/ISectionInHierarchy";
import {ISelectable} from "../ISelectable";
import {ICourseProgress, ICourseProgressWithParticipantInfo} from "./models/ICourseProgress";
import {ISectionsStatistics} from "./models/ISectionStatistics";
import {ISectionProgress} from "./models/ISectionProgress";

export class ProgressService {
    public static NAME = "courses.ProgressService";

    public COURSE_PROGRESS_ID_STORAGE_KEY = "vhs.application.courses.course.progress.id";

    public static $inject = [
        "$http",
        "$sce",
        UrlService.NAME
    ];

    constructor(private $http: angular.IHttpService,
                private $sce: angular.ISCEService,
                private url: UrlService) {
    }

    public startCourse(courseId: number, participationFormData?: any): angular.IPromise<ICourseProgress> {
        return this.$http.post(this.url.base("/api/progress/course/:courseId/start")
            .param(":courseId", courseId)
            .service(), participationFormData)
            .then(result => result.data);
    }

    public getCourseProgress(courseId: number, courseProgressId?: number, userId?: number): angular.IPromise<ICourseProgress> {
        // userId MUST be empty (null or undefined) for calling this method while viewing a Course as a student
        // specify userId only when viewing students' Progress as a Course owner
        return this.$http.get(this.url.base("/api/progress/course/:courseId?userId=:userId&courseProgressId=:courseProgressId")
            .param(":courseId", courseId)
            .param(":userId", userId || "")
            .param(":courseProgressId", courseProgressId || "")
            .service())
            .then(result => result.data);
    }
    public getCourseProgressHistory(courseId: number): angular.IPromise<ICourseProgressWithParticipantInfo[]> {
        return this.$http.get(this.url.base("/api/progress/course/:courseId/history")
            .param(":courseId", courseId)
            .service())
            .then(result => result.data);
    }

    public getCourseSections(courseId: number, courseProgressId: number): angular.IPromise<Array<ISectionInHierarchyExtended>> {
        return this.$http.get(this.url.base("/api/progress/structure/courses/:courseId/sections?courseProgressId=:courseProgressId")
            .param(":courseId", courseId)
            .param(":courseProgressId", courseProgressId || "")
            .service())
            .then(result => result.data);
    }

    public getModuleSections(courseId: number, moduleId: number, courseProgressId: number): angular.IPromise<Array<ISectionInHierarchyExtended>> {
        return this.$http.get(this.url.base("/api/progress/structure/courses/:courseId/modules/:moduleId/sections?courseProgressId=:courseProgressId")
            .param(":courseId", courseId)
            .param(":moduleId", moduleId)
            .param(":courseProgressId", courseProgressId || "")
            .service())
            .then(result => result.data);
    }

    public getCourseUsers(courseId: number): angular.IPromise<Array<ISelectable>> {
        return this.$http.get(this.url.base("/api/courses/:courseId/users")
            .param(":courseId", courseId)
            .service())
            .then(result => result.data);
    }

    public getCourseGroups(courseId: number): angular.IPromise<Array<ISelectable>> {
        return this.$http.get(this.url.base("/api/courses/:courseId/groups")
            .param(":courseId", courseId)
            .service())
            .then(result => result.data);
    }

    public getProgressForUser(courseId: number, userId?: number): angular.IPromise<Array<ISectionProgress>> {
        if (userId) {
            return this.$http.get(this.url.base("/api/progress/course/:courseId/sections?userId=:userId")
                .param(":courseId", courseId)
                .param(":userId", userId)
                .service())
                .then(result => result.data);
        } else {
            return this.$http.get(this.url.base("/api/progress/course/:courseId/sections")
                .param(":courseId", courseId)
                .service())
                .then(result => result.data);
        }
    }

    public getProgressForQuiz(courseId: number, sectionId: number, userIds: Array<number>, groupIds: Array<number>): angular.IPromise<Array<ISectionProgress>> {
        let requestConfig: angular.IRequestShortcutConfig = {
            params: {
                // TODO: limit the number of list items because URI length should not exceed 2083 characters
                userIdsFilter: userIds,
                groupIdsFilter: groupIds
            }
        };
        // TODO: parameters 'page' and 'pageSize' are for debug purposes here,
        // TODO: they should be removed later and replaced with our Grid
        return this.$http.get(this.url.base("/api/progress/course/:courseId/sections/:sectionId?page=0&pageSize=1000")
                .param(":courseId", courseId)
                .param(":sectionId", sectionId)
                .service(),
            requestConfig
        ).then(result => result.data);
    }

    public getSectionsStatistics(courseId: number, userIds: Array<number>, groupIds: Array<number>): angular.IPromise<Array<ISectionsStatistics>> {
        let requestConfig: angular.IRequestShortcutConfig = {
            params: {
                // TODO: limit the number of list items because URI length should not exceed 2083 characters
                userIdsFilter: userIds,
                groupIdsFilter: groupIds
            }
        };
        return this.$http.get(this.url.base("/api/progress/course/:courseId/sections/statistics")
                .param(":courseId", courseId)
                .service(),
            requestConfig).then(result => result.data);
    }

    public setSectionProgress(courseId: number, sectionId: number, status: string, courseProgressId: number) {
        return this.$http.put(this.url.base("/api/progress/course/:courseId/sections/:sectionId/status/:status")
            .param(":courseId", courseId)
            .param(":sectionId", sectionId)
            .param(":status", status.toUpperCase())
            .service(), courseProgressId ? JSON.stringify(courseProgressId) : null)
            .then(result => result.data);
    }

    public getSectionProgress(courseId: number, sectionId: number, courseProgressId: number): angular.IPromise<ISectionProgress> {
        return this.$http.get(this.url.base("/api/progress/course/:courseId/sections/:sectionId/progress/:courseProgressId")
            .param(":courseId", courseId)
            .param(":sectionId", sectionId)
            .param(":courseProgressId", courseProgressId)
            .service())
            .then(result => result.data);
    }
}