import {IPage} from "./IPage";
import {SortEntry} from "./SortEntry";
import {IGridModel} from "./IGridModel";

export interface IHttpGridService<T> {
    page(page: number, pageSize: number, pageMode: string): IHttpGridService<T>;
    sorting(sortBy: string, sortDir: string): IHttpGridService<T>;
    cache(): IHttpGridService<T>
    getItems(page?: number, size?: number, mode?: string, filter?: any, sorting?: Array<SortEntry>): angular.IPromise<IPage<T>>;
}

export class HttpGridService<T> implements IHttpGridService<T> {

    private $http: angular.IHttpService;
    private apiUrl: string;
    private params: any = {};
    private enableCache: boolean = false;

    constructor($http: angular.IHttpService, apiUrl: string) {
        this.$http = $http;
        this.apiUrl = apiUrl;
    }

    public getItems(page?: number, size?: number, mode?: string, filter?: any, sorting?: Array<SortEntry>): angular.IPromise<IPage<T>> {

        if (typeof filter !== "undefined") {
            for (var key in filter) {
                if (filter.hasOwnProperty(key)) {
                    this.params[key + "Filter"] = filter[key];
                }
            }
        }
        if (typeof sorting !== "undefined") {
            this.params["sorting"] = sorting;
        }
        if (!this.enableCache) {
            this.addNoCache();
        }
        this.page(page, size, mode);

        return this.$http.get(this.apiUrl, {params: this.params})
            .then(result => result.data);
    }

    public page(page: number, pageSize: number, pageMode: string): IHttpGridService<T> {
        this.params["page"] = page;
        this.params["pageSize"] = pageSize;
        this.params["pageMode"] = pageMode;
        return this;
    }

    public sorting(sortBy: string, sortDir: string): IHttpGridService<T> { //todo:
        this.params["sortBy"] = sortBy;
        this.params["sortDir"] = sortDir;
        return this;
    }

    public cache(): IHttpGridService<T> {
        this.enableCache = true;
        return this;
    }

    private addNoCache() {
        this.params["no-cache"] = new Date().getTime();
        return this;
    }

    public static grid<T>($http: angular.IHttpService, apiUrl: string): IHttpGridService<T> {
        return new HttpGridService<T>($http, apiUrl);
    }

}

export class HttpGridHelper {

    public static createLoader<T>($http: angular.IHttpService, apiUrl: string): (m: IGridModel<T>) => angular.IPromise<IPage<T>> {
        var httpGridService = HttpGridService.grid($http, apiUrl);
        return model => httpGridService.getItems(model.currentPage, model.getPageSize(), model.requestsPageMode, model.filter, model.sorting);
    }

    public static createLoaderWithCallback<T>($http: angular.IHttpService,
                                              apiUrl: string,
                                              callback: (page: IPage<T>) => angular.IPromise<IPage<T>>): (m: IGridModel<T>) => angular.IPromise<IPage<T>> {
        var httpGridService = HttpGridService.grid($http, apiUrl);
        return model => httpGridService.getItems(model.currentPage, model.pageSize, model.filter, model.sorting)
            .then((result: IPage<T>) => callback(result));
    }
}