import {IReferenceEntityMetadata} from "./models/ReferenceMetadata";
import {ReferenceEntitiesParams, IReferenceEntitiesParams} from "./models/IReferenceEntitiesParams";
import {IReferenceEntityAndMetadataResult} from "./models/IReferenceEntityAndMetadataResult";
import {IReferenceEntitiesAndMetadataResult} from "./models/IReferenceEntitiesAndMetadataResult";
import {ISelectableReferenceEntity} from "./models/ISelectableReferenceEntity";
import {UrlService} from "./../utils/UrlService";

export class ReferenceService {
    public static NAME = "reference.ReferenceService";

    public static $inject = [
        "$http",
        "$q",
        UrlService.NAME
    ];

    constructor(private $http: ng.IHttpService,
                private $q: ng.IQService,
                private url: UrlService) {
    }

    public getActiveReferenceEntities(referenceName: string, referenceParams: IReferenceEntitiesParams): ng.IPromise<IReferenceEntitiesAndMetadataResult> {
        return this.$http.get(
            this.url.base("/api/reference/:name/entities/active")
                .param(":name", referenceName).service(), this.createReferenceFilterParams(referenceParams))
            .then(result => result.data);
    }

    public getActiveReferenceEntitiesForSelector(referenceName: string, referenceParams: IReferenceEntitiesParams, includedIds: Array<number>): ng.IPromise<Array<ISelectableReferenceEntity>> {
        var params = this.createReferenceFilterParams(referenceParams);
        if (includedIds && includedIds.length > 0) {
            params.params["includedIds"] = includedIds;
        }
        return this.$http.get(
            this.url.base("/api/reference/:name/entities/forSelector")
                .param(":name", referenceName).service(), params)
            .then(result => result.data);
    }

    public getActiveReferenceEntitiesForSelectorWithoutParams(referenceName: string, includedIds: Array<number>): ng.IPromise<Array<ISelectableReferenceEntity>> {
        var params = this.createReferenceFilterParams(new ReferenceEntitiesParams(undefined, undefined, undefined, undefined));
        if (includedIds && includedIds.length > 0) {
            params.params["includedIds"] = includedIds;
        }
        return this.$http.get(
            this.url.base("/api/reference/:name/entities/forSelector")
                .param(":name", referenceName).service(), params)
            .then(result => result.data);
    }

    public getHierarchicalReferenceEntities(referenceName: string, referenceHierarchicalProperty: string): ng.IPromise<IReferenceEntitiesAndMetadataResult> {
        return this.$http.get(
            this.url.base("/api/reference/:name/entities/hierarchical/:hierarchy")
                .param(":name", referenceName)
                .param(":hierarchy", referenceHierarchicalProperty).service())
            .then(result => result.data);
    }

    public getReferenceEntity(referenceName: string, id: number): ng.IPromise<IReferenceEntityAndMetadataResult> {
        return this.$http.get(
            this.url.base("/api/reference/:name/entity/:id")
                .param(":name", referenceName)
                .param(":id", id).service())
            .then(result => result.data);
    }

    public getEmptyReferenceEntity(referenceName: string): ng.IPromise<IReferenceEntityAndMetadataResult> {
        return this.$http.get(this.url.base("/api/reference/:name/entity/empty")
            .param(":name", referenceName).service())
            .then(result => result.data);
    }

    public getEmptyReferenceEntityNoMetadata(referenceName: string): ng.IPromise<Map<string, any>> {
        return this.$http.get(this.url.base("/api/reference/:name/entity/empty/no/metadata")
            .param(":name", referenceName).service())
            .then(result => result.data);
    }

    public getReferenceMetadata(referenceName: string): ng.IPromise<IReferenceEntityMetadata> {
        return this.$http.get(this.url.base("/api/reference/:name/metadata").param(":name", referenceName).service())
            .then(result => result.data);
    }

    public getAssociationReferencesMetadata(referenceName: string): ng.IPromise<Map<string, IReferenceEntityMetadata>> {
        return this.$http.get(this.url.base("/api/reference/:name/metadata/association")
            .param(":name", referenceName).service())
            .then(result => result.data);
    }

    public createReferenceEntity(referenceName: string, map: Map<string, any>): ng.IPromise<Map<string, any>> {
        return this.$http.post(this.url.base("/api/reference/:name/entity/create")
            .param(":name", referenceName).service(), map)
            .then(result => result.data);
    }

    public updateReferenceEntity(referenceName: string, id: number, map: Map<string, any>): ng.IPromise<Map<string, any>> {
        return this.$http.put(this.url.base("/api/reference/:name/entity/update/:id")
            .param(":name", referenceName)
            .param(":id", id).service(), map)
            .then(result => result.data);
    }

    public setActiveReferenceEntity(referenceName: string, id: number, version: number, isActive: boolean): ng.IPromise<Map<string, any>> {
        return this.$http.put(this.url.base("/api/reference/:referenceName/entity/:id?isActive=:isActive&version=:version")
            .param(":referenceName", referenceName)
            .param(":id", id)
            .param(":version", version)
            .param(":isActive", isActive).service(), null)
            .then(result => result.data);
    }

    private createReferenceFilterParams(referenceParams: IReferenceEntitiesParams): any {
        return {
            params: {
                referenceFilter: referenceParams.referenceFilter,
                referenceSorting: referenceParams.referenceSorting,
                referenceSearchString: referenceParams.referenceSearchString,
                referenceSplitParam: referenceParams.referenceSplitParam
            }
        }
    }
}