import {ReferenceUtils} from "./../ReferenceUtils"
import {IReferenceEntityMetadata} from "./../models/ReferenceMetadata"

export class ReferenceIsRequiredValidator {

    protected utils: ReferenceUtils;
    protected entity: Map<string, any>;
    protected metadata: IReferenceEntityMetadata;
    protected validationErrorsMap: Map<string, string>;

    constructor(utils: ReferenceUtils,
                entity: Map<string, any>,
                metadata: IReferenceEntityMetadata,
                validationErrorsMap: Map<string, string>) {
        this.utils = utils;
        this.entity = entity;
        this.metadata = metadata;
        this.validationErrorsMap = validationErrorsMap;
    }

    public valid(): boolean {
        if (!this.utils || !this.entity || !this.metadata || !this.validationErrorsMap) {
            return false;
        }
        var result = true;
        for (var i = 0; i < this.metadata.editableAttributes.length; ++i) {
            var attr = this.metadata.editableAttributes[i];
            var attrSysName = attr.systemName;
            var attrIsRequired = attr.isRequired;
            if (!attrIsRequired) {
                continue;
            }
            if (!this.utils.validAttribute(attr.type, this.entity[attrSysName])) {
                this.validationErrorsMap.set(attrSysName, this.buildErrorMessage(attr.title));
                result = false;
            }
        }
        return result;
    }

    private buildErrorMessage(title: string): string {
        return "Please specify a value for the field \"%1\"".replace("%1", title);
    }
}