import {ReferenceUtils} from "./ReferenceUtils";
import {ReferenceService} from "./ReferenceService";
import {IReferenceEntityMetadata} from "./models/ReferenceMetadata";
import {IGridModel, GridModel} from "./../IGridModel";
import {HttpGridHelper} from "./../IHttpGridService";
import {UrlService} from "./../utils/UrlService";
import {MenuHelper} from "./../utils/MenuHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";
import {PermissionsService} from "./../security/PermissionsService";
import {SecurityService} from "./../security/SecurityService";
import {ReferenceRouter} from "./../urls/ReferenceRouter";
import {StringBuilder} from "./../utils/StringBuilder";

export class ReferenceController {
    public static NAME = "reference.ReferenceController";

    public referenceEntitiesGridModel: IGridModel<Map<string, any>>;
    public referenceMetadata: IReferenceEntityMetadata;

    public systemPermissions: ISystemPermissions;
    protected referenceDefaultValues: string;
    protected referenceFilter: string;
    protected referenceAssociationFilters: string;

    public static $inject = [
        "$http",
        "$routeParams",
        "$location",
        UrlService.NAME,
        ReferenceRouter.NAME,
        ReferenceUtils.NAME,
        ReferenceService.NAME,
        PermissionsService.NAME,
        SecurityService.NAME
    ];

    constructor(protected $http: ng.IHttpService,
                protected $routeParams: ng.route.IRouteParamsService,
                protected $location: angular.ILocationService,
                protected url: UrlService,
                protected referenceRouter: ReferenceRouter,
                public referenceUtils: ReferenceUtils,
                protected referenceApi: ReferenceService,
                protected permissionsApi: PermissionsService,
                private securityService: SecurityService) {
        this.securityService.demandAuthentication()
            .demandSystemPermissions(systemPermissions => systemPermissions.viewReferenceData);

        let referenceName: string = this.$routeParams[ReferenceRouter.REFERENCE_NAME_PARAM];
        MenuHelper.setCurrentMenuItem("administration_" + referenceName);

        this.referenceDefaultValues = this.$location.search()[ReferenceRouter.REFERENCE_DEFAULT_VALUES_PARAM];
        this.referenceFilter = this.$location.search()[ReferenceRouter.REFERENCE_FILTER_PARAM];
        this.referenceAssociationFilters = this.$location.search()[ReferenceRouter.REFERENCE_ASSOCIATION_FILTERS_PARAM];

        this.referenceEntitiesGridModel = new GridModel(HttpGridHelper.createLoader<Map<string, any>>(this.$http,
            this.url.base("/api/reference/:name/entities")
                .param(":name", referenceName).service()));
        this.referenceEntitiesGridModel.filter.referenceFilter = this.referenceFilter;
        this.referenceEntitiesGridModel.filter.referenceSearchString = "";
        this.referenceEntitiesGridModel.filter.referenceSplitParam = false;

        this.permissionsApi.getSystemPermissions().then(p => {
            this.systemPermissions = p;
            this.referenceApi.getReferenceMetadata(referenceName)
                .then(result => {
                    this.referenceMetadata = result;
                    PageHeader.set().addActionLinkIf(this.systemPermissions.addReferenceData && !this.referenceMetadata.isInternal,
                            '+ New', this.getReferenceEditUrl('new'))
                        .update();
                });
        });
    }

    public getReferenceEditUrl(id: any): string {
        return this.referenceMetadata
            ? StringBuilder.interpolate(this.referenceRouter.editUrl(this.referenceMetadata.key, id))
                .add("?")
                .addIf(this.referenceDefaultValues != null, ReferenceRouter.REFERENCE_DEFAULT_VALUES_PARAM, "=",
                    this.referenceUtils.getStringInBrowserFormat(this.referenceDefaultValues), "&")
                .addIf(this.referenceFilter != null, ReferenceRouter.REFERENCE_FILTER_PARAM, "=",
                    this.referenceUtils.getStringInBrowserFormat(this.referenceFilter), "&")
                .addIf(this.referenceAssociationFilters != null, ReferenceRouter.REFERENCE_ASSOCIATION_FILTERS_PARAM, "=",
                    this.referenceUtils.getStringInBrowserFormat(this.referenceAssociationFilters), "&")
                .build()
            : null;
    }

    public activateEntity(index: number, entity: Map<string, any>): void {
        let referenceName: string = this.referenceMetadata.key;
        let id: number = entity[ReferenceUtils.entityIdProperty];
        let version: number = entity[ReferenceUtils.entityVersionProperty];
        this.referenceApi.setActiveReferenceEntity(referenceName, id, version, true).then(result => {
            this.referenceEntitiesGridModel.entries[index] = result;
        });
    }
}
