import {UrlService} from "./../utils/UrlService";
import IPromise = angular.IPromise;
import {IBundle} from "./models/IBundle";
import {IBundleFile} from "./models/IBundleFile";
import {BundleFileTypeEnum} from "./models/BundleFileType";
import {IBundleInput} from "./models/IBundleInput";

export class BundleService {
    public static NAME = "bundle.BundleService";

    public static $inject = [
        "$http",
        "$q",
        UrlService.NAME
    ];

    constructor(private $http: angular.IHttpService,
                private $q: angular.IQService,
                private url: UrlService) {
    }

    public getBundle(bundleId: number): angular.IPromise<IBundle> {
        return this.$http.get(this.url.base("/api/bundles/:bundleId")
            .param(":bundleId", bundleId)
            .service())
            .then(result => {
                var bundle: IBundle = result.data;
                this.createFilesMap(bundle);
                return result.data;
            });
    }

    public updateBundleFromFiles(bundleInput: IBundleInput): angular.IPromise<any> {
        return this.$http.put(this.url.base("/api/bundles/files")
            .service(), bundleInput)
            .then(result => {
                var bundle: IBundle = result.data["bundle"];
                this.createFilesMap(bundle);
                return result.data;
            });
    }

    private createFilesMap(bundle: IBundle): void {
        //temp version
        bundle.filesMap = new Map<string, Array<IBundleFile>>();
        bundle.files.forEach((file: IBundleFile) => {
            //console.log('bundle file url ' + file.url);
            //file.url = file.url.replace("vhs-aopa-static-1-client", "vhs-aopa-content-1-main");
            //file.url = file.url.replace("vhs-aopa-static-1-client.azureedge.net", "vhsaopacontentblob1.blob.core.windows.net");
            //console.log('bundle file url replaced: ' + file.url);
            bundle.filesMap.set(file.key, file);
        });
    }
}