import {MenuHelper} from "./../utils/MenuHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {CoursesRouter} from "./../urls/CoursesRouter";
import {SecurityService} from "./../security/SecurityService";
import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";

export class QuizDetailsController {
    public static NAME = "courses.QuizDetailsController";

    public quizNumber: number;

    public static $inject = [
        "$location",
        "$routeParams",
        SecurityService.NAME
    ];

    constructor(private $location: angular.ILocationService,
                private $routeParams: ng.route.IRouteParamsService,
                private securityService: SecurityService) {
        this.quizNumber = $routeParams[CoursesRouter.COURSE_QUIZ_ID_PARAM];
        let quizNumberStr = String(this.quizNumber);
        this.securityService.demandAuthentication();
        // TODO: Add a check permission "evaluationQuiz" when will be ready the server part
            //.demandQuizPermissions(quizNumberStr, quizPermissions => quizPermissions.evaluation);

        var menuItemKey: string = MenuHelper.getCurrentMenuItemKey();
        if (!menuItemKey || menuItemKey.length == 0) {
            MenuHelper.setCurrentMenuItem(MenuHelper.EDUCATION_COURSES_MENU_KEY);
        }
        PageHeader.set().header("Test #" + this.quizNumber +
            " of English language").update(); // TODO: real title

    }

    public isQuizNumberOdd(quizNumber): boolean {
        return this.quizNumber % 2 != 0;
    }
}