import {BaseReportController} from "./BaseReportController";
import {MaterialReportTarget} from "./models/MaterialReportTarget";
import {StatisticReportResult} from "./models/StatisticReportResult";
import {ReportGroupType} from "./models/ReportGroupType";
import {StatisticReportsService} from "./StatisticReportsService";
import {IReportWriter, LineDiagramsReportWriter} from "./utils/ReportWriters";
import {getLineDiagramsReportOptions} from "./utils/ReportOptions";
import {IReportTargetNamesMaker, OwnerNamesMaker, MaterialTypeNamesMaker, SubjectNamesMaker} from "./utils/ReportTargetNamesMaker";
import {SecurityService} from "./../security/SecurityService";
import {PermissionsService} from "./../security/PermissionsService";
import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";
import {ISubject} from "./../ISubject";
import {UserService} from "./../users/UserService";
import {ISelectable} from "./../ISelectable";
import {Moment} from "./../utils/Moment";
import {MenuHelper} from "./../utils/MenuHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";

export class MaterialViewReportController extends BaseReportController {
    public static NAME = "statistics.MaterialViewReportController";

    private report: StatisticReportResult<any, Array<any>, number>;
    private lineDiagramsReportWriter: IReportWriter = new LineDiagramsReportWriter();
    private reportTargetNamesMaker: IReportTargetNamesMaker;
    private reportTarget: MaterialReportTarget;
    private groupType: ReportGroupType;

    public systemPermissions: ISystemPermissions;
    public owners: Array<ISelectable> = [];
    public ownerIds: Array<number> = [];
    public materialTypeIds: Array<number> = [];
    public subjectIds: Array<number> = [];
    public dateFromText: string;
    public dateToText: string;

    public lineDiagramsReportData: Array<any> = null;
    public lineDiagramsReportOptions: any = getLineDiagramsReportOptions("The report on view of materials");

    public static $inject = [
        StatisticReportsService.NAME,
        UserService.NAME,
        PermissionsService.NAME,
        SecurityService.NAME
    ];

    constructor(private statisticReportsService: StatisticReportsService,
                private userService: UserService,
                private permissionsApi: PermissionsService,
                private securityService: SecurityService) {
        super();
        this.securityService.demandAuthentication();
        MenuHelper.setCurrentMenuItem(MenuHelper.ANALYTICS_MATERIAL_VIEW_REPORT_MENU_KEY);
        PageHeader.set().header("The report on view of materials").update();

        this.permissionsApi.getSystemPermissions().then(systemPermissions => {
            this.systemPermissions = systemPermissions;
            if (this.systemPermissions.viewAnalytics) {
                this.userService.getUsersWithoutExcludedUsersForSelector().then(result =>{
                    this.owners = angular.copy(result);
                });
                this.reportTarget = MaterialReportTarget.OWNERS;
                this.reportTargetNamesMaker = new OwnerNamesMaker();
            } else {
                this.reportTarget = MaterialReportTarget.MATERIAL_TYPES;
                this.reportTargetNamesMaker = new MaterialTypeNamesMaker();
            }
            this.viewReportGroupByDays();
        });
    }

    public viewReportGroupByDays(): void {
        this.viewReportGroupBy(ReportGroupType.DAYS);
    }

    public viewReportGroupByWeeks(): void {
        this.viewReportGroupBy(ReportGroupType.WEEKS);
    }

    public viewReportGroupByMonths(): void {
        this.viewReportGroupBy(ReportGroupType.MONTHS);
    }

    public viewReportGroupByYears(): void {
        this.viewReportGroupBy(ReportGroupType.YEARS);
    }

    private viewReportGroupBy(groupType: ReportGroupType): void {
        this.groupType = groupType;
        this.lineDiagramsReportData = null;
        this.statisticReportsService.viewMaterialViewReport(this.reportTarget, this.groupType, this.ownerIds, this.materialTypeIds,
                this.subjectIds, Moment.textToJavaDate(this.dateFromText), Moment.textToJavaDate(this.dateToText)).then(result => {
            this.report = result;
            this.lineDiagramsReportOptions.series = this.reportTargetNamesMaker.make(this.report, (name: string) => {
                return {label: name}
            });
            this.lineDiagramsReportData = this.lineDiagramsReportWriter.reporting(this.report, (value: Array<any>) => {
                return [moment(value[0]).format("YYYY-MM-DD"), value[1]];
            });
        });
    }

    public isCurrentOwnersReportTarget(): boolean {
        return this.reportTarget == MaterialReportTarget.OWNERS;
    }

    public isCurrentMaterialTypesReportTarget(): boolean {
        return this.reportTarget == MaterialReportTarget.MATERIAL_TYPES;
    }

    public isCurrentSubjectsReportTarget(): boolean {
        return this.reportTarget == MaterialReportTarget.SUBJECTS;
    }

    public setCurrentOwnersReportTarget(): void {
        this.setCurrentReportTarget(MaterialReportTarget.OWNERS, new OwnerNamesMaker());
    }

    public setCurrentMaterialTypesReportTarget(): void {
        this.setCurrentReportTarget(MaterialReportTarget.MATERIAL_TYPES, new MaterialTypeNamesMaker());
    }

    public setCurrentSubjectsReportTarget(): void {
        this.setCurrentReportTarget(MaterialReportTarget.SUBJECTS, new SubjectNamesMaker());
    }

    private setCurrentReportTarget(reportTarget: MaterialReportTarget, reportTargetNamesMaker: IReportTargetNamesMaker): void {
        this.ownerIds = [];
        this.materialTypeIds = [];
        this.subjectIds = [];
        this.reportTarget = reportTarget;
        this.reportTargetNamesMaker = reportTargetNamesMaker;
    }
}