import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";
import {IPermissionWithUsersAndGroups} from "./../security/permissions/IPermissionWithUsersAndGroups";
import {SecuredObjectType} from "./../security/permissions/SecuredObjectType";
import {SecurityService} from "./../security/SecurityService";
import {PermissionsService} from "./../security/PermissionsService";
import {ISelectable} from "./../ISelectable";
import {MenuHelper} from "./../utils/MenuHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {UrlService} from "./../utils/UrlService";
import {Moment} from "./../utils/Moment";
import {notifier} from "./../INotifier";
import {ActionLogService} from "./../statistics/ActionLogService";
import {ActionType} from "./../statistics/models/ActionType";
import {ReferencedObjectType} from "./../ReferencedObjectType";
import {UserService} from "./../users/UserService";
import {AuthService} from "./../auth/AuthService";
import {UploadUtils} from "./../utils/UploadUtils";
import {UploadsService} from "./../storage/UploadsService";
import {ContentUtils} from "./../utils/ContentUtils";

import {IMultimediaSource} from "./../components/models/IMultimedia";

import {IMaterial} from "./models/IMaterial";
import {MaterialType, MaterialTypeEnum} from "./models/MaterialType";
import {MaterialService} from "./MaterialService";
import {MaterialsRouter} from "./../urls/MaterialsRouter";
import {StringBuilder} from "./../utils/StringBuilder";
import {ObjectTypeUtils} from "../utils/ObjectTypeUtils";

export interface IFile extends File {
    title?: string;
}

export class EditMaterialController {
    public static NAME = "materials.EditMaterialController";

    public isValidityInPerpetuity: boolean;
    public attachFormat: string;
    public file: File;
    public preview: IFile;
    public material: IMaterial;
    public disabledSaveButton: boolean;
    public disabledUploadButton: boolean;
    public permissionsWithUsersAndGroups: Array<IPermissionWithUsersAndGroups> = [];
    public showUploadButton: boolean;
    public typeId: number;
    public uploadContentType: number;
    public multimediaSources: IMultimediaSource[];
    public youtubeMultimediaSources: IMultimediaSource[];

    public hasFile: boolean;
    public isMaterialFileStatusReady: boolean;
    public isAudioOrVideo: boolean;
    public isPhoto: boolean;
    public isYoutube: boolean;

    public materialId: number;
    public fileName: string;
    public previewUrl: string;
    public hasPreviewUrlCanceled: boolean = false;
    private owners: Array<ISelectable> = [];
    private ownersMap: Map<number, ISelectable>;

    public static $inject = [
        "$location",
        UrlService.NAME,
        MaterialService.NAME,
        SecurityService.NAME,
        PermissionsService.NAME,
        "$routeParams",
        MaterialsRouter.NAME,
        ActionLogService.NAME,
        UserService.NAME,
        AuthService.NAME,
        UploadUtils.NAME,
        "$http"
    ];

    constructor(private $location: angular.ILocationService,
                private url: UrlService,
                private materialService: MaterialService,
                private securityService: SecurityService,
                private permissionsService: PermissionsService,
                private $routeParams: angular.route.IRouteParamsService,
                private materialsRouter: MaterialsRouter,
                private actionLogService: ActionLogService,
                private userService: UserService,
                private authService: AuthService,
                private uploadUtils: UploadUtils,
                private $http: angular.IHttpService) {
        this.materialId = $routeParams[MaterialsRouter.MATERIAL_ID_PARAM];
        let materialIdStr: string = String(this.materialId);
        let hasMaterialId: boolean = this.materialId != null;

        this.securityService.demandAuthentication()
            .demandSystemPermissionsIf(!hasMaterialId, systemPermissions => systemPermissions.addMaterial)
            .demandMaterialPermissionsIf(hasMaterialId, materialIdStr, materialPermissions => materialPermissions.edit);

        this.isValidityInPerpetuity = true;
        this.attachFormat = "";
        this.material = {};
        this.disabledSaveButton = true;
        this.disabledUploadButton = false;

        this.userService.getUsersWithoutExcludedUsersForSelector().then(result =>{
            this.owners = angular.copy(result);
            this.ownersMap = new Map<number, ISelectable>();
            this.owners.forEach((owner: ISelectable) => {
                this.ownersMap.set(owner.id, owner);
            });
        });

        if(hasMaterialId) {
            this.showUploadButton = false;
            PageHeader.set().header("Edit a Material").update();
            this.loadMaterial();
            this.enableButtonForLinkUpload();
            this.disabledSaveButton = false;
            this.loadPermissionsWithUsersAndGroups(SecuredObjectType.MATERIAL, materialIdStr);
        } else {
            this.showUploadButton = true;
            MenuHelper.setCurrentMenuItem(MenuHelper.OPERATIONS_UPLOADS_MENU_KEY);
            PageHeader.set().header("Uploads").update();
            this.typeId = 1;
            this.uploadContentType = 1;
            this.authService.getCurrentUser().then(user => {
                this.material.ownerId = user.id;
                this.material.ownerName = user.fullName;
            });
            this.loadPermissionsWithUsersAndGroups(SecuredObjectType.MATERIAL);
        }
    }

    private loadMaterial(): angular.IPromise<IMaterial> {
        var promise: angular.IPromise<IMaterial> = this.materialService.get(this.materialId);
        promise.then(result => {
            angular.copy(result, this.material);
            this.material.validityText =  Moment.isValidDate(result.validity);
            if(this.material.validityText) {
                this.isValidityInPerpetuity = false;
            }
            this.previewUrl = this.material.previewUrl ? this.material.previewUrl : null;
            this.fileName = result.fileName;
            this.typeId = result.typeId;
            if(result.link == null) {
                this.uploadContentType = 1;
            } else {
                this.uploadContentType = 2;
            }
            if (this.material.typeId != 3) {
                this.showUploadButton = !this.material.fileId;
            }
            this.multimediaSources = [{
                src: this.material.downloadUrl || '',
                type: this.material.contentType
            }];
        });
        return promise;
    }

    private loadPermissionsWithUsersAndGroups(securedObjectType: SecuredObjectType, objectId?: string): void {
        this.permissionsService.getAllObjectPermissionsWithUsersAndGroups(securedObjectType, objectId).then(result => {
            this.permissionsWithUsersAndGroups = result;
        });
    }

    public saveOrUpdateMaterial(material: IMaterial, permissionsWithUsersAndGroups: Array<IPermissionWithUsersAndGroups>): void {
        this.disabledSaveButton = true;
        this.disabledUploadButton = true;
        let file: File = this.file;
        let preview: File = this.preview;
        this.material.validity = Moment.textToJavaDate(material.validityText);
        this.material.typeId = this.typeId;

        if(this.material.typeId == MaterialType.LINK || file == null) {
            var promise = this.materialId
                ? this.materialService.update(material, permissionsWithUsersAndGroups, this.hasPreviewUrlCanceled, file, preview)
                : this.materialService.create(material, permissionsWithUsersAndGroups, file, preview);
            promise.then(result => {
                this.material = angular.copy(result[UploadsService.MATERIAL_KEY]);
                if (preview) {
                    if (this.materialId) {
                        this.uploadUtils.cancelMaterialPreviews(material.id);
                    }
                    let previewUploadUrl: string = result[UploadsService.PREVIEW_UPLOAD_URL_KEY];
                    let previewUploadMethod: string = result[UploadsService.PREVIEW_UPLOAD_METHOD_KEY];
                    this.uploadUtils.uploadMaterialByChunks(preview, this.material.previewId, this.material.id, previewUploadUrl, previewUploadMethod);
                }
                this.processSuccessCallback(this.material.id);
            }).catch(response => {
                this.enableButton();
                if (response.data.error == "error.storage.not.available") {
                    notifier.error("No available storage");
                }
            });
        } else {
            var fileExt = ContentUtils.getFileExt(file.name);
            if (ContentUtils.checkFileExt(material.typeId, fileExt)) {
                var promise = this.materialId
                    ? this.materialService.update(material, permissionsWithUsersAndGroups, this.hasPreviewUrlCanceled, file, preview)
                    : this.materialService.create(material, permissionsWithUsersAndGroups, file, preview);
                promise.then(result => {
                    this.material = angular.copy(result[UploadsService.MATERIAL_KEY]);
                    if (preview != null) {
                        if (this.materialId) {
                            this.uploadUtils.cancelMaterialPreviews(material.id);
                        }
                        let previewUploadUrl: string = result[UploadsService.PREVIEW_UPLOAD_URL_KEY];
                        let previewUploadMethod: string = result[UploadsService.PREVIEW_UPLOAD_METHOD_KEY];
                        this.uploadUtils.uploadMaterialByChunks(preview, this.material.previewId, this.material.id, previewUploadUrl, previewUploadMethod);
                    }

                    if (this.materialId) {
                        this.uploadUtils.cancelMaterialFiles(material.id);
                    }
                    let fileUploadUrl: string = result[UploadsService.FILE_UPLOAD_URL_KEY];
                    let fileUploadMethod: string = result[UploadsService.FILE_UPLOAD_METHOD_KEY];
                    this.uploadUtils.uploadMaterialByChunks(file, this.material.fileId, this.material.id, fileUploadUrl, fileUploadMethod);
                    this.processSuccessCallback(this.material.id);
                }).catch(response => {
                    this.enableButton();
                    if (response.data.error == "error.storage.not.available") {
                        notifier.error("No available storage");
                    }
                });
            } else { // TODO: a specific error code should be caught here instead of all possible exceptions
                notifier.error("Failed to upload this file: please check whether the file format is valid");
                this.enableButton();
            }
        }
    }

    private processSuccessCallback(materialId: number): void {
        this.actionLogService.create(materialId, ReferencedObjectType.MATERIAL, ActionType.MATERIAL_CREATION);
        notifier.success("The material has been saved");
        this.redirectOrClearFields();
    }

    private redirectOrClearFields(): void {
        if(this.materialId) {
            if (this.material && this.material.permissions && this.material.permissions.view) {
                this.$location.url(this.materialsRouter.watchUrl(this.materialId));
            } else {
                this.$location.url(this.materialsRouter.searchUrl());
            }
        } else {
            this.cleanFields();
        }
    }

    public enableButton(): void {
        this.disabledSaveButton = false;
        this.disabledUploadButton = false;
    }

    public enableButtonForLinkUpload(): void {
        this.disabledSaveButton = false;
    }

    private cleanFields() : void {
        this.isValidityInPerpetuity = true;
        this.material = {};
        this.uploadContentType = 1;
        this.authService.getCurrentUser().then(user => {
            this.material.ownerId = user.id;
            this.material.ownerName = user.fullName;
        });
        this.file = undefined;
        this.preview = undefined;
        this.disabledUploadButton = false;
        this.permissionsWithUsersAndGroups = [];
        this.loadPermissionsWithUsersAndGroups(SecuredObjectType.MATERIAL);
    }

    public cleanValidity(): void {
        this.material.validityText = "";
    }

    public resetPreview(): void {
        if (this.preview) {
            this.preview = undefined;
        } else if (this.previewUrl) {
            this.previewUrl = undefined;
            this.hasPreviewUrlCanceled = true;
        }
    }

    public restorePreviewUrl(): void {
        this.preview = undefined;
        this.previewUrl = this.material.previewUrl;
        this.hasPreviewUrlCanceled = false;
    }

    public reupload(): void {
        if(this.showUploadButton) {
            this.fileName = this.material.fileName;
        } else {
            this.fileName = "";
        }
        this.showUploadButton = !this.showUploadButton;
        this.file = undefined;
    }

    public setMaterialType(): void {
        if (this.file) {
            let fileExt = ContentUtils.getFileExt(this.file.name);
            this.typeId = this.material.typeId = ContentUtils.findMaterialTypeByFileExtension(fileExt);
            // console.log('setMaterialType', fileExt, MaterialTypeEnum[this.material.typeId]);
        }
    }

    public setFileOrLinkType(): void {
        if(this.uploadContentType == 1) {
            this.typeId = MaterialType.AUDIO; //default value
            this.showUploadButton = true;
        } else {
            this.typeId = MaterialType.LINK;
        }
    }

    public showPreview(): void {
        $(".modal-body").css("overflow-y","auto");
        $("#previewDialog").modal({
            keyboard: false,
            show: true
        });
        $("#previewDialog").on('hide.bs.modal', function() {
            $('video').trigger('pause');
            $('audio').trigger('pause');
        });

        this.hasFile = !!this.file;
        this.isMaterialFileStatusReady = this.materialId && this.material.fileStatus == 'READY';
        this.isAudioOrVideo = this.material.typeKey === 'VIDEO' || this.material.typeKey === 'AUDIO';
        this.isPhoto = this.material.typeKey === 'PHOTO';
        this.isYoutube = this.typeId === MaterialType.LINK && ObjectTypeUtils.isYoutubeUrl(this.material.link);
        this.youtubeMultimediaSources = [{
            src: this.material.link,
            type: "video/youtube"
        }];
    }

    public stopVideoAndAudioOnHidePreview(): void {
        this.hasFile = false;
        this.isMaterialFileStatusReady = false;
        this.isAudioOrVideo = false;
        this.isPhoto = false;
        this.isYoutube = false;

        $("#previewDialog").on('hidden.bs.modal', function() {
            $('video').trigger('pause');
            $('audio').trigger('pause');
        });
    }

    public materialSubjectChangeCallback = (name: string) => {
        this.material.subjectName = name;
    };

    public materialLanguageChangeCallback = (name: string) => {
        this.material.languageName = name;
    };

    public materialOwnerChangeCallback = () => {
        this.material.ownerName = this.ownersMap.get(this.material.ownerId).name;
    };

    public multimediaErrorCallback(code: number, message: string): angular.IPromise<IMaterial> {
        return this.loadMaterial();
    }

    public getMaterialValidity(): number {
        this.material.validityText ? this.material.validity = Moment.textToJavaDate(this.material.validityText)
        : this.material.validity = null;
        return this.material.validity;
    }
}
