import {UserService} from "./UserService";
import {UsersRouter} from "./../urls/UsersRouter";
import {IGridModel, GridModel} from "./../IGridModel";
import {HttpGridHelper} from "./../IHttpGridService";
import {PermissionsService} from "./../security/PermissionsService";
import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";
import {UrlService} from "./../utils/UrlService";
import {MenuHelper} from "./../utils/MenuHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {notifier} from "./../INotifier";
import {IUserWithProgress} from "./models/IUserWithProgress";

export class UsersListController {
    public static NAME = "users.UsersListController";

    private systemPermissions: ISystemPermissions;

    public usersGridModel: IGridModel<IUserWithProgress>;

    public static $inject = [
        "$timeout",
        "$window",
        "$log",
        "$http",
        UserService.NAME,
        PermissionsService.NAME,
        UsersRouter.NAME,
        UrlService.NAME
    ];

    constructor(private $timeout: angular.ITimeoutService,
                private $window: angular.IWindowService,
                private $log: angular.ILogService,
                $http: angular.IHttpService,
                private usersApi: UserService,
                private permissionsApi: PermissionsService,
                public usersRouter: UsersRouter,
                private url: UrlService) {
        MenuHelper.setCurrentMenuItem(MenuHelper.ADMINISTRATION_USERS_MENU_KEY);
        PageHeader.set().header("Users").update();

        permissionsApi.getSystemPermissions().then(p => {
            PageHeader.set().preserveHeader()
                .addActionLinkIf(p.createUser, '+&nbsp;New', this.usersRouter.addUrl()).update();
            this.systemPermissions = p;
        });

        this.usersGridModel = new GridModel(HttpGridHelper.createLoader<IUserWithProgress>($http, this.url.service("/api/users")))
    }

    public getIsActiveGridStyle(isActive: boolean): any {
        return isActive
            ? {}
            : {"color": "#999999"};
    }

    public convertIsActive(isActive: boolean): string {
        return isActive != undefined
            ? isActive ? 'Yes' : 'No'
            : undefined;
    }

    public activate(index: number, id: number) {
        let promise = this.usersApi.setActive(id, true);
        promise.then(u => {
            notifier.success("User '" + u.fullName + "' has been activated");
            this.usersGridModel.entries[index] = u;
        });
    }

    public downloadUserCertificate(user: IUserWithProgress): void {
        let parameters: any = {
            courseId: user.courseId,
            userId: user.id,
            progressId: user.courseProgressId,
            certificateId: null,
            format: 'pdf'
        };
        this.usersApi.downloadCertificate(parameters).then(result => {
            this.$window.open(result);
        });
    }
}
