import {SortEntry} from "./SortEntry";
import {IPage} from "./IPage";
import {TimeoutUtils} from "./utils/TimeoutUtils";
import {PageMode} from "./PageMode";

export interface IGridModel<T> {
    entries: Array<T>;
    filter: any;
    nonPersistedFilterFields: Array<string>;
    pageSize: number;
    currentPage: number;
    totalCount: number;
    totalCountRevised: number;
    requestsPageMode: string;
    totalPageCount: number;
    selectedPage: number;

    sorting: Array<SortEntry>;
    gridColumns: Array<GridColumn>;
    visibleColumnsCount: number;

    isLoading: boolean;

    loadPage(pageNumber: number);
    loadCurrentPage();
    loadWithDelay(timeoutUtils: TimeoutUtils, uuid: any);
    loadPageForFilter(pageNumber: number, filter: any);
    addLoadingListener(listener: IGridLoadingListener);
    clearLoadListeners();
    addColumnVisibilityListener(listener: IColumnVisibilityChangeListener);
    addPageSizeListener(listener: IPageSizeChangeListener);
    getPageSize(): number;
    setDefaultPageSize(): GridModel<T>;
    setMaxPageSize(): GridModel<T>;

    recalculateVisibleColumnsCount();
    changeColumnVisibility(columnIndex: number);

    onChangePageSize();
}

export interface IGridLoadingListener {
    startLoading();
    endLoading();
}
export interface IColumnVisibilityChangeListener {
    onColumnVisibilityChange(column: GridColumn);
}
export interface IPageSizeChangeListener {
    onChangePageSize();
}

export class PersistedGridModelConfiguration {
    public gridColumns: Array<GridColumn>;
    public sortEntries: Array<SortEntry>;
    public pageSize: number;
    public filter: any;

    public static fromGridModel(gridModel: IGridModel<any>): PersistedGridModelConfiguration {
        var persisted = new PersistedGridModelConfiguration();
        persisted.pageSize = gridModel.pageSize;
        persisted.gridColumns = gridModel.gridColumns;
        persisted.sortEntries = gridModel.sorting;
        persisted.filter = {};
        if (gridModel.filter) {
            for (var fieldName in gridModel.filter) {
                if (gridModel.filter.hasOwnProperty(fieldName) && gridModel.nonPersistedFilterFields.indexOf(fieldName) < 0) {
                    persisted.filter[fieldName] = angular.copy(gridModel.filter[fieldName]);
                }
            }
        }
        return persisted;
    }
}

export class GridColumn {
    index: number;
    position: number;
    title: string;
    visible: boolean = true;
    alwaysVisible: boolean = false;
    fixedColumn: boolean = false;

    public reverseVisibility() {
        this.visible = !this.visible;
    }
}

export class GridModel<T> implements IGridModel<T> {
    public static DEFAULT_PAGE_SIZE: number = 10;
    public static MAX_USER_PAGE_SIZE: number = 5000;
    public static MAX_PAGE_SIZE: number = 2147483647;

    public entries: Array<T> = [];
    public sorting: Array<SortEntry> = [];
    public gridColumns: Array<GridColumn> = [];
    public visibleColumnsCount: number;
    public totalCount = 0;
    public totalCountRevised = 0;
    public pageMode: string;
    public requestsPageMode: string = PageMode[PageMode.ENTRIES_AND_TOTAL_ESTIMATES];
    public totalPageCount = 1;
    public currentPage = 0;
    public selectedPage = 1;
    public pageSize = GridModel.DEFAULT_PAGE_SIZE;
    public filter = {};
    public nonPersistedFilterFields: Array<string> = ["searchString"];
    public isLoading = false;
    public isTotalLoading = false;
    private latestRevisedTotalFilter: any;

    private loadListeners: Array<IGridLoadingListener> = [];
    private columnsListeners: Array<IColumnVisibilityChangeListener> = [];
    private pageSizeListeners: Array<IPageSizeChangeListener> = [];

    private loadEntitiesFunction: (gridModel: IGridModel<T>) => angular.IPromise<IPage<T>>;

    constructor(loadEntitiesApi: (gridModel: IGridModel<T>) => angular.IPromise<IPage<T>>) {
        this.loadEntitiesFunction = loadEntitiesApi;
    }

    public setDefaultPageSize(): GridModel<T> {
        this.pageSize = GridModel.DEFAULT_PAGE_SIZE;
        return this;
    }

    public setMaxPageSize(): GridModel<T> {
        this.pageSize = GridModel.MAX_PAGE_SIZE;
        return this;
    }

    public addSortEntry(sortEntry: SortEntry): GridModel<T> {
        this.sorting.push(sortEntry);
        return this;
    }

    public addLoadingListener(listener: IGridLoadingListener) {
        this.loadListeners.push(listener);
    }

    public clearLoadListeners(){
        this.loadListeners.splice(0, this.loadListeners.length);
    }

    public addColumnVisibilityListener(listener: IColumnVisibilityChangeListener) {
        this.columnsListeners.push(listener);
    }

    public addPageSizeListener(listener: IPageSizeChangeListener) {
        this.pageSizeListeners.push(listener);
    }

    public loadPage(pageNumber: number) {
        this.loadPageForFilter(pageNumber, this.filter);
    }

    public loadWithDelay(timeoutUtils: TimeoutUtils, uuid: any) {
        timeoutUtils.processWithDelay(uuid, a => this.loadPage(0), 500);
    }

    public loadCurrentPage() {
        this.loadPageForFilter(this.currentPage, this.filter)
    }

    public loadPageForFilter(pageNumber: number, filter: any, pageMode?: string) {
        this.isLoading = true;
        for (var i = 0; i < this.loadListeners.length; i++) {
            var listener: IGridLoadingListener = this.loadListeners[i];
            listener.startLoading();
        }
        this.currentPage = pageNumber;
        let initialRequestsMode = this.requestsPageMode;
        if (pageMode) {
            this.requestsPageMode = pageMode;
        }
        if (this.selectedPage !== pageNumber + 1) {
            this.selectedPage = pageNumber + 1;
        }
        this.filter = filter; //todo:
        this.loadEntitiesFunction(this)
            .then(result => {
                this.totalCount = result.totalCount;
                if (result.pageMode !== "TOTAL") {
                    this.entries = result.entries;
                    this.pageMode = result.pageMode;
                }
                if (result.pageMode === "ENTRIES_AND_TOTAL" || result.pageMode === "TOTAL") {
                    this.totalCountRevised = result.totalCount;
                    this.latestRevisedTotalFilter = angular.copy(this.filter);
                    this.isTotalLoading = false;
                } else if (result.pageMode === "ENTRIES_AND_TOTAL_ESTIMATES"
                    && !angular.equals(filter, this.latestRevisedTotalFilter)) {
                    this.totalCountRevised = null;
                    this.latestRevisedTotalFilter = null;
                }
                this.calculateTotalPages();
                for (const listener of this.loadListeners) {
                    listener.endLoading();
                }
                this.isLoading = false;
            });
        if (pageMode) {
            this.requestsPageMode = initialRequestsMode;
        }
    }

    public loadRecordsCount() {
        this.latestRevisedTotalFilter = angular.copy(this.filter);
        this.isTotalLoading = true;
        let initialRequestsMode = this.requestsPageMode;
        this.requestsPageMode = "TOTAL";
        this.loadEntitiesFunction(this)
            .then((result:IPage<T>) => {
                this.totalCount = result.totalCount;
                if (result.pageMode === "TOTAL") {
                    this.totalCountRevised = result.totalCount;
                }
                this.isTotalLoading = false;
                this.calculateTotalPages();
            });
        this.requestsPageMode = initialRequestsMode;
    }

    public getPageSize() {
        return this.pageSize && !isNaN(this.pageSize) ? this.pageSize : GridModel.DEFAULT_PAGE_SIZE;
    }

    public recalculateVisibleColumnsCount() {
        var count = 0;
        for (var i = 0; i < this.gridColumns.length; i++) {
            var column: GridColumn = this.gridColumns[i];
            if (column.alwaysVisible || column.visible) {
                count++;
            }
        }
        this.visibleColumnsCount = count;
    }

    public changeColumnVisibility(columnIndex: number) {
        var column: GridColumn = null;
        for (var i = 0; i < this.gridColumns.length; i++) {
            column = this.gridColumns[i];
            if (column.index == columnIndex) {
                break;
            } else {
                column = null;
            }
        }
        if (column != null) {
            column.reverseVisibility();
            this.recalculateVisibleColumnsCount();
            for (var i = 0; i < this.columnsListeners.length; i++) {
                this.columnsListeners[i].onColumnVisibilityChange(column);
            }
        }
    }

    public onChangePageSize() {
        for (var i = 0; i < this.pageSizeListeners.length; i++) {
            this.pageSizeListeners[i].onChangePageSize();
        }
    }

    private calculateTotalPages() {
        let knownToUsTotalCount = this.totalCountRevised ? this.totalCountRevised : this.totalCount;
        const totalPages = this.pageSize < 1 ? 1 : Math.ceil(knownToUsTotalCount / this.pageSize);
        this.totalPageCount = Math.max(totalPages || 0, 1);
    }
}