import {StringBuilder} from "./../utils/StringBuilder";
import {ReferencedObjectType} from "./../ReferencedObjectType";
import {ObjectTypeUtils} from "./../utils/ObjectTypeUtils";
import {BaseRouter} from "./BaseRouter";

export class FinancesRouter extends BaseRouter {
    public static NAME = "urls.FinancesRouter";

    public static OBJECT_ID_PARAM = "objectId";
    public static OBJECT_TYPE_PARAM = "objectType";

    public static balanceRoute = "/finances/balance";
    public static purchaseHistoryRoute = "/finances/purchaseHistory";
    public static accountManagementRoute = "/finances/accountManagement";
    public static purchaseRoute = `/purchase/:${FinancesRouter.OBJECT_TYPE_PARAM}/:${FinancesRouter.OBJECT_ID_PARAM}`;

    public static $inject = [
        "$location"
    ];

    constructor($location: ng.ILocationService) {
        super($location);
    }

    public balanceUrl(): string {
        return this.url(FinancesRouter.balanceRoute);
    }

    public purchaseHistoryUrl(): string {
        return this.url(FinancesRouter.purchaseHistoryRoute);
    }

    public accountManagementUrl(): string {
        return this.url(FinancesRouter.accountManagementRoute);
    }

    public purchaseUrl(objectId: number, objectType: ReferencedObjectType): string {
        return this.url(StringBuilder.interpolate(FinancesRouter.purchaseRoute)
            .with(':' + FinancesRouter.OBJECT_ID_PARAM, objectId)
            .with(':' + FinancesRouter.OBJECT_TYPE_PARAM, ObjectTypeUtils.getReferencedObjectTypeAsString(objectType).toLowerCase())
            .build());
    }
}