import {MaterialType, MaterialTypeEnum} from "./../materials/models/MaterialType"

export class ContentUtils {

    static MAP_FILE_EXTENSIONS_TO_MATERIAL_TYPES = {

        'mp3': MaterialTypeEnum.AUDIO,

        'mp4': MaterialTypeEnum.VIDEO,

        'jpg': MaterialTypeEnum.PHOTO,
        'png': MaterialTypeEnum.PHOTO,

        'txt': MaterialTypeEnum.DOCUMENTS,
        'doc': MaterialTypeEnum.DOCUMENTS,
        'docx': MaterialTypeEnum.DOCUMENTS,
        'ppt': MaterialTypeEnum.DOCUMENTS,
        'pptx': MaterialTypeEnum.DOCUMENTS,
        'pdf': MaterialTypeEnum.DOCUMENTS,
        'xls': MaterialTypeEnum.DOCUMENTS,
        'xlsx': MaterialTypeEnum.DOCUMENTS
    };

    public static getFileExt(fileName: string): string {
        return fileName.substr((~-fileName.lastIndexOf(".") >>> 0) + 2);
    }

    public static checkFileExt(materialTypeId: number, fileExt: string): boolean {
        return (ContentUtils.MAP_FILE_EXTENSIONS_TO_MATERIAL_TYPES[fileExt] === materialTypeId);
    }

    public static findMaterialTypeByFileExtension(fileExt: string): number {
        return ContentUtils.MAP_FILE_EXTENSIONS_TO_MATERIAL_TYPES[fileExt];
    }
}