import {GroupEnum} from "./models/IGroup";
import {IUser, PredefinedUsersEnum} from "./models/IUser";
import {UserService} from "./UserService";
import {ISystemLocale} from "./../settings/models/ISystemLocale";
import {SettingsService} from "./../settings/SettingsService";
import {AuthService} from "./../auth/AuthService";
import {AuthRouter} from "./../urls/AuthRouter";
import {AuthRedirect} from "./../authcore/AuthRedirect";
import {MenuHelper} from "./../utils/MenuHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {notifier} from "./../INotifier";
import {IUserLogon} from "./models/IUserLogon";
import {ILogonProvider} from "./../auth/ILogonProvider";
import {MainRouter} from "./../urls/MainRouter";
import {SecurityService} from "./../security/SecurityService";
import {LogonProviderEnum} from "./../security/LogonProviderEnum";
import {StorageWrapper, Storages} from "./../infrastructure/Storage";
import {UrlService} from "../utils/UrlService";

interface IUserExtended extends IUser {
    userLogons?: Array<IUserLogon>;
}

export class UserSignupController {
    public static NAME = "users.UserSignupController";
    private LAST_USED_PROVIDER_STORAGE_KEY = "vhs.application.logon.last.used.provider";
    private PROVIDER_STORAGE_KEY_LOCAL = "LOCAL";
    private EN_US_LOCALE = "en_US";

    public userLogon: IUserLogon = {
        name: "",
        password: "",
        providerKey: LogonProviderEnum[LogonProviderEnum.LOCAL],
        isActive: true
    };
    public user: IUserExtended = {
        fullName: "",
        isActive: true,
        newGroups: [GroupEnum.REGISTERED_USERS],
        locale: null,
        userLogons: [this.userLogon]
    };
    public confirmEmail: string;
    public confirmPassword: string;
    public disableSaveButton: boolean = false;
    public error: string;

    public static $inject = [
        "$location",
        "$window",
        UserService.NAME,
        SettingsService.NAME,
        AuthService.NAME,
        AuthRouter.NAME,
        AuthRedirect.NAME,
        MainRouter.NAME,
        SecurityService.NAME,
        Storages.LOCAL_NAME,
        UrlService.NAME
    ];

    constructor(private $location: angular.ILocationService,
                private $window: angular.IWindowService,
                private userApi: UserService,
                private settingsApi: SettingsService,
                private authApi: AuthService,
                private authRouter: AuthRouter,
                private authRedirect: AuthRedirect,
                private mainRouter: MainRouter,
                private securityService: SecurityService,
                private localStorage: StorageWrapper,
                private url: UrlService) {
        if (securityService.isAuthenticated()) {
            this.$location.url(this.mainRouter.mainUrl());
        }
        MenuHelper.setCurrentMenuItem(MenuHelper.MAIN_MENU_KEY);
        PageHeader.set().header("Self sign-up").update();

        $("#confirmMailField").bind('paste', () => false);
    }

    public save() {
        this.disableSaveButton = true;
        this.confirmEmail = null;
        this.confirmPassword = null;
        this.userLogon.name = this.user.email;
        this.user.fullName = this.user.firstName + "\u00A0" + this.user.lastName;

        let promise = this.userApi.selfSignup(this.user);
        promise.then(() => {
            this.disableSaveButton = false;
            notifier.success("User '" + this.user.fullName + "' has been saved");
            //this.authRedirect.clearRedirection();  //AOPA: go to course after signup
            this.logOn();
        }).catch(response => {
            this.disableSaveButton = false;
            if (response.status == 409 && response.data.error) {
                switch (response.data.error) {
                    case "error.user.duplicate.name":
                        this.error = "Full name " + this.user.fullName + " already exists";
                        break;
                    case "error.user.duplicate.email":
                        this.error = "Email " + this.user.email + " already exists";
                        break;
                    case "error.duplicate.logon.provider":
                        this.error = "Only one logon for each type is allowed";
                        break;
                    default:
                        if (response.data.error.indexOf("error.duplicate.login.for.provider.") === 0) {
                            var providerKey = response.data.error.substring("error.duplicate.login.for.provider.".length);
                            var logonFound = null;
                            for (var j = 0; j < this.user.userLogons.length; j++) {
                                if (this.user.userLogons[j].providerKey == providerKey) {
                                    logonFound = this.user.userLogons[j].name;
                                    break;
                                }
                            }
                            if (logonFound) {
                                this.error = "Username " + logonFound + " already exists";
                            } else {
                                this.error = "Query has failed: please contact your system administrator or help desk";
                            }
                        } else {
                            this.error = "Query has failed: please contact your system administrator or help desk";
                        }
                }
            }
            notifier.error(this.error);
        });
    }

    public logOn(): void {
        this.localStorage.put(this.LAST_USED_PROVIDER_STORAGE_KEY, this.PROVIDER_STORAGE_KEY_LOCAL);
        this.authApi.logOn(this.PROVIDER_STORAGE_KEY_LOCAL + ":" + this.userLogon.name, this.userLogon.password).then(user => {
            this.authRedirect.returnAfterLogOn(false /*native*/);
        });
    }

    public doCancel(): void {
        if (this.$window.history.length > 1) {
            this.$window.history.back();
        } else {
            this.$location.url(this.mainRouter.mainUrl());
        }
    }
}
