import {NewsRouter} from "./../urls/NewsRouter";
import {NewsService} from "./NewsService";

import {MenuHelper} from "./../utils/MenuHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {IGridModel, GridModel} from "./../IGridModel";
import {HttpGridHelper} from "./../IHttpGridService";
import {Moment} from "./../utils/Moment";
import {notifier} from "./../INotifier";

import {AuthManager} from "./../authcore/AuthManager";
import {UrlService} from "./../utils/UrlService";
import {PermissionsService} from "./../security/PermissionsService";
import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";
import {SecurityService} from "./../security/SecurityService";
import {PersonalService} from "./../personal/PersonalService";

import {INews} from "./models/INews"

export class NewsSearchController {
    public static NAME = "news.NewsSearchController";

    private systemPermissions: ISystemPermissions;

    public isAuthenticated: boolean;

    public newsType: string;
    public newsGridModel: IGridModel<INews>;
    public subscribe: boolean = false;

    public static $inject = [
        "$http",
        "$location",
        "$routeParams",
        AuthManager.NAME,
        UrlService.NAME,
        PermissionsService.NAME,
        SecurityService.NAME,
        PersonalService.NAME,
        NewsService.NAME,
        NewsRouter.NAME
    ];

    constructor($http: angular.IHttpService,
                private $location: angular.ILocationService,
                private $routeParams: angular.route.IRouteParamsService,
                private authManager: AuthManager,
                private url: UrlService,
                private permissionsService: PermissionsService,
                private securityService: SecurityService,
                private personalService: PersonalService,
                private newsService: NewsService,
                private newsRouter: NewsRouter) {
        this.isAuthenticated = this.authManager.isAuthenticated();
        this.newsType = $routeParams[NewsRouter.NEWS_TYPE_PARAM];

        switch (this.newsType) {
            case "all":
                MenuHelper.setCurrentMenuItem(MenuHelper.ALL_NEWS_MENU_KEY);
                PageHeader.set().header("All News").update();
                break;
            case "students":
                MenuHelper.setCurrentMenuItem(MenuHelper.STUDENTS_OFFERS_MENU_KEY);
                PageHeader.set().header("News for Students").update();
                this.securityService.demandAuthentication();
                break;
        }

        permissionsService.getSystemPermissions().then(p => {
            PageHeader.set().preserveHeader()
                .addActionLinkIf(p.addNews, '+&nbsp;New',
                this.newsRouter.oneNewsCreateUrl()).update();
            this.systemPermissions = p;
        });

        this.newsGridModel = new GridModel(HttpGridHelper.createLoader<INews>($http, this.url.service("/api/news")));
        this.newsGridModel.filter.type = this.newsType.toUpperCase();
        this.newsGridModel.loadCurrentPage();

        this.checkSubscription();
    }

    public openOneNews(oneNewsId: number) {
        this.$location.url(this.newsRouter.oneNewsUrl(oneNewsId));
    }

    public applyFilter() {
        this.newsGridModel.filter.dateFrom = Moment.textToJavaDate(this.newsGridModel.filter.dateFromText);
        this.newsGridModel.filter.dateTo = Moment.textToJavaDate(this.newsGridModel.filter.dateToText);
        this.newsGridModel.loadCurrentPage();
    }

    public clearFilter() {
        this.newsGridModel.filter.searchString = null;
        this.newsGridModel.filter.tags = null;
        this.newsGridModel.filter.authorName = null;
        this.newsGridModel.filter.dateFrom = null;
        this.newsGridModel.filter.dateFromText = null;
        this.newsGridModel.filter.dateTo = null;
        this.newsGridModel.filter.dateToText = null;
        this.newsGridModel.loadCurrentPage();
    }

    public checkSubscription() {
        this.newsService.checkNewsSubscription(this.newsType.toUpperCase()).then(result => {
            this.subscribe = result;
        })
    }

    public changeNewsSubscription() {
        this.personalService.changeNewsSubscription(this.newsType.toUpperCase()).then(result => {
            result
                ? this.newsType == "all"
                ? notifier.success("You successfully subscribed to all news")
                : notifier.success("You successfully subscribed to news for students")
                : this.newsType == "all"
                ? notifier.success("You successfully unsubscribed from all news")
                : notifier.success("You successfully unsubscribed from news for students");
            this.subscribe = result;
        })
    }
}
