interface IMenuItem {
    key?: string;
    name?: string;
    url?: string;
    urlFunc?: Function;
    routeParamKey?: string;
    allowed?: boolean;
    allowFunc?: (p) => boolean;
    items?: Array<IMenuItem>;
}

interface IMenuGroup {
    key?: string;
    imgKey?: string;
    name?: string;
    url?: string;
    items?: Array<IMenuItem>;
    allowed?: boolean;
}

export interface IBreadcrumb {
    text: string;
    url: string;
}

