    function link($scope: angular.IScope, element: angular.IAugmentedJQuery,
                  attributes: angular.IAttributes, controller: PersonalMenuController,
                  transclude: angular.ITranscludeFunction) {
    }

    var directive: any = {};
    directive.restrict = "A";
    directive.templateUrl = url.template("components/menu/navigation-personal-menu.html");
