import {AuthService} from "./AuthService";
import {AuthManager} from "./../authcore/AuthManager";
import {AuthRedirect} from "./../authcore/AuthRedirect";
import {AuthRouter} from "./../urls/AuthRouter";
import {AppProperties} from "./../utils/AppProperties";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {MenuHelper} from "./../utils/MenuHelper";
import {ILogonProvider} from "./ILogonProvider";
import {Storages, StorageWrapper} from "./../infrastructure/Storage";
import {LogonProviderEnum} from "./../security/LogonProviderEnum";
import {UsersRouter} from "./../urls/UsersRouter";
import {UrlService} from "../utils/UrlService";
import {InfoRouter} from "./../urls/InfoRouter";
import {RenewalType} from "./RenewalType";
import IRouteService = angular.route.IRouteService;

export class LogOnController {
    public static NAME = "auth.LogOnController";

    private LAST_USED_PROVIDER_STORAGE_KEY = "vhs.application.logon.last.used.provider";

    public username: string;
    public password: string;
    public error: string;
    public inProgress: boolean;
    public restore: boolean;
    public accessDenied: boolean;
    public tokenExpired: boolean;
    public logonRequired: boolean;
    public renewalType: string;

    public providerKey: string;
    public providers: Array<ILogonProvider>;
    public internalProviders: Array<ILogonProvider>;
    public localProviderAvailable: boolean;
    public activeDirectoryLocalProviderAvailable: boolean;
    public activeDirectorySsoProviderAvailable: boolean;
    public noSpnego: boolean;

    public static $inject = [
        "$timeout",
        "$log",
        "$route",
        "$sce",
        UrlService.NAME,
        AuthRedirect.NAME,
        AuthService.NAME,
        AuthManager.NAME,
        "$location",
        Storages.LOCAL_NAME,
        UsersRouter.NAME,
        AuthRouter.NAME,
        InfoRouter.NAME
    ];

    constructor(private $timeout: angular.ITimeoutService,
                private $log: angular.ILogService,
                private $route: IRouteService,
                private $sce: angular.ISCEService,
                private url: UrlService,
                private authRedirect: AuthRedirect,
                private authApi: AuthService,
                private authManager: AuthManager,
                private $location: angular.ILocationService,
                private localStorage: StorageWrapper,
                public usersRouter: UsersRouter,
                public authRouter: AuthRouter,
                public infoRouter: InfoRouter) {
        this.inProgress = false;
        this.restore = false;
        this.error = null;
        this.renewalType = $location.search()[AuthRouter.RENEWAL_TYPE_PARAM_NAME];
        if (!RenewalType[this.renewalType]) {
            this.renewalType = RenewalType[RenewalType.NEW];
            this.$location.search(AuthRouter.RENEWAL_TYPE_PARAM_NAME, this.renewalType);
        }
        this.accessDenied = $location.search()[AuthRouter.ACCESS_DENIED_PARAM_NAME];
        this.tokenExpired = $location.search()[AuthRouter.TOKEN_EXPIRED_PARAM_NAME];
        this.logonRequired = $location.search()[AuthRouter.LOGON_REQUIRED_PARAM_NAME];

        this.noSpnego = this.localStorage.contains("vhs.application.no.spnego");
        var spnegoLoginFailed = this.localStorage.contains("vhs.application.spnego.login.failed");
        var spnegoLoginNotSupported = this.localStorage.contains("vhs.application.spnego.login.not.supported");
        if (spnegoLoginNotSupported) {
            this.error = "Automatic Windows login is not supported";
            this.localStorage.remove("vhs.application.spnego.login.not.supported");
        } else if (spnegoLoginFailed) {
            this.error = "Automatic Windows login failed";
            this.localStorage.remove("vhs.application.spnego.login.failed");
        }


        MenuHelper.setCurrentMenuItem("");
        PageHeader.set().header("BasicMed Online Course Login").update();
        if (this.authManager.restoreAvailable()) {
            this.restoreSession();
        } else {
            this.loadProviders();
        }
    }

    private loadProviders() {
        this.authApi.getProvidersForLogon()
            .then(result => this.initProviders(result));
    }

    private initProviders(providers: Array<ILogonProvider>) {
        this.providers = providers;
        this.internalProviders = [];

        this.localProviderAvailable = false;
        this.activeDirectoryLocalProviderAvailable = false;
        this.activeDirectorySsoProviderAvailable = false;

        angular.forEach(this.providers, provider => {
            if (provider.key === LogonProviderEnum[LogonProviderEnum.LOCAL]) {
                this.localProviderAvailable = true;
                this.internalProviders.push({key: provider.key, name: "Internal users"});
            } else if (provider.key === LogonProviderEnum[LogonProviderEnum.AD_LOCAL]) {
                this.activeDirectoryLocalProviderAvailable = true;
                this.internalProviders.push({key: provider.key, name: "ActiveDirectory users"});
            } else if (provider.key === LogonProviderEnum[LogonProviderEnum.AD_SSO]) {
                this.activeDirectorySsoProviderAvailable = true;
            } else if (provider.key === LogonProviderEnum[LogonProviderEnum.PERSONIFY_USERPASSWORD] ||
                        provider.key === LogonProviderEnum[LogonProviderEnum.LOCAL_OR_PERSONIFY]) {
                this.localProviderAvailable = true;
                this.internalProviders.push({key: provider.key, name: provider.name})
            }
        });

        if (this.internalProviders.length === 0) {
            this.providerKey = null;
        } if (this.internalProviders.length > 0) {
            var previousProvider: string = this.localStorage.get<string>(this.LAST_USED_PROVIDER_STORAGE_KEY);
            var previousProviderExists = false;
            if (previousProvider) {
                angular.forEach(this.internalProviders, provider => {
                    if (provider.key == previousProvider) {
                        previousProviderExists = true;
                    }
                });
            }
            if (previousProvider && previousProviderExists) {
                this.providerKey = previousProvider;
            } else {
                this.providerKey = this.internalProviders[0].key;
            }
        }

        this.tryToNegotiate();
    }

    public tryToNegotiate() {
        if (this.activeDirectorySsoProviderAvailable && !this.noSpnego) {
            console.log("Try to SPNEGO");
            this.restore = true;
            this.authRedirect.forwardToSpnego();
        }
    }

    public forceNegotiation() {
        if (this.activeDirectorySsoProviderAvailable) {
            this.authManager.enableSpnego();
            console.log("Try to SPNEGO (forced)");
            this.restore = true;
            this.authRedirect.forwardToSpnego();
        }
    }

    public logOn(): void {
        if (this.authManager.isPersonifyOrLocalEnabled()) {
            this.providerKey = LogonProviderEnum[LogonProviderEnum.LOCAL_OR_PERSONIFY];
        } else {
            this.providerKey = LogonProviderEnum[LogonProviderEnum.LOCAL];
        }
        this.error = "";
        this.inProgress = true;
        this.localStorage.put(this.LAST_USED_PROVIDER_STORAGE_KEY, this.providerKey);
        this.authApi.logOn(this.providerKey + ":" + this.username, this.password).then(user => {
            //todo: only 1 locale for AOPA
            var locale = user.locale ? user.locale : AppProperties.currentLocale;
            if (!LogOnController.reloadApplicationForLocaleIfNeeded(locale)) {
                this.authManager.enableSpnego();
                this.authRedirect.returnAfterLogOn(false /*native*/);
            }
            this.inProgress = false;
        })
            .catch(response => {
                this.inProgress = false;
                this.error = response.data["error"] === "invalid_user"
                    ? this.$sce.getTrustedHtml("Invalid Email or Password.<br/> Click <a href='{0}'>HERE</a> to reset your password.<br/><i>Reminder: The BasicMed Course is not linked to your AOPA account. You will need to create a separate account for the BasicMed Course.</i>".replace("{0}",
                        this.buildRestorePasswordLinkWithUserNameParam()
                    ))
                    : "Query has failed: please contact your system administrator or help desk"
            });
    }

    public restorePassword() {
        this.$location.$logonName = this.username;
        this.$location.url(this.authRouter.restorePassword());
    }

    private buildRestorePasswordLinkWithUserNameParam(): string {
        let url = this.authRouter.restorePassword();
        if (url.indexOf("?") === -1) {
            url += "?";
        }
        return "#" + url + "&" + AuthRouter.LOGON_PARAM_NAME + "=" + this.username;
    }

    private restoreSession() {
        var refreshToken = this.authManager.getToken().refreshToken;
        this.restore = true;
        this.authApi.restoreSession(refreshToken).then(user => {
            // var locale = user.locale ? user.locale : AppProperties.currentLocale;
            // if (!LogOnController.reloadApplicationForLocaleIfNeeded(locale)) {
                this.authRedirect.returnAfterLogOn(false /*native*/);
            // }
            // this should prevent stuck if redirect was not performed (i.e. redirect was to logon url)
            this.restore = false;
            this.loadProviders();
        })
            .catch(response => {
                this.restore = false;
                this.authManager.logOff();
                // TODO: after logging off
                this.loadProviders();
            });
    }

    public changeRenewalType(renewalsType: RenewalType) {
        if(RenewalType[renewalsType] != undefined) {
            this.renewalType = renewalsType.toString();
            this.$location.search(AuthRouter.RENEWAL_TYPE_PARAM_NAME, this.renewalType);
        }
    }

    public static reloadApplicationForLocaleIfNeeded(userLocale: string): boolean {
        //if (AppProperties.currentLocale != userLocale) {
        //    var LOCALE_STORAGE_KEY = "vhs.application.current.locale";
        //    localStorage.setItem(LOCALE_STORAGE_KEY, userLocale);
        //    numeral.language(userLocale.substr(0, 2)); // e.g. 'en' or 'ru'
        //    window.location.reload(false);
        //    return true;
        //} else {
            return false;
        //}
    }
}
