import {SettingsService} from "./SettingsService";
import {ISystemCommonSettings} from "./models/ISystemCommonSettings";
import {ISystemLocale} from "./models/ISystemLocale";
import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";
import {PermissionsService} from "./../security/PermissionsService";
import {UrlService} from "./../utils/UrlService";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {MenuHelper} from "./../utils/MenuHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {notifier} from "./../INotifier";
import {SystemSettingsRouter} from "./../urls/SystemSettingsRouter";

export class CommonSettingsController {
    public static NAME = "settings.CommonSettingsController";

    public settings: ISystemCommonSettings = {};
    public systemPermissions: ISystemPermissions;
    public availableLocales: Array<ISystemLocale>;

    public static $inject = [
        UrlService.NAME,
        "$http",
        SettingsService.NAME,
        PermissionsService.NAME,
        SystemSettingsRouter.NAME
    ];

    constructor(private url: UrlService,
                private $http: angular.IHttpService,
                private settingsApi: SettingsService,
                permissionsApi: PermissionsService,
                public systemSettingsRouter: SystemSettingsRouter) {
        MenuHelper.setCurrentMenuItem(MenuHelper.ADMINISTRATION_COMMON_SETTINGS_MENU_KEY);
        PageHeader.set().header("General system settings").update();
        permissionsApi.getSystemPermissions().then(s => {
            this.systemPermissions = s;
        });

        this.loadSettings();
        this.loadAvailableLocales();
    }

    public loadSettings(): void {
        this.settingsApi.getSystemCommonSettings().then(p => {
            angular.copy(p, this.settings);
        });
    }

    public saveSettings() {
        this.settingsApi.update(this.settings).then(
                r => {
                notifier.success("Settings have been saved");
                angular.copy(r, this.settings);
            }).catch(r => {
                notifier.error(r.data.message);
            }
        );
    }

    private loadAvailableLocales() {
        this.settingsApi.getAvailableLocales().then(locales => {
            this.availableLocales = locales;
        });
    }
}