import {NewsRouter} from "./../urls/NewsRouter";
import {NewsService} from "./NewsService";

import {UrlService} from "./../utils/UrlService";
import {SocialService} from "./../social/SocialService";
import {SecurityService} from "./../security/SecurityService";

import {AuthManager} from "./../authcore/AuthManager";
import {AuthRedirect} from "./../authcore/AuthRedirect";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {MenuHelper} from "./../utils/MenuHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {ITreeElementInfo} from "./../tree/models/TreeElementInfo";
import {TreeElementsInfoFiller} from "./../tree/TreeElementsInfoFiller";
import {IGridModel, GridModel, IGridLoadingListener} from "./../IGridModel";
import {HttpGridHelper} from "./../IHttpGridService";
import {notifier} from "./../INotifier";

import {INews} from "./models/INews";
import {IComment} from "./../social/models/IComment";
import {ICommentInHierarchy} from "./../social/models/ICommentInHierarchy";
import {ReferencedObjectType} from "./../ReferencedObjectType";
import {PermissionsService} from "../security/PermissionsService";
import {ISystemPermissions} from "../security/permissions/ISystemPermissions";

interface ICommentInHierarchyExtended extends ITreeElementInfo<ICommentInHierarchyExtended>, ICommentInHierarchy {
    reply?: boolean;
}

export class OneNewsController implements IGridLoadingListener {
    public static NAME = "news.OneNewsController";

    private newsId: number;
    public news: INews = {};
    public commentsGridModel: IGridModel<ICommentInHierarchyExtended>;
    public newComment: IComment = {};
    public newReplyComment: IComment = {};
    public anchorComplete: boolean;
    public commentsCount: number;

    public isAuthenticated: boolean;
    public systemPermissions: ISystemPermissions;

    public static $inject = [
        "$http",
        "$anchorScroll",
        "$timeout",
        "$location",
        "$routeParams",
        UrlService.NAME,
        AuthManager.NAME,
        AuthRedirect.NAME,
        NewsService.NAME,
        SocialService.NAME,
        SecurityService.NAME,
        NewsRouter.NAME,
        PermissionsService.NAME
    ];

    constructor($http: angular.IHttpService,
                private $anchorScroll: angular.IAnchorScrollService,
                private $timeout: angular.ITimeoutService,
                private $location: angular.ILocationService,
                private $routeParams: angular.route.IRouteParamsService,
                private url: UrlService,
                private authManager: AuthManager,
                public authRedirect: AuthRedirect,
                public newsService: NewsService,
                public socialService: SocialService,
                private securityService: SecurityService,
                public newsRouter: NewsRouter,
                private permissionsApi: PermissionsService) {
        this.newsId = $routeParams[NewsRouter.ONE_NEWS_ID_PARAM];
        this.isAuthenticated = this.authManager.isAuthenticated();
        this.anchorComplete = false;
        if (this.isAuthenticated) {
            this.permissionsApi.getSystemPermissions().then(systemPermissions => {
                this.systemPermissions = systemPermissions;
            });
        }

        MenuHelper.setCurrentMenuItem(MenuHelper.ALL_NEWS_MENU_KEY);

        this.loadNews();

        this.commentsGridModel = new GridModel(HttpGridHelper.createLoader<ICommentInHierarchyExtended>($http,
            this.url.base("/api/social/comments?objectId=:objectId&objectType=:objectType")
                .param(":objectId", this.newsId)
                .param(":objectType", ReferencedObjectType[ReferencedObjectType.NEWS])
                .service()));
        this.commentsGridModel.addLoadingListener(this);
        this.loadCommentsCount();
    }

    public startLoading() {
        //do nothing
    }

    public endLoading() {
        TreeElementsInfoFiller.fillHierarchyInfoWithMaxLevel(this.commentsGridModel.entries, Number.MAX_VALUE);
        this.$timeout(()=> {
            this.scrollAnchor();
        }, 0);

    }

    public scrollAnchor() {
        if (!this.anchorComplete) {
            this.$anchorScroll(this.$location.hash());
            this.anchorComplete = true;
        }
    }

    public loadNews() {
        this.newsService.getNews(this.newsId).then(n => {
            angular.copy(n, this.news);
            if (n.type == "STUDENTS") {
                MenuHelper.setCurrentMenuItem(MenuHelper.STUDENTS_OFFERS_MENU_KEY);
                this.securityService.demandAuthentication();
            }
            PageHeader.set().header(n.title).update();
        });
    }

    private loadCommentsCount() {
        this.socialService.getCommentsCount(this.newsId, ReferencedObjectType.NEWS).then(result => {
            this.commentsCount = result;
        });
    }

    public addComment(reply: boolean, parentId: number) {
        if (reply) {
            this.newReplyComment.parentId = parentId;
        }
        this.socialService.addComment(this.newsId, ReferencedObjectType.NEWS, reply ? this.newReplyComment : this.newComment).then(result => {
            notifier.success("Comment has been added");
            this.commentsGridModel.loadCurrentPage();
            this.newComment.text = "";
            this.newReplyComment.text = "";
            this.newReplyComment.parentId = null;
            this.loadCommentsCount();
        });
    }

    public cleanComment() {
        this.newComment.text = "";
        this.newReplyComment.text = "";
        this.newReplyComment.parentId = null;
    }

    public replyComment(commentId: number) {
        this.commentsGridModel.entries.forEach(c => {
            c.reply = c.id == commentId;
        });
    }
}
