import {UrlService} from "./../utils/UrlService";
import {IRatingInfo} from "./models/IRatingInfo";
import {IComment} from "./models/IComment";
import {ReferencedObjectType} from "./../ReferencedObjectType";

export class SocialService {
    public static NAME = "social.SocialService";

    public static $inject = [
        "$http",
        UrlService.NAME
    ];

    constructor(private $http: angular.IHttpService,
                private url: UrlService) {
    }

    public changeObjectRating(objectId: number, objectType: ReferencedObjectType, isLike: boolean): angular.IPromise<IRatingInfo> {
        return this.$http.post(this.url.base("/api/social/rating?objectId=:objectId&objectType=:objectType&isLike=:isLike")
            .param(":objectId", objectId)
            .param(":objectType", ReferencedObjectType[objectType])
            .param(":isLike", isLike)
            .service(), null)
            .then(result => result.data);
    }

    public addComment(objectId: number, objectType: ReferencedObjectType, comment: IComment) {
        return this.$http.post(this.url.base("/api/social/comments?objectId=:objectId&objectType=:objectType")
            .param(":objectId", objectId)
            .param(":objectType", ReferencedObjectType[objectType])
            .service(), comment)
            .then(result => result.data);
    }

    public getCommentsCount(objectId: number, objectType: ReferencedObjectType): angular.IPromise<number> {
        return this.$http.get(this.url.base("/api/social/comments/count?objectId=:objectId&objectType=:objectType")
            .param(":objectId", objectId)
            .param(":objectType", ReferencedObjectType[objectType])
            .service())
            .then(result => result.data);
    }
}
