import {BaseRouter} from "./BaseRouter";

export class BroadcastsRouter extends BaseRouter {
    public static NAME = "urls.BroadcastsRouter";
    public static studentRoute = "/broadcasts/student";
    public static teacherRoute = "/broadcasts/teacher";

    public static $inject = [
        "$location"
    ];

    constructor($location: ng.ILocationService) {
        super($location);
    }

    public studentUrl(): string {
        return this.url(BroadcastsRouter.studentRoute);
    }

    public teacherUrl(): string {
        return this.url(BroadcastsRouter.teacherRoute);
    }
}
