import {StatisticReportsService} from "./StatisticReportsService";
import {ActionLogService} from "./ActionLogService";
import {MaterialViewReportController} from "./MaterialViewReportController";
import {MaterialDownloadReportController} from "./MaterialDownloadReportController";
import {MaterialUploadReportController} from "./MaterialUploadReportController";

import {CommonModule} from "./../common/CommonModule";
import {SecurityModule} from "./../security/SecurityModule";
import {UrlsModule} from "./../urls/UrlsModule";

export var StatisticsModule: angular.IModule = angular
    .module("statistics", [
        CommonModule.name,
        SecurityModule.name,
        UrlsModule.name
    ])
    .service(StatisticReportsService.NAME, StatisticReportsService)
    .service(ActionLogService.NAME, ActionLogService)
    .controller(MaterialViewReportController.NAME, MaterialViewReportController)
    .controller(MaterialDownloadReportController.NAME, MaterialDownloadReportController)
    .controller(MaterialUploadReportController.NAME, MaterialUploadReportController);