interface SelectPlaceholderAttributes extends angular.IAttributes {
    ngModel: string;
}

SelectPlaceholderDirectiveFactory.$inject = [
];

export function SelectPlaceholderDirectiveFactory(): ng.IDirective {
    function setPlaceholder(element: any) {
        let options = element.find("option:selected");
        let elem = angular.element(options[0]);
        if (elem.val() == "") {
            element.removeClass("simple-text-color");
            element.addClass("placeholder-color");
        } else {
            element.removeClass("placeholder-color");
            element.addClass("simple-text-color");
        }

        options = element.find("option");
        for (let pos = 0; pos < options.length; pos++) {
            let elem = angular.element(options[pos]);
            if (elem.val() != "") {
                elem.addClass("simple-text-color");
            } else {
                elem.addClass("placeholder-color");
            }
        }
    }

    function link($scope: angular.IScope, element: any, attributes: SelectPlaceholderAttributes) {
        $scope.$watch(attributes.ngModel, () => {
            setPlaceholder(element);
        });
    }

    var directive: ng.IDirective = {};
    directive.priority = 0;
    directive.require = ['ngModel'];
    directive.link = link;
    return directive;
}