import {UrlService} from "./../utils/UrlService";
import {IComment} from "./../social/models/IComment";
import {ReferencedObjectType} from "./../ReferencedObjectType";

export class PersonalService {
    public static NAME = "personal.PersonalService";

    public static $inject = [
        "$http",
        UrlService.NAME
    ];

    constructor(private $http: angular.IHttpService,
                private url: UrlService) {
    }

    public addToFavorites(objectId: number, objectType: ReferencedObjectType) {
        return this.$http.post(this.url.base("/api/personal/favorites?objectId=:objectId&objectType=:objectType")
            .param(":objectId", objectId)
            .param(":objectType", ReferencedObjectType[objectType])
            .service(), null)
            .then(result => result.data);
    }

    public removeFromFavorites(objectId: number, objectType: ReferencedObjectType) {
        return this.$http.delete(this.url.service("/api/personal/favorites"), {params: {
            objectId: objectId,
            objectType: ReferencedObjectType[objectType]
        }}).then(result => result.data);
    }

    public getUserCommentsForObject(objectId: number, objectType: ReferencedObjectType): angular.IPromise<Array<IComment>> {
        return this.$http.get(this.url.base("/api/personal/comments?objectId=:objectId&objectType=:objectType")
            .param(":objectId", objectId)
            .param(":objectType", ReferencedObjectType[objectType])
            .service())
            .then(result => result.data);
    }

    public changeAuthorSubscription(authorId: number): angular.IPromise<boolean> {
        return this.$http.post(this.url.base("/api/personal/subscribe/author?authorId=:authorId")
            .param(":authorId", authorId)
            .service(), null)
            .then(result => result.data);
    }

    public changeNewsSubscription(newsType: string): angular.IPromise<boolean> {
        return this.$http.post(this.url.service("/api/personal/subscribe/news"), newsType)
            .then(result => result.data);
    }

    public loadSubscriptions(): angular.IPromise<any> {
        return this.$http.get(this.url.service("/api/personal/subscriptions"))
            .then(result => result.data);
    }

    public changeSubscriptions(subscriptions: any) {
        return this.$http.post(this.url.service("/api/personal/subscribe/all"), subscriptions)
            .then(result => result.data);
    }

    public sendMessageToSupport(messageToSupport: string) {
        return this.$http.post(this.url.service("/api/personal/support"), messageToSupport)
            .then(result => result.data);
    }
}
