export class BaseReportController {

    constructor() {
        $(document).ready(() => {
            $['jsDate'].regional['ru'] = {
                monthNames: ['','','','','','','','','','','',''],
                monthNamesShort: ['','','','','','','','','','','',''],
                dayNames: ['','','','','','',''],
                dayNamesShort: ['','','','','','',''],
                formatString: '%d-%m-%Y %H:%M:%S'
            };
            document.documentElement.setAttribute('lang', 'en');
        });
    }
}