import {AuthManager} from "./../authcore/AuthManager";
import {CoursesRouter} from "./../urls/CoursesRouter";
import {MenuHelper} from "./../utils/MenuHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {PermissionsService} from "./../security/PermissionsService";
import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";
import {IGridModel, GridModel} from "./../IGridModel";
import {ICourse} from "./models/ICourse";
import {UrlService} from "./../utils/UrlService";
import {HttpGridHelper} from "./../IHttpGridService";
import {ObjectTypeUtils} from "./../utils/ObjectTypeUtils";
import {PersonalService} from "./../personal/PersonalService";
import {notifier} from "./../INotifier";
import {ReferencedObjectType} from "./../ReferencedObjectType";

export class CoursesController {
    public static NAME = "courses.CoursesController";

    private SUBJECT_MODULE: string = 'subject';
    private COMPLEXITY_LEVEL_MODULE: string = 'complexityLevel';
    private LANGUAGE_MODULE: string = 'language';
    private SOURCE_MODULE: string = 'source';
    private STUDENT_MODULE: string = 'student';
    private DEFAULT_TOP_MENU_ITEM: string = 'course';

    public selectedItemInModule: Map<string, number> = new Map<string, number>();
    public menuKey: any;
    public systemPermissions: ISystemPermissions;
    public isAuthenticated: boolean;
    public viewType: number;
    public rowShow: Map<number, boolean> = new Map<number, boolean>();
    public coursesGridModel: IGridModel<ICourse>;

    public static $inject = [
        "$http",
        "$location",
        CoursesRouter.NAME,
        PermissionsService.NAME,
        AuthManager.NAME,
        UrlService.NAME,
        PersonalService.NAME
    ];

    constructor($http: angular.IHttpService,
                private $location: ng.ILocationService,
                public coursesRouter: CoursesRouter,
                private permissionsApi: PermissionsService,
                private authManager: AuthManager,
                private url: UrlService,
                private personalService: PersonalService) {
        this.selectedItemInModule.set(this.SUBJECT_MODULE, 0);
        this.selectedItemInModule.set(this.COMPLEXITY_LEVEL_MODULE, 0);
        this.selectedItemInModule.set(this.LANGUAGE_MODULE, 0);
        this.selectedItemInModule.set(this.SOURCE_MODULE, 0);
        this.selectedItemInModule.set(this.STUDENT_MODULE, 1);

        this.viewType = 1;
        this.menuKey = {
            topMenu: this.DEFAULT_TOP_MENU_ITEM
        };
        this.isAuthenticated = this.authManager.isAuthenticated();

        MenuHelper.setCurrentMenuItem(MenuHelper.EDUCATION_COURSES_MENU_KEY);
        PageHeader.set().header("Courses").update();

        if (this.isAuthenticated) {
            this.permissionsApi.getSystemPermissions().then(systemPermissions => {
                this.systemPermissions = systemPermissions;
            });
        }

        this.coursesGridModel = new GridModel(HttpGridHelper.createLoader<ICourse>($http, this.url.service("/api/courses")));
        this.coursesGridModel.pageSize = 8;
    }

    public setSearch(module: string, item: number): void {
        if (this.selectedItemInModule.get(module) == item) {
            this.selectedItemInModule.set(module, 0);
        } else {
            this.selectedItemInModule.set(module, item);
        }
        this.searchCourse();
    }

    public isSearchItemActive(module: string, searchItem: number): boolean {
        return this.selectedItemInModule.get(module) == searchItem;
    }

    public isSearchItemHide(module: string, searchItem: number): boolean {
        return !!(this.selectedItemInModule.get(module) != 0 && this.selectedItemInModule.get(module) != searchItem);
    }

    public isTopMenuActive(topMenuKey: string): boolean {
        return this.menuKey.topMenu == topMenuKey;
    }

    public showDetails(index: number) {
        this.rowShow.set(index, !this.rowShow.get(index));
    }

    public loadCourse(courseId: number): void {
        this.$location.url(this.coursesRouter.detailsUrl(courseId));
    }

    public searchCourse(): void {
        this.coursesGridModel.filter.searchString = null;
        this.coursesGridModel.loadCurrentPage();
    }

    public addToFavorites(course: ICourse) {
        this.rowShow.set(course.id, !this.rowShow.get(course.id));
        this.personalService.addToFavorites(course.id, ReferencedObjectType.COURSE).then(result => {
            notifier.success("The course has been added to your favorites");
            course.favorite = true;
        }).catch(response => {
            if(response.data.error == "object.already.in.favorite") {
                notifier.error("This course is already in your favorites");
            }
        });
    }

    public removeFromFavorites(course: ICourse) {
        this.rowShow.set(course.id, !this.rowShow.get(course.id));
        this.personalService.removeFromFavorites(course.id, ReferencedObjectType.COURSE).then(result => {
            notifier.success("The course has been removed from your favorites");
            course.favorite = false;
        }).catch(response => {
            if(response.data.error == "object.not.in.favorite") {
                notifier.error("Failed to remove from your favorites");
            }
        });
    }
}
