import {StatisticReportResult} from "./../models/StatisticReportResult";
import {IPair} from "./../../IPair";

export interface IReportWriter {
    reporting(report: StatisticReportResult<any, Array<any>, any>, func: (value: Array<any>) => any): Array<any>;
}

export class LineDiagramsReportWriter implements IReportWriter {

    public reporting(report: StatisticReportResult<any, Array<any>, number>, func: (value: Array<any>) => any): Array<any> {
        let result: Array<Array<any>> = [];
        report.data.forEach((d: IPair<any, Array<any>>) => {
            let newValue: Array<any> = [];
            d.value.forEach((v: Array<any>) => {
                newValue.push(func(v));
            });
            result.push(newValue);
        });
        return result;
    }
}

