import {UrlService} from "../../utils/UrlService"

import {RandomUtils} from "../../utils/RandomUtils";
import {DomUtils} from "../../utils/DomUtils";
import {StringBuilder} from "../../utils/StringBuilder";

import {IBundleFile} from "../../bundles/models/IBundleFile";
import {BundleFileTypeEnum} from "../../bundles/models/BundleFileType";

import {VhsEmbeddedMaterialController} from "./../VhsEmbeddedMaterialDirective";
import {RichTextBindController} from "./RichTextBindDirective";
import {IFindRichTextBindControllerEventData} from "./../models/IFindRichTextBindControllerEventData";
import {Event} from "../models/Event";

export interface IVhsEmbeddedBundleDirectiveScope extends angular.IScope {
    fileKey?: string;
    vm?: VhsEmbeddedBundleController;
}

export class VhsEmbeddedBundleController {
    public static TA_ELEMENT_SELECTOR = "p.ta-vhs-embedded-bundle,img.ta-vhs-embedded-bundle";
    public static EMBEDDED_MATERIALS_IN_CONTENT_SELECTOR = "div.vhs-embedded-material";
    public static DOWNLOAD_BUNDLES_LINKS_SELECTOR = "a.vhs-download-bundle";
    public static TA_DIRECTIVE_HTML = "<div vhs-embedded-bundle vhs-file-key=':fileKey' :imageAttributes></div>";
    public static TA_RENDER_FUNCTION = (element: angular.IAugmentedJQuery,
                                        $compile: angular.ICompileService,
                                        $rootScope: angular.IRootScopeService,
                                        $timeout: angular.ITimeoutService) => {
        let id: string = RandomUtils.generateRandomId('vhsEmbeddedBundle_');
        let fileKey: string = element.is("img") ? element.attr('alt') : element.text();
        if (fileKey) {
           fileKey = fileKey.trim();
        }
        var imageAttributes = "";
        if (element.is("img")) {
            imageAttributes = "width='" + element.attr("width") + "' height='" + element.attr("height") + "'" +
                " style='" + element.attr("style") + "'";
        }
        element.replaceWith("<div id='" + id + "'></div>");
        $timeout(() => {
            $('#' + id).replaceWith($compile(
                StringBuilder.interpolate(VhsEmbeddedBundleController.TA_DIRECTIVE_HTML)
                    .with(":fileKey", fileKey)
                    .with(":imageAttributes", imageAttributes)
                    .build()
            )($rootScope));
        }, 0);
    };

    public fileKey: string;
    public bundleFile: IBundleFile;

    private element: angular.IAugmentedJQuery;

    private richTextBindController: RichTextBindController;

    public static $inject = [
        "$scope",
        "$rootScope",
        "$timeout",
        "$http",
        "$compile"
    ];

    constructor(private $scope: IVhsEmbeddedBundleDirectiveScope,
                private $rootScope: ng.IRootScopeService,
                private $timeout: angular.ITimeoutService,
                private $http: angular.IHttpService,
                private $compile: angular.ICompileService) {
        $scope.vm = this;
    }

    public init($scope: IVhsEmbeddedBundleDirectiveScope,
                element: angular.IAugmentedJQuery) {
        this.element = element;
        this.fileKey = $scope.fileKey;
        //load bundles after element add to DOM
        this.$timeout(() => {
            var eventData: IFindRichTextBindControllerEventData = {
                richTextElementId: this.findRichTextElementId(),
                richTextBindController: null
            };
            this.$rootScope.$broadcast(Event.TA_FIND_RICH_TEXT_BIND_CONTROLLER, eventData);
            this.richTextBindController = eventData.richTextBindController;
            this.loadBundle();
        }, 0);
    }

    public loadBundle() {
        if (!this.richTextBindController) {
            console.log('RichTextBind Controller not found!');
            return;
        }
        var promise: ng.IPromise<any> = this.richTextBindController.getBundleFile(this.fileKey)
            .then((bundleFile: IBundleFile) => {
                this.bundleFile = bundleFile;
                if (this.bundleFile && this.bundleFile.url) {
                    if (this.bundleFile.type == BundleFileTypeEnum[BundleFileTypeEnum.JS] || this.bundleFile.type == BundleFileTypeEnum[BundleFileTypeEnum.HTML]) {
                        return this.$http.get(this.bundleFile.url, this.getHttpConfig()).then(result => result.data);
                    } else if (this.bundleFile.type == BundleFileTypeEnum[BundleFileTypeEnum.CSS]) {
                        let mobileString = "";
                        if (this.bundleFile.url.lastIndexOf("_mobile.css") != -1){
                            mobileString = "media='handheld'";
                        }
                        this.element.replaceWith("<link rel='stylesheet' href='" + this.bundleFile.url + "' " + mobileString + " >");
                    } else if (this.bundleFile.type == BundleFileTypeEnum[BundleFileTypeEnum.IMAGE]) {
                        this.element.append("<img src='" + this.bundleFile.url + "' alt='' width='" +
                            this.element.attr("width") + "' height='" + this.element.attr("height") + "' style='" + this.element.attr("style") + "' />");
                    }
                } else {
                    console.log('Bundle file not found: ' + this.fileKey);
                }
                return null;
            });

        this.richTextBindController.loadContentSynchronized(promise)
            .then((content: string) => {
                if (content) {
                    this.processFileContent(content, this.bundleFile);
                }
            });
    }

    private processFileContent(fileContent: string, bundleFile: IBundleFile) {
        if (fileContent && bundleFile) {
            switch (bundleFile.type) {
                case BundleFileTypeEnum[BundleFileTypeEnum.JS]:
                    try {
                        $.globalEval('"use strict";' + fileContent);
                    } catch (ex) {
                        //do nothing
                    }
                    break;
                case BundleFileTypeEnum[BundleFileTypeEnum.HTML]:
                    var basePath = "";
                    try {
                        if (bundleFile.url && bundleFile.url.indexOf("/") >= 0) {
                            basePath = bundleFile.url.substr(0, bundleFile.url.lastIndexOf("/") + 1);
                        }
                        fileContent = this.replaceImagePaths(fileContent, basePath);
                    } catch (ex) {
                        //do nothing
                    }
                    var htmlContent: angular.IAugmentedJQuery = angular.element(fileContent);
                    this.element.replaceWith(htmlContent);
                    htmlContent.find(VhsEmbeddedBundleController.EMBEDDED_MATERIALS_IN_CONTENT_SELECTOR).each((index, el) => {
                        //console.log('embedded material found in content ' + el);
                        if ($(el).attr('id')) {
                            VhsEmbeddedMaterialController.TA_COMPILE_AND_REPLACE_ELEMENT_FUNCTION(el, $(el).attr('id'), this.$compile, this.$scope);
                        }
                    });
                    if (basePath) {
                        htmlContent.find(VhsEmbeddedBundleController.DOWNLOAD_BUNDLES_LINKS_SELECTOR).each((index, el) => {
                            var a: angular.IAugmentedJQuery = <angular.IAugmentedJQuery> $(el);
                            var href = a.attr('href');
                            if (href && href.indexOf('http') != 0 && href.indexOf("#") != 0) {
                                a.attr('href', basePath + href);
                            }
                        });
                    }
                    break;
            }
        }
    }

    private replaceImagePaths(content: string, baseUrl: string) {
        var regexp: RegExp =  new RegExp('<img(.*?)src="((?!http).*)"', "mg");
        content = content.replace(regexp, '<img$1src="' + baseUrl + '$2"');
        regexp = new RegExp("<img(.*?)src='((?!http).*)'", "mg");
        return content.replace(regexp, '<img$1src="' + baseUrl + '$2"');
    }

    private findRichTextElementId(): string {
        var richEditor: angular.IAugmentedJQuery = DomUtils.findParentWithAttr(this.element, "vhs-rich-text-bind");
        if (!richEditor) {
            richEditor = DomUtils.findParentWithAttr(this.element, "ta-bind");
        }
        return richEditor ? richEditor.attr('id') : null;
    }

    private getHttpConfig() : any {
        return { cache: true, withoutAuthorization: true, ignore404: true };
    }
}

export function VhsEmbeddedBundleDirectiveFactory(url: UrlService): angular.IDirective {
    function link($scope: IVhsEmbeddedBundleDirectiveScope,
                  element: angular.IAugmentedJQuery,
                  attributes: angular.IAttributes,
                  controller: VhsEmbeddedBundleController) {
        controller.init($scope, element);
    }

    let directive: angular.IDirective = {
        restrict: "AE",
        link: link,
        controller: VhsEmbeddedBundleController,
        scope: {
            fileKey: "@?vhsFileKey"
        }
    };
    return directive;
}

VhsEmbeddedBundleDirectiveFactory.$inject = [
    UrlService.NAME
];