import {AuthManager} from "./../authcore/AuthManager";
import {AuthRedirect} from "./../authcore/AuthRedirect";
import {RedirectReason} from "./../authcore/RedirectReason";
import {AuthRouter} from "./../urls/AuthRouter";
import {notifier} from "./../INotifier";
import {CommonNavigation} from "../common/CommonNavigation";
import {VIEWS_URL_PREFIX} from "./../utils/UrlService";

export class HttpInterceptor {
    public static NAME = "http.HttpInterceptor";

    public static Factory($q: angular.IQService,
                          authRedirect: AuthRedirect,
                          authManager: AuthManager,
                          commonNavigation: CommonNavigation) {
        return new HttpInterceptor($q, authRedirect, authManager, commonNavigation);
    }

    constructor(private $q: angular.IQService,
                private authRedirect: AuthRedirect,
                private authManager: AuthManager,
                private commonNavigation: CommonNavigation) {
    }

    public request = (config) => {
        config.headers = config.headers || {};
        var token = this.authManager.getToken();
        if (token != null && config.url.indexOf(VIEWS_URL_PREFIX) != 0 && !config.withoutAuthorization) {
            config.headers.Authorization = token.tokenType + " " + token.accessToken;
        }
        config.headers["X-Timezone"] = moment(new Date()).utcOffset();
        return config;
    };

    public responseError = (rejection) => {
        if (rejection.status === 401) {
            this.redirectToLogOn(RedirectReason.ACCESS_DENIED);
        } else if (rejection.status === 403) {
            if (this.authManager.isAuthenticated()) {
                this.authRedirect.forwardToAccessDenied();
            } else if (!this.authManager.hasToken()) {
                this.redirectToLogOn(RedirectReason.ACCESS_DENIED);
            } else {
                this.authManager.logOff();
                this.redirectToLogOn(RedirectReason.TOKEN_EXPIRED);
            }
        } else if (rejection.status >= 500) {
            notifier.error("Query has failed: please contact your system administrator or help desk");
        } else if (rejection.status === 409) {
            if (rejection.data.error === "InvalidState" || rejection.data.error === "OptimisticLock")
                notifier.error("This data has been modified by another user: please refresh the page and try again");
        } else if (rejection.status === 404 && true !== rejection.config.ignore404) {
            this.commonNavigation.redirectToNotFound();
        }
        return this.$q.reject(rejection);
    };

    private redirectToLogOn(reason: RedirectReason): void {
        this.authManager.eraseToken();
        this.authRedirect.redirectToLogOn(reason);
    }
}

HttpInterceptor.Factory.$inject = [
    "$q",
    AuthRedirect.NAME,
    AuthManager.NAME,
    CommonNavigation.NAME
];