import {UrlService} from "./../utils/UrlService";
import {IReferencedObjectInput} from "./../IReferencedObjectInput";
import {ReferencedObjectType} from "./../ReferencedObjectType";

export class PurchaseService {
    public static NAME = "finances.PurchaseService";

    public static $inject = [
        "$http",
        UrlService.NAME
    ];

    constructor(private $http: angular.IHttpService,
                private url: UrlService) {
    }

    public purchase(referencedObjectType: ReferencedObjectType, referencedObjectId: number): angular.IPromise<any> {
        let purchasedObject: IReferencedObjectInput = {
            objectType: referencedObjectType,
            objectId: referencedObjectId
        };
        return this.$http.post(this.url.service('api/finance/purchase'), purchasedObject)
            .then(result => result.data);
    }
}
