import {AuthService} from "./AuthService";

import {AuthRouter} from "../urls/AuthRouter";
import {MenuHelper} from "../utils/MenuHelper";
import {PageHeader} from "../utils/PageHeaderHelper";
import {MainRouter} from "../urls/MainRouter";
import {notifier} from "../INotifier";
import {UrlService} from "../utils/UrlService";

export class RestorePasswordController {
    public static NAME = "auth.RestorePasswordController";

    public logonName: string;
    public hasError: boolean = false;

    public static $inject = [
        "$location",
        "$scope",
        AuthService.NAME,
        AuthRouter.NAME,
        UrlService.NAME
    ];

    constructor(private $location: angular.ILocationService,
                private $scope: angular.IScope,
                private authService: AuthService,
                private authRouter: AuthRouter,
                private url: UrlService) {
        this.logonName = this.$location.$logonName;

        MenuHelper.setCurrentMenuItem("");
        PageHeader.set().header("Restore Password").update();
    }

    public restorePassword() {
        this.hasError = false;
        this.authService.restorePassword(this.logonName).then(result => {
            if (result.error === "error.password.restore.impossible") {
                this.hasError = true;
                const popup: any = $("#restore_link_result_popup");
                popup.showPopup();
            } else  {
                this.$location.$logonName = this.logonName;
                this.$location.url(this.authRouter.checkValidCode());
            }
        }).catch((response) => {
            switch (response.data.error) {
                case "error.password.restore.impossible": {
                    notifier.error("Incorrect code or code expired. Try to restore the password again");
                    return;
                }
            }
        })
    }

    public cancel() {
        this.$location.url(this.authRouter.logonUrl());
    }

    public onClosePopup() {
        const popup: any = $('#restore_link_result_popup');
        popup.hidePopup();
        if (!this.hasError) {
            this.cancel();
        }
    }
}