import {StringBuilder} from "./../utils/StringBuilder";
import {BaseRouter} from "./BaseRouter";

export class NewsRouter extends BaseRouter {
    public static NAME = "urls.NewsRouter";
    public static ONE_NEWS_ID_PARAM = "newsId";
    public static NEWS_TYPE_PARAM = "newsType";

    public static newsSearchRoute = `/news/search/:${NewsRouter.NEWS_TYPE_PARAM}`;
    public static oneNewsCreateRoute = `/news/create`;
    public static oneNewsRoute = `/news/:${NewsRouter.ONE_NEWS_ID_PARAM}`;
    public static oneNewsEditRoute = `/news/:${NewsRouter.ONE_NEWS_ID_PARAM}/edit`;

    public static $inject = [
        "$location"
    ];

    constructor($location: ng.ILocationService) {
        super($location);
    }

    public newsSearchUrl(newsType: string): string {
        return this.url(StringBuilder.interpolate(NewsRouter.newsSearchRoute)
            .with(':' + NewsRouter.NEWS_TYPE_PARAM, newsType.toLowerCase())
            .build());
    }

    public oneNewsUrl(newsId: number): string {
        return this.url(StringBuilder.interpolate(NewsRouter.oneNewsRoute)
            .with(':' + NewsRouter.ONE_NEWS_ID_PARAM, newsId)
            .build());
    }

    public oneNewsEditUrl(newsId: number): string {
        return this.url(StringBuilder.interpolate(NewsRouter.oneNewsEditRoute)
            .with(':' + NewsRouter.ONE_NEWS_ID_PARAM, newsId)
            .build());
    }

    public oneNewsCreateUrl(): string {
        return this.url(NewsRouter.oneNewsCreateRoute);
    }
}
