import {MaterialsRouter} from "./../urls/MaterialsRouter";
import {MaterialService} from "./MaterialService";
import {IMaterial} from "./models/IMaterial";
import {ISelectable} from "./../ISelectable";

import {AuthManager} from "./../authcore/AuthManager";
import {AuthRedirect} from "./../authcore/AuthRedirect";
import {MenuHelper} from "./../utils/MenuHelper";
import {ObjectTypeUtils} from "./../utils/ObjectTypeUtils";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {notifier} from "./../INotifier";
import {IMultimediaSource} from "./../components/models/IMultimedia";
import {PersonalService} from "./../personal/PersonalService";
import {SocialService} from "./../social/SocialService";
import {Moment} from "./../utils/Moment";
import {IGridModel, GridModel, IGridLoadingListener} from "./../IGridModel";
import {HttpGridHelper} from "./../IHttpGridService";
import {UrlService} from "./../utils/UrlService";
import {IComment} from "./../social/models/IComment";
import {ICommentInHierarchy} from "./../social/models/ICommentInHierarchy";
import {ITreeElementInfo} from "./../tree/models/TreeElementInfo";
import {TreeElementsInfoFiller} from "./../tree/TreeElementsInfoFiller";
import {IPage} from "./../IPage";
import {ActionLogService} from "./../statistics/ActionLogService";
import {ActionType} from "./../statistics/models/ActionType";
import {ReferencedObjectType} from "./../ReferencedObjectType";
import {FinancesRouter} from "./../urls/FinancesRouter";
import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";
import {PermissionsService} from "./../security/PermissionsService";
import {CourseService} from "./../courses/CourseService";
import {ICourse} from "./../courses/models/ICourse";
import {PlaylistService} from "./PlaylistService";
import {IPlaylist} from "./models/IPlaylist";
import {StringBuilder} from "../utils/StringBuilder";
import {UploadUtils} from "../utils/UploadUtils";
import {MaterialTypeKeyEnum} from "./models/MaterialType";

interface ICommentInHierarchyExtended extends ITreeElementInfo<ICommentInHierarchyExtended>, ICommentInHierarchy {
    reply?: boolean;
}

export class MaterialController implements IGridLoadingListener {
    public static NAME = "materials.MaterialController";

    public materialId: number;
    public material: IMaterial = {};
    public multimediaSources: IMultimediaSource[];
    public isAuthenticated: boolean;
    public relatedMaterials: IMaterial[];
    public commentsGridModel: IGridModel<ICommentInHierarchyExtended>;
    public newComment: IComment;
    public newReplyComment: IComment;
    public anchorComplete: boolean;
    public commentsCount: number;
    public systemPermissions: ISystemPermissions;
    public disabledRemoveButton: boolean = false;

    public isMaterialEmpty: boolean;
    public isMaterialNotReady: boolean;
    public isViewAllowed: boolean;
    public materialType: string;

    public static $inject = [
        "$http",
        "$location",
        "$anchorScroll",
        "$timeout",
        UrlService.NAME,
        "$routeParams",
        "$q",
        MaterialsRouter.NAME,
        FinancesRouter.NAME,
        MaterialService.NAME,
        PersonalService.NAME,
        SocialService.NAME,
        AuthRedirect.NAME,
        AuthManager.NAME,
        "$window",
        ActionLogService.NAME,
        PermissionsService.NAME,
        CourseService.NAME,
        PlaylistService.NAME,
        UploadUtils.NAME
    ];

    constructor($http: angular.IHttpService,
                private $location: angular.ILocationService,
                private $anchorScroll: angular.IAnchorScrollService,
                private $timeout: angular.ITimeoutService,
                private url: UrlService,
                private $routeParams: angular.route.IRouteParamsService,
                private $q: angular.IQService,
                public materialsRouter: MaterialsRouter,
                public financeRouter: FinancesRouter,
                public materialService: MaterialService,
                public personalService: PersonalService,
                public socialService: SocialService,
                public authRedirect: AuthRedirect,
                private authManager: AuthManager,
                private $window: angular.IWindowService,
                private actionLogService: ActionLogService,
                private permissionsApi: PermissionsService,
                private courseService: CourseService,
                private playlistService: PlaylistService,
                private uploadUtils: UploadUtils) {
        this.materialId = $routeParams[MaterialsRouter.MATERIAL_ID_PARAM];
        this.isAuthenticated = this.authManager.isAuthenticated();
        this.anchorComplete = false;

        if (this.isAuthenticated) {
            this.permissionsApi.getSystemPermissions().then(systemPermissions => {
                this.systemPermissions = systemPermissions;
            });
        }

        if (!MenuHelper.getCurrentMenuItemKey()) {
            MenuHelper.setCurrentMenuItem(MenuHelper.SEARCH_MENU_KEY);
        }
        this.loadMaterial(this.materialId);
        this.materialService.getRelatedMaterials(this.materialId).then(resultRelated => {
            this.relatedMaterials = resultRelated;
        });

        this.commentsGridModel = new GridModel(HttpGridHelper.createLoader<ICommentInHierarchyExtended>($http,
            this.url.base("/api/social/comments?objectId=:objectId&objectType=:objectType")
                .param(":objectId", this.materialId)
                .param(":objectType", ReferencedObjectType[ReferencedObjectType.MATERIAL])
                .service()));
        this.commentsGridModel.addLoadingListener(this);
        this.loadCommentsCount();
    }

    public startLoading() {
        //do nothing
    }

    public endLoading() {
        TreeElementsInfoFiller.fillHierarchyInfoWithMaxLevel(this.commentsGridModel.entries, Number.MAX_VALUE);
        this.$timeout(()=> {
            this.scrollAnchor();
        }, 0);

    }

    public scrollAnchor() {
        if (!this.anchorComplete) {
            this.$anchorScroll(this.$location.hash());
            this.anchorComplete = true;
        }
    }

    public getFileTypeName(format: string): string {
        switch (format) {
            case 'video':
                return "video";
            case 'audio':
                return "audio";
            case 'presentation':
                return "presentation";
            case 'text':
                return "text file";
            default:
                return "video";
        }
    }

    public isYoutubeLink(material: IMaterial): boolean {
        return material.typeId == 3 && ObjectTypeUtils.isYoutubeUrl(material.link);
    }

    public loadMaterial(materialId: number): angular.IPromise<IMaterial> {
        this.newComment = {text: ""};
        this.newReplyComment = {text: ""};

        let promise: angular.IPromise<IMaterial> = this.materialService.get(materialId);
        promise.then(result => {
            this.material = angular.copy(result);

            if (this.isYoutubeLink(this.material)) {
                this.materialType = 'YOUTUBE';
            } else {
                this.materialType = this.material.typeKey;
            }

            this.isMaterialEmpty = this.material.id && this.material.typeKey !== MaterialTypeKeyEnum[MaterialTypeKeyEnum.LINK] &&
                (!this.material.fileStatus || this.material.fileStatus === 'EMPTY');
            this.isMaterialNotReady = !this.isMaterialEmpty && this.material.id &&
                this.material.typeKey !== MaterialTypeKeyEnum[MaterialTypeKeyEnum.LINK] && this.material.fileStatus
                && this.material.fileStatus !== 'EMPTY' && this.material.fileStatus !== 'READY';
            this.isViewAllowed = this.material.permissions.view && !this.isMaterialEmpty && !this.isMaterialNotReady;

            this.multimediaSources = [{
                src: this.isYoutubeLink(this.material) ? this.material.link : (this.material.downloadUrl || ''),
                type: this.isYoutubeLink(this.material) ? "video/youtube" : this.material.contentType
            }];
            if (this.material.permissions.view) {
                this.actionLogService.create(this.material.id, ReferencedObjectType.MATERIAL, ActionType.MATERIAL_VIEW);
            }
        }).catch(response => {
            if (response.data.error == "material.is.not.available") {
                notifier.error("Access to the material is restricted");
                let savedUrl: string = this.$location.url();
                this.$timeout(() => {
                    if (savedUrl === this.$location.url()) {
                        this.goBack();
                    }
                }, 5000); // TODO: peer review
            }
            else {
                this.material = {
                    typeName: '',
                    title: 'Failed to retrieve the material details'
                };
            }
        }).finally(() => {
            PageHeader.set().header((this.material.typeName || '') +
                ' ' + (this.material.title || '') + ''
            ).update();
        });
        return promise;
    }

    private loadCommentsCount() {
        this.socialService.getCommentsCount(this.materialId, ReferencedObjectType.MATERIAL).then(result => {
            this.commentsCount = result;
        });
    }

    public addToFavorites() {
        this.personalService.addToFavorites(this.materialId, ReferencedObjectType.MATERIAL).then(result => {
            notifier.success("The material has been added to your favorites");
            this.material.favorite = true;
        }).catch(response => {
            if (response.data.error == "object.already.in.favorite") {
                notifier.error("This material is already in your favorites");
            }
        });
    }

    public removeFromFavorites() {
        this.personalService.removeFromFavorites(this.materialId, ReferencedObjectType.MATERIAL).then(result => {
            notifier.success("The material has been removed from your favorites");
            this.material.favorite = false;
        }).catch(response => {
            if (response.data.error == "object.not.in.favorite") {
                notifier.error("Failed to remove from favorites");
            }
        });
    }

    public changeMaterialRating(isLike: boolean) {
        if (!this.isAuthenticated) {
            this.authRedirect.redirectToLogOn();
        } else {
            this.socialService.changeObjectRating(this.materialId, ReferencedObjectType.MATERIAL, isLike).then(result => {
                this.material.likesCount = result.likesCount;
                this.material.dislikesCount = result.dislikesCount;
                if (isLike) {
                    this.material.liked = !this.material.liked;
                    this.material.disliked = false;
                }
                if (!isLike) {
                    this.material.disliked = !this.material.disliked;
                    this.material.liked = false;
                }
            });
        }
    }

    public multimediaErrorCallback(code: number, message: string): angular.IPromise<IMaterial> {
        return this.loadMaterial(this.materialId);
    }

    public addComment(reply: boolean, parentId: number) {
        if (reply) {
            this.newReplyComment.parentId = parentId;
        }
        this.socialService.addComment(this.materialId, ReferencedObjectType.MATERIAL, reply ? this.newReplyComment : this.newComment).then(result => {
            notifier.success("Comment has been added");
            this.commentsGridModel.loadCurrentPage();
            this.newComment.text = "";
            this.newReplyComment.text = "";
            this.newReplyComment.parentId = null;
            this.loadCommentsCount();
        });
    }

    public cleanComment() {
        this.newComment.text = "";
        this.newReplyComment.text = "";
        this.newReplyComment.parentId = null;
    }

    public replyComment(commentId: number) {
        this.commentsGridModel.entries.forEach((c: ICommentInHierarchyExtended) => {
            c.reply = c.id == commentId;
        });
    }

    public editMaterial(material: IMaterial) {
        this.$location.url(this.materialsRouter.updateUrl(material.id));
    }

    public deleteMaterial(material: IMaterial) {
        this.disabledRemoveButton = true;
        this.courseService.findCoursesByMaterial(material.id).then(courses => {
            if (courses.length == 0) {
                this.materialService.isMaterialPurchased(material.id).then(purchased => {
                    if (!purchased) {
                        this.playlistService.findPlaylistsByMaterial(material.id).then(playlists => {
                            let formattedFunc: (playlist: ISelectable, index: number) => string = (playlist: IPlaylist, index: number) => {
                                return (index + 1) + ". " + playlist.name;
                            };
                            let message: string = playlists.length == 0
                                ? "This material will be completely removed from the system. Continue?" + "\n"
                                : StringBuilder.interpolate("The material is included into the following playlists: :lists Confirm removing the material?")
                                .with(":lists", "\n\n" + this.getItemsFormattedString<IPlaylist>(playlists, formattedFunc, "\n") + "\n")
                                .build() + "\n";
                            if (confirm(message)) {
                                this.materialService.delete(material.id).then(() => {
                                    notifier.success("The material has been removed");
                                    this.disabledRemoveButton = false;
                                    this.$location.url(this.materialsRouter.searchUrl());
                                });
                                this.uploadUtils.cancelMaterialPreviews(material.id);
                                this.uploadUtils.cancelMaterialFiles(material.id);
                            }
                        });
                    } else {
                        alert("This material has been purchased. Impossible to remove");
                    }
                });
            } else {
                let formattedFunc: (course: ICourse, index: number) => string = (course: ICourse, index: number) => {
                    return (index + 1) + ". " + course.subjectName + ". " + course.topic;
                };
                let message: string = StringBuilder.interpolate("The material can not be removed. It is used in courses::courses")
                    .with(":courses", "\n\n" + this.getItemsFormattedString<ICourse>(courses, formattedFunc, "\n"))
                    .build();
                alert(message);
            }
        });
    }

    private getItemsFormattedString<T>(items: Array<T>, formattedFunc: (item: T, index: number) => string, separator: string): string {
        let result = "";
        items.forEach((item: T, index: number) => {
            result += formattedFunc(item, index) + separator;
        });
        return result;
    }

    public createMaterialDownloadActionLog(materialId: number): void {
        this.actionLogService.create(materialId, ReferencedObjectType.MATERIAL, ActionType.MATERIAL_DOWNLOAD);
    }

    public goBack(): void {
        if (this.$window.history.length > 1) {
            this.$window.history.back();
        } else {
            this.authRedirect.forwardToAccessDenied();
        }
    }

    public changeAuthorSubscription() {
        this.personalService.changeAuthorSubscription(this.material.ownerId).then(result => {
            result
                ? notifier.success("You successfully subscribed to publications of" + " " + this.material.ownerName)
                : notifier.success("You successfully unsubscribed from publications of" + " " + this.material.ownerName);

            this.material.subscribe = result;
        })
    }
}