import {AuthManager} from "../authcore/AuthManager";
import {AuthRedirect} from "../authcore/AuthRedirect";
import {CoursesRouter} from "../urls/CoursesRouter";
import {CourseService} from "./CourseService";
import {ProgressService} from "./ProgressService";
import {PermissionsService} from "../security/PermissionsService";
import {QuizService} from "../quizzes/QuizService";
import {AuthService} from "../auth/AuthService";
import {IUserInfo} from "../security/IUserInfo";
import {ISelectable} from "../ISelectable";
import {ISectionProgress} from "./models/ISectionProgress";
import {ISectionsStatistics} from "./models/ISectionStatistics";
import {SectionProgressStatus} from "./models/SectionProgressStatus";
import {SectionResultStatistic} from "./models/SectionResultStatistic";
import {MenuHelper} from "../utils/MenuHelper";
import {SecurityService} from "../security/SecurityService";
import {PageHeader} from "../utils/PageHeaderHelper";
import {ICourse} from "./models/ICourse";
import {IGridModel} from "../IGridModel";
import {HttpGridHelper} from "../IHttpGridService";
import {UrlService} from "../utils/UrlService";
import {GridModel} from "../IGridModel";

export interface IExtendedSelectable extends ISelectable{
    selected?: boolean;
}

export interface IExtendedSectionStatistics extends ISectionsStatistics{
    status?: string; //from SectionResultStatistic.ts
}

export class CourseProgressController {
    public static NAME = "courses.CourseProgressController";

    private currentUser: IUserInfo = {};

    public courseId: number;
    public owners: Array<ISelectable> = [];
    public userProgress: Array<ISectionProgress> = [];
    public courseUsers: Array<IExtendedSelectable> = [];
    public courseGroups: Array<IExtendedSelectable> = [];
    public sectionsStatistics: Array<IExtendedSectionStatistics> = [];
    public selectedUserIds: Array<number> = [];
    public selectedGroupIds: Array<number> = [];
    public selectedQuizProgress: ISectionProgress = {};
    public currentUserInOwners: boolean = false;
    public searchType: number;
    public course: ICourse = {};
    public sectionProgressGridModel: IGridModel<ISectionProgress>;
    public isSectionSelected: boolean = false;
    public selectedSectionStatistic: IExtendedSectionStatistics = {};
    public isAuthenticated: boolean;

    public static $inject = [
        "$http",
        "$location",
        "$routeParams",
        UrlService.NAME,
        SecurityService.NAME,
        CoursesRouter.NAME,
        CourseService.NAME,
        ProgressService.NAME,
        AuthService.NAME
    ];

    constructor(private $http: angular.IHttpService,
                private $location: angular.ILocationService,
                private $routeParams: ng.route.IRouteParamsService,
                private url: UrlService,
                private securityService: SecurityService,
                private coursesRouter: CoursesRouter,
                private courseService: CourseService,
                private progressService: ProgressService,
                private authService: AuthService) {
        this.courseId = $routeParams[CoursesRouter.COURSE_ID_PARAM];
        let courseIdStr = String(this.courseId);
        this.securityService
            .demandCoursePermissions(courseIdStr, coursePermissions => coursePermissions.view);

        var menuItemKey: string = MenuHelper.getCurrentMenuItemKey();
        if (!menuItemKey || menuItemKey.length == 0) {
            MenuHelper.setCurrentMenuItem(MenuHelper.EDUCATION_COURSES_MENU_KEY);
        }
        this.searchType = 0;
        this.loadCourse();
        this.authService.getCurrentUser().then(user => {
            angular.copy(user, this.currentUser);
            this.loadProgressForCurrentUser();
        });
    }

    private loadCourse() {
        var promise: angular.IPromise<ICourse> = this.courseService.get(this.courseId);
        promise.then(result => {
            this.course = angular.copy(result);
            PageHeader.set().header(this.course.topic || this.course.subjectName).update();
        });
    }

    private loadProgressForCurrentUser() {
        this.courseService.getCourseOwners(this.courseId).then(owners => {
            angular.copy(owners, this.owners);
            for(let i = 0; i < this.owners.length; i++) {
                if (this.owners[i].id == this.currentUser.id) {
                    this.currentUserInOwners = true;
                }
            }
            this.loadProgress();
        });
    }

    private loadProgress(){
        if (this.currentUserInOwners) {
            this.getUsers();
            this.getGroups();
        } else {
            this.getUserProgress();
        }
    }

////////// sections progress
    private getUsers(){
        this.progressService.getCourseUsers(this.courseId).then(users => {
            users.forEach(u => {
                var extendedUserInfo: IExtendedSelectable = {selected: false};
                angular.copy(u, extendedUserInfo);
                extendedUserInfo.selected = false;
                this.courseUsers.push(extendedUserInfo);
            });
        });
    }

    private getGroups(){
        this.progressService.getCourseGroups(this.courseId).then(groups => {
            groups.forEach(g => {
                var extendedGroupInfo: IExtendedSelectable = {selected: false};
                angular.copy(g, extendedGroupInfo);
                extendedGroupInfo.selected = false;
                this.courseGroups.push(extendedGroupInfo);
            });
        });
    }

    public getSectionsStatistics(): void {
        var userIds = [];
        var groupIds = [];
        if(this.searchType == 0) {
            userIds = this.selectedUserIds.length > 0 ? this.selectedUserIds : [];
        } else {
            groupIds = this.selectedGroupIds.length > 0 ? this.selectedGroupIds : [];
        }
        this.sectionsStatistics = [];
        this.progressService.getSectionsStatistics(this.courseId, userIds, groupIds).then(result => {
            result.forEach(r => {
                var extendedSectionStatisticsInfo: IExtendedSectionStatistics = {status: ""};
                angular.copy(r, extendedSectionStatisticsInfo);
                extendedSectionStatisticsInfo.status = this.getSectionResultStatus(r);
                this.sectionsStatistics.push(extendedSectionStatisticsInfo);
            });
        });
    }

    private getSectionResultStatus(sectionStatistics: ISectionsStatistics): string {
        if(sectionStatistics.count[SectionProgressStatus[SectionProgressStatus.COMPLETED]] > 0
            && sectionStatistics.count[SectionProgressStatus[SectionProgressStatus.NOT_ALLOWED]] == 0
            && sectionStatistics.count[SectionProgressStatus[SectionProgressStatus.ALLOWED]] == 0
            && sectionStatistics.count[SectionProgressStatus[SectionProgressStatus.OPENED]] == 0
            && sectionStatistics.count[SectionProgressStatus[SectionProgressStatus.STARTED]] == 0
            && sectionStatistics.count[SectionProgressStatus[SectionProgressStatus.SUBMITTED]] == 0
            && sectionStatistics.count[SectionProgressStatus[SectionProgressStatus.FAILED]] == 0
            && sectionStatistics.count[SectionProgressStatus[SectionProgressStatus.SOS]] == 0 ) {
            return SectionResultStatistic[SectionResultStatistic.COMPLETED];
        } else if(sectionStatistics.count[SectionProgressStatus[SectionProgressStatus.SUBMITTED]] > 0
            || sectionStatistics.count[SectionProgressStatus[SectionProgressStatus.FAILED]] > 0
            || sectionStatistics.count[SectionProgressStatus[SectionProgressStatus.SOS]] > 0
            || sectionStatistics.count[SectionProgressStatus[SectionProgressStatus.COMPLETED]] > 0) {
            return SectionResultStatistic[SectionResultStatistic.SUBMITTED];
        } else if(sectionStatistics.count[SectionProgressStatus[SectionProgressStatus.FAILED]] > 0
            && sectionStatistics.count[SectionProgressStatus[SectionProgressStatus.NOT_ALLOWED]] == 0
            && sectionStatistics.count[SectionProgressStatus[SectionProgressStatus.ALLOWED]] == 0
            && sectionStatistics.count[SectionProgressStatus[SectionProgressStatus.OPENED]] == 0
            && sectionStatistics.count[SectionProgressStatus[SectionProgressStatus.STARTED]] == 0
            && sectionStatistics.count[SectionProgressStatus[SectionProgressStatus.SUBMITTED]] == 0
            && sectionStatistics.count[SectionProgressStatus[SectionProgressStatus.SOS]] == 0
            && sectionStatistics.count[SectionProgressStatus[SectionProgressStatus.SUBMITTED]] == 0 ) {
            return SectionResultStatistic[SectionResultStatistic.FAILED];
        } else {
            return "";
        }
    }

////////// user progress
    public getUserProgress(): void {
        this.progressService.getProgressForUser(this.courseId).then(result => {
            angular.copy(result, this.userProgress);
        });
    }

    public getProgressForUser(user: IExtendedSelectable): void {
        if(!user.selected) {
            this.selectedUserIds.push(user.id);
            this.courseUsers[this.courseUsers.indexOf(user)].selected = true;
            if(this.selectedUserIds.length == 1) {
                this.progressService.getProgressForUser(this.courseId, user.id).then(result => {
                    if(result) {
                        angular.copy(result, this.userProgress);
                    }
                });
            } else {
                this.getSectionsStatistics();
            }
        } else {
            var index = this.selectedUserIds.indexOf(user.id);
            this.selectedUserIds.splice(index, 1);
            this.courseUsers[this.courseUsers.indexOf(user)].selected = false;
            if(this.selectedUserIds.length > 1) {
                this.getSectionsStatistics();
            } else if(this.selectedUserIds.length == 1){
                this.progressService.getProgressForUser(this.courseId, this.selectedUserIds[0]).then(result => {
                    if(result) {
                        angular.copy(result, this.userProgress);
                    }
                });
            }
        }
    }

////////// group progress
    public getProgressForGroup(group: IExtendedSelectable) {
        if(!group.selected) {
            this.selectedGroupIds = [];
            this.selectedGroupIds.push(group.id);
            this.courseGroups[this.courseGroups.indexOf(group)].selected = true;
            this.courseGroups.forEach(g => {
               if(g.id != group.id) {
                   this.courseGroups[this.courseGroups.indexOf(g)].selected = false;
               }
            });
        } else {
            var index = this.selectedGroupIds.indexOf(group.id);
            this.selectedGroupIds.splice(index, 1);
            this.courseGroups[this.courseGroups.indexOf(group)].selected = false;
        }
        this.cleanSelectedSection();
        this.getSectionsStatistics();
    }

////////// quiz\section progress
    public getUserProgressForQuiz(sectionId: number): void {
        this.userProgress.forEach(p => {
            if(p.sectionId == sectionId) {
                angular.copy(p, this.selectedQuizProgress);
            }
        });
    }

    public getSectionProgress(sectionStatistics: IExtendedSectionStatistics): void {
        let isLoadingNeeded: boolean = (this.selectedSectionStatistic && !!this.selectedSectionStatistic.sectionId); // to eliminate double query when a grid is shown for the first time
        angular.copy(sectionStatistics, this.selectedSectionStatistic);
        this.isSectionSelected = true;
        var userIds = [];
        var groupIds = [];
        if(this.searchType == 0) {
            userIds = this.selectedUserIds.length > 0 ? this.selectedUserIds : [];
        } else {
            groupIds = this.selectedGroupIds.length > 0 ? this.selectedGroupIds : [];
        }
        this.sectionProgressGridModel = new GridModel(HttpGridHelper.createLoader<ISectionProgress>(this.$http,
            this.url.service("/api/progress/course/" + this.courseId + "/sections/" + sectionStatistics.sectionId)));
        this.sectionProgressGridModel.filter.userIds = userIds;
        this.sectionProgressGridModel.filter.groupIds = groupIds;
        if (isLoadingNeeded) {
            this.sectionProgressGridModel.loadPage(0);
        } // when a grid is shown for the first time (when ng-if condition becomes true),
        // its gridModel.loadPage() will be called automatically during initialization.
    }

    public getUserInSectionProgressStatus(status: string): string {
        if (status === SectionProgressStatus[SectionProgressStatus.NOT_ALLOWED]) {
            return "Not started";
        } else if (status === SectionProgressStatus[SectionProgressStatus.ALLOWED]) {
            return "Not started yet";
        } else if (status === SectionProgressStatus[SectionProgressStatus.OPENED]) {
            return "Opened";
        } else if (status === SectionProgressStatus[SectionProgressStatus.STARTED]) {
            return "Started";
        } else if (status === SectionProgressStatus[SectionProgressStatus.SUBMITTED]) {
            return "Submitted";
        } else if (status === SectionProgressStatus[SectionProgressStatus.FAILED]) {
            return "Failed";
        } else if (status === SectionProgressStatus[SectionProgressStatus.SOS]) {
            return "Failed. No more attempts are allowed";
        } else if (status === SectionProgressStatus[SectionProgressStatus.COMPLETED]) {
            return "Passed";
        } else {
            return "N/A";
        }
    }

////////// redirect
    private redirectToDetails(): void {
        this.$location.url(this.coursesRouter.detailsUrl(this.courseId));
    }

    private redirectToInfo(): void {
        this.$location.url(this.coursesRouter.infoUrl(this.courseId));
    }

    public editCourse(): void {
        this.$location.url(this.coursesRouter.updateUrl(this.courseId));
    }

    public changeFilter(){
        if(this.searchType == 0) {
            this.courseUsers.forEach((u: IExtendedSelectable) => {
               u.selected = false;
            });
            this.selectedGroupIds = [];
        } else {
            this.courseGroups.forEach((g: IExtendedSelectable) => {
                g.selected = false;
            });
            this.selectedUserIds = [];
        }
        this.cleanSelectedSection();
    }

    public cleanSelectedSection(){
        this.selectedSectionStatistic = {};
        this.selectedQuizProgress = {};
    }

    public isTopMenuActive(topMenuKey: string): boolean {
        return 'progress' == topMenuKey;
    }

    public isShowProgressTab(): boolean {
        return true;
    }
}