import {IPermissionWithUsersAndGroups} from "./../security/permissions/IPermissionWithUsersAndGroups";
import {UrlService} from "./../utils/UrlService";
import {IMaterial} from "./models/IMaterial";

interface IExtMaterial extends IMaterial {
    file?: any;
    preview?: any;
    permissionsWithUsersAndGroups?: Array<IPermissionWithUsersAndGroups>;
    isResetPreview?: boolean;
}

export class MaterialService {
    public static NAME = "materials.MaterialService";

    public static $inject = [
        "$http",
        "$sce",
        UrlService.NAME
    ];

    constructor(private $http: angular.IHttpService,
                private $sce: angular.ISCEService,
                private url: UrlService) {
    }

    public get(id: number): angular.IPromise<IMaterial> {
        return this.$http.get(this.url.base("/api/materials/:id")
            .param(":id", id)
            .service())
            .then(result => result.data);
    }

    public getPublicInfo(materialId: number): angular.IPromise<IMaterial> {
        return this.$http.get(this.url.base("/api/materials/:id/publicInfo")
            .param(":id", materialId)
            .service())
            .then(result => result.data);
    }

    public create(material: IExtMaterial, permissionsWithUsersAndGroups: Array<IPermissionWithUsersAndGroups>, file?: File, preview?: File):
            ng.IPromise<IMaterial> {
        material.file = file != null
            ? {name: file.name, type: file.type, size: file.size}
            : null;
        material.preview = preview != null
            ? {name: preview.name, type: preview.type, size: preview.size}
            : null;
        material.permissionsWithUsersAndGroups = permissionsWithUsersAndGroups;
        return this.$http.post(this.url.service("/api/materials"), material)
            .then(result => result.data);
    }

    public update(material: IExtMaterial, permissionsWithUsersAndGroups: Array<IPermissionWithUsersAndGroups>, isResetPreview: boolean,
                  file?: File, preview?: File): ng.IPromise<IMaterial> {
        material.file = file != null
            ? {name: file.name, type: file.type, size: file.size}
            : null;
        material.preview = preview != null
            ? {name: preview.name, type: preview.type, size: preview.size}
            : null;
        material.permissionsWithUsersAndGroups = permissionsWithUsersAndGroups;
        material.isResetPreview = isResetPreview;
        return this.$http.put(this.url.service("/api/materials"), material)
            .then(result => result.data);
    }

    public delete(materialId: number): ng.IPromise<any> {
        return this.$http.delete(this.url.base("/api/materials/:id").param(":id", materialId).service())
            .then(result => result.data);
    }

    public getRelatedMaterials(id: number): angular.IPromise<IMaterial[]> {
        return this.$http.get(this.url.base("/api/materials/:id/related").param(":id", id).service())
            .then(result => result.data);
    }

    public isMaterialPurchased(materialId: number): angular.IPromise<boolean> {
        return this.$http.get(this.url.base("/api/materials/:id/is/purchased").param(":id", materialId).service())
            .then(result => result.data);
    }
}
