export class StringBuilder {
    private base: string;

    public static interpolate(base: string): StringBuilder {
        return new StringBuilder(base);
    }

    constructor(base: string) {
        this.base = base;
    }

    public with(param: string, value: any): StringBuilder {
        this.base = this.base.replace(param, value);
        return this;
    }

    public add(...strings: string[]): StringBuilder {
        strings.forEach(str => this.base = this.base.concat(str));
        return this;
    }

    public addIf(condition: boolean, ...strings: string[]): StringBuilder {
        if (condition) {
            strings.forEach(str => this.base = this.base.concat(str));
        }
        return this;
    }

    public build(): string {
        return this.base;
    }
}