import {UrlService} from "../../utils/UrlService";
import IPromise = angular.IPromise;

interface IRichTextContentLoadingData {
    loaderFunction?: IPromise<any>;
    deferred?: angular.IDeferred<string>;
}

// provide ordered callbacks call after load all data in rich text (load bundles in appropriate order)
export class RichTextContentLoadingSynchronizer {
    public static NAME = "components.RichTextContentLoadingSynchronizer";

    private richTextLoadersMap: Map<angular.IAugmentedJQuery, Array<IRichTextContentLoadingData>> =
        new Map<angular.IAugmentedJQuery, Array<IRichTextContentLoadingData>>();

    private forcedLoading: boolean = false;

    public static $inject = [
        "$http",
        "$q",
        "$timeout"
    ];

    constructor(private $http: angular.IHttpService,
                private $q: angular.IQService,
                private $timeout: angular.ITimeoutService) {
    }

    public loadContent(key: any, dataLoader: IPromise<any>): IPromise<string> {
        if (!this.richTextLoadersMap.has(key)) {
            this.richTextLoadersMap.set(key, []);
        }
        let deferred: angular.IDeferred<string> = this.$q.defer<string>();
        let loadingData: IRichTextContentLoadingData = {
            loaderFunction: dataLoader,
            deferred: deferred
        };
        this.richTextLoadersMap.get(key).push(loadingData);
        if (!this.forcedLoading) {
            this.forcedLoading = true;
            this.$timeout(() => {this.performLoadContent();}, 0);
        }
        return deferred.promise;
    }

    private performLoadContent() {
        this.richTextLoadersMap.forEach((value, key) => {
            var promises: Array<IPromise<any>> = [];
            var callbacks: Array<angular.IDeferred<string>> = [];
            value.forEach((data: IRichTextContentLoadingData) => {
                promises.push(data.loaderFunction.catch(() => {return null}));
                callbacks.push(data.deferred);
            });
            this.$q.all(promises).then((values: Array<any>) => {
                //console.log(values);
                for (var i = 0; i < callbacks.length; i++) {
                     callbacks[i].resolve((values[i]));
                }
            });
        });
        this.forcedLoading = false;
        this.richTextLoadersMap.clear();
    }
}