interface SelectOptionsDisabledAttributes extends angular.IAttributes {
    selectOptionsDisabled: string;
    ngModel: string;
}

SelectOptionsDisabledDirectiveFactory.$inject = [
    "$parse"
];

export function SelectOptionsDisabledDirectiveFactory($parse: angular.IParseService): ng.IDirective {
    function disableOptions($scope: angular.IScope, attribute: string, element: any, data: any,
                            fnDisableIfTrue: angular.ICompiledExpression) {
        let options = element.find("option");
        for (let pos = 0, index = 0; pos < options.length; pos++) {
            let elem = angular.element(options[pos]);
            if (elem.val() != "") {
                let locals = {};
                locals[attribute] = data[index];
                elem.attr("disabled", fnDisableIfTrue($scope, locals));
                index++;
            }
        }
    }

    function link($scope: angular.IScope, element: any, attributes: SelectOptionsDisabledAttributes) {
        let expElements: RegExpMatchArray = attributes.selectOptionsDisabled.match(/^\s*(.+)\s+for\s+(.+)\s+in\s+(.+)?\s*/);
        let attrToWatch: string = expElements[3];
        let fnDisableIfTrue: angular.ICompiledExpression = $parse(expElements[1]);

        $scope.$watch(attrToWatch, (newValue) => {
            if (newValue) {
                disableOptions($scope, expElements[2], element, newValue, fnDisableIfTrue);
            }
        }, true);

        $scope.$watch(attributes.ngModel, (newValue) => {
            var disOptions = $parse(attrToWatch)($scope);
            if (newValue) {
                disableOptions($scope, expElements[2], element, disOptions, fnDisableIfTrue);
            }
        });
    }

    var directive: ng.IDirective = {};
    directive.priority = 0;
    directive.require = 'ngModel';
    directive.link = link;
    return directive;
}