import {UrlService} from "../utils/UrlService"

import {IBundleFile} from "../bundles/models/IBundleFile";
import {BundleFileTypeEnum} from "../bundles/models/BundleFileType";

export interface IVhsLoadBundleDirectiveScope extends angular.IScope {
    bundleFile?: IBundleFile;
}

export class VhsLoadBundleFileController {

    public bundleFile: IBundleFile;

    private element: angular.IAugmentedJQuery;

    public static $inject = [
        "$scope",
        "$http"
    ];

    constructor(private $scope: IVhsLoadBundleDirectiveScope,
                private $http: angular.IHttpService) {
    }

    public init($scope: IVhsLoadBundleDirectiveScope,
                element: angular.IAugmentedJQuery) {
        this.element = element;
        this.bundleFile = $scope.bundleFile;

        this.processBundleFile();
    }

    public processBundleFile() {
        if (!this.bundleFile || !this.bundleFile.url) {
            return;
        }
        if (this.bundleFile.type == BundleFileTypeEnum[BundleFileTypeEnum.JS]) {
            return this.$http.get(this.bundleFile.url, this.getHttpConfig())
                .then(result => result.data)
                .then((content: string) => this.processFileContent(content, this.bundleFile));
        } else if (this.bundleFile.type == BundleFileTypeEnum[BundleFileTypeEnum.CSS]) {
            this.element.replaceWith("<link rel='stylesheet' href=" + this.bundleFile.url + ">");
        } else if (this.bundleFile.type == BundleFileTypeEnum[BundleFileTypeEnum.IMAGE] && this.element.is('img')) {
            this.element.attr("src", this.bundleFile.url);
        } else {
            console.log('Incorrect bundle file type for load: ' + this.bundleFile.type);
        }
    }

    private processFileContent(fileContent: string, bundleFile: IBundleFile) {
        if (fileContent && bundleFile) {
            switch (bundleFile.type) {
                case BundleFileTypeEnum[BundleFileTypeEnum.JS]:
                    $.globalEval('"use strict";' + fileContent);
                    break;
            }
        }
    }

    private getHttpConfig() : any {
        return { cache: true, withoutAuthorization: true, ignore404: true };
    }
}

export function LoadBundleFileDirectiveFactory(url: UrlService): angular.IDirective {
    function link($scope: IVhsLoadBundleDirectiveScope,
                  element: angular.IAugmentedJQuery,
                  attributes: angular.IAttributes,
                  controller: VhsLoadBundleFileController) {
        controller.init($scope, element);
    }

    let directive: angular.IDirective = {
        restrict: "AE",
        link: link,
        controller: VhsLoadBundleFileController,
        scope: {
            bundleFile: "=vhsBundleFile"
        }
    };
    return directive;
}

LoadBundleFileDirectiveFactory.$inject = [
    UrlService.NAME
];