import {UrlService} from "../../utils/UrlService";
import {RandomUtils} from "../../utils/RandomUtils";
import IPromise = angular.IPromise;
import {Event} from "../models/Event";
import {IBundle} from "../../bundles/models/IBundle";
import {IBundleFile} from "../../bundles/models/IBundleFile";
import {IFindRichTextBindControllerEventData} from "../models/IFindRichTextBindControllerEventData";
import {BundleService} from "./../../bundles/BundleService";
import {RichTextContentLoadingSynchronizer} from "./RichTextContentLoadingSynchronizer";

interface RichTextBindAttributes extends angular.IAttributes {
    bundleId?: number;
}

interface RichTextBindScope extends angular.IScope {
    ngModel?: any;
    bundle?: IBundle;
}

export class RichTextBindController {

    private element: angular.IAugmentedJQuery;
    private elementId: string;
    private bundle: IBundle;
    private loadBundlePromise: IPromise<IBundle>;
    private waitContentLoadingDeferred: angular.IDeferred<any>;
    private loadingContent: boolean = false;

    public static $inject = [
        "$http",
        "$q",
        "$timeout",
        BundleService.NAME,
        RichTextContentLoadingSynchronizer.NAME
    ];

    constructor(private $http: angular.IHttpService,
                private $q: angular.IQService,
                private $timeout: angular.ITimeoutService,
                private bundleApi: BundleService,
                private loadingSynchronizer: RichTextContentLoadingSynchronizer) {
    }

    public init($scope: RichTextBindScope,
                element: angular.IAugmentedJQuery,
                bundleId: number) {
        this.element = element;
        this.elementId = element.attr('id');
        if (!this.elementId) {
            this.elementId = RandomUtils.generateRandomId('rich_text_element_');
            this.element.attr('id', this.elementId);
        }
        if ($scope.bundle) {
            this.bundle = $scope.bundle;
        }
        $scope.$watch("ngModel", value => {
            //console.log('Change richText model');
            this.onRichTextChange(value);
        });
        if (!this.bundle && bundleId) {
            this.loadBundlePromise = this.bundleApi.getBundle(bundleId);
            this.loadBundlePromise.then((bundle) => {
                this.bundle = bundle;
                this.loadBundlePromise = null;
            })
            .catch(() => {
                    this.loadBundlePromise = null;
            });
        }
        $scope.$on(Event.TA_FIND_RICH_TEXT_BIND_CONTROLLER, (event: ng.IAngularEvent, data: IFindRichTextBindControllerEventData) => {
            //console.log('On find rich text bind event');
            if (!data.richTextBindController && data.richTextElementId && data.richTextElementId == this.elementId) {
                data.richTextBindController = this;
                if (event.stopPropagation) {
                    event.stopPropagation();
                }
            }
        });

    }

    private onRichTextChange(value: any) {
        if (value) {
            this.loadingContent = true;
            //this.element.addClass('rich-text-loading');
            // delay displaying for 2 digest cycles + waiting for bundles loading
            this.$timeout(() => {
                this.$timeout(() => {
                    this.loadingSynchronizer
                        // todo: fix removeClass, load content at least 0.3 sec to show progress
                        .loadContent(this.elementId, this.$timeout(() => {return ''}, 300))
                        .finally(() => {
                            this.$timeout(() => {
                                //console.log('After load in richtext');
                                if (this.waitContentLoadingDeferred) {
                                    //console.log('Resolving wait content deferred after content loaded...');
                                    this.waitContentLoadingDeferred.resolve();
                                    this.waitContentLoadingDeferred = null;
                                }
                                this.loadingContent = false;
                                //this.element.removeClass('rich-text-loading');
                            }, 0)}
                        );
                }, 0);
            }, 0);
        }
    }

    public getBundleFile(bundleFileKey: string): IPromise<IBundleFile> {
        if (this.bundle) {
            return this.$q.when(this.bundle.filesMap.get(bundleFileKey));
        } else if (this.loadBundlePromise) {
            return this.loadBundlePromise.then((bundle) => {
                if (bundle) {
                    return bundle.filesMap.get(bundleFileKey);
                } else {
                    return null;
                }
            })
        }
        return this.$q.when(null);
    }

    public loadContentSynchronized(loadDataPromise: angular.IPromise<any>): angular.IPromise<string> {
        return this.loadingSynchronizer.loadContent(this.elementId, loadDataPromise);
    }

    public waitForContentLoaded(): IPromise<any> {
        if (this.waitContentLoadingDeferred) {
            this.waitContentLoadingDeferred.reject();
        }
        //we should wait model change for 1 digest, otherwise - resolve RichText loading deferred
        this.$timeout(() => {
            if (!this.loadingContent && this.waitContentLoadingDeferred) {
                //console.log('RichText Model was not changed, content is not loading... Reject waitContentLoadingDeferred.');
                this.waitContentLoadingDeferred.reject();
                this.waitContentLoadingDeferred = null;
            }
        }, 0);
        this.waitContentLoadingDeferred = this.$q.defer<any>();
        return this.waitContentLoadingDeferred.promise;
    }
}

export function RichTextBindDirectiveFactory(url: UrlService): angular.IDirective {
    function link($scope: RichTextBindScope,
                  element: angular.IAugmentedJQuery,
                  attributes: RichTextBindAttributes,
                  controller: RichTextBindController) {
        controller.init($scope, element, attributes.bundleId);
    }

    let directive: angular.IDirective = {
        restrict: "A",
        link: link,
        controller: RichTextBindController,
        scope: {
            ngModel: "=?",
            bundle: "=?vhsBundle"
        }
    };
    return directive;
}

RichTextBindDirectiveFactory.$inject = [
    UrlService.NAME
];
