import {SettingsService} from "./SettingsService";
import {CommonSettingsController} from "./CommonSettingsController";
import {StoragePlacesRegistryController} from "./StoragePlacesRegistryController";
import {StoragePlaceEditController} from "./StoragePlaceEditController";

import {SecurityModule} from "./../security/SecurityModule";
import {UrlsModule} from "./../urls/UrlsModule";

export var SettingsModule: angular.IModule = angular
        .module("settings", [
            SecurityModule.name,
            UrlsModule.name
        ])
        .service(SettingsService.NAME, SettingsService)
        .controller(CommonSettingsController.NAME, CommonSettingsController)
        .controller(StoragePlacesRegistryController.NAME, StoragePlacesRegistryController)
        .controller(StoragePlaceEditController.NAME, StoragePlaceEditController)
    ;