    public getCertificate(download: boolean, participationInfo?: any, progressId?: number) {
        let parameters: any = {
            courseId: this.courseId,
            userId: this.currentUser.id,
            progressId: progressId ? progressId : this.courseProgressId,
            certificateId: this.certificateParameters.certificateId,
            format: this.certificateParameters.format
        };
        if (participationInfo) {
            parameters.participationData = JSON.stringify(participationInfo);
        }
        // if (parameters.certificateId == null) {
        //     notifier.error("Impossible to generate the certificate. The template of certificate is not found");
        // } else {
            if (download) {
                this.courseService.downloadCertificate(parameters).then(result => {
                    this.$window.open(result);
                    this.certificateDownloadLink = result;
                    this.$timeout(() => {
                        const popup: any = $("#certificate_link_popup");
                        popup.showPopup();
                    }, 300);
                });
            } else {
                parameters.format = 'png';
                this.courseService.downloadCertificate(parameters).then(result => this.certificateContent = "<img style=\"width: 100%;\" src=\"" + result + "\"/>");
            }
        // }
    }

