    public getModulePreviewImageBundle(module: IModule): IBundleFile {
        if (this.courseBundle == undefined) {
            return undefined;
        }
        if (!this.courseBundle || !module || !module.id) {
            return null;
        }
        let fileKey = StringBuilder.interpolate(CourseDetailsController.MODULE_PREVIEW_IMAGE_BUNLE_FILE_TEMPLATE)
            .with(":index", module.sortIndex)
            .build();
        if (this.courseBundle.filesMap.has(fileKey)) {
            return this.courseBundle.filesMap.get(fileKey);
        } else {
            return null;
        }
    }

