    public LMSGetValue(key: string): any {
        //console.log('ask for ' + key);
        switch (key) {
            case "vhs.current_section.status":
                return this.selectedSection.status;
            case "vhs.current_section.quiz_taken":
                return this.sectionQuizResults.length > 0;
            case "vhs.current_section.quiz_passed":
                //console.log('LMS: isQuizPassed(): ' + this.isQuizPassed());
                return this.isQuizPassed();
            case "vhs.current_section.quiz_passed_score":
                //console.log('LMS: quiz_passed_score: ' + this.isQuizPassed());
                let maxScore = 0;
                for (let i = 0; i < this.sectionQuizResults.length; i++) {
                    let currentScore = this.sectionQuizResults[i].correctAnswersPercentage;
                    if (currentScore > maxScore) {
                        maxScore = currentScore;
                    }
                }
                //console.log('LMS: quiz_passed_score: ' + maxScore);
                return maxScore;
            case "vhs.current_section.quiz_last_score":
                return this.sectionQuizResults[this.sectionQuizResults.length - 1].correctAnswersPercentage;
            case "vhs.current_section.quiz_last_passed":
                return this.sectionQuizResults[this.sectionQuizResults.length - 1].passed;
            case "vhs.current_section.quiz_last_correct_answers":
                return this.sectionQuizResults[this.sectionQuizResults.length - 1].correctAnswersCount;
            case "vhs.current_section.quiz_questions_count":
                //console.log('quizes count = ' + this.sectionQuiz.questions.length);
                return this.sectionQuiz.questions.length;
            case "vhs.current_date":
                return this.currentDate;
            case "vhs.current_section.course-certificates":
                return this.courseCertificates ? this.courseCertificates : [];
            case "vhs.course.participation_form_allowed":
                return this.coursePassed;
        }
        return undefined;
    }

    public LMSSetValue(key: string, value: any): any {
        switch (key) {
            case "vhs.current_section.quiz.complete.listener":
                if (typeof(value) == "function") {
                    this.lmsQuizCompleteListeners.push(<()=>any> value);
                    return true;
                } else {
                    return false;
                }
            case "vhs.current_section.course-certificate-id":
                this.certificateParameters.certificateId = parseInt(value);
                return parseInt(value);
        }
    }

    public LMSCommit(key: string): any {
        switch (key) {
            case "vhs.current_section.start_quiz":
                return this.startSectionQuiz();
            case "vhs.current_section.go_back":
                if (this.prevSection && this.prevSection.id) {
                    return this.setLeftMenuItem(this.prevSection);
                } else {
                    return false;
                }
            case "vhs.current_section.go_next":
                if (this.nextSection) {
                    return this.setLeftMenuItem(this.nextSection);
                } else {
                    return false;
                }
            case "vhs.current_section.go_to_start":
                if (this.modules && this.modules.length > 0 && this.sectionsMap.get(this.modules[0].id).length > 0) {
                    this.setLeftMenuItem(this.sectionsMap.get(this.modules[0].id)[0].section);
                    return true;
                } else {
                    return false;
                }
            case "vhs.current_section.quiz.hide_info":
                return this.showQuizInfoSection = false;
            case "vhs.current_section.quiz.hide_results":
                return this.showQuizResultsSection = false;
            case "vhs.current_section.quiz.hide_start_section":
                return this.showQuizStartSection = false;
            case "vhs.current_section.landing_page.hide_start_section":
                return this.showLandingPageStartCourseButton = false;
            case "vhs.start_course":
                if (!this.showParticipationForm && this.showStartButton) {
                    return this.startOrContinueCourse();
                } else {
                    return false;
                }
            case "vhs.show_participation_form":
                if (!this.showParticipationForm) {
                    this.startEditCourseParticipationForm(true);
                    return true;
                } else {
                    return false;
                }
            case "vhs.current_section.get_certificate":
                if (this.isQuizPassed() /* && this.courseCertificates && this.courseCertificates.length > 0*/) {
                    return this.getCertificate(true);
                } else {
                    if (!this.courseCertificates || this.courseCertificates.length == 0) {
                        notifier.error("Impossible to generate the certificate. The template of certificate is not found");
                    } else {
                        notifier.error("Impossible to generate the certificate.")
                    }
                    return false;
                }
        }
    }

    public startOrContinueCourse() {
        if (!this.courseStarted || this.isCourseShouldBeRestarted()) {
            this.startCourse();
        } else {
            if (this.modules && this.modules.length > 0 && this.sectionsMap.get(this.modules[0].id).length > 0) {
                this.setLeftMenuItem(this.sectionsMap.get(this.modules[0].id)[0].section);
            } else if (this.nextSection) {
                this.setLeftMenuItem(this.nextSection);
            }
        }
        return true;
    }

