import {BaseRouter} from "./BaseRouter";

export class InfoRouter extends BaseRouter {
    public static NAME = "urls.InfoRouter";
    public static aboutProjectRoute = "/info/about/project";
    public static aboutCompanyRoute = "/info/about/company";
    public static faqRoute = "/info/faq";
    public static contactsRoute = "/info/contacts";

    public static $inject = [
        "$location"
    ];

    constructor($location: ng.ILocationService) {
        super($location);
    }

    public aboutProjectUrl(): string {
        return this.url(InfoRouter.aboutProjectRoute);
    }

    public aboutCompanyUrl(): string {
        return this.url(InfoRouter.aboutCompanyRoute);
    }

    public faqUrl(): string {
        return this.url(InfoRouter.faqRoute);
    }

    public contactsUrl(): string {
        return this.url(InfoRouter.contactsRoute);
    }
}