import {UrlService} from "./../utils/UrlService";

export function PageFooterDirectiveFactory(url: UrlService): angular.IDirective {
    var directive: angular.IDirective = {
        restrict: "A",
        templateUrl: url.template("components/page-footer.html")
    };
    return directive;
}

PageFooterDirectiveFactory.$inject = [
    UrlService.NAME
];
