import {ReferenceUtils} from "./../ReferenceUtils"
import {IReferenceEntityMetadata, IReferenceRequiredAttributesGroupMetadata} from "./../models/ReferenceMetadata"

export class ReferenceRequiredAttributesGroupValidator {

    protected utils: ReferenceUtils;
    protected entity: Map<string, any>;
    protected metadata: IReferenceEntityMetadata;
    protected validationErrorsMap: Map<string, string>;

    constructor(utils: ReferenceUtils,
                entity: Map<string, any>,
                metadata: IReferenceEntityMetadata,
                validationErrorsMap: Map<string, string>) {
        this.utils = utils;
        this.entity = entity;
        this.metadata = metadata;
        this.validationErrorsMap = validationErrorsMap;
    }

    public valid(): boolean {
        if (!this.utils || !this.entity || !this.metadata || !this.validationErrorsMap) {
            return false;
        }
        if (!this.metadata.requiredAttributesGroup) {
            return true;
        }
        return this.entityByRequiredAttributesGroupValid(this.entity, this.metadata.requiredAttributesGroup);
    }

    private entityByRequiredAttributesGroupValid(referenceEntity: Map<string, any>,
                                                 requiredAttributesGroup: IReferenceRequiredAttributesGroupMetadata): boolean {
        var logicalOperatorType = requiredAttributesGroup.logicalOperatorType;
        var result = this.entityByRequiredAttributesValid(referenceEntity, requiredAttributesGroup);
        for (var i = 0; i < requiredAttributesGroup.requiredAttributesGroups.length; ++i) {
            var requiredAttrsGroup = requiredAttributesGroup.requiredAttributesGroups[i];
            if (this.utils.isAndLogicalOperatorType(logicalOperatorType)) {
                result = result && this.entityByRequiredAttributesGroupValid(referenceEntity, requiredAttrsGroup);
            } else if (this.utils.isOrLogicalOperatorType(logicalOperatorType)) {
                result = result || this.entityByRequiredAttributesGroupValid(referenceEntity, requiredAttrsGroup);
            }
        }
        return result;
    }

    private entityByRequiredAttributesValid(referenceEntity: Map<string, any>,
                                            requiredAttributesGroup: IReferenceRequiredAttributesGroupMetadata): boolean {
        var attributes = requiredAttributesGroup.attributes;
        var logicalOperatorType = requiredAttributesGroup.logicalOperatorType;
        if (!attributes || attributes.length == 0) {
            return this.utils.isAndLogicalOperatorType(logicalOperatorType);
        }
        var firstAttr = attributes[0];
        var result = this.utils.validAttribute(firstAttr.type, referenceEntity[firstAttr.systemName]);
        for (var i = 1; i < attributes.length; ++i) {
            var attr = attributes[i];
            if (this.utils.isAndLogicalOperatorType(logicalOperatorType)) {
                result = result && this.utils.validAttribute(attr.type, referenceEntity[attr.systemName]);
            } else if (this.utils.isOrLogicalOperatorType(logicalOperatorType)) {
                result = result || this.utils.validAttribute(attr.type, referenceEntity[attr.systemName]);
            }
        }
        return result;
    }
}