import {CoursesController} from "./CoursesController";
import {CourseDetailsController} from "./CourseDetailsController";
import {QuizDetailsController} from "./QuizDetailsController";
import {CourseEditController} from "./CourseEditController";
import {CourseService} from "./CourseService";
import {ProgressService} from "./ProgressService";

import {SecurityModule} from "./../security/SecurityModule";
import {AuthCoreModule} from "./../authcore/AuthCoreModule";
import {UrlsModule} from "./../urls/UrlsModule";
import {QuizzesModule} from "./../quizzes/QuizzesModule";
import {CourseProgressController} from "./CourseProgressController";
import {CourseInfoController} from "./CourseInfoController";

export var CoursesModule: angular.IModule = angular
    .module("courses", [
        SecurityModule.name,
        AuthCoreModule.name,
        UrlsModule.name,
        QuizzesModule.name
    ])
    .service(CourseService.NAME, CourseService)
    .service(ProgressService.NAME, ProgressService)
    .controller(CoursesController.NAME, CoursesController)
    .controller(CourseDetailsController.NAME, CourseDetailsController)
    .controller(QuizDetailsController.NAME, QuizDetailsController)
    .controller(CourseEditController.NAME, CourseEditController)
    .controller(CourseProgressController.NAME, CourseProgressController)
    .controller(CourseInfoController.NAME, CourseInfoController);