import {MaterialsRouter} from "./../urls/MaterialsRouter";
import {FinancesRouter} from "./../urls/FinancesRouter";

import {VhsSearchController} from "./../components/VhsSearchDirective";
import {MenuHelper} from "./../utils/MenuHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {IGridModel, GridModel, IGridLoadingListener} from "./../IGridModel";
import {HttpGridHelper} from "./../IHttpGridService";
import {UrlService} from "./../utils/UrlService";
import {UserService} from "./../users/UserService";
import {Moment} from "./../utils/Moment";
import {ObjectTypeUtils} from "./../utils/ObjectTypeUtils";
import {MaterialType} from "./models/MaterialType";
import {IMaterial} from "./models/IMaterial";
import {ISelectable} from "./../ISelectable";
import {MaterialService} from "./MaterialService";
import {ReferencedObjectType} from "./../ReferencedObjectType";

interface IExtMaterial extends IMaterial {
    shown?: boolean;
}

export class SearchController {
    public static NAME = "materials.SearchController";

    private searchType: string;
    private searchQuery: string;

    public materialsGridModel: IGridModel<IMaterial>;
    public viewType: number;
    public searchViewType: number;
    public owners: Array<ISelectable> = [];

    public static $inject = [
        "$http",
        "$location",
        "$timeout",
        UrlService.NAME,
        UserService.NAME,
        MaterialsRouter.NAME,
        FinancesRouter.NAME
    ];

    constructor($http: angular.IHttpService,
                private $location: angular.ILocationService,
                private $timeout: angular.ITimeoutService,
                private url: UrlService,
                private userService: UserService,
                public materialsRouter: MaterialsRouter,
                public financesRouter: FinancesRouter) {
        this.searchType = $location.search()[VhsSearchController.FILE_FORMAT_PARAMETER];
        this.searchQuery = $location.search()[VhsSearchController.SEARCH_STRING_PARAMETER];

        MenuHelper.setCurrentMenuItem(MenuHelper.SEARCH_MENU_KEY);
        PageHeader.set().header("Search").update();

        this.materialsGridModel = new GridModel(HttpGridHelper.createLoader<IMaterial>($http, this.url.service("/api/materials")));
        this.materialsGridModel.pageSize = 9;
        this.searchType != null ? this.materialsGridModel.filter.fileType = this.searchType : "";
        this.searchQuery != null ? this.materialsGridModel.filter.searchString = this.searchQuery : "";
        this.searchViewType = 1;
        this.viewType = null;

        this.userService.getUsersWithoutExcludedUsersForSelector().then(result =>{
            this.owners = angular.copy(result);
        });
    }

    public searchMaterial(): void {
        this.materialsGridModel.filter.dateFrom = Moment.textToJavaDate(this.materialsGridModel.filter.dateFromText);
        this.materialsGridModel.filter.dateTo = Moment.textToJavaDate(this.materialsGridModel.filter.dateToText);
        this.materialsGridModel.filter.viewType = this.viewType;
        this.materialsGridModel.loadPage(0);
    }

    public filterMaterialViewType(viewType: number): void {
        this.viewType = viewType;
        this.searchMaterial();
    }

    public clearSearch() {
        this.viewType = null;
        this.materialsGridModel.filter.searchString = null;
        this.materialsGridModel.filter.viewType = null;
        this.materialsGridModel.filter.ownerId = "";
        this.materialsGridModel.filter.subjectId = "";
        this.materialsGridModel.filter.topic = null;
        this.materialsGridModel.filter.languageId = "";
        this.materialsGridModel.filter.fileType = "";
        this.materialsGridModel.filter.dateFrom = null;
        this.materialsGridModel.filter.dateFromText = null;
        this.materialsGridModel.filter.dateTo = null;
        this.materialsGridModel.filter.dateToText = null;
        this.materialsGridModel.loadPage(0);
    }

    public loadMaterialPage(materialId: number) {
        this.$location.url(this.materialsRouter.watchUrl(materialId));
    }

    public doPurchase(materialId: number): void {
        this.$location.url(this.financesRouter.purchaseUrl(materialId, ReferencedObjectType.MATERIAL));
    }

    public showPuchaseHint(id: number): void {
        $('#button-purchase-' + id).popover('show');
        this.$timeout(() => $('#button-purchase-' + id).popover('hide'), 5000);
    }

    public getMaterialTypeClass(materialTypeId: number): string {
        return ObjectTypeUtils.getMaterialGlyphiconClass(materialTypeId);
    }

    public getMaterialTypeTitle(materialTypeId: number): string {
        return ObjectTypeUtils.getMaterialTypeTitle(materialTypeId);
    }

    public showDetails(material: IExtMaterial) {
        material.shown = !material.shown;
    }
}