import {HttpInterceptor} from "./HttpInterceptor";

import {AuthCoreModule} from "./../authcore/AuthCoreModule";
import {CommonModule} from "./../common/CommonModule";

export var HttpModule: angular.IModule = angular
    .module("http", [
        AuthCoreModule.name,
        CommonModule.name
    ])
    .factory(HttpInterceptor.NAME, HttpInterceptor.Factory);