import {MaterialTypeEnum} from "./../materials/models/MaterialType";

export interface IVhsThumbnailModel {
    previewUrl?: string;
    materialTypeId?: number;
    typeId?: number;
    contentTypeKey?: string;
    typeKey?: string;
}

export interface IVhsThumbnailScope extends angular.IScope {
    vhsThumbnailModel: IVhsThumbnailModel;
}

export function VhsThumbnailDirectiveFactory(): angular.IDirective {
    function link($scope: IVhsThumbnailScope) {
        $scope.vhsThumbnailModel.previewUrl = $scope.vhsThumbnailModel.previewUrl ||
            ("themes/default/img/thumbnail-" +
            (MaterialTypeEnum[$scope.vhsThumbnailModel.materialTypeId || $scope.vhsThumbnailModel.typeId]
            || $scope.vhsThumbnailModel.contentTypeKey || $scope.vhsThumbnailModel.typeKey) +
            ".png").toLowerCase();
    }

    return {
        priority: 1,
        restrict: "A",
        scope: {
            vhsThumbnailModel: "="
        },
        link: link
    };
}
