import {PermissionsService} from "./../security/PermissionsService";
import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";

import {UrlService} from "./../utils/UrlService";
import {UserService} from "./UserService";
import {UsersRouter} from "./../urls/UsersRouter";

import {MenuHelper} from "./../utils/MenuHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {IGridModel, GridModel, IGridLoadingListener} from "./../IGridModel";
import {HttpGridHelper} from "./../IHttpGridService";
import {ITreeElementInfo} from "./../tree/models/TreeElementInfo";
import {TreeElementsInfoFiller} from "./../tree/TreeElementsInfoFiller";

import {IGroupInHierarchy} from "./models/IGroupInHierarchy";

interface IGroupInHierarchyExtended extends ITreeElementInfo<IGroupInHierarchyExtended>, IGroupInHierarchy {
}

export class GroupsController implements IGridLoadingListener {
    public static NAME = "users.GroupsController";

    public groupsGridModel: IGridModel<IGroupInHierarchyExtended>;
    public systemPermissions: ISystemPermissions;

    public static $inject = [
        "$http",
        UrlService.NAME,
        UserService.NAME,
        PermissionsService.NAME,
        UsersRouter.NAME
    ];

    constructor($http: angular.IHttpService,
                private url: UrlService,
                private usersService: UserService,
                private permissionsService: PermissionsService,
                public usersRouter: UsersRouter) {
        MenuHelper.setCurrentMenuItem(MenuHelper.ADMINISTRATION_GROUPS_MENU_KEY);
        PageHeader.set().header("User Groups").update();

        permissionsService.getSystemPermissions().then(p => {
            PageHeader.set().preserveHeader()
                .addActionLinkIf(p.createGroup, '+&nbsp;New',
                this.usersRouter.groupsAddUrl()).update();
            this.systemPermissions = p;
        });

        this.groupsGridModel = new GridModel(HttpGridHelper.createLoader<IGroupInHierarchyExtended>($http,
            this.url.service("/api/users/groups/all")));
        this.groupsGridModel.addLoadingListener(this);
    }

    public getGroupEditUrl(id: number): string {
        return this.usersRouter.groupEditUrl(id);
    }

    public activateGroup(id: number): void {
        this.usersService.setActiveGroupWithChildren(true, id)
            .then(result => {
                this.groupsGridModel.loadCurrentPage();
            });
    }

    public getIsActiveGridStyle(isActive: boolean): any {
        return isActive
            ? {}
            : {"color": "#999999"};
    }

    public convertIsActive(isActive: boolean): string {
        return isActive != undefined
            ? isActive ? 'Yes' : 'No'
            : undefined;
    }

    public startLoading() {
        //do nothing
    }

    public endLoading() {
        TreeElementsInfoFiller.fillHierarchyInfoWithMaxLevel(this.groupsGridModel.entries, Number.MAX_VALUE);
    }
}