import {BaseRouter} from "./BaseRouter";

export class PersonalRouter extends BaseRouter {
    public static NAME = "urls.PersonalRouter";
    public static profileRoute = "/personal/profile";
    public static favoritesRoute = "/personal/favorites";
    public static uploadsRoute = "/personal/uploads";
    public static coursesRoute = "/personal/courses";
    public static quizzesRoute = "/personal/quizzes";
    public static playlistsRoute = "/personal/playlists";
    public static subscriptionsRoute = "/personal/subscriptions";
    public static newCommentsRoute = "/personal/comments/news";
    public static supportRoute = "/personal/support";
    public static couponsRoute = "/personal/coupons";
    public static purchasesHistoryRoute = "/personal/purchases/history";
    public static balanceRoute = "/personal/balance";
    public static settingsRoute = "/personal/settings";
    public static settingsAopaRoute = "/personal/settings/password";

    public static $inject = [
        "$location"
    ];

    constructor($location: ng.ILocationService) {
        super($location);
    }

    public profileUrl(): string {
        return this.url(PersonalRouter.profileRoute);
    }

    public favoritesUrl(): string {
        return this.url(PersonalRouter.favoritesRoute);
    }

    public uploadsUrl(): string {
        return this.url(PersonalRouter.uploadsRoute);
    }

    public coursesUrl(): string {
        return this.url(PersonalRouter.coursesRoute);
    }

    public quizzesUrl(): string {
        return this.url(PersonalRouter.quizzesRoute);
    }

    public playlistsUrl(): string {
        return this.url(PersonalRouter.playlistsRoute);
    }

    public subscriptionsUrl(): string {
        return this.url(PersonalRouter.subscriptionsRoute);
    }

    public newCommentsUrl(): string {
        return this.url(PersonalRouter.newCommentsRoute);
    }

    public supportUrl(): string {
        return this.url(PersonalRouter.supportRoute);
    }

    public couponsUrl(): string {
        return this.url(PersonalRouter.couponsRoute);
    }

    public purchasesHistoryUrl(): string {
        return this.url(PersonalRouter.purchasesHistoryRoute);
    }

    public balanceUrl(): string {
        return this.url(PersonalRouter.balanceRoute);
    }

    public settingsUrl(): string {
        return this.url(PersonalRouter.settingsRoute);
    }
    public settingsAopaUrl(): string {
        return this.url(PersonalRouter.settingsAopaRoute);
    }
}
