import {UrlService} from "./../utils/UrlService";
import {ISelectableReferenceEntity} from "./../reference/models/ISelectableReferenceEntity";
import {ReferenceEntitiesParams} from "./../reference/models/IReferenceEntitiesParams";
import {ReferenceUtils} from "./../reference/ReferenceUtils";
import {ReferenceService} from "./../reference/ReferenceService";

export interface IIReferenceMultipleSelectorDirectiveScope extends ng.IScope {
    referenceName?: string;
    referenceFilter?: string;
    referenceSorting?: string;
    model?: any;
    modelProperty?: string;
    disabled?: boolean;

    vm?: ReferenceMultipleSelectorController;
}

export class ReferenceMultipleSelectorController {

    public selectedIds: Array<number>;
    public referenceEntities: Array<ISelectableReferenceEntity>;

    public static $inject = [
        "$scope",
        ReferenceService.NAME,
        ReferenceUtils.NAME
    ];

    constructor(protected $scope: IIReferenceMultipleSelectorDirectiveScope,
                protected referenceApi: ReferenceService,
                protected referenceUtils: ReferenceUtils) {
        $scope.vm = this;
        this.selectedIds = angular.copy(this.$scope.model[this.$scope.modelProperty]);

        this.$scope.$watch(() => this.$scope.model[this.$scope.modelProperty], () => {
            this.selectedIds = angular.copy(this.$scope.model[this.$scope.modelProperty]);
            this.setModelIds(this.selectedIds);
        });

        this.referenceApi.getActiveReferenceEntitiesForSelector(this.$scope.referenceName, new ReferenceEntitiesParams(
            this.$scope.referenceFilter, this.$scope.referenceSorting, undefined, undefined), null)
            .then(result => {
                this.referenceEntities = result;
            });
    }

    public setModelIds(selectedIds: any): void {
        this.$scope.model[this.$scope.modelProperty].splice(0, this.$scope.model[this.$scope.modelProperty].length);
        for (var i = 0; i < selectedIds.length; ++i) {
            this.$scope.model[this.$scope.modelProperty].push(selectedIds[i]);
        }
    }
}

export function ReferenceMultipleSelectorDirectiveFactory(url: UrlService): ng.IDirective {
    var directive: ng.IDirective = {};
    directive.restrict = "A";
    directive.controller = ReferenceMultipleSelectorController;
    directive.scope = {
        referenceName: "=vhsReferenceName",
        referenceFilter: "=?vhsReferenceFilter",
        referenceSorting: "=?vhsReferenceSorting",
        model: "=vhsModel",
        modelProperty: "=vhsModelProperty",
        disabled: "=?vhsDisabled"
    };
    directive.templateUrl = url.template("components/reference-multiple-selector.html");
    directive.transclude = true;
    return directive;
}

ReferenceMultipleSelectorDirectiveFactory.$inject = [
    UrlService.NAME
];
