import {TokenStorage} from "./TokenStorage";
import {AuthRedirect} from "./AuthRedirect";
import {AuthManager} from "./AuthManager";

import {UrlsModule} from "./../urls/UrlsModule";
import {InfrastructureModule} from "./../infrastructure/InfrastructureModule";

export var AuthCoreModule: angular.IModule = angular
    .module("authcore", [
        UrlsModule.name,
        InfrastructureModule.name
    ])
    .service(TokenStorage.NAME, TokenStorage)
    .service(AuthRedirect.NAME, AuthRedirect)
    .service(AuthManager.NAME, AuthManager);