import INgModelController = angular.INgModelController;
import IScope = angular.IScope;
import {Moment} from "../utils/Moment";
interface DateTimePickerAttributes extends angular.IAttributes {
    navigation: string;
    ngModel: string;
    maxCurrentDate: boolean
}
interface DatePickerScope extends IScope {
    minDate?: string;
    maxDate?: string;
    minDateValidation: boolean;
    maxDateValidation: boolean;
}

export function DateTimePickerDirectiveFactory(): angular.IDirective {
    function link($scope: DatePickerScope, element: angular.IAugmentedJQuery,
                  attributes: DateTimePickerAttributes, ngModel: INgModelController,
                  transclude: angular.ITranscludeFunction) {

        element.datetimepicker({
            pickTime: false,
            language: "en",

            // TODO: absent
            format: "M/D/YYYY",
            collapse: true,
            keyboardNavigation: attributes.navigation || true,
            maxDate: $scope.maxDateValidation ? moment($scope.maxDate).toDate() : null
        });

        element.bind("dp.change", function () {
            element.trigger('input');
        });

        $scope.$watch(attributes.ngModel, value => {
            if (value) {
                element.trigger("change");
            }
        });

        $scope.$on("$destroy",
            () => element.datetimepicker().data('DateTimePicker').destroy());

        if ($scope.maxDateValidation) {
            ngModel.$validators["maxDate"] = (modelValue: string, viewValue: string) => {
                if (!$scope.maxDate || !viewValue) {
                   return true;
                }
                const max = moment($scope.maxDate);

                const value = Moment.parse(viewValue);
                return !max.isValid() || !value.isValid() || !value.isAfter(max);
            };
            $scope.$watch("maxDate", () => {
                ngModel.$validate();
            });
        }

        if ($scope.minDateValidation) {
            ngModel.$validators["minDate"] = (modelValue: string, viewValue: string) => {
                if (!$scope.minDate || !viewValue) {
                    return true;
                }
                const min = moment($scope.minDate);

                const value = Moment.parse(viewValue);
                return !min.isValid() || !value.isValid() || !value.isBefore(min);
            };
            $scope.$watch("minDate", () => {
                ngModel.$validate();
            });
        }
    }

    return {
        restrict: "A",
        require: "?ngModel",
        scope: {
            minDate: "=?vhsMin",
            maxDate: "=?vhsMax",
            minDateValidation: "=?vhsMinDateValidation",
            maxDateValidation: "=?vhsMaxDateValidation"
        },
        link: link
    };
}