import {AuthService} from "../auth/AuthService";
import {CourseService} from "./CourseService";
import {ProgressService} from "./ProgressService";
import {CoursesRouter} from "../urls/CoursesRouter";
import {SecurityService} from "../security/SecurityService";
import {MenuHelper} from "../utils/MenuHelper";
import {ICourse} from "./models/ICourse";
import {PageHeader} from "../utils/PageHeaderHelper";
import {Moment} from "../utils/Moment";
import {ISelectable} from "../ISelectable";
import {IUserInfo} from "../security/IUserInfo";
import {CourseProgressStatus} from "./models/CourseProgressStatus";
import {AuthManager} from "../authcore/AuthManager";
import {ILandingPage} from "../courses/models/ILandingPage";
import {StorageWrapper, Storages} from "./../infrastructure/Storage";
import {ICourseProgress} from "./models/ICourseProgress";

export class CourseInfoController {
    public static NAME = "courses.CourseInfoController";

    public courseId: number;
    public owners: Array<ISelectable> = [];
    public course: ICourse = {};
    public courseStarted: boolean;
    public currentUserInOwners: boolean = false;
    private currentUser: IUserInfo = {};
    public isAuthenticated: boolean;
    public landingPage: ILandingPage;
    private courseProgressId: number;
    private courseProgress: ICourseProgress;

    public static $inject = [
        "$location",
        SecurityService.NAME,
        "$routeParams",
        CoursesRouter.NAME,
        CourseService.NAME,
        ProgressService.NAME,
        AuthService.NAME,
        AuthManager.NAME,
        Storages.SESSION_NAME
    ];

    constructor(private $location: angular.ILocationService,
                private securityService: SecurityService,
                private $routeParams: ng.route.IRouteParamsService,
                private coursesRouter: CoursesRouter,
                private courseService: CourseService,
                private progressService: ProgressService,
                private authService: AuthService,
                private authManager: AuthManager,
                private sessionStorageWrapper: StorageWrapper) {
        this.courseId = $routeParams[CoursesRouter.COURSE_ID_PARAM];
        let courseIdStr = String(this.courseId);
        this.isAuthenticated = this.authManager.isAuthenticated();
        this.securityService
            .demandCoursePermissions(courseIdStr, coursePermissions => coursePermissions.view);
        var menuItemKey: string = MenuHelper.getCurrentMenuItemKey();
        if (!menuItemKey || menuItemKey.length == 0) {
            MenuHelper.setCurrentMenuItem(MenuHelper.EDUCATION_COURSES_MENU_KEY);
        }
        this.authService.getCurrentUser().then(user => {
            angular.copy(user, this.currentUser);
        });
        this.loadCourse();
    }

    private loadCourse() {
        var promise: angular.IPromise<ICourse> = this.courseService.get(this.courseId);
        promise.then(result => {
            this.course = angular.copy(result);
            PageHeader.set().header(this.course.topic || this.course.subjectName).update();
            this.course.endDateText = Moment.isValidDate(result.endDate);
            this.course.startDateText = Moment.isValidDate(result.startDate);
            this.loadOwners();
        });
    }

    private loadOwners() {
        this.courseService.getCourseOwners(this.courseId).then(result => {
            angular.copy(result, this.owners);
            this.isCurrentUserInOwners();
            this.getCourseProgress();
        });
    }

    private isCurrentUserInOwners(): void {
        for (let i = 0; i < this.owners.length; i++) {
            if (this.owners[i].id == this.currentUser.id) {
                this.currentUserInOwners = true;
            }
        }
    }

    // TODO: This method here is only to calculate 'courseStarted' boolean value, which determines whether to show 'Progress' tab.
    // TODO: If we could eliminate this query, it would be good for performance.
    private getCourseProgress(): void {
        this.courseProgressId = this.sessionStorageWrapper.get<number>(this.progressService.COURSE_PROGRESS_ID_STORAGE_KEY);
        this.progressService.getCourseProgress(this.courseId, this.courseProgressId)
            .then(result => {
                this.courseProgress = angular.copy(result);
                this.courseProgressId = this.courseProgress.id;
                this.sessionStorageWrapper.put<number>(this.progressService.COURSE_PROGRESS_ID_STORAGE_KEY, this.courseProgressId);
                this.courseStarted = (result.status && result.status !== CourseProgressStatus[CourseProgressStatus.NOT_STARTED]);
            });
    }

////////// redirect
    private redirectToProgress(): void {
        this.$location.url(this.coursesRouter.progressUrl(this.courseId));
    }

    private redirectToDetails(): void {
        this.$location.url(this.coursesRouter.detailsUrl(this.courseId));
    }

    public editCourse(): void {
        this.$location.url(this.coursesRouter.updateUrl(this.courseId));
    }

    public loadLandingPage(): void {
        this.courseService.getLandingPage(this.courseId)
            .then(result => {
                this.landingPage = angular.copy(result);
            });
    }

    public isTopMenuActive(topMenuKey: string): boolean {
        return 'info' == topMenuKey;
    }

    public isShowProgressTab(): boolean {
        return (this.courseStarted || this.currentUserInOwners || this.course.permissions && this.course.permissions.edit) && this.isAuthenticated;
    }
}