import {MaterialService} from "./MaterialService";
import {SearchController} from "./SearchController";
import {MaterialController} from "./MaterialController";
import {MyDownloadsController} from "./MyDownloadsController";
import {EditMaterialController} from "./EditMaterialController";
import {PlaylistsController} from "./PlaylistsController";
import {PlaylistDetailsController} from "./PlaylistDetailsController";
import {PlaylistEditController} from "./PlaylistEditController";
import {PlaylistService} from "./PlaylistService";

import {AuthCoreModule} from "./../authcore/AuthCoreModule";
import {SecurityModule} from "./../security/SecurityModule";
import {UrlsModule} from "./../urls/UrlsModule";
import {PersonalModule} from "./../personal/PersonalModule";
import {SocialModule} from "./../social/SocialModule";
import {StorageModule} from "./../storage/StorageModule";
import {UsersModule} from "./../users/UsersModule";
import {CoursesModule} from "./../courses/CoursesModule";

export var MaterialsModule: angular.IModule = angular
        .module("materials", [
            AuthCoreModule.name,
            SecurityModule.name,
            UrlsModule.name,
            PersonalModule.name,
            SocialModule.name,
            StorageModule.name,
            UsersModule.name,
            CoursesModule.name
        ])
        .service(MaterialService.NAME, MaterialService)
        .service(PlaylistService.NAME, PlaylistService)
        .controller(SearchController.NAME, SearchController)
        .controller(MaterialController.NAME, MaterialController)
        .controller(MyDownloadsController.NAME, MyDownloadsController)
        .controller(EditMaterialController.NAME, EditMaterialController)
        .controller(PlaylistsController.NAME, PlaylistsController)
        .controller(PlaylistDetailsController.NAME, PlaylistDetailsController)
        .controller(PlaylistEditController.NAME, PlaylistEditController);
