import {UserService} from "./UserService";
import {CurrentUserProfileController} from "./CurrentUserProfileController";
import {UserEditController} from "./UserEditController";
import {UserSignupController} from "./UserSignupController";
import {UsersListController} from "./UsersController";
import {GroupsController} from "./GroupsController";
import {GroupEditController} from "./GroupEditController";

import {SettingsModule} from "./../settings/SettingsModule";
import {SecurityModule} from "./../security/SecurityModule";
import {UrlsModule} from "./../urls/UrlsModule";

export var UsersModule: angular.IModule = angular
    .module("users", [
        SettingsModule.name,
        SecurityModule.name,
        UrlsModule.name
    ])
    .service(UserService.NAME, UserService)
    .controller(CurrentUserProfileController.NAME, CurrentUserProfileController)
    .controller(UserSignupController.NAME, UserSignupController)
    .controller(UserEditController.NAME, UserEditController)
    .controller(UsersListController.NAME, UsersListController)
    .controller(GroupsController.NAME, GroupsController)
    .controller(GroupEditController.NAME, GroupEditController);