import {RichTextContentLoadingSynchronizer} from "./richtext/RichTextContentLoadingSynchronizer";

import {AuthCoreModule} from "./../authcore/AuthCoreModule";
import {AuthModule} from "./../auth/AuthModule";
import {SecurityModule} from "./../security/SecurityModule";
import {UrlsModule} from "./../urls/UrlsModule";
import {InfrastructureModule} from "./../infrastructure/InfrastructureModule";
import {MainModule} from "./../main/MainModule";
import {MaterialsModule} from "./../materials/MaterialsModule";
import {BroadcastsModule} from "./../broadcasts/BroadcastsModule";
import {NewsModule} from "./../news/NewsModule";
import {InfoModule} from "./../info/InfoModule";
import {ReferenceModule} from "./../reference/ReferenceModule";
import {StorageModule} from "./../storage/StorageModule";
import {LogsModule} from "./../logs/LogsModule";
import {SettingsModule} from "../settings/SettingsModule";
import {CurrentYearDirectiveFactory} from "./CurrentYearDirective";
import {SuffixValidatorDirectiveFactory} from "./SuffixValidatorDirective";

export var ComponentsModule: angular.IModule = angular
    .module("components", [
        AuthCoreModule.name,
        AuthModule.name,
        SecurityModule.name,
        MainModule.name,
        UrlsModule.name,
        InfrastructureModule.name,
        MaterialsModule.name,
        BroadcastsModule.name,
        NewsModule.name,
        InfoModule.name,
        ReferenceModule.name,
        StorageModule.name,
        LogsModule.name,
        SettingsModule.name
    ])
    .service(RichTextContentLoadingSynchronizer.NAME, RichTextContentLoadingSynchronizer)

    .directive("autofocus", AutoFocusDirectiveFactory)
    .directive("datePicker", DateTimePickerDirectiveFactory)
    .directive("vhsGridConfiguration", GridConfigurationDirectiveFactory)
    .directive("vhsGrid", GridDirectiveFactory)
    .directive("vhsGridEmpty", GridEmptyDirectiveFactory)
    .directive("vhsGridExternalConfiguration", GridExternalConfigurationFactory)
    .directive("vhsGridPagination", GridPaginationDirectiveFactory)
    .directive("login", LoginDirectiveFactory)
    .directive('multipleCheckboxSelector', MultipleCheckboxSelectorDirectiveFactory)
    .directive("vhsPageFooter", PageFooterDirectiveFactory)
    .directive("vhsPageHeader", PageHeaderDirectiveFactory)
    .directive('vhsSelectAll', VhsSelectAllDirectiveFactory)
    .directive('referenceCheckboxSelector', ReferenceCheckboxSelectorDirectiveFactory)
    .directive('referenceDropdownSelector', ReferenceDropdownSelectorDirectiveFactory)
    .directive('referenceMultipleSelector', ReferenceMultipleSelectorDirectiveFactory)
    .directive("tooltip", TooltipDirectiveFactory)
    .directive("vhsSearch", VhsSearchDirectiveFactory)
    .directive("personalMenu", PersonalMenuDirectiveFactory)
    .directive("navigationPersonalMenu", NavigationPersonalMenuDirectiveFactory)
    .directive("leftPersonalMenu", LeftPersonalMenuDirectiveFactory)
    .directive("vhsBreadcrumbs", VhsBreadcrumbsDirectiveFactory)
    .directive("vhsVideo", VhsVideoDirectiveFactory)
    .directive("vhsYoutubeVideo", VhsYoutubeVideoDirectiveFactory)
    .directive("vhsAudio", VhsAudioDirectiveFactory)
    .directive("usersAndGroupsSelector", UsersAndGroupsSelectorDirectiveFactory)
    .directive("selectOptionsDisabled", SelectOptionsDisabledDirectiveFactory)
    .directive("selectPlaceholder", SelectPlaceholderDirectiveFactory)
    .directive("uploadsStatus", UploadsStatusDirectiveFactory)
    .directive("vhsThumbnail", VhsThumbnailDirectiveFactory)
    .directive("vhsMaterialEditing", VhsMaterialEditingDirectiveFactory)
    .directive("vhsEmbeddedMaterial", VhsEmbeddedMaterialDirectiveFactory)
    .directive("vhsSelectMaterial", VhsSelectMaterialDirectiveFactory)
    .directive("vhsRichTextEditor", RichTextEditorDirectiveFactory)
    .directive("vhsRichTextBind", RichTextBindDirectiveFactory)
    .directive("vhsEmbeddedBundle", VhsEmbeddedBundleDirectiveFactory)
    .directive("vhsSelectEmbeddedBundle", VhsSelectEmbeddedBundleDirectiveFactory)
    .directive("vhsSelectSandbox", VhsSelectSandboxDirectiveFactory)
    .directive("vhsSandbox", VhsSandboxDirectiveFactory)
    .directive("vhsDownloadBundle", VhsDownloadBundleDirectiveFactory)
    .directive("vhsLoadBundleFile", LoadBundleFileDirectiveFactory)
    .directive("vhsFullScreen", FullScreenDirectiveFactory)
    .directive("vhsPageTitle", PageTitleDirectiveFactory)
    .directive("vhsCurrentYear", CurrentYearDirectiveFactory)
    .directive("entitiesSelect2Selector", EntitiesSelect2SelectorDirectiveFactory)
    .directive("suffixValidator", SuffixValidatorDirectiveFactory);
