/////////////// edit course hierarchy
    public saveModulesHierarchy(index: number, item: IModule): void {
        if (index != this.modules.length - 1) {
            index++;
        }
        if (index != item.sortIndex) {
            item.sortIndex = index;
            this.courseService.updateModuleHierarchy(this.courseId, item).then(result => {
                notifier.success("Course hierarchy has been changed");
                this.loadModules();
            });
        }
    }

    public saveSectionsHierarchy(index: number, item: ISectionInHierarchyExtended, newModuleId: number): void {
        this.isSectionEditing = false;
        this.editingSection = {};
        let oldModuleId = item.section.moduleId;
        if (item.parentId != null && newModuleId == oldModuleId) {
            index = this.recalculateSectionWithParentIndex(index, item, newModuleId);
        }
        if (item.parentId == null && (index != item.section.sortIndex || oldModuleId != newModuleId) ||
            item.parentId != null && index != item.section.sortIndex && oldModuleId == newModuleId) {
            if (item.parentId == null && ( (oldModuleId == newModuleId && index != this.sectionsMap.get(newModuleId).length - 1) ||
                (oldModuleId != newModuleId && index != this.sectionsMap.get(newModuleId).length) )) {
                index++;
            }
            item.section.moduleId = newModuleId;
            item.section.sortIndex = index;
            this.courseService.updateSectionHierarchy(this.courseId, item.section.moduleId, item.section).then(result => {
                notifier.success("Course hierarchy has been changed");
                this.loadSectionsForModule(newModuleId);
                if (oldModuleId != newModuleId) {
                    this.loadSectionsForModule(oldModuleId);
                }
            });
        } else {
            this.loadSectionsForModule(newModuleId);
            if (oldModuleId != newModuleId) {
                this.loadSectionsForModule(oldModuleId);
            }
        }
    }

    private recalculateSectionWithParentIndex(index: number, item: ISectionInHierarchyExtended, newModuleId: number): number {
        let parentIndex: number = 0;
        let children: Array<ISectionInHierarchyExtended> = null;
        this.sectionsMap.get(newModuleId).forEach(section => {
            if (section.id == item.parentId) {
                parentIndex = this.sectionsMap.get(newModuleId).indexOf(section);
                children = section.children;
            }
        });
        if (index < parentIndex) {
            index = 1;
        } else if (index >= parentIndex + children.length) {
            index = children[children.length - 1].section.sortIndex;
        } else {
            index = this.sectionsMap.get(newModuleId)[index + 1].section.sortIndex;
        }
        return index;
    }

