import {AuthService} from "./AuthService";
import {LogOnController} from "./LogOnController";
import {LogOutController} from "./LogOutController";
import {RestorePasswordController} from "./RestorePasswordController";
import {CreateNewPasswordController} from "./CreateNewPasswordController";
import {CheckValidCodeController} from "./CheckValidCodeController";

import {AuthCoreModule} from "./../authcore/AuthCoreModule";
import {UrlsModule} from "./../urls/UrlsModule";
import {InfrastructureModule} from "./../infrastructure/InfrastructureModule";

export var AuthModule: angular.IModule = angular
    .module("auth", [
        AuthCoreModule.name,
        UrlsModule.name,
        InfrastructureModule.name
    ])
    .service(AuthService.NAME, AuthService)
    .controller(LogOutController.NAME, LogOutController)
    .controller(LogOnController.NAME, LogOnController)
    .controller(RestorePasswordController.NAME, RestorePasswordController)
    .controller(CreateNewPasswordController.NAME, CreateNewPasswordController)
    .controller(CheckValidCodeController.NAME, CheckValidCodeController);