import {SortOrder, SortEntry} from "./../SortEntry";
import {StorageWrapper, Storages} from "./../infrastructure/Storage";
import {TimeoutUtils} from "./../utils/TimeoutUtils";
import {ObjectFieldResolver} from "./../utils/ObjectFieldResolver";
import {
    GridColumn,
    IGridModel,
    GridModel,
    IGridLoadingListener,
    IColumnVisibilityChangeListener,
    IPageSizeChangeListener,
    PersistedGridModelConfiguration
} from "./../IGridModel";

interface GridAttributes extends angular.IAttributes {
    gridModelPath: string;
    vhsGridUuid: string;
    vhsHasConfiguration?: boolean;
}

class ElementSortingInfo {
    public sortPath: string;
    public index: number = null;
    public order: SortOrder = null;
    public mandatory: boolean = false;

    public static fromString(str: string): ElementSortingInfo {
        //example: project.name desc(=2)
        var info: ElementSortingInfo = null;
        if (str) {
            var strParts: Array<string> = str.split(/[ ()]/g);
            info = new ElementSortingInfo();
            if (strParts.length > 0) {
                info.sortPath = strParts[0];
            }
            if (strParts.length > 1) {
                info.order = strParts[1] == "asc" ? SortOrder.ASC : SortOrder.DESC;
            }
            if (strParts.length > 2) {
                var index: string = strParts[2];
                if (index.charAt(0) == "=") {
                    info.mandatory = true;
                    index = index.substr(1);
                }
                info.index = +index;
            }
        }
        return info;
    }
}

export class GridController<T> implements IGridLoadingListener, IColumnVisibilityChangeListener, IPageSizeChangeListener {
    public parentScope;
    private directiveScope: angular.IScope;
    public gridModel: IGridModel<T>;

    private timeoutUtils: TimeoutUtils;
    private gridContainer: angular.IAugmentedJQuery;
    private tableContainer: angular.IAugmentedJQuery;
    private gridUuid: string = null;
    private hasConfiguration: boolean = false;
    private hasChangesForTemplateReload: boolean = false;
    private tableTemplate: string;


    private SORTABLE_HEADERS_CLASSES: Array<string> = ["sortable", "sortable-desc", "sortable-asc", "sortable-0", "sortable-1", "sortable-2", "sortable-3"];
    public static TABLE_SELECTOR = "table[data-vhs-table] ";
    public static SORTABLE_HEADERS_SELECTOR = "tr th[data-sortable]";
    public static TABLE_HEADERS_SELECTOR = "thead tr th";
    public static TABLE_CELLS_IN_TEMPLATE_SELECTOR = "tbody tr[ng-repeat] td";
    public static TABLE_CELLS_IN_TEMPLATE_SELECTOR_FOR_REPEAT_START = "tbody tr[ng-repeat-start]:not([data-columns-always-visible]) td";
    public static TABLE_CELLS_IN_TEMPLATE_SELECTOR_FOR_REPEAT_END = "tbody tr[ng-repeat-end]:not([data-columns-always-visible]) td";
    private SEARCH_ELEMENTS_SELECTOR = "[data-vhs-auto-search] select,[data-vhs-auto-search] input";
    private MAX_SORT_INDEX = 3;

    public static $inject = [
        "$scope",
        "$timeout",
        "$compile",
        Storages.LOCAL_NAME
    ];

    constructor($scope,
                private $timeout: angular.ITimeoutService,
                private $compile: angular.ICompileService,
                private localStorage: StorageWrapper) {
        $scope.gridVm = this;
        this.parentScope = $scope.$parent;
        this.directiveScope = $scope;
        this.timeoutUtils = new TimeoutUtils();
    }

    public init(element: angular.IAugmentedJQuery, modelPathInController: string, uuid: string, tableTemplate: string) {
        this.gridContainer = element;
        this.tableContainer = this.gridContainer.find(GridController.TABLE_SELECTOR);
        this.tableTemplate = tableTemplate;
        this.gridUuid = uuid ? uuid : "grid_" + new Date().getTime();
        if (typeof modelPathInController != 'undefined') {
            this.gridModel = ObjectFieldResolver.findForPath(this.parentScope, modelPathInController);
            this.gridModel.addLoadingListener(this);
            this.gridModel.addColumnVisibilityListener(this);
            this.gridModel.addPageSizeListener(this);
        }
        this.collectGridColumns();
        this.loadColumnsConfigurationFromStorage();
        this.loadFilterFromStorage();
        this.addPredefinedSorting();

        this.addAutoSearchListeners(); //currently listeners only outside table

        this.initDomElementsHandlers();

        this.processTemplate();

        this.gridModel.loadCurrentPage();
    }

    private initDomElementsHandlers() {
        this.addHeadersSortingHandlers();
        this.updateHeaderClasses();
    }

    public setIsConfigurable(hasConfiguration: boolean) {
        this.hasConfiguration = hasConfiguration;
        if (hasConfiguration) {
            this.processTemplate();
        }
    }

    public applyFilter() {
        this.gridModel.loadWithDelay(this.timeoutUtils, this);
    }

    public addSorting(fieldName: string): SortEntry {
        var info = this.findSortEntryInfo(fieldName);
        var sortEntry: SortEntry = null;
        if (info != null) {
            sortEntry = info.sortEntry;
        }
        if (sortEntry == null) {
            sortEntry = SortEntry.asc(fieldName);
            this.gridModel.sorting.push(sortEntry);
        } else {
            sortEntry.reverseOrder()
        }
        this.gridModel.loadCurrentPage();
        return sortEntry;
    }

    public resetSorting(fieldName: string) {
        var info = this.findSortEntryInfo(fieldName);
        var foundIndex: number = -1;
        if (info != null) {
            foundIndex = info.index;
        }
        if (foundIndex > -1) {
            this.gridModel.sorting.splice(foundIndex, 1);
        }
        this.gridModel.loadCurrentPage();
    }

    public goToPage(pageNumber: number) {
        if (this.gridModel.currentPage != pageNumber && pageNumber >= 0 && pageNumber < this.gridModel.totalPageCount) {
            this.gridModel.loadPage(pageNumber);
        }
    }

    public getPagesForPagination(): Array<number> {
        var currentPage: number = this.gridModel.currentPage;
        var totalPages: number = this.gridModel.totalPageCount;
        var pages: Array<number> = [];

        for (var i: number = currentPage; i >= 0 && (currentPage - i ) < 5; i--) {
            pages.push(i);
        }
        pages = pages.reverse();

        for (var i: number = currentPage + 1; i < totalPages && (i - currentPage) < 5; i++) {
            pages.push(i);
        }
        return pages;
    }

    public startLoading() {
        this.gridContainer.addClass("loading");
    }

    public endLoading() {
        this.gridContainer.removeClass("loading");
    }

    public onColumnVisibilityChange(column: GridColumn) {
        if (this.hasConfiguration && column != null) {
            this.hasChangesForTemplateReload = true;
            this.processTemplate();
        }
    }

    public upColumnPosition(column: GridColumn) {
        if (column && this.hasConfiguration) {
            if (column.position > 0) {
                this.doUpdateColumnPosition(column, column.position - 1);
            }
        }
    }

    public downColumnPosition(column: GridColumn) {
        if (column && this.hasConfiguration) {
            if (column.position < this.gridModel.gridColumns.length - 1) {
                this.doUpdateColumnPosition(column, column.position + 1);
            }
        }
    }

    private doUpdateColumnPosition(column: GridColumn, newPosition: number) {
        var currentColumnInPosition = this.findColumnByPosition(newPosition);
        if (currentColumnInPosition) {
            currentColumnInPosition.position = column.position;
        }
        column.position = newPosition;

        this.hasChangesForTemplateReload = true;
        this.processTemplate();
    }

    private findColumnByPosition(targetPosition: number) {
        for (var i = 0; i < this.gridModel.gridColumns.length; i++) {
            var column: GridColumn = this.gridModel.gridColumns[i];
            if (column.position == targetPosition) {
                return column;
            }
        }
    }

    private findColumnByIndex(columnIndex: number): GridColumn {
        return this.gridModel.gridColumns[columnIndex];
    }

    private fixColumnPositions() {
        var orderedColumns: Array<GridColumn> = [];
        for (var i = 0; i < this.gridModel.gridColumns.length; i++) {
            orderedColumns.push(this.gridModel.gridColumns[i]);
        }
        orderedColumns.sort((o1, o2) => {
            //fixed columns to the end of list
            if (o1.fixedColumn && o2.fixedColumn) return 0;
            if (o1.fixedColumn && !o2.fixedColumn) return 1;
            if (!o1.fixedColumn && o2.fixedColumn) return -1;
            //columns without positions to the end of the list (before fixed columns)
            if (!o1.position && !o2.position) return o1.index > o2.index ? 1 : -1;
            if (o1.position && !o2.position) return 1;
            if (!o1.position && o2.position) return -1;
            //columns with same position should be compared by index
            if (o1.position == o2.position) {
                return o1.index > o2.index ? 1 : -1;
            }
            return o1.position > o2.position ? 1 : -1;
        });
        for (var i = 0; i < orderedColumns.length; i++) {
            if (orderedColumns[i].position != i) {
                this.hasChangesForTemplateReload = true;
            }
            orderedColumns[i].position = i;
        }
    }

    public onChangePageSize() {
        if (!this.gridModel.pageSize) return;
        if (isNaN(this.gridModel.pageSize)) {
            this.gridModel.pageSize = GridModel.DEFAULT_PAGE_SIZE;
        }
        if (this.gridModel.pageSize > GridModel.MAX_USER_PAGE_SIZE) {
            this.gridModel.pageSize = GridModel.MAX_USER_PAGE_SIZE;
        }
        this.gridModel.pageSize = Math.round(this.gridModel.pageSize);
        this.applyFilter();
    }

    private loadColumnsConfigurationFromStorage() {
        if (this.gridModel) {
            var persistedModel = this.localStorage.get<PersistedGridModelConfiguration>(this.gridUuid);
            if (persistedModel) {
                this.gridModel.pageSize = persistedModel.pageSize;
                if (!this.gridModel.pageSize || isNaN(this.gridModel.pageSize)) {
                    this.gridModel.pageSize = GridModel.DEFAULT_PAGE_SIZE;
                } else {
                    this.gridModel.pageSize = Math.round(this.gridModel.pageSize);
                }
                for (var i = 0; i < this.gridModel.gridColumns.length; i++) {
                    var column: GridColumn = this.gridModel.gridColumns[i];
                    for (var j = 0; j < persistedModel.gridColumns.length; j++) {
                        var persistedColumn: GridColumn = persistedModel.gridColumns[j];
                        if (persistedColumn.index == column.index) {
                            if (column.visible != persistedColumn.visible) {
                                this.hasChangesForTemplateReload = true;
                                column.visible = persistedColumn.visible;
                            }
                            if (persistedColumn.position != undefined && persistedColumn.position != null
                                && column.position != persistedColumn.position) {
                                this.hasChangesForTemplateReload = true;
                                column.position = persistedColumn.position;
                            }
                            break;
                        }
                    }
                }
                this.fixColumnPositions();
                this.gridModel.recalculateVisibleColumnsCount();
            }
        }
    }

    private loadFilterFromStorage() {
        if (this.gridModel) {
            var persistedModel = this.localStorage.get<PersistedGridModelConfiguration>(this.gridUuid);
            if (persistedModel) {
                for (var fieldName in persistedModel.filter) {
                    if (persistedModel.filter.hasOwnProperty(fieldName) && this.gridModel.nonPersistedFilterFields.indexOf(fieldName) < 0) {
                        this.gridModel.filter[fieldName] = angular.copy(persistedModel.filter[fieldName]);
                    }
                }
            }
        }
    }

    public saveGridConfiguration() {
        this.localStorage.put(this.gridUuid, PersistedGridModelConfiguration.fromGridModel(this.gridModel));
    }

    private findSortEntryInfo(fieldName: string) {
        for (var i = 0; i < this.gridModel.sorting.length; i++) {
            var sortEntry = this.gridModel.sorting[i];
            if (sortEntry.fieldName === fieldName) {
                return {sortEntry: sortEntry, index: i};
            }
        }
        return null;
    }

    private setHeaderClass(targetClasses: Array<string>, element: JQuery) {
        for (var i = 0; i < this.SORTABLE_HEADERS_CLASSES.length; i++) {
            var elClass = this.SORTABLE_HEADERS_CLASSES[i];
            if (targetClasses.indexOf(elClass) != -1) {
                element.addClass(elClass);
            } else {
                element.removeClass(elClass);
            }
        }
    }

    private updateHeaderClasses() {
        var self = this;
        this.tableContainer.find(GridController.SORTABLE_HEADERS_SELECTOR).each(
            function (index: any, el: Element) {
                var jQueryElement: JQuery = $(el);
                var sortString = ElementSortingInfo.fromString(jQueryElement.attr('data-sortable')).sortPath;
                var info = self.findSortEntryInfo(sortString);
                var targetClasses: Array<string> = [];
                if (info != null) {
                    if (info.index < self.MAX_SORT_INDEX) {
                        targetClasses.push("sortable-" + info.index);
                    }
                    targetClasses.push(info.sortEntry.sortOrder == SortOrder.ASC ? "sortable-asc" : "sortable-desc");
                } else {
                    targetClasses.push("sortable");
                }
                self.setHeaderClass(targetClasses, jQueryElement);
            }
        );
    }

    private collectGridColumns() {
        var self = this;
        this.tableContainer.find(GridController.TABLE_HEADERS_SELECTOR).each(
            function (index: number, th: Element) {
                var el = $(th);
                var column = new GridColumn();
                column.index = index;
                column.position = index;
                column.visible = true;
                var columnExcluded = el.attr("data-column-always-visible");
                var fixedColumn = el.attr("data-column-fixed");
                if (columnExcluded != undefined) {
                    column.alwaysVisible = true;
                }
                if (fixedColumn != undefined) {
                    column.fixedColumn = true;
                }
                var columnName = el.attr("data-column-name");
                if (columnName) {
                    column.title = columnName
                } else {
                    column.title = el.text();
                }
                self.gridModel.gridColumns.push(column);
            }
        );
        this.gridModel.recalculateVisibleColumnsCount();
    }

    private addHeadersSortingHandlers() {
        var self = this;
        this.tableContainer.find(GridController.SORTABLE_HEADERS_SELECTOR).each(
            function (index: any, th: Element) {
                var el = $(th);
                var sortingInfo = ElementSortingInfo.fromString(el.attr('data-sortable'));
                var sortString = sortingInfo.sortPath;
                //change sorting
                el.click(function (event: JQueryEventObject) {
                    var sortEntry: SortEntry = self.addSorting(sortString);
                    var sortIndex = self.gridModel.sorting.indexOf(sortEntry);
                    var targetClasses: Array<string> = [];
                    if (sortIndex < self.MAX_SORT_INDEX) {
                        targetClasses.push("sortable-" + sortIndex);
                    }
                    targetClasses.push(sortEntry.sortOrder == SortOrder.ASC ? "sortable-asc" : "sortable-desc");
                    self.setHeaderClass(targetClasses, el);
                });
                //reset sorting
                if (!sortingInfo.mandatory) { //reset sorting only if it is not mandatory
                    el.dblclick(function (event: JQueryEventObject) {
                        self.resetSorting(sortString);
                        self.updateHeaderClasses();
                    });
                }
            }
        );
    }

    private addPredefinedSorting() {
        var predefinedSorting: Array<ElementSortingInfo> = [];
        var sortFields: Array<string> = [];
        this.tableContainer.find(GridController.SORTABLE_HEADERS_SELECTOR).each(
            function (index: any, th: Element) {
                var el = $(th);
                var sortingInfo = ElementSortingInfo.fromString(el.attr('data-sortable'));
                sortFields.push(sortingInfo.sortPath);
                if (sortingInfo.order != null) {
                    predefinedSorting.push(sortingInfo);
                }
            }
        );

        predefinedSorting.sort((o1: ElementSortingInfo, o2: ElementSortingInfo) => {
            if (o1.index == null && o2.index == null) return 0;
            if (o1.index != null && o2.index == null) return -1;
            if (o1.index == null && o2.index != null) return 1;
            if (o1.index > o2.index) return 1;
            if (o1.index < o2.index) return -1;
            return 0;
        });

        var persistedSorting: Array<SortEntry> = [];
        var persistedModel = this.localStorage.get<PersistedGridModelConfiguration>(this.gridUuid);
        if (persistedModel && persistedModel.sortEntries) {
            //filter persisted columns by existence in new table definition
            for (var i = 0; i < persistedModel.sortEntries.length; i++) {
                if (sortFields.indexOf(persistedModel.sortEntries[i].fieldName) >= 0) {
                    persistedSorting.push(persistedModel.sortEntries[i]);
                }
            }
            //remove non-mandatory columns from current definition and mandatory from persisted
            for (var i = predefinedSorting.length - 1; i >= 0; i--) {
                var predefinedSortingInfo = predefinedSorting[i];
                if (predefinedSortingInfo.mandatory) {
                    var persistedIndex = -1;
                    for (var j = 0; j < persistedSorting.length; j++) {
                        if (persistedSorting[j].fieldName == predefinedSortingInfo.sortPath) {
                            persistedIndex = j;
                            break;
                        }
                    }
                    if (persistedIndex >= 0) {
                        predefinedSorting[i].order = persistedSorting[persistedIndex].sortOrder;
                        persistedSorting.splice(persistedIndex, 1);
                    }
                } else {
                    predefinedSorting.splice(i, 1);
                }
            }
            //add persisted columns to default sorting
            for (var i = 0; i < persistedSorting.length; i++) {
                var sortingInfo = new ElementSortingInfo();
                sortingInfo.order = persistedSorting[i].sortOrder;
                sortingInfo.sortPath = persistedSorting[i].fieldName;
                predefinedSorting.push(sortingInfo);
            }
        }

        for (var i = 0; i < predefinedSorting.length; i++) {
            var sorting: ElementSortingInfo = predefinedSorting[i];
            this.addPredefinedSortingToModel(sorting.sortPath, sorting.order);
        }
    }

    private addPredefinedSortingToModel(fieldName: string, sortOrder: SortOrder) {
        var sortEntry = new SortEntry(fieldName, sortOrder);
        if (this.findSortEntryInfo(fieldName) == null) {
            this.gridModel.sorting.push(sortEntry);
        }
    }

    private addAutoSearchListeners() {
        var self = this;
        this.gridContainer.find(this.SEARCH_ELEMENTS_SELECTOR).each(
            function (index: any, el: Element) {
                var inputElement: JQuery = $(el);
                var func = function (event: JQueryEventObject) {
                    self.applyFilter();
                };
                switch (inputElement.prop("type").toLowerCase()) {
                    case 'checkbox':
                    case 'radio':
                        inputElement.click(func);
                        break;
                    case 'text':
                        inputElement.keyup(func);
                        break;
                    default:
                        inputElement.change(func);
                }
            }
        );
    }

    private processTemplate() {
        if (this.hasChangesForTemplateReload) {
            this.tableContainer.html(this.tableTemplate);

            var cellsPositionsMap: Map<number, number> = new Map<number, number>();
            for (var i = 0; i < this.gridModel.gridColumns.length; i++) {
                var column: GridColumn = this.gridModel.gridColumns[i];
                cellsPositionsMap.set(column.position, column.index);
            }

            this.processTableCellsInTemplate(this.tableContainer.find(GridController.TABLE_HEADERS_SELECTOR), cellsPositionsMap);
            this.processTableCellsInTemplate(this.tableContainer.find(GridController.TABLE_CELLS_IN_TEMPLATE_SELECTOR), cellsPositionsMap);
            this.processTableCellsInTemplate(this.tableContainer.find(GridController.TABLE_CELLS_IN_TEMPLATE_SELECTOR_FOR_REPEAT_START), cellsPositionsMap);
            this.processTableCellsInTemplate(this.tableContainer.find(GridController.TABLE_CELLS_IN_TEMPLATE_SELECTOR_FOR_REPEAT_END), cellsPositionsMap);

            this.$compile(this.tableContainer.contents())(this.directiveScope);
            this.hasChangesForTemplateReload = false;
            this.initDomElementsHandlers();
        }
    }

    private processTableCellsInTemplate(cells: angular.IAugmentedJQuery, positionsMap: Map<number, number>) {
        var hideColumnFunc = (index: number, td: Element) => {
            var column: GridColumn = this.findColumnByIndex(index);
            if (!column.fixedColumn && !column.visible) {
                $(td).remove();
            }
        };
        var cellsPositions: Map<number, JQuery> = new Map<number, JQuery>();
        for (var position = 0; position < this.gridModel.gridColumns.length; position++) {
            var positionIndex: number = positionsMap.get(position);
            if (positionIndex != null) {
                var cellInPosition: JQuery = cells.eq(positionIndex);
                cellsPositions.set(position, cellInPosition);
            }
        }
        for (var position = 0; position < this.gridModel.gridColumns.length; position++) {
            var cellInPosition: JQuery = cellsPositions.get(position);
            var previousCell: JQuery = position > 0 ? cellsPositions.get(position - 1) : null;
            if (previousCell) {
                $(cellInPosition).insertAfter($(previousCell));
            }
        }
        cells.each(hideColumnFunc);
    }
}

export function GridDirectiveFactory(): angular.IDirective {
    var tableTemplate;

    function link($scope: angular.IScope, element: angular.IAugmentedJQuery,
                  attributes: GridAttributes, controller: GridController<any>,
                  transclude: angular.ITranscludeFunction) {
        element.addClass("grid-container");
        controller.init(element, attributes.gridModelPath, attributes.vhsGridUuid, tableTemplate);
        if (attributes.vhsHasConfiguration) {
            controller.setIsConfigurable(true);
        }
    }

    var directive: any = {};
    directive.restrict = "A";
    directive.compile = function compile(element: angular.IAugmentedJQuery,
                                         templateAttributes: angular.IAttributes,
                                         transclude: angular.ITranscludeFunction) {
        tableTemplate = $(element).find(GridController.TABLE_SELECTOR).html();
        return {
            pre: null,
            post: link
        };
    };
    directive.controller = GridController;
    directive.scope = true;
    directive.trunsclude = false;
    return directive;
}