import {IPermissionWithUsersAndGroups} from "./../security/permissions/IPermissionWithUsersAndGroups";
import {UrlService} from "./../utils/UrlService";
import {ISelectable} from "./../ISelectable";
import {IPlaylist} from "./models/IPlaylist";
import {IMaterial} from "./models/IMaterial";

interface IExtPlaylist extends IPlaylist {
    permissionsWithUsersAndGroups?: Array<IPermissionWithUsersAndGroups>;
}

export class PlaylistService {
    public static NAME = "materials.PlaylistService";

    public static $inject = [
        "$http",
        "$sce",
        UrlService.NAME
    ];

    constructor(private $http: angular.IHttpService,
                private $sce: angular.ISCEService,
                private url: UrlService) {
    }

    public findPlaylistsByMaterial(materialId: number): angular.IPromise<Array<ISelectable>> {
        return this.$http.get(this.url.base("/api/playlists/by/material/:materialId")
            .param(":materialId", materialId)
            .service())
            .then(result => result.data);
    }

    public get(id: number): angular.IPromise<IPlaylist> {
        return this.$http.get(this.url.base("/api/playlists/:id?")
            .param(":id", id)
            .service())
            .then(result => result.data);
    }

    public create(playlist: IExtPlaylist, permissionsWithUsersAndGroups: Array<IPermissionWithUsersAndGroups>): angular.IPromise<IPlaylist> {
        playlist.permissionsWithUsersAndGroups = permissionsWithUsersAndGroups;
        return this.$http.post(this.url.service("/api/playlists"), playlist).then(result => result.data);
    }

    public update(playlist: IExtPlaylist, permissionsWithUsersAndGroups: Array<IPermissionWithUsersAndGroups>): angular.IPromise<IPlaylist> {
        playlist.permissionsWithUsersAndGroups = permissionsWithUsersAndGroups;
        return this.$http.put(this.url.service("/api/playlists"), playlist).then(result => result.data);
    }

    public getMaterials(id: number): angular.IPromise<Array<IMaterial>> {
        return this.$http.get(this.url.base("/api/playlists/:id/materialsList")
            .param(":id", id)
            .service())
            .then(result => result.data);
    }
}
