import {IReferenceAttributeMetadata, IReferenceEntityMetadata} from "./models/ReferenceMetadata";
import {MenuHelper} from "./../utils/MenuHelper";

export interface IAttributeConverter {
    convertStringAttribute(value: string): string;
    convertNumberAttribute(value: number): string;
    convertBooleanAttribute(value: boolean): string;
    convertDateAttribute(value: number): string;
    convertMoneyAttribute(value: number): string;
    convertGroupAttribute(entity: Map<string, any>, groupTemplate: string,
                          childAttributes: Array<IReferenceAttributeMetadata>): string;
    convertOneAssociationAttribute(entity: Map<string, any>, associationEntity: IReferenceEntityMetadata): string;
    convertOneAssociationAttributeToNumber(entity: Map<string, any>): number;
    convertManyAssociationAttributeToNumbers(entities: Array<Map<string, any>>): Array<number>;
    convertOneAssociationAttributeForGrid(entity: Map<string, any>, associationAttribute: IReferenceAttributeMetadata): string;
}

export interface IDefaultValueConvert {
    convertStringDefaultValue(value: string): string;
    convertBooleanDefaultValue(value: string): boolean;
    convertDateDefaultValue(value: string): string;
    convertMoneyDefaultValue(value: string): string;
    convertOneAssociationDefaultValue(value: string): number;
    convertManyAssociationDefaultValue(value: string): Array<number>;
}

export interface IAttributeValidator {
    validStringAttribute(value: string): boolean;
    validBooleanAttribute(value: boolean): boolean;
    validDateAttribute(value: string): boolean;
    validMoneyAttribute(value: string): boolean;
    validOneAssociationAttribute(value: number): boolean;
    validManyAssociationAttribute(value: Array<number>): boolean;
    validAttribute(attrType: string, value: any): boolean;
}

export interface IAttributeTypeIdentifier {
    isTextAttributeType(attributeType: string): boolean;
    isNumberAttributeType(attributeType: string): boolean;
    isBooleanAttributeType(attributeType: string): boolean;
    isDateAttributeType(attributeType: string): boolean;
    isMoneyAttributeType(attributeType: string): boolean;
    isGroupAttributeType(attributeType: string): boolean;
    isOneAssociationAttributeType(attributeType: string): boolean;
    isManyAssociationAttributeType(attributeType: string): boolean;
}

export interface ILogicalOperatorTypeIdentifier {
    isAndLogicalOperatorType(logicalOperatorType: string): boolean;
    isOrLogicalOperatorType(logicalOperatorType: string): boolean;
}

export interface IEntityManipulator {
    getEntityNameByAttribute(entity: Map<string, any>, attribute: IReferenceAttributeMetadata) : string;
    fixInputReferenceEntityAttributes(referenceEntity: Map<string, any>,
                                      referenceMetadata: IReferenceEntityMetadata): void;
}

export class ReferenceUtils implements IAttributeConverter, IDefaultValueConvert, IAttributeValidator,
        IAttributeTypeIdentifier, ILogicalOperatorTypeIdentifier, IEntityManipulator {
    public static NAME = "reference.ReferenceUtils";
    public static entityIdProperty = "id";
    public static entityVersionProperty = "version";
    public static entityIsActiveProperty = "isActive";

    public getEntityIdProperty(): string {
        return ReferenceUtils.entityIdProperty;
    }

    public getEntityIsActiveProperty(): string {
        return ReferenceUtils.entityIsActiveProperty;
    }

    public getIsActiveGridStyle(isActive: boolean): any {
        return isActive
            ? {}
            : {"color": "#999999"};
    }

    public getReferenceName(meta: IReferenceEntityMetadata): string {
        if (!meta) {
            return null;
        }
        return meta.title;
    }

    public convertStringAttribute(value: string): string {
        return value;
    }

    public convertNumberAttribute(value: number): string {
        return value.toString();
    }

    public convertBooleanAttribute(value: boolean): string {
        return value != undefined
            ? value ? 'Yes' : 'No'
            : undefined;
    }

    public convertDateAttribute(value: number): string {
        return value != undefined
            ? moment(value).format("M/D/YYYY")
            : undefined;
    }

    public convertMoneyAttribute(value: number): string {
        return new String(value).toString();
    }

    public convertGroupAttribute(entity: Map<string, any>, groupTemplate: string,
                                 childAttributes: Array<IReferenceAttributeMetadata>): string {
        var result = "";
        var templateParts = groupTemplate.split("|");
        for (var i = 0, j = 0; i < templateParts.length || j < childAttributes.length; ++i) {
            var part = templateParts[i];
            if (part.indexOf("?", 0) == 0) {
                var attr = childAttributes[j];
                var attrType = attr.type;
                var attrSysName = attr.systemName;
                var value;
                if (this.isTextAttributeType(attrType)) {
                    value = this.convertStringAttribute(entity[attrSysName]);
                } else if (this.isBooleanAttributeType(attrType)) {
                    value = this.convertBooleanAttribute(entity[attrSysName]);
                } else if (this.isDateAttributeType(attrType)) {
                    value = this.convertDateAttribute(entity[attrSysName]);
                } else if (this.isMoneyAttributeType(attrType)) {
                    value = this.convertMoneyAttribute(entity[attrSysName]);
                }
                if (value && value.length > 0) {
                    part = part.substr(1, part.length).replace("%1", value);
                    result += part;
                }
                ++j;
            } else {
                result += part;
            }
        }
        return result;
    }

    public convertOneAssociationAttribute(entity: Map<string, any>,
                                          associationEntity: IReferenceEntityMetadata): string {
        return this.getEntityNameByAttribute(entity, associationEntity.selectorAttribute);
    }

    public convertOneAssociationAttributeForGrid(entity: Map<string, any>,
                                                 associationAttribute: IReferenceAttributeMetadata): string {
        return entity[associationAttribute.systemName + "_" + associationAttribute.association.selectorAttribute.systemName];
    }

    public convertOneAssociationAttributeToNumber(entity: Map<string, any>): number {
        return entity != undefined
            ? entity["id"]
            : undefined;
    }

    public convertManyAssociationAttributeToNumbers(entities: Array<Map<string, any>>): Array<number> {
        var result: Array<any> = [];
        if (entities && entities.length > 0) {
            for (var i = 0; i < entities.length; ++i) {
                var entity = entities[i];
                result.push(this.convertOneAssociationAttributeToNumber(entity));
            }
        }
        return result;
    }

    public convertStringDefaultValue(value: string): string {
        return value;
    }

    public convertBooleanDefaultValue(value: string): boolean {
        return value && value == "true"
            ? true
            : false;
    }

    public convertDateDefaultValue(value: string): string {
        return value;
    }

    public convertMoneyDefaultValue(value: string): string {
        return value;
    }

    public convertOneAssociationDefaultValue(value: string): number {
        return parseInt(value, 10);
    }

    public convertManyAssociationDefaultValue(value: string): Array<number> {
        var result: Array<number> = [];
        var ids = value.split(",");
        for (var i = 0; i < ids.length; ++i) {
            result.push(this.convertOneAssociationDefaultValue(ids[i]));
        }
        return result;
    }

    public validStringAttribute(value: string): boolean {
        return value && value.length > 0;
    }

    public validBooleanAttribute(value: boolean): boolean {
        return value != undefined;
    }

    public validDateAttribute(value: string): boolean {
        return value && value.length > 0;
    }

    public validMoneyAttribute(value: string): boolean {
        return value && value.length > 0;
    }

    public validOneAssociationAttribute(value: number): boolean {
        return value && value > 0;
    }

    public validManyAssociationAttribute(value: Array<number>): boolean {
        return value && value.length > 0;
    }

    public validAttribute(attrType: string, value: any): boolean {
        if (this.isTextAttributeType(attrType)) {
            return this.validStringAttribute(value);
        } else if (this.isBooleanAttributeType(attrType)) {
            return this.validBooleanAttribute(value);
        } else if (this.isDateAttributeType(attrType)) {
            return this.validDateAttribute(value);
        } else if (this.isMoneyAttributeType(attrType)) {
            return this.validMoneyAttribute(value);
        } else if (this.isOneAssociationAttributeType(attrType)) {
            return this.validOneAssociationAttribute(value);
        } else if (this.isManyAssociationAttributeType(attrType)) {
            return this.validManyAssociationAttribute(value);
        } else {
            return true;
        }
    }

    public isTextAttributeType(attributeType: string): boolean {
        return attributeType == 'TEXT';
    }

    public isNumberAttributeType(attributeType: string): boolean {
        return attributeType == 'NUMBER';
    }

    public isBooleanAttributeType(attributeType: string): boolean {
        return attributeType == 'BOOLEAN';
    }

    public isDateAttributeType(attributeType: string): boolean {
        return attributeType == 'DATE';
    }

    public isMoneyAttributeType(attributeType: string): boolean {
        return attributeType == 'MONEY';
    }

    public isGroupAttributeType(attributeType: string): boolean {
        return attributeType == 'GROUP';
    }

    public isOneAssociationAttributeType(attributeType: string): boolean {
        return attributeType == 'ONE_ASSOCIATION';
    }

    public isManyAssociationAttributeType(attributeType: string): boolean {
        return attributeType == 'MANY_ASSOCIATION';
    }

    public isAndLogicalOperatorType(logicalOperatorType: string): boolean {
        return logicalOperatorType == "AND";
    }

    public isOrLogicalOperatorType(logicalOperatorType: string): boolean {
        return logicalOperatorType == "OR";
    }

    public getEntityNameByAttribute(entity: Map<string, any>, attribute: IReferenceAttributeMetadata): string {
        if (!entity || !attribute) {
            return "";
        }
        var attrType = attribute.type;
        var attrSysName = attribute.systemName;
        if (this.isTextAttributeType(attrType)) {
            return this.convertStringAttribute(entity[attrSysName]);
        } else if (this.isBooleanAttributeType(attrType)) {
            return this.convertBooleanAttribute(entity[attrSysName]);
        } else if (this.isDateAttributeType(attrType)) {
            return this.convertDateAttribute(entity[attrSysName]);
        } else if (this.isMoneyAttributeType(attrType)) {
            return this.convertMoneyAttribute(entity[attrSysName]);
        } else if (this.isGroupAttributeType(attrType)) {
            return this.convertGroupAttribute(entity, attribute.groupTemplate, attribute.childAttributes);
        }
    }

    public fixInputReferenceEntityAttributes(referenceEntity: Map<string, any>,
                                             referenceMetadata: IReferenceEntityMetadata): void {
        for (var i = 0; i < referenceMetadata.defaultAttributes.length; ++i) {
            var attr = referenceMetadata.defaultAttributes[i];
            var attrType = attr.type;
            var attrSysName = attr.systemName;
            if (this.isTextAttributeType(attrType)) {
                referenceEntity[attrSysName] = this.convertStringAttribute(
                    referenceEntity[attrSysName]);
            } else if (this.isDateAttributeType(attrType)) {
                referenceEntity[attrSysName] = this.convertDateAttribute(
                    referenceEntity[attrSysName]);
            } else if (this.isMoneyAttributeType(attrType)) {
                referenceEntity[attrSysName] = this.convertMoneyAttribute(
                    referenceEntity[attrSysName]);
            } else if (this.isOneAssociationAttributeType(attrType)) {
                referenceEntity[attrSysName] = this.convertOneAssociationAttributeToNumber(
                    referenceEntity[attrSysName]);
            } else if (this.isManyAssociationAttributeType(attrType)) {
                referenceEntity[attrSysName] = this.convertManyAssociationAttributeToNumbers(
                    referenceEntity[attrSysName]);
            }
        }
    }

    public getStringInBrowserFormat(str: string): string {
        let result: string = "";
        angular.forEach(str, ch => {
            switch (ch) {
                case "=": result = result.concat("%3D"); break;
                default: result = result.concat(ch);
            }
        });
        return result;
    }
}
