import {IPermissionWithUsersAndGroups} from "./../security/permissions/IPermissionWithUsersAndGroups";
import {UrlService} from "./../utils/UrlService";
import {ICourse} from "./models/ICourse";
import {IMaterial} from "./../materials/models/IMaterial";
import {ICourseMaterial} from "../courses/models/ICourseMaterial";
import {IModule} from "./models/IModule";
import {ISection} from "./models/ISection";
import {ISectionInHierarchyExtended} from "./models/ISectionInHierarchy";
import IPromise = angular.IPromise;
import {ISelectable} from "../ISelectable";
import {IQuiz} from "../quizzes/models/IQuiz";
import {ICourseProgress} from "../courses/models/ICourseProgress";
import {ISectionProgress} from "../courses/models/ISectionProgress";
import {IUserInfo} from "../security/IUserInfo";
import {IGroup} from "../users/models/IGroup";
import {ISectionsStatistics} from "./models/ISectionStatistics";
import {ILandingPage} from "./models/ILandingPage";
import {IBundle} from "../bundles/models/IBundle";
import {ICourseParticipantInfo} from "./models/ICourseParticipantInfo";

interface IExtCourse extends ICourse {
    preview?: any;
    permissionsWithUsersAndGroups?: Array<IPermissionWithUsersAndGroups>;
    resetPreview?: boolean;
}

export class CourseService {
    public static NAME = "courses.CourseService";

    public static $inject = [
        "$http",
        "$sce",
        UrlService.NAME
    ];

    constructor(private $http: angular.IHttpService,
                private $sce: angular.ISCEService,
                private url: UrlService) {
    }

    public findCoursesByMaterial(materialId: number): angular.IPromise<Array<ICourse>> {
        return this.$http.get(this.url.base("/api/courses/by/material/:materialId")
            .param(":materialId", materialId)
            .service())
            .then(result => result.data);
    }

    public get(id: number): angular.IPromise<ICourse> {
        return this.$http.get(this.url.base("/api/courses/:id?")
            .param(":id", id)
            .service())
            .then(result => result.data);
    }

    public createCourseBundle(courseId: number, courseVersion: number): angular.IPromise<IBundle> {
        return this.$http.post(this.url.base("/api/courses/:courseId/bundles?courseVersion=:courseVersion")
            .param(":courseId", courseId)
            .param(":courseVersion", courseVersion)
            .service(), null)
            .then(result => result.data);
    }

    public create(course: IExtCourse, permissionsWithUsersAndGroups: Array<IPermissionWithUsersAndGroups>, preview?: File): angular.IPromise<ICourse> {
        course.preview = preview != null
            ? {name: preview.name, type: preview.type, size: preview.size}
            : null;
        course.permissionsWithUsersAndGroups = permissionsWithUsersAndGroups;
        return this.$http.post(this.url.service("/api/courses"), course).then(result => result.data);
    }

    public update(course: IExtCourse, permissionsWithUsersAndGroups: Array<IPermissionWithUsersAndGroups>, isResetPreview: boolean,
                  preview?: File): angular.IPromise<ICourse> {
        course.preview = preview != null
            ? {name: preview.name, type: preview.type, size: preview.size}
            : null;
        course.permissionsWithUsersAndGroups = permissionsWithUsersAndGroups;
        course.resetPreview = isResetPreview;
        return this.$http.put(this.url.service("/api/courses"), course).then(result => result.data);
    }

    public getModules(courseId: number): angular.IPromise<Array<IModule>> {
        return this.$http.get(this.url.base("/api/courses/:id/modules")
            .param(":id", courseId)
            .service())
            .then(result => result.data);
    }

    public getModuleSections(courseId: number, moduleId: number): angular.IPromise<Array<ISectionInHierarchyExtended>> {
        return this.$http.get(this.url.base("/api/courses/:courseId/modules/:moduleId/sections")
            .param(":courseId", courseId)
            .param(":moduleId", moduleId)
            .service())
            .then(result => result.data);
    }

    public createModule(courseId: number, module: IModule): angular.IPromise<IModule> {
        return this.$http.post(this.url.base("/api/courses/:courseId/modules").param(":courseId", courseId).service(), module)
            .then(result => result.data);
    }

    public updateModule(courseId: number, module: IModule): angular.IPromise<IModule> {
        return this.$http.put(this.url.base("/api/courses/:courseId/modules")
            .param(":courseId", courseId)
            .service(),
            module)
            .then(result => result.data);
    }

    public updateModuleHierarchy(courseId: number, module: IModule): angular.IPromise<IModule> {
        return this.$http.put(this.url.base("/api/courses/:courseId/modules/updateHierarchy")
                .param(":courseId", courseId)
                .service(),
            module)
            .then(result => result.data);
    }

    public createSection(courseId: number, moduleId: number, section: ISection): angular.IPromise<ISection> {
        return this.$http.post(this.url.base("/api/courses/:courseId/modules/:moduleId/sections")
            .param(":courseId", courseId)
            .param(":moduleId", moduleId)
            .service(),
            section)
            .then(result => result.data);
    }

    public updateSection(courseId: number, moduleId: number, section: ISection): angular.IPromise<ISection> {
        return this.$http.put(this.url.base("/api/courses/:courseId/modules/:moduleId/sections")
            .param(":courseId", courseId)
            .param(":moduleId", moduleId)
            .service(),
            section)
            .then(result => result.data);
    }

    public updateSectionName(courseId: number, moduleId: number, section: ISection): angular.IPromise<ISection> {
        return this.$http.put(this.url.base("/api/courses/:courseId/modules/:moduleId/sections/updateName")
            .param(":courseId", courseId)
            .param(":moduleId", moduleId)
            .service(),
            section)
            .then(result => result.data);
    }

    public updateSectionHierarchy(courseId: number, moduleId: number, section: ISection): angular.IPromise<ISection> {
        return this.$http.put(this.url.base("/api/courses/:courseId/modules/:moduleId/sections/updateHierarchy")
                .param(":courseId", courseId)
                .param(":moduleId", moduleId)
                .service(),
            section)
            .then(result => result.data);
    }

    public getAvailableSectionsNoChildren(courseId: number, moduleId: number, sectionId: number): ng.IPromise<Array<ISelectable>> {
        return this.$http.get(this.url.base("/api/courses/:courseId/modules/:moduleId/sections/available/no/children/:sectionId")
            .param(":courseId", courseId)
            .param(":moduleId", moduleId)
            .param(":sectionId", sectionId).service())
            .then(result => result.data);
    }

    // TODO: replace IMaterial with ICourseMaterial - add fields only if needed
    public getSectionUploadedOrLinkMaterials(courseId: number, moduleId: number, sectionId: number): angular.IPromise<Array<IMaterial>> {
        return this.$http.get(this.url.base("/api/courses/:courseId/modules/:moduleId/sections/:sectionId/materials/uploadedOrLink")
            .param(":courseId", courseId)
            .param(":moduleId", moduleId)
            .param(":sectionId", sectionId)
            .service())
            .then(result => result.data);
    }

    // TODO: replace IMaterial with ICourseMaterial - add fields only if needed
    public getSectionAllMaterials(courseId: number, moduleId: number, sectionId: number): angular.IPromise<Array<IMaterial>> {
        return this.$http.get(this.url.base("/api/courses/:courseId/modules/:moduleId/sections/:sectionId/materials/all")
            .param(":courseId", courseId)
            .param(":moduleId", moduleId)
            .param(":sectionId", sectionId)
            .service())
            .then(result => result.data);
    }

    public deleteModule(courseId: number, moduleId: number, moduleVersion: number): angular.IPromise<any> {
        return this.$http.delete(this.url.base("api/courses/:courseId/modules/:moduleId?version=:version")
            .param(":courseId", courseId)
            .param(":moduleId", moduleId)
            .param(":version", moduleVersion)
            .service())
            .then(result => result.data);
    }

    public deleteSection(courseId: number, moduleId: number, sectionId: number, sectionVersion: number,
                         removeCurrentUserSectionProgress: boolean): angular.IPromise<any> {
        return this.$http.delete(this.url.base("api/courses/:courseId/modules/:moduleId/sections/:sectionId?version=:version&removeProgress=:removeProgress")
            .param(":courseId", courseId)
            .param(":moduleId", moduleId)
            .param(":sectionId", sectionId)
            .param(":version", sectionVersion)
            .param(":removeProgress", removeCurrentUserSectionProgress)
            .service())
            .then(result => result.data);
    }

    public getCourseOwners(courseId: number): IPromise<Array<ISelectable>> {
        return this.$http.get(this.url.base("api/courses/:courseId/owners")
            .param(":courseId", courseId)
            .service())
            .then(result => result.data);

    }

    public getSectionQuiz(courseId: number, moduleId: number, sectionId: number): angular.IPromise<IQuiz> {
        return this.$http.get(this.url.base("/api/courses/:courseId/modules/:moduleId/sections/:sectionId/quiz")
            .param(":courseId", courseId)
            .param(":moduleId", moduleId)
            .param(":sectionId", sectionId)
            .service())
            .then(result => result.data);
    }

    public createLandingPage(courseId: number,
                             landingPage: ILandingPage): angular.IPromise<ILandingPage> {
        return this.$http.post(this.url.base("/api/courses/:courseId/landingPage")
            .param(":courseId", courseId)
            .service(), landingPage)
            .then(result => result.data);
    }

    public getLandingPage(courseId: number): angular.IPromise<ILandingPage> {
        return this.$http.get(this.url.base("/api/courses/:courseId/landingPage")
            .param(":courseId", courseId)
            .service())
            .then(result => result.data);
    }

    public updateLandingPage(courseId: number,
                             landingPage: ILandingPage): angular.IPromise<ILandingPage> {
        return this.$http.put(this.url.base("/api/courses/:courseId/landingPage")
            .param(":courseId", courseId)
            .service(), landingPage)
            .then(result => result.data);
    }

    public downloadCertificate(parameters: any): ng.IPromise<string> {
        let reportType = 'COURSE_CERTIFICATE';
        return this.$http.post(
            this.url.base("/api/reports/downloadToken/:reportType")
                .param(":reportType", reportType)
                .service(), {
                parameters: JSON.stringify(parameters)
            }
        ).then((result: any) =>  this.url.base("/api/reports/download/:token").param(":token", result.data.token).service());
    }

    public viewCertificate(parameters: any): ng.IPromise<string> {
        let reportType = 'COURSE_CERTIFICATE';
        return this.$http.post(
            this.url.base("/api/reports/preview/:reportType")
                .param(":reportType", reportType)
                .service(),
            JSON.stringify(parameters)
        ).then(result => result.data);
    }

    public getCourseCertificates(courseId: number, editCourse: boolean): angular.IPromise<Array<ISelectable>> {
        return this.$http.get(this.url.base("/api/courses/:courseId/certificates?editCourse=:editCourse")
            .param(":courseId", courseId)
            .param(":editCourse", editCourse)
            .service())
            .then(result => result.data);
    }

    public removeCourseCertificates(courseId: number, certificatesIds: any): ng.IPromise<boolean> {
        return this.$http.delete(this.url.base("/api/courses/:courseId/certificates")
            .param(":courseId", courseId)
            .service(), {params: {
            certificatesIds: certificatesIds
        }}).then(result => result.data);
    }

    public getEmbeddedMaterial(materialId: number): angular.IPromise<ICourseMaterial> {
        return this.$http.get(this.url.base("/api/courses/embedded/material/:materialId")
            .param(":materialId", materialId)
            .service())
            .then(result => result.data);
    }

    public getCourseParticipationInfo(courseId: number, courseProgressId: number): angular.IPromise<ICourseParticipantInfo> {
        return this.$http.get(this.url.base("/api/courses/:courseId/participationInfo")
            .param(":courseId", courseId)
            .service(), {params: {
            courseProgressId: courseProgressId
        }}).then(result => result.data);
    }

    public updateCourseParticipationInfo(courseId: number, courseProgressId: number, participationFormData: any):
                angular.IPromise<ICourseParticipantInfo> {
        return this.$http.put(this.url.base("/api/courses/:courseId/participationInfo?courseProgressId=:courseProgressId")
            .param(":courseId", courseId)
            .param(":courseProgressId", courseProgressId)
            .service(), participationFormData)
            .then(result => result.data);
    }
}