import {StorageWrapper, Storages} from "./../infrastructure/Storage";
import {Event} from "./../authcore/Event";
import {UrlService} from "./../utils/UrlService";
import {AppProperties} from "./../utils/AppProperties";
import {
    IMenuUpdateListener,
    MenuHelper
} from "./../utils/MenuHelper";
import {
    IPageHeaderUpdateListener,
    PageHeaderHelper,
    PageHeader
} from "./../utils/PageHeaderHelper";


export class PageTitleController implements IPageHeaderUpdateListener {

    public currentPageName: string = AppProperties.appTitle;

    public static $inject = [
        "$scope",
        "$sce",
        "$location",
        Storages.SESSION_NAME
    ];

    constructor($scope,
                private $sce: angular.ISCEService,
                private $location: angular.ILocationService,
                private sessionStorage: StorageWrapper) {
        $scope.vm = this;

        //subscribe to update events
        PageHeaderHelper.addPageHeaderUpdateListener(this);
    }

    public onHeaderUpdate(pageHeader: PageHeader) {
        if (!pageHeader.preservePageName) {
            this.currentPageName = pageHeader.pageName;
            document.title = this.currentPageName ? this.currentPageName : AppProperties.appTitle;
        }
    }
}

export function PageTitleDirectiveFactory(): angular.IDirective {
    function link($scope: angular.IScope, element: angular.IAugmentedJQuery,
                  attributes: angular.IAttributes, controller: PageTitleController,
                  transclude: angular.ITranscludeFunction) {
    }

    var directive: any = {};
    directive.restrict = "A";
    directive.link = link;
    directive.controller = PageTitleController;
    return directive;
}