import {IPermissionWithUsersAndGroups} from "./../security/permissions/IPermissionWithUsersAndGroups";
import {UrlService, IUrlBuilder} from "./../utils/UrlService";

import {IQuiz} from "./models/IQuiz";
import {IQuizResult} from "./models/IQuizResult";
import {IQuizAnswerResult} from "./models/IQuizAnswerResult";

interface IExtQuiz extends IQuiz {
    permissionsWithUsersAndGroups?: Array<IPermissionWithUsersAndGroups>;
}

export class QuizService {
    public static NAME = "quizzes.QuizService";

    public static $inject = [
        "$http",
        "$sce",
        UrlService.NAME
    ];

    constructor(private $http: angular.IHttpService,
                private $sce: angular.ISCEService,
                private url: UrlService) {
    }

    public get(id: number): angular.IPromise<IQuiz> {
        return this.$http.get(this.url.base("/api/quizzes/:id?")
            .param(":id", id)
            .service())
            .then(result => result.data);
    }

    public create(quiz: IExtQuiz, permissionsWithUsersAndGroups: Array<IPermissionWithUsersAndGroups>): angular.IPromise<any> {
        quiz.permissionsWithUsersAndGroups = permissionsWithUsersAndGroups;
        return this.$http.post(this.url.service("/api/quizzes"), quiz).then(result => result.data);
    }

    public update(quiz: IExtQuiz, permissionsWithUsersAndGroups: Array<IPermissionWithUsersAndGroups>): angular.IPromise<any> {
        quiz.permissionsWithUsersAndGroups = permissionsWithUsersAndGroups;
        return this.$http.put(this.url.service("/api/quizzes"), quiz).then(result => result.data);
    }

    public startQuizByUser(quizId: number, sourceType: string, sectionId?: number, courseProgressId?: number): angular.IPromise<IQuizResult> {
        return this.$http.post(this.url.service("/api/quizzes/results"),
            {quizId: quizId, sourceType: sourceType, sectionId: sectionId, courseProgressId: courseProgressId})
            .then(result => result.data);
    }

    public completeQuizByUser(quizResult: IQuizResult) {
        return this.$http.put(this.url.service("/api/quizzes/results"), quizResult).then(result => result.data);
    }

    public getUserQuizResults(quizId: number, sourceType?: string, sectionId?: number, courseProgressId?: number): angular.IPromise<IQuizResult[]> {
        let url: string = "/api/quizzes/results/:quizId";
        if (sourceType != null) {
            url += "?sourceType=:sourceType";
        }
        if (sectionId != null) {
            url += "&sectionId=:sectionId";
        }
        if (courseProgressId) {
            url += "&courseProgressId=:courseProgressId";
        }
        let urlBuilder: IUrlBuilder = this.url.base(url)
            .param(":quizId", quizId);
        if (sourceType != null) {
            urlBuilder.param(":sourceType", sourceType);
        }
        if (sectionId != null) {
            urlBuilder.param(":sectionId", sectionId);
        }
        if (courseProgressId) {
            urlBuilder.param(":courseProgressId", courseProgressId);
        }
        return this.$http.get(urlBuilder.service())
            .then(result => {
                return result.data;
            });
    }

    public getUserQuizCurrentResult(quizId: number, attempt: number): angular.IPromise<IQuizResult> {
        return this.$http.get(this.url.base("/api/quizzes/results/current/:quizId?attempt=:attempt")
            .param(":quizId", quizId)
            .param(":attempt", attempt || "")
            .service())
            .then(result => result.data);
    }

    public hasUserCreatedQuizzes(): angular.IPromise<boolean> {
        return this.$http.get(this.url.service("/api/quizzes/profileQuizzes/hasCreated"))
            .then(result => result.data);
    }
}