import {PlaylistService} from "./PlaylistService";
import {MaterialsRouter} from "./../urls/MaterialsRouter";
import {IPlaylist} from "./models/IPlaylist";
import {IMaterial} from "./models/IMaterial";

import {SecurityService} from "./../security/SecurityService";
import {PermissionsService} from "./../security/PermissionsService";
import {UrlService} from "./../utils/UrlService";
import {AuthService} from "./../auth/AuthService";

import {IPermissionWithUsersAndGroups} from "./../security/permissions/IPermissionWithUsersAndGroups";
import {MenuHelper} from "./../utils/MenuHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {SecuredObjectType} from "./../security/permissions/SecuredObjectType";
import {Moment} from "./../utils/Moment";
import {notifier} from "./../INotifier";
import {IGridModel, GridModel} from "./../IGridModel";
import {HttpGridHelper} from "./../IHttpGridService";

interface IMaterialExtended extends IMaterial {
    number?: number;
}

export interface IFile extends File {
    title?: string;
}

export class PlaylistEditController {
    public static NAME = "materials.PlaylistEditController";

    public playlistId: number;
    public playlist: IPlaylist;
    public permissionsWithUsersAndGroups: Array<IPermissionWithUsersAndGroups> = [];
    public playlistMaterials: Array<IMaterial> = [];
    public userMaterialsGridModel: IGridModel<IMaterial>;
    public preview: IFile;

    public disabledSaveButton: boolean;
    public isValidityInPerpetuity: boolean;


    public static $inject = [
        "$http",
        "$location",
        "$routeParams",
        "Upload",
        UrlService.NAME,
        AuthService.NAME,
        PlaylistService.NAME,
        SecurityService.NAME,
        PermissionsService.NAME,
        MaterialsRouter.NAME
    ];

    constructor($http: angular.IHttpService,
                private $location: angular.ILocationService,
                private $routeParams: angular.route.IRouteParamsService,
                private $uploader: ng.angularFileUpload.IUploadService,
                private url: UrlService,
                private authService: AuthService,
                private playlistService: PlaylistService,
                private securityService: SecurityService,
                private permissionsService: PermissionsService,
                private materialsRouter: MaterialsRouter) {
        this.playlistId = $routeParams[MaterialsRouter.PLAYLIST_ID_PARAM];
        MenuHelper.setCurrentMenuItem(MenuHelper.EDUCATION_PLAYLISTS_MENU_KEY);

        let playlistIdStr: string = String(this.playlistId);
        let hasPlaylistId: boolean = this.playlistId != null;

        this.securityService.demandAuthentication()
            .demandSystemPermissionsIf(!hasPlaylistId, systemPermissions => systemPermissions.addPlaylists)
            .demandPlaylistPermissionsIf(hasPlaylistId, playlistIdStr, playlistPermissions => playlistPermissions.edit);

        this.isValidityInPerpetuity = true;
        this.playlist = {};

        if(hasPlaylistId) {
            PageHeader.set().header("Edit a Playlist").update();
            this.loadPlaylist();
            this.loadPlaylistMaterials();
            this.disabledSaveButton = false;
            this.loadPermissionsWithUsersAndGroups(SecuredObjectType.PLAYLIST, playlistIdStr);
        } else {
            PageHeader.set().header("New Playlist").update();
            this.authService.getCurrentUser().then(user => {
                this.playlist.ownerId = user.id;
                this.playlist.ownerFullName = user.fullName;
            });
            this.loadPermissionsWithUsersAndGroups(SecuredObjectType.PLAYLIST);
        }

        this.userMaterialsGridModel = new GridModel(HttpGridHelper.createLoader<IMaterial>($http,
            this.url.service("/api/playlists/userMaterials")));
        this.userMaterialsGridModel.filter.playlistId = this.playlistId;
        this.userMaterialsGridModel.loadCurrentPage();
    }

    private loadPlaylist() {
        var promise: angular.IPromise<IPlaylist> = this.playlistService.get(this.playlistId);
        promise.then(result => {
            angular.copy(result, this.playlist);
            this.playlist.validityText = Moment.isValidDate(result.validity);
            if(this.playlist.validityText) {
                this.isValidityInPerpetuity = false;
            }
        });
    }

    private loadPlaylistMaterials() {
        this.playlistService.getMaterials(this.playlistId).then(result => {
            angular.copy(result, this.playlistMaterials);
        })
    }

    private loadPermissionsWithUsersAndGroups(securedObjectType: SecuredObjectType, objectId?: string): void {
        this.permissionsService.getAllObjectPermissionsWithUsersAndGroups(securedObjectType, objectId).then(result => {
            this.permissionsWithUsersAndGroups = result;
        });
    }

    public save() {
        this.disabledSaveButton = true;
        this.playlist.validity = this.getValidity();
        this.playlist.materialIds = this.playlistMaterials.map((material: IMaterial) => {
            return material.id;
        });
        var promise = this.playlistId
            ? this.playlistService.update(this.playlist, this.permissionsWithUsersAndGroups)
            : this.playlistService.create(this.playlist, this.permissionsWithUsersAndGroups);
        promise.then(playlist => {
            if(this.preview != null) {
                this.upload(this.preview, playlist.id, "preview").then(r => {
                    this.processSuccessCallback(playlist.id);
                }).catch(response => {
                    if (response.data.error == "error.storage.not.available") {
                        notifier.error("No available storage. Thumbnail has not been saved");
                    }
                    this.processSuccessCallback(playlist.id);
                });
            } else {
                this.processSuccessCallback(playlist.id);
            }
        }).catch(err => {
            notifier.error(err);
        });
    }

    private processSuccessCallback(playlistId: number): void {
        this.disabledSaveButton = false;
        notifier.success("The playlist has been saved");
        this.$location.url(this.materialsRouter.playlistDetailsUrl(playlistId));
    }

    private upload(file: File, playlistId: number, fileType: string): angular.angularFileUpload.IUploadPromise<File> {
        var urlBuilder = this.url.base("/api/playlists/upload?playlistId=:playlistId&fileType=:fileType")
            .param(":playlistId", playlistId)
            .param(":fileType", fileType);
        return this.$uploader.upload({
                method: "POST",
                url: angular.copy(urlBuilder)
                    .service(),
                file: file}
        );
    }

    private getValidity(): number {
        this.playlist.validityText ? this.playlist.validity = Moment.textToJavaDate(this.playlist.validityText)
            : this.playlist.validity = null;
        return this.playlist.validity;
    }

    private redirectOrClearFields(): void {
        if(this.playlistId) {
            this.$location.url(this.materialsRouter.playlistDetailsUrl(this.playlistId));
        } else {
            this.cleanFields();
        }
    }

    private cleanFields() : void {
        this.isValidityInPerpetuity = true;
        this.playlist = {};
        this.authService.getCurrentUser().then(user => {
            this.playlist.ownerId = user.id;
            this.playlist.ownerFullName = user.fullName;
        });
        this.permissionsWithUsersAndGroups = [];
        this.preview = null;
        this.loadPermissionsWithUsersAndGroups(SecuredObjectType.PLAYLIST);
    }

    public cleanValidity(): void {
        this.playlist.validityText = "";
    }

    public addMaterialToPlaylist(material: IMaterial) {
        this.playlistMaterials.push(material);
        var index = this.userMaterialsGridModel.entries.indexOf(material);
        this.userMaterialsGridModel.entries.splice(index, 1);
    }

    public removeMaterialFromPlaylist(index: number, material: IMaterial) {
        this.playlistMaterials.splice(index, 1);
        this.userMaterialsGridModel.entries.push(material);
    }

    public moveUp(index: number): void {
        var placeholder = {};
        var objectToMove = this.playlistMaterials.splice(index, 1, placeholder)[0];
        this.playlistMaterials.splice(index - 1, 0, objectToMove);
        this.playlistMaterials.splice(this.playlistMaterials.indexOf(placeholder), 1);
    }

    public moveDown(index: number): void {
        var placeholder = {};
        var objectToMove = this.playlistMaterials.splice(index + 1, 1, placeholder)[0];
        this.playlistMaterials.splice(index, 0, objectToMove);
        this.playlistMaterials.splice(this.playlistMaterials.indexOf(placeholder), 1);
    }

    public isMoveUp(index: number): boolean {
        return index > 0;
    }

    public isMoveDown(index: number): boolean {
        return index < this.playlistMaterials.length - 1;
    }
}