import {ISelectable} from "./../ISelectable";
import {UrlService} from "./../utils/UrlService";

export interface IMultipleCheckboxSelectorDirectiveScope extends angular.IScope {
    entities?: Array<ISelectable>;
    model?: Array<number>;

    vm?: MultipleCheckboxSelectorController;
}

export class MultipleCheckboxSelectorController {
    public static $inject = [
        "$scope"
    ];

    constructor(private $scope: IMultipleCheckboxSelectorDirectiveScope) {
        $scope.vm = this;

        this.pushDataModel();
    }

    protected pushDataModel(): void {
        for (var i = 0; i < this.$scope.entities.length; ++i) {
            var entity: ISelectable = this.$scope.entities[i];
            entity["selected"] = false;
            for (var j = 0; j < this.$scope.model.length; ++j) {
                var id = this.$scope.model[j];
                if (entity.id == id) {
                    entity["selected"] = true;
                    break;
                }
            }
        }
    }

    public itemSelection(entity: ISelectable): void {
        for (var i = 0; i < this.$scope.model.length; ++i) {
            var id = this.$scope.model[i];
            if (id == entity.id) {
                this.$scope.model.splice(i, 1);
                return;
            }
        }
        this.$scope.model.push(entity.id);
    }
}

export function MultipleCheckboxSelectorDirectiveFactory(url: UrlService): angular.IDirective {
    var directive: angular.IDirective = {};
    directive.restrict = "A";
    directive.controller = MultipleCheckboxSelectorController;
    directive.scope = {
        entities: "=vhsEntities",
        model: "=vhsModel"
    };
    directive.templateUrl = url.template("components/multiple-checkbox-selector.html");
    directive.transclude = true;
    return directive;
}

MultipleCheckboxSelectorDirectiveFactory.$inject = [
    UrlService.NAME
];
