import {IQuiz} from "./models/IQuiz";
import {IQuizQuestion} from "./models/IQuizQuestion";
import {IQuizAnswer} from "./models/IQuizAnswer";
import {IQuizResult} from "./models/IQuizResult";
import {IQuizAnswerResult} from "./models/IQuizAnswerResult";
import {QuizzesRouter} from "./../urls/QuizzesRouter";
import {QuizService} from "./QuizService";

import {UrlService} from "./../utils/UrlService";
import {MenuHelper} from "./../utils/MenuHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";

export class QuizPassingController {
    public static NAME = "quizzes.QuizPassingController";

    private quizId: number;
    public quiz: IQuiz = {};
    public quizResult: IQuizResult = {};
    public duration: number;
    public disableCompleteButton: boolean = false;

    public static $inject = [
        "$scope",
        "$location",
        "$routeParams",
        QuizService.NAME,
        QuizzesRouter.NAME
    ];

    constructor(private $scope: angular.IScope,
                private $location: angular.ILocationService,
                private $routeParams: angular.route.IRouteParamsService,
                private quizService: QuizService,
                private quizzesRouter: QuizzesRouter) {
        this.quizId = $routeParams[QuizzesRouter.QUIZ_ID_PARAM];
        MenuHelper.setCurrentMenuItem(MenuHelper.EDUCATION_QUIZZES_MENU_KEY);

        this.loadQuiz();

        this.$scope.$on('$locationChangeStart', (event) => {
            if (!this.disableCompleteButton && this.quizResult && this.quizResult.id) {
                alert("Quiz not completed");
                event.preventDefault();
            }
        });
    }

    private loadUserQuizResult() {
        this.quizService.getUserQuizCurrentResult(this.quizId, this.quizResult.attempt).then(result => {
            this.quizResult = angular.copy(result);
            if (this.quizResult && this.quizResult.id) {
                this.duration = (this.quiz.duration != null)
                    ? Math.round(this.quiz.duration - (new Date().getTime() - result.startTime) / 1000)
                    : null;
                if (this.duration) {
                    if (this.duration <= 0) {
                        this.completeQuiz();
                    } else {
                        this.$scope.$broadcast('timer-set-countdown', this.duration);
                        this.$scope.$broadcast('timer-start');
                    }
                }
            } else {
                console.log("Cannot find your unfinished session for this Quiz.");
                this.$location.url(this.quizzesRouter.detailsUrl(this.quizId));
            }
        });
    }

    private loadQuiz() {
        this.quizService.get(this.quizId).then(q => {
            angular.copy(q, this.quiz);
            PageHeader.set().header(q.title).update();
            this.loadUserQuizResult();
        });
    }

    public selectCurrentAnswer(question: IQuizQuestion, answer: IQuizAnswer): void {
        question.answers.forEach((a: IQuizAnswer) => {
            a.selected = false;
        });
        answer.selected = true;
    }

    public completeQuiz() {
        this.disableCompleteButton = true;
        this.quizResult.answers = [];
        this.quiz.questions.forEach((question: IQuizQuestion) => {
            var result: IQuizAnswerResult = {
                quizResultId: this.quizResult.id,
                questionId: question.id,
                answerIds: question.answers.filter((a: IQuizAnswer) => a.selected).map((a: IQuizAnswer) => {
                    return a.id;
                })
            };
            this.quizResult.answers.push(result);
        });
        this.quizService.completeQuizByUser(this.quizResult).then(result => {
            this.$location.url(this.quizzesRouter.detailsUrl(this.quizId));
        })
    }
}