import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";
import {SecurityService} from "./../security/SecurityService";
import {MenuHelper} from "./../utils/MenuHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {ContentRouter} from "./../urls/ContentRouter";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {UrlService} from "./../utils/UrlService";
import {IPurchaseHistory} from "./../finances/models/IPurchaseHistory";
import {IGridModel, GridModel} from "./../IGridModel";
import {HttpGridHelper} from "./../IHttpGridService";
import {ReferencedObjectType} from "./../ReferencedObjectType";

export class PurchaseHistoryController {
    public static NAME = "finances.PurchaseHistoryController";

    public viewType: number;
    public purchaseHistoryGridModel: IGridModel<IPurchaseHistory>;

    public static $inject = [
        "$http",
        "$location",
        UrlService.NAME,
        ContentRouter.NAME,
        SecurityService.NAME
    ];

    constructor($http: angular.IHttpService,
                private $location: angular.ILocationService,
                private url: UrlService,
                public contentRouter: ContentRouter, // used in the HTML
                private securityService: SecurityService) {
        this.securityService.demandAuthentication()
            .demandSystemPermissions(systemPermissions => systemPermissions.viewPurchaseHistory);

        this.purchaseHistoryGridModel = new GridModel(HttpGridHelper.createLoader<IPurchaseHistory>($http, this.url.service("/api/finance/purchaseHistory")));
        this.viewType = 0;

        // TODO: this menu item was deleted, needs updating // MenuHelper.setCurrentMenuItem(MenuHelper.PURCHASES_PURCHASE_HISTORY_MENU_KEY);
        // TODO: // PageHeader.set().header(...).update();
    }
}
