import {IQuiz} from "./models/IQuiz";
import {QuizzesRouter} from "./../urls/QuizzesRouter";

import {MenuHelper} from "./../utils/MenuHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {PermissionsService} from "./../security/PermissionsService";
import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";
import {IGridModel, GridModel} from "./../IGridModel";
import {UrlService} from "./../utils/UrlService";
import {HttpGridHelper} from "./../IHttpGridService";
import {UserService} from "./../users/UserService";
import {ISelectable} from "./../ISelectable";

export class QuizzesController {
    public static NAME = "quizzes.QuizzesController";

    public systemPermissions: ISystemPermissions;
    public rowShow: Map<number, boolean> = new Map<number, boolean>();
    public quizzesGridModel: IGridModel<IQuiz>;
    public viewType: number;
    public owners: Array<ISelectable> = [];

    public static $inject = [
        "$http",
        "$location",
        QuizzesRouter.NAME,
        PermissionsService.NAME,
        UrlService.NAME,
        UserService.NAME
    ];

    constructor($http: angular.IHttpService,
                private $location: ng.ILocationService,
                public quizzesRouter: QuizzesRouter,
                private permissionsApi: PermissionsService,
                private url: UrlService,
                private userService: UserService) {

        MenuHelper.setCurrentMenuItem(MenuHelper.EDUCATION_QUIZZES_MENU_KEY);
        PageHeader.set().header("Quizzes").update();

        this.viewType = 1;
        this.quizzesGridModel = new GridModel(HttpGridHelper.createLoader<IQuiz>($http, this.url.service("/api/quizzes")));
        this.quizzesGridModel.pageSize = 8;

        this.userService.getUsersWithoutExcludedUsersForSelector().then(result => {
            this.owners = angular.copy(result);
        });

        this.permissionsApi.getSystemPermissions().then(systemPermissions => {
            this.systemPermissions = systemPermissions;
        });
    }

    public showDetails(index: number) {
        this.rowShow.set(index, !this.rowShow.get(index));
    }

    public searchQuiz(): void {
        this.quizzesGridModel.loadCurrentPage();
    }

    public loadQuizDetails(quizId: number) {
        this.$location.url(this.quizzesRouter.detailsUrl(quizId));
    }
}