import {UploadsService, CurrentlyUploadableFile} from "./../storage/UploadsService";
import {UrlService} from "./../utils/UrlService";

interface IUploadsStatusDirectiveScope extends angular.IScope {
    vm?: UploadsStatusController
}

class UploadsStatusController {

    private static CURRENTLY_UPLOADABLE_FILES_DELAY = 1000;

    private currentlyUploadableFiles: Array<CurrentlyUploadableFile> = [];

    public showFiles: boolean = true;

    public static $inject = [
        "$scope",
        "$interval",
        UploadsService.NAME
    ];

    constructor(private $scope: IUploadsStatusDirectiveScope,
                private $interval: angular.IIntervalService,
                private uploadsService: UploadsService) {
        this.$scope.vm = this;
        this.$interval(this.currentlyUploadableFilesFunc, UploadsStatusController.CURRENTLY_UPLOADABLE_FILES_DELAY);
    }

    public cancel(file: CurrentlyUploadableFile): void {
        this.uploadsService.cancel(file);
    }

    public toggleFiles(): void {
        this.showFiles = !this.showFiles;
    }

    private currentlyUploadableFilesFunc: () => void = () => {
        this.currentlyUploadableFiles = this.uploadsService.getCurrentlyUploadableFiles();
    }
}

UploadsStatusDirectiveFactory.$inject = [
    UrlService.NAME
];

export function UploadsStatusDirectiveFactory(url: UrlService): angular.IDirective {
    var directive: any = {};
    directive.restrict = "A";
    directive.scope = true;
    directive.controller = UploadsStatusController;
    directive.templateUrl = url.template("components/uploads-status.html");
    return directive;
}