import {CommonModule} from "./../common/CommonModule";
import {SecurityModule} from "./../security/SecurityModule";
import {UrlsModule} from "./../urls/UrlsModule";

import {ContentService} from "./ContentService";

export var ContentModule: angular.IModule = angular
        .module("content", [
            CommonModule.name,
            SecurityModule.name,
            UrlsModule.name
        ])
        .service(ContentService.NAME, ContentService)
    ;
