import {UrlService} from "../../utils/UrlService"

import {IGridModel, GridModel, IGridLoadingListener} from "../../IGridModel";
import {HttpGridHelper} from "../../IHttpGridService";

import {IPage} from "../../IPage";
import {IBundle} from "../../bundles/models/IBundle";
import {IBundleFile} from "../../bundles/models/IBundleFile";
import {BundleFileTypeEnum} from "../../bundles/models/BundleFileType";
import {Event} from "../models/Event"
import {ITextAngularEventData} from "../models/ITextAngularEventData"
import {StringBuilder} from "../../utils/StringBuilder"
import {RandomUtils} from "../../utils/RandomUtils";
import {DomUtils} from "../../utils/DomUtils";
import {BundleService} from "../../bundles/BundleService";

export interface IVhsSelectSandboxDirectiveScope extends angular.IScope {
    vm?: VhsSelectSandboxController;
    bundleId?: number;
}

export class VhsSelectSandboxController {

    public static EMBEDDED_IMG_TA_HTML: string = '<img class="ta-vhs-sandbox" id=":bundleId" src=":imageSrc" width="600" height="400"/>';
    private static SANDBOX_TA_IMG_PATH: string = "./themes/default/img/ta_sandbox.png";

    public bundleFilesGridModel: IGridModel<IBundleFile>;
    private lastEventData: ITextAngularEventData;
    private element: angular.IAugmentedJQuery;
    private richEditorParent: angular.IAugmentedJQuery;
    private loadPopupContent: boolean = undefined;

    public static $inject = [
        "$scope",
        "$timeout",
        "$http",
        BundleService.NAME,
        UrlService.NAME
    ];

    constructor(private $scope: IVhsSelectSandboxDirectiveScope,
                private $timeout: angular.ITimeoutService,
                private $http: angular.IHttpService,
                private bundleApi: BundleService,
                private url: UrlService) {
        $scope.vm = this;
        $scope.$on(Event.TA_SELECT_SANDBOX, (event: ng.IAngularEvent, data: ITextAngularEventData) => {
            if (this.isEventProcessable(data)) {
                this.loadPopupContent = true;
                this.lastEventData = data;
                this.element.find("div.modal").modal({
                    backdrop: 'static',
                    keyboard: false,
                    show: true
                });
            }
        });
        this.bundleFilesGridModel = new GridModel(HttpGridHelper.createLoader<IBundleFile>($http, this.url.service("/api/bundles/" + $scope.bundleId + "/files")));
        this.bundleFilesGridModel.pageSize = 6;
        this.bundleFilesGridModel.filter.bundleFileType = BundleFileTypeEnum[BundleFileTypeEnum.HTML];
    }

    public init($scope: IVhsSelectSandboxDirectiveScope,
                element: angular.IAugmentedJQuery) {
        this.element = element;
        this.richEditorParent = DomUtils.findParentWithClass(this.element, "vhs-rich-editor-content");
    }

    public onBundleFileSelect(bundleFile: IBundleFile) {
        var html = StringBuilder.interpolate(VhsSelectSandboxController.EMBEDDED_IMG_TA_HTML)
                                       .with(":bundleId", RandomUtils.generateRandomId(bundleFile.key + "___"))
                                       .with(":imageSrc", this.url.staticResourceUrl(VhsSelectSandboxController.SANDBOX_TA_IMG_PATH))
                                       .build();

        this.lastEventData.callbackFunction(html);
    }

    private isEventProcessable(data: ITextAngularEventData) {
        if (data.eventSourceButton != null) {
            var parent = DomUtils.findParentWithClass(data.eventSourceButton, "vhs-rich-editor-content");
            if (parent != null && parent.is(this.richEditorParent)) {
                return true;
            }
        }
        return false;
    }
}

export function VhsSelectSandboxDirectiveFactory(url: UrlService): angular.IDirective {
    function link($scope: IVhsSelectSandboxDirectiveScope,
                  element: angular.IAugmentedJQuery,
                  attributes: angular.IAttributes,
                  controller: VhsSelectSandboxController) {
        controller.init($scope, element);
    }

    let directive: angular.IDirective = {
        restrict: "A",
        link: link,
        controller: VhsSelectSandboxController,
        scope: {
            bundleId: "@vhsBundleId"
        },
        templateUrl: url.template("components/vhs-select-sandbox.html"),
    };
    return directive;
}

VhsSelectSandboxDirectiveFactory.$inject = [
    UrlService.NAME
];
