import {SettingsService} from "../settings/SettingsService";
import IScope = angular.IScope;
import IAugmentedJQuery = angular.IAugmentedJQuery;
import IDirective = angular.IDirective;
import IAttributes = angular.IAttributes;
import ITranscludeFunction = angular.ITranscludeFunction;

export function CurrentYearDirectiveFactory(settingsService: SettingsService): IDirective {
    function link($scope: IScope, element: IAugmentedJQuery,
                  attributes: IAttributes, controller: any,
                  transclude: ITranscludeFunction) {
        settingsService.getCurrentDate()
            .then(date => $scope["currentDate"] = date);
    }


    const directive: any = {};
    directive.restrict = "A";
    directive.template = "{{::currentDate | date:'yyyy'}}";
    directive.link = link;
    directive.scope = true;
    return directive;
}

CurrentYearDirectiveFactory.$inject = [
    SettingsService.NAME
];
