import {UrlService} from "./../utils/UrlService";
import {ReferencedObjectType} from "./../ReferencedObjectType";
import {IContentView} from "./models/IContentView";

export class ContentService {
    public static NAME = "content.ContentService";

    public static $inject = [
        "$http",
        UrlService.NAME
    ];

    constructor(private $http: angular.IHttpService,
                private url: UrlService) {
    }

    public getContentView(objectId: number, objectType: any): angular.IPromise<IContentView> {
        let myObjectType: string =
            (typeof objectType === 'string') ? objectType :
                (typeof objectType === 'number') ? ReferencedObjectType[objectType] : undefined;
        return this.$http.get(this.url.base("/api/content/:type/:id")
            .param(":id", objectId)
            .param(":type", myObjectType)
            .service())
            .then(result => result.data);
    }
}
