import {PermissionsService} from "./PermissionsService";
import {ISystemPermissions} from "./permissions/ISystemPermissions";
import {IMaterialPermissions} from "./permissions/IMaterialPermissions";
import {IPlaylistPermissions} from "./permissions/IPlaylistPermissions";
import {ICoursePermissions} from "./permissions/ICoursePermissions";
import {IQuizPermissions} from "./permissions/IQuizPermissions";
import {INewsPermissions} from "./permissions/INewsPermissions";

import {AuthManager} from "./../authcore/AuthManager";
import {AuthRedirect} from "./../authcore/AuthRedirect";
import {RedirectReason} from "./../authcore/RedirectReason";
import {AuthRouter} from "./../urls/AuthRouter";

export class SecurityService {
    public static NAME = "security.SecurityService";

    public static $inject = [
        "$location",
        AuthManager.NAME,
        AuthRedirect.NAME,
        PermissionsService.NAME,
        AuthRouter.NAME
    ];

    constructor(private $location: ng.ILocationService,
                private authManager: AuthManager,
                private authRedirect: AuthRedirect,
                private permissionsApi: PermissionsService,
                private authRouter: AuthRouter) {
    }

    public demandAuthentication(): SecurityService {
        if (!this.authManager.hasToken()) {
            this.authRedirect.redirectToLogOn(RedirectReason.ACCESS_DENIED);
        } else if (!this.authManager.isAuthenticated()) {
            this.authRedirect.redirectToLogOn(RedirectReason.TOKEN_EXPIRED);
        }
        return this;
    }

    public isAuthenticated(): boolean {
        if (!this.authManager.hasToken()) {
            return false;
        } else if (!this.authManager.isAuthenticated()) {
            return false;
        }
        return true;
    }

    public demandNotAuthenticated(): SecurityService { // Guest user is considered not authenticated on the client side;
        if (this.authManager.isAuthenticated()) {
            this.authRedirect.forwardToAccessDenied();
        }
        return this;
    }

    public demandSystemPermissions(func: (systemPermissions: ISystemPermissions) => boolean): SecurityService {
        return this.demandSystemPermissionsIf(true, func);
    }

    public demandSystemPermissionsIf(condition: boolean, func: (systemPermissions: ISystemPermissions) => boolean): SecurityService {
        if (condition) {
            this.permissionsApi.getSystemPermissions().then(systemPermissions => {
                if (!func(systemPermissions)) {
                    this.forwardToAccessDeniedOrRedirectLogOn();
                }
            });
        }
        return this;
    }

    public demandMaterialPermissions(materialId: string, func: (materialPermissions: IMaterialPermissions) => boolean): SecurityService {
        return this.demandMaterialPermissionsIf(true, materialId, func);
    }

    public demandMaterialPermissionsIf(condition: boolean, materialId: string,
                                       func: (materialPermissions: IMaterialPermissions) => boolean): SecurityService {
        if (condition) {
            this.permissionsApi.getMaterialPermissions(materialId).then(materialPermissions => {
                if (!func(materialPermissions)) {
                    this.forwardToAccessDeniedOrRedirectLogOn();
                }
            });
        }
        return this;
    }

    public demandPlaylistPermissions(playlistId: string, func: (playlistPermissions: IPlaylistPermissions) => boolean): SecurityService {
        return this.demandPlaylistPermissionsIf(true, playlistId, func);
    }

    public demandPlaylistPermissionsIf(condition: boolean, playlistId: string,
                                       func: (playlistPermissions: IPlaylistPermissions) => boolean): SecurityService {
        if (condition) {
            this.permissionsApi.getPlaylistPermissions(playlistId).then(playlistPermissions => {
                if (!func(playlistPermissions)) {
                    this.forwardToAccessDeniedOrRedirectLogOn();
                }
            });
        }
        return this;
    }

    public demandCoursePermissions(courseId: string, func: (coursePermissions: ICoursePermissions) => boolean): SecurityService {
        return this.demandCoursePermissionsIf(true, courseId, func);
    }

    public demandCoursePermissionsIf(condition: boolean, courseId: string,
                                     func: (coursePermissions: ICoursePermissions) => boolean): SecurityService {
        if (condition) {
            this.permissionsApi.getCoursePermissions(courseId).then(coursePermissions => {
                if (!func(coursePermissions)) {
                    this.forwardToAccessDeniedOrRedirectLogOn();
                }
            });
        }
        return this;
    }

    public demandQuizPermissions(quizId: string, func: (quizPermissions: IQuizPermissions) => boolean): SecurityService {
        return this.demandQuizPermissionsIf(true, quizId, func);
    }

    public demandQuizPermissionsIf(condition: boolean, quizId: string,
                                   func: (quizPermissions: IQuizPermissions) => boolean): SecurityService {
        if (condition) {
            this.permissionsApi.getQuizPermissions(quizId).then(quizPermissions => {
                if (!func(quizPermissions)) {
                    this.forwardToAccessDeniedOrRedirectLogOn();
                }
            });
        }
        return this;
    }

    public demandNewsPermissions(newsId: string, func: (newsPermissions: INewsPermissions) => boolean): SecurityService {
        return this.demandMaterialPermissionsIf(true, newsId, func);
    }

    public demandNewsPermissionsIf(condition: boolean, newsId: string,
                                   func: (newsPermissions: INewsPermissions) => boolean): SecurityService {
        if (condition) {
            this.permissionsApi.getNewsPermissions(newsId).then(newsPermissions => {
                if (!func(newsPermissions)) {
                    this.forwardToAccessDeniedOrRedirectLogOn();
                }
            });
        }
        return this;
    }

    private forwardToAccessDeniedOrRedirectLogOn(): void {
        if (this.authManager.isAuthenticated()) {
            this.authRedirect.forwardToAccessDenied();
        } else if (!this.authManager.hasToken()) {
            this.authManager.eraseToken();
            this.authRedirect.redirectToLogOn(RedirectReason.ACCESS_DENIED);
        } else {
            this.authManager.logOff();
            this.authManager.eraseToken();
            this.authRedirect.redirectToLogOn(RedirectReason.TOKEN_EXPIRED);
        }
    }
}