export function getLineDiagramsReportOptions(reportName: string): any {
    return {
        title: reportName,
        legend: {
            show: true
        },
        highlighter: {
            show: true
        },
        axes: {
            xaxis: {
                renderer: $['jqplot']['DateAxisRenderer'],
                tickOptions: {
                    formatString: '%b %d, %Y'
                }
            },
            yaxis:{
                tickOptions: {
                    formatString: '%.0f'
                }
            }
        }
    };
}

