import {StringBuilder} from "./../utils/StringBuilder";
import {BaseRouter} from "./BaseRouter";

export class CoursesRouter extends BaseRouter {
    public static NAME = "urls.CoursesRouter";
    public static COURSE_ID_PARAM = "courseId";
    public static COURSE_QUIZ_ID_PARAM = "quizId";

    public static indexRoute = "/courses";
    public static detailsRoute = `/courses/:${CoursesRouter.COURSE_ID_PARAM}`;
    public static quizDetailsRoute = `/courses/quizzes/:${CoursesRouter.COURSE_QUIZ_ID_PARAM}`;
    public static updateRoute = `/courses/update/:${CoursesRouter.COURSE_ID_PARAM}`;
    public static createRoute = "/courses/create";
    public static progressRoute = `/courses/:${CoursesRouter.COURSE_ID_PARAM}` + "/progress";
    public static infoRoute = `/courses/:${CoursesRouter.COURSE_ID_PARAM}` + "/info";

    public static $inject = [
        "$location"
    ];

    constructor($location: ng.ILocationService) {
        super($location);
    }

    public indexUrl(): string {
        return this.url(CoursesRouter.indexRoute);
    }

    public createUrl(): string {
        return this.url(CoursesRouter.createRoute);
    }

    public detailsUrl(courseId: number): string {
        return this.url(StringBuilder
            .interpolate(CoursesRouter.detailsRoute)
            .with(':' + CoursesRouter.COURSE_ID_PARAM, courseId.toString())
            .build());
    }

    public quizDetailsUrl(quizId: number): string {
        return this.url(StringBuilder
            .interpolate(CoursesRouter.quizDetailsRoute)
            .with(':' + CoursesRouter.COURSE_QUIZ_ID_PARAM, quizId.toString())
            .build());
    }

    public updateUrl(courseId: number): string {
        return this.url(StringBuilder.interpolate(CoursesRouter.updateRoute)
            .with(':' + CoursesRouter.COURSE_ID_PARAM, courseId.toString())
            .build());
    }

    public progressUrl(courseId: number): string {
        return this.url(StringBuilder
            .interpolate(CoursesRouter.progressRoute)
            .with(':' + CoursesRouter.COURSE_ID_PARAM, courseId.toString())
            .build());
    }

    public infoUrl(courseId: number): string {
        return this.url(StringBuilder
            .interpolate(CoursesRouter.infoRoute)
            .with(':' + CoursesRouter.COURSE_ID_PARAM, courseId.toString())
            .build());
    }
}