interface TooltipAttributes extends angular.IAttributes {
}

class TooltipController {
    public static $inject = [
        "$timeout"
    ];

    constructor(private $timeout: angular.ITimeoutService) {
    }

    public makeTooltip(element: any) {
        this.$timeout(a => element.tooltip(), 0, false);
    }
}

export function TooltipDirectiveFactory(): angular.IDirective {
    function link($scope: angular.IScope, element: any,
                  attributes: TooltipAttributes, controller: TooltipController,
                  transclude: angular.ITranscludeFunction) {
        controller.makeTooltip(element);
    }

    var directive: any = {};
    directive.restrict = "A";
    directive.link = link;
    directive.controller = TooltipController;
    directive.scope = true;
    directive.trunsclude = false;
    return directive;
}
