export class DomUtils {

    public static findParentWithClass(sourceElement: angular.IAugmentedJQuery, targetClass: string) {
        var parent: angular.IAugmentedJQuery = <angular.IAugmentedJQuery> sourceElement.parent();
        while(parent) {
            if (parent.hasClass(targetClass)) {
                return <angular.IAugmentedJQuery> $(parent[0]);
            }
            parent = <angular.IAugmentedJQuery> parent.parent();
        }
        return null;
    }
    public static findParentWithAttr(sourceElement: angular.IAugmentedJQuery, attrName: string) {
        var parent: angular.IAugmentedJQuery = <angular.IAugmentedJQuery> sourceElement.parent();
        while(parent && parent.length > 0) {
            if (parent.length > 0 && parent.attr(attrName) != undefined) {
                return <angular.IAugmentedJQuery> $(parent[0]);
            }
            parent = <angular.IAugmentedJQuery> parent.parent();
        }
        return null;
    }

}
