import {NewsService} from "./NewsService";
import {NewsRouter} from "./../urls/NewsRouter";
import {NewsSearchController} from "./NewsSearchController";
import {OneNewsController} from "./OneNewsController";
import {NewsEditController} from "./NewsEditController";

import {AuthCoreModule} from "./../authcore/AuthCoreModule";
import {SocialModule} from "./../social/SocialModule";
import {SecurityModule} from "./../security/SecurityModule";

export var NewsModule: angular.IModule = angular
    .module("news", [
        AuthCoreModule.name,
        SocialModule.name,
        SecurityModule.name
    ])
    .service(NewsService.NAME, NewsService)
    .controller(NewsSearchController.NAME, NewsSearchController)
    .controller(OneNewsController.NAME, OneNewsController)
    .controller(NewsEditController.NAME, NewsEditController);