import {PermissionsService} from "./../security/PermissionsService";
import {SecurityService} from "./../security/SecurityService";
import {MenuHelper} from "./../utils/MenuHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {MaterialsRouter} from "./../urls/MaterialsRouter";
import {IGridModel, GridModel} from "./../IGridModel";
import {IMaterial} from "./models/IMaterial";
import {HttpGridHelper} from "./../IHttpGridService";
import {UrlService} from "./../utils/UrlService";
import {PlaylistService} from "./PlaylistService";
import {IPlaylist} from "./models/IPlaylist";
import {ObjectTypeUtils} from "./../utils/ObjectTypeUtils";
import {ActionLogService} from "./../statistics/ActionLogService";
import {ActionType} from "./../statistics/models/ActionType";
import {ReferencedObjectType} from "./../ReferencedObjectType";
import {notifier} from "./../INotifier";

export class PlaylistDetailsController {
    public static NAME = "materials.PlaylistDetailsController";

    public rowShow: Map<number, boolean> = new Map<number, boolean>();
    public viewType: number;
    public playlistId: number;
    public materialsGridModel: IGridModel<IMaterial>;

    private playlist: IPlaylist = {};

    public static $inject = [
        "$location",
        "$routeParams",
        "$http",
        MaterialsRouter.NAME,
        SecurityService.NAME,
        UrlService.NAME,
        PlaylistService.NAME,
        ActionLogService.NAME
    ];

    constructor(private $location: angular.ILocationService,
                private $routeParams: angular.route.IRouteParamsService,
                $http: angular.IHttpService,
                public materialsRouter: MaterialsRouter,
                private securityService: SecurityService,
                private url: UrlService,
                private playlistService: PlaylistService,
                private actionLogService: ActionLogService) {
        this.playlistId = $routeParams[MaterialsRouter.PLAYLIST_ID_PARAM];

        this.materialsGridModel = new GridModel(HttpGridHelper.createLoader<IMaterial>($http, this.url.service("/api/playlists/" + this.playlistId + "/materials")));
        this.materialsGridModel.entries.forEach(e => {
            this.rowShow.set(e.id, false);
        });
        var menuItemKey: string = MenuHelper.getCurrentMenuItemKey();
        if (!menuItemKey || menuItemKey.length == 0) {
            MenuHelper.setCurrentMenuItem(MenuHelper.EDUCATION_PLAYLISTS_MENU_KEY);
        }
        this.loadPlaylist();
        this.viewType = 0;
    }

    private loadPlaylist(): void {
        var promise: angular.IPromise<IPlaylist> = this.playlistService.get(this.playlistId);
        promise.then(result => {
            angular.copy(result, this.playlist);
            this.actionLogService.create(this.playlist.id, ReferencedObjectType.PLAYLIST, ActionType.PLAYLIST_VIEW);
            PageHeader.set().header(' ' + result.name + '').update(); // TODO: real title
        });
    }

    public getMaterialTypeClass(materialTypeId: number): string {
        return ObjectTypeUtils.getMaterialGlyphiconClass(materialTypeId);
    }

    public loadMaterialPage(material: IMaterial) {
        if(!material.available) {
            notifier.error("Access to the material is restricted");
        } else {
            this.$location.url(this.materialsRouter.watchUrl(material.id));
        }
    }

    public showDetails(index: number): void {
        this.rowShow.set(index, !this.rowShow.get(index));
    }
}
