import {StringBuilder} from "../utils/StringBuilder";
import {LocationUtils} from "../utils/LocationUtils";
import {BaseRouter} from "./BaseRouter";

export class AuthRouter extends BaseRouter {
    public static NAME = "urls.AuthRouter";

    public static ACCESS_DENIED_PARAM_NAME = "accessDenied";
    public static TOKEN_EXPIRED_PARAM_NAME = "tokenExpired";
    public static LOGON_REQUIRED_PARAM_NAME = "logonRequired";
    public static LOGON_PARAM_NAME = "logonName";
    public static PASSWORD_TOKEN_PARAM_NAME = "token";
    public static PASSWORD_CODE_PARAM_NAME = "code";
    public static RENEWAL_TYPE_PARAM_NAME = "renewalType";

    public static logonRoute = "/auth/logon";
    public static logoutRoute = "/auth/logout";
    public static accessDeniedRoute = "/auth/accessDenied";
    public static restorePasswordRoute = "/auth/restore/password";
    public static checkValidCodeRoute = "/auth/check/code";
    public static createNewPasswordRoute = `/auth/create/new/password/token/:${AuthRouter.PASSWORD_TOKEN_PARAM_NAME}`;
    public static createNewPasswordByCodeRoute = `/auth/create/new/password/code/:${AuthRouter.PASSWORD_CODE_PARAM_NAME}`;

    public static $inject = [
        "$location"
    ];

    constructor($location: ng.ILocationService) {
        super($location);
    }

    public logonUrl(): string {
        return this.url(AuthRouter.logonRoute);
    }

    public logoutUrl(): string {
        return this.url(AuthRouter.logoutRoute);
    }

    public accessDeniedUrl(): string {
        return this.url(AuthRouter.accessDeniedRoute);
    }

    public restorePassword(): string {
        return this.url(AuthRouter.restorePasswordRoute);
    }

    public checkValidCode(): string {
        return this.url(AuthRouter.checkValidCodeRoute);
    }

    public createNewPassword(token: string): string {
        return this.url(StringBuilder.interpolate(AuthRouter.createNewPasswordRoute)
            .with(':' + AuthRouter.PASSWORD_TOKEN_PARAM_NAME, token)
            .build());
    }

    public createNewPasswordByCode(code: string): string {
        return this.url(StringBuilder.interpolate(AuthRouter.createNewPasswordByCodeRoute)
            .with(':' + AuthRouter.PASSWORD_CODE_PARAM_NAME, code)
            .build());
    }
}
