import {AuthManager} from "./../authcore/AuthManager";
import {PersonalService} from "./PersonalService";
import {PermissionsService} from "./../security/PermissionsService";
import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";
import {SecurityService} from "./../security/SecurityService";
import {MenuHelper} from "./../utils/MenuHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {MaterialsRouter} from "./../urls/MaterialsRouter";
import {CoursesRouter} from "./../urls/CoursesRouter";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {IGridModel, GridModel} from "./../IGridModel";
import {HttpGridHelper} from "./../IHttpGridService";
import {IFavorite} from "./models/IFavorite";
import {UrlService} from "./../utils/UrlService";
import {ObjectTypeUtils} from "./../utils/ObjectTypeUtils";
import {notifier} from "./../INotifier";
import {ReferencedObjectType} from "./../ReferencedObjectType";

export class FavoritesController {
    public static NAME = "personal.FavoritesController";

    public rowShow: Map<number, boolean> = new Map<number, boolean>();
    public viewType: number;
    public favoritesGridModel: IGridModel<IFavorite>;
    public disableRemoveButton: boolean = false;

    public static $inject = [
        "$http",
        "$location",
        UrlService.NAME,
        AuthManager.NAME,
        PersonalService.NAME,
        MaterialsRouter.NAME,
        SecurityService.NAME,
        CoursesRouter.NAME
    ];

    constructor($http: angular.IHttpService,
                private $location: angular.ILocationService,
                private url: UrlService,
                private authManager: AuthManager,
                public personalService: PersonalService,
                public materialsRouter: MaterialsRouter,
                private securityService: SecurityService,
                public coursesRouter: CoursesRouter) {
        this.securityService.demandAuthentication();

        this.viewType = 1;

        MenuHelper.setCurrentMenuItem(MenuHelper.FAVORITES_MENU_KEY);
        PageHeader.set().header("Favorites").update();

        this.favoritesGridModel = new GridModel(HttpGridHelper.createLoader<IFavorite>($http, this.url.service("/api/personal/favorites")));
        this.favoritesGridModel.pageSize = 8;
    }

    public showDetails(index: number, favorite: IFavorite) {
        if(favorite.available) {
            this.rowShow.set(index, !this.rowShow.get(index));
        }
    }

    public loadFavoritePage(favorite: IFavorite) {
        if (favorite.objectType == ReferencedObjectType[ReferencedObjectType.MATERIAL]) {
            if(!favorite.available) {
                notifier.error("Access to this material is restricted");
            } else {
                this.$location.url(this.materialsRouter.watchUrl(favorite.objectId));
            }
        }
        if (favorite.objectType == ReferencedObjectType[ReferencedObjectType.PLAYLIST]) {
            if(!favorite.available) {
                notifier.error("Access to this playlist is restricted");
            } else {
                this.$location.url(this.materialsRouter.playlistDetailsUrl(favorite.objectId));
            }
        }
        if (favorite.objectType == ReferencedObjectType[ReferencedObjectType.COURSE]) {
            if(!favorite.available) {
                notifier.error("Access to this course is restricted");
            } else {
                this.$location.url(this.coursesRouter.detailsUrl(favorite.objectId));
            }
        }
    }

    public isMaterialObject(referenceObjectType: string): boolean {
        return ObjectTypeUtils.isMaterialObject(referenceObjectType);
    }

    public isPlaylistObject(referenceObjectType: string): boolean {
        return ObjectTypeUtils.isPlaylistObject(referenceObjectType);
    }

    public isCourseObject(referenceObjectType: string): boolean {
        return ObjectTypeUtils.isCourseObject(referenceObjectType);
    }

    public getMaterialTypeClass(materialTypeKey: string): string {
        return ObjectTypeUtils.getMaterialGlyphiconClassFromKey(materialTypeKey);
    }

    public getContentTypeTitle(contentTypeKey: string): string {
        return ObjectTypeUtils.getContentTypeTitle(contentTypeKey);
    }

    public removeFromFavorites(favoriteId: number, favoriteType: string) {
        this.disableRemoveButton = true;
        this.personalService.removeFromFavorites(favoriteId, ObjectTypeUtils.getReferenceObjectTypeFromString(favoriteType)).then(result => {
            notifier.success(this.getRemoveMessage(favoriteType));
            this.favoritesGridModel.loadCurrentPage();
            this.disableRemoveButton = false;
        }).catch(response => {
            notifier.error("Failed to remove it from your favorites");
            this.disableRemoveButton = false;
        });
    }

    private getRemoveMessage(favoriteType: string): string {
        switch (favoriteType) {
            case "MATERIAL":
                return "Material removed from your favorites";
            case "PLAYLIST":
                return "Playlist removed from your favorites";
            case "COURSE":
                return "Course removed from your favorites";
            default:
                return "Material removed from your favorites";
        }
    }

    public getLanguages(languages: Array<string>): string {
        var languagesStr: string = "";
        languages.forEach(l => {
            languagesStr +=  l + ", ";
        });
        return languagesStr.substring(0, languagesStr.length-2);
    }
}
