import {BaseRouter} from "./BaseRouter";

export class CommonRouter extends BaseRouter {
    public static NAME = "urls.CommonRouter";

    public static notFoundRoute = "/notFound";

    public static $inject = [
        "$location"
    ];

    constructor($location: ng.ILocationService) {
        super($location);
    }

    public notFoundUrl(): string {
        return this.url(CommonRouter.notFoundRoute);
    }
}
