import {LocationUtils} from "./../utils/LocationUtils";

export function FullScreenDirectiveFactory($location: angular.ILocationService, $rootScope: angular.IRootScopeService): angular.IDirective {
    var FULL_SCREEN_CLASS = "full-screen-view";
    function link($scope: angular.IScope,
                  element: angular.IAugmentedJQuery,
                  attributes: angular.IAttributes,
                  controller: any) {
        var applyFullScreenFunction = function() {
            if (LocationUtils.isFullScreen($location)) {
                element.addClass(FULL_SCREEN_CLASS);
            } else {
                element.removeClass(FULL_SCREEN_CLASS);
            }
        };
        $rootScope.$on('$locationChangeSuccess', ($locationEvent) => {
            applyFullScreenFunction();
        });
        applyFullScreenFunction();
        element.removeClass("invisible-on-load");
    }

    let directive: angular.IDirective = {
        restrict: "A",
        link: link
    };
    return directive;
}

FullScreenDirectiveFactory.$inject = [
    "$location",
    "$rootScope"
];