export class PageHeader {
    private static STATUS_TEMPLATE = '%s <span class="%s-status-color-%s">%s&nbsp;</span><span class="%s-status-%s"></span>';
    private static STATUS_WITH_TOOLTIP_TEMPLATE = '%s <span class="%s-status-color-%s">%s&nbsp;</span><span class="%s-status-%s" data-toggle="tooltip" tooltip data-placement="top" title="%s"></span>';
    private static LINK_TEMPLATE = '<a role="button" href="#%s">%s</a>';

    public pageName: string;
    public statusPanel: string;
    public actions: Array<string> = [];
    public preserveStatusPanel: boolean = false;
    public preserveActionsPanel: boolean = false;
    public preservePageName: boolean = false;

    public static set(): PageHeader {
        return new PageHeader();
    }

    public header(pageName: string): PageHeader {
        this.pageName = pageName;
        this.preservePageName = false;
        return this;
    }

    public preserveHeader(): PageHeader {
        this.preservePageName = true;
        return this;
    }

    public addActionLink(name: string, link: string): PageHeader {
        var a = PageHeader.format(PageHeader.LINK_TEMPLATE, [link, name]);
        this.actions.push(a);
        this.preserveActionsPanel = false;
        return this;
    }

    public addActionLinkIf(condition: boolean, name: string, link: string): PageHeader {
        if (condition) {
            return this.addActionLink(name, link);
        }
        return this;
    }

    public preserveLinks(): PageHeader {
        this.preserveActionsPanel = true;
        return this;
    }

    public update(): void {
        PageHeaderHelper.updateHeader(this);
    }

    private static format(str: string, args: Array<any>): string {
        var newStr = str;
        var i = 0;
        while (/%s/.test(newStr)) {
            newStr = newStr.replace("%s", args[i++]);
        }
        return newStr;
    }
}

export interface IPageHeaderUpdateListener {
    onHeaderUpdate(pageHeader: PageHeader);
}

export class PageHeaderHelper {

    private static pageHeaderListeners: Array<IPageHeaderUpdateListener> = [];

    public static updateHeader(headerInfo: PageHeader) {
        for (var i = 0; i < this.pageHeaderListeners.length; i++) {
            this.pageHeaderListeners[i].onHeaderUpdate(headerInfo);
        }
    }

    public static addPageHeaderUpdateListener(listener: IPageHeaderUpdateListener) {
        if (this.pageHeaderListeners.indexOf(listener) == -1) {
            this.pageHeaderListeners.push(listener);
        }
    }

    public static removePageHeaderUpdateListener(listener: IPageHeaderUpdateListener) {
        var index = this.pageHeaderListeners.indexOf(listener);
        if (index > -1) {
            this.pageHeaderListeners.splice(index, 1);
        }
    }
}