import {IStoragePlace} from "./models/IStoragePlace";
import {StorageTypeEnum} from "./models/StorageTypeEnum";
import {SettingsService} from "./SettingsService";
import {SystemSettingsRouter} from "./../urls/SystemSettingsRouter";
import {MenuHelper} from "./../utils/MenuHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {notifier} from "./../INotifier";

export class StoragePlaceEditController {
    public static NAME = "settings.StoragePlaceEditController";

    private storagePlaceId: number;

    public storagePlace: IStoragePlace = {
        id: null,
        version: null,
        name: "",
        externalUrl: null,
        connectionString: null,
        login: null,
        password: null,
        type: null,
        capacity: null,
        usedSpace: null,
        secured: true,
        active: true
    };
    public confirmPassword: string;
    public error: string;
    public disableSaveButton: boolean = false;
    public availableStorageTypes: string[] = [];

    public static $inject = [
        "$http",
        "$routeParams",
        "$location",
        SettingsService.NAME,
        SystemSettingsRouter.NAME
    ];

    constructor(private $http: angular.IHttpService,
                private $routeParams: angular.route.IRouteParamsService,
                private $location: angular.ILocationService,
                private settingsApi: SettingsService,
                public systemSettingsRouter: SystemSettingsRouter) {

        MenuHelper.setCurrentMenuItem(MenuHelper.ADMINISTRATION_STORAGE_PLACES_MENU_KEY);
        this.storagePlaceId = this.$routeParams["storagePlaceId"];
        if (this.storagePlaceId) {
            this.load();
        } else {
            PageHeader.set().header("New storage").update();
        }
        for (let s in StorageTypeEnum) {
            if (isNaN(s)) {
                this.availableStorageTypes.push(s);
            }
        }
    }

    private load() {
        this.settingsApi.getStoragePlace(this.storagePlaceId).then(result => {
            PageHeader.set().header(result.name).update();
            this.storagePlace = result;
            this.confirmPassword = result.password;
        });
    }

    public save() {
        this.disableSaveButton = true;

        var promise = (this.storagePlaceId)
            ? this.settingsApi.updateStoragePlace(this.storagePlace)
            : this.settingsApi.createStoragePlace(this.storagePlace);
        promise.then(result => {
            this.disableSaveButton = false;
            notifier.success(
                "Storage " +
                result.name +
                " has been saved"
            );
            if (!this.storagePlaceId) {
                this.$location.url(this.systemSettingsRouter.storagePlaceEditUrl(result.id));
            } else {
                this.load();
            }
        });
    }

    public deactivate() {
        this.settingsApi.setStoragePlaceActive(this.storagePlace.id, false)
            .then(storagePlace => {
                this.$location.url(this.systemSettingsRouter.storagePlacesIndexUrl());
                notifier.success(
                    "Storage " +
                    storagePlace.name +
                    " has been deactivated");
            })
    }
}
