import {UrlService} from "./../utils/UrlService";

export function GridPaginationDirectiveFactory(url: UrlService): angular.IDirective {
    var directive: any = {};
    directive.restrict = "A";
    directive.templateUrl = url.template("components/grid-pagination.html");
    directive.require = "^vhsGrid";
    return directive;
}

GridPaginationDirectiveFactory.$inject = [
    UrlService.NAME
];
