import {IQuiz} from "./models/IQuiz";
import {IQuizResult} from "./models/IQuizResult";
import {QuizzesRouter} from "./../urls/QuizzesRouter";
import {QuizService} from "./QuizService";

import {AuthManager} from "./../authcore/AuthManager";
import {AuthRedirect} from "./../authcore/AuthRedirect";
import {UrlService} from "./../utils/UrlService";
import {MenuHelper} from "./../utils/MenuHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {notifier} from "./../INotifier";

export class QuizController {
    public static NAME = "quizzes.QuizController";

    public isAuthenticated: boolean;
    private quizId: number;
    public quiz: IQuiz = {};
    public quizResults: Array<IQuizResult> = [];

    public static $inject = [
        "$location",
        "$routeParams",
        AuthManager.NAME,
        AuthRedirect.NAME,
        QuizService.NAME,
        QuizzesRouter.NAME
    ];

    constructor(private $location: angular.ILocationService,
                private $routeParams: angular.route.IRouteParamsService,
                private authManager: AuthManager,
                public authRedirect: AuthRedirect,
                private quizService: QuizService,
                private quizzesRouter: QuizzesRouter) {
        this.quizId = $routeParams[QuizzesRouter.QUIZ_ID_PARAM];
        this.isAuthenticated = this.authManager.isAuthenticated();

        MenuHelper.setCurrentMenuItem(MenuHelper.EDUCATION_QUIZZES_MENU_KEY);

        this.loadQuiz();
    }

    private loadQuiz() {
        this.quizService.get(this.quizId).then(q => {
            angular.copy(q, this.quiz);
            PageHeader.set().header(q.title).update();
            this.loadUserQuizResult();
        })
    }

    private loadUserQuizResult() {
        this.quizService.getUserQuizResults(this.quizId, "QUIZ").then(result => {
            angular.copy(result, this.quizResults);
        });
    }

    public startQuiz() {
        if (!this.isAuthenticated) {
            this.authRedirect.redirectToLogOn();
        } else {
            if (this.quizResults.length == 0) {
                this.quizService.startQuizByUser(this.quizId, "QUIZ").then(result => {
                    this.$location.url(this.quizzesRouter.passingUrl(this.quizId));
                });
            } else {
                notifier.error("You have reached the limit of retries for this quiz");
            }
        }
    }
}