import {StorageWrapper, Storages} from "./../infrastructure/Storage";
import {Event} from "./../authcore/Event";
import {UrlService} from "./../utils/UrlService";
import {
    IMenuUpdateListener,
    MenuHelper
} from "./../utils/MenuHelper";
import {
    IPageHeaderUpdateListener,
    PageHeaderHelper,
    PageHeader
} from "./../utils/PageHeaderHelper";

interface PageHeaderAttributes extends angular.IAttributes {
}

export class PageHeaderController implements IMenuUpdateListener, IPageHeaderUpdateListener {
    private static MULTIPLE_ACTIONS_LINK_TEMPLATE_START = '<li role="presentation">';
    private static MULTIPLE_ACTIONS_LINK_TEMPLATE_END = '</li>';
    private static VHS_SESSION_HISTORY_KEY = "vhs_session_navigation_history";
    private static MAX_HISTORY_DEPTH = 3;

    public currentPageName: string = "&nbsp;";
    public actionsPanelHtml: string;
    public statusPanelHtml: string;
    public multipleActionsOnPanel: boolean = false;
    public hasHistory: boolean = false;
    public history: Array<string> = [];

    public static $inject = [
        "$scope",
        "$sce",
        "$location",
        Storages.SESSION_NAME
    ];

    constructor($scope,
                private $sce: angular.ISCEService,
                private $location: angular.ILocationService,
                private sessionStorage: StorageWrapper) {
        $scope.vm = this;

        //subscribe to update events
        MenuHelper.addMenuUpdateListener(this);
        PageHeaderHelper.addPageHeaderUpdateListener(this);

        $scope.$on(Event.LOGGED_ON, () => {
            this.history.splice(0, this.history.length);
            this.hasHistory = false;
            this.saveHistory();
        });
        $scope.$on(Event.LOGGED_OFF, () => {
            this.history.splice(0, this.history.length);
            this.hasHistory = false;
            this.saveHistory();
        });
        if (this.sessionStorage.contains(PageHeaderController.VHS_SESSION_HISTORY_KEY)) {
            var storedHistory = this.sessionStorage.get<string>(PageHeaderController.VHS_SESSION_HISTORY_KEY);
            for (var i = 0; i < storedHistory.length; i++) {
                this.history.push(storedHistory[i]);
            }
            this.hasHistory = this.history.length > 1;
        }
    }

    public goBack() {
        if (this.hasHistory) {
            this.history.pop(); // forgot current url
            var target = this.history.pop();
            this.saveHistory();
            this.$location.url(target);
        }
        this.hasHistory = this.history.length > 1;
    }

    public onMenuItemUpdate(newMenuItemKey: string, oldMenuItemKey: string) {
        this.currentPageName = "&nbsp;";
        this.actionsPanelHtml = "";
        this.statusPanelHtml = "";
        this.multipleActionsOnPanel = false;

        var lastIndex = this.history.length - 1;
        if (lastIndex < 0 || this.history[lastIndex] != this.$location.path()) {
            if (this.history.length > PageHeaderController.MAX_HISTORY_DEPTH) {
                this.history.splice(0, 1);
            }
            this.history.push(this.$location.path());
            this.hasHistory = this.history.length > 1;
            this.saveHistory();
        }
    }

    public onHeaderUpdate(pageHeader: PageHeader) {
        if (!pageHeader.preservePageName) {
            this.currentPageName = pageHeader.pageName;
        }
        if (!pageHeader.preserveActionsPanel) {
            var html = "";
            this.multipleActionsOnPanel = false;
            if (pageHeader.actions.length == 1) {
                html = pageHeader.actions[0];
            } else if (pageHeader.actions.length > 0) {
                for (var i = 0; i < pageHeader.actions.length; i++) {
                    html += PageHeaderController.MULTIPLE_ACTIONS_LINK_TEMPLATE_START + pageHeader.actions[i] + PageHeaderController.MULTIPLE_ACTIONS_LINK_TEMPLATE_END;
                }
                this.multipleActionsOnPanel = true;
            }
            this.actionsPanelHtml = this.$sce.getTrustedHtml(html);
        }
        if (!pageHeader.preserveStatusPanel) {
            this.statusPanelHtml = this.$sce.getTrustedHtml(pageHeader.statusPanel);
        }
    }

    private saveHistory() {
        this.sessionStorage.put(PageHeaderController.VHS_SESSION_HISTORY_KEY, this.history);
    }
}

export function PageHeaderDirectiveFactory(url: UrlService): angular.IDirective {
    function link($scope: angular.IScope, element: angular.IAugmentedJQuery,
                  attributes: PageHeaderAttributes, controller: PageHeaderController,
                  transclude: angular.ITranscludeFunction) {
    }

    var directive: any = {};
    directive.restrict = "A";
    directive.link = link;
    directive.scope = {};
    directive.controller = PageHeaderController;
    directive.templateUrl = url.template("components/page-header.html");
    return directive;
}

PageHeaderDirectiveFactory.$inject = [
    UrlService.NAME
];
