export class StorageWrapper {
    constructor(private storage: Storage) {
    }

    public put<T>(key: string, value: T) {
        var stringified = JSON.stringify(value);
        this.storage.setItem(key, stringified);
    }

    public get<T>(key: string): T {
        var stringified = this.storage.getItem(key);
        return stringified && stringified.length > 0
            ? $.parseJSON(stringified)
            : null;
    }

    public contains(key: string): boolean {
        var stringified = this.storage.getItem(key);
        return stringified && stringified.length > 0;
    }

    public remove(key: string) {
        this.storage.removeItem(key);
    }
}

export class Storages {
    public static LOCAL_NAME = "infrastructure.LocalStorage";
    public static SESSION_NAME = "infrastructure.SessionStorage";

    public static LOCAL: StorageWrapper = new StorageWrapper(localStorage);
    public static SESSION: StorageWrapper = new StorageWrapper(sessionStorage);
}
