import {UrlService} from "./../utils/UrlService";
import {UserService} from "./UserService";
import {UsersRouter} from "./../urls/UsersRouter";

import {MenuHelper} from "./../utils/MenuHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {notifier} from "./../INotifier";
import {PermissionsService} from "./../security/PermissionsService";
import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";

import {IGroup, GroupEnum} from "./models/IGroup";

export class GroupEditController {
    public static NAME = "users.GroupEditController";

    private systemPermissions: ISystemPermissions;
    private groupId: number;
    public group: IGroup = {
        name: "",
        isActive: true,
        parentId: null,
        permissionsIds: []
    };
    public groupsEntities: Array<IGroup>;

    public disableSaveButton: boolean = false;

    public static $inject = [
        "$routeParams",
        "$location",
        UserService.NAME,
        PermissionsService.NAME,
        UsersRouter.NAME
    ];

    constructor(private $routeParams: angular.route.IRouteParamsService,
                private $location: angular.ILocationService,
                private usersService: UserService,
                private permissionsService: PermissionsService,
                public usersRouter: UsersRouter) {
        MenuHelper.setCurrentMenuItem(MenuHelper.ADMINISTRATION_GROUPS_MENU_KEY);

        this.groupId = this.$routeParams[UsersRouter.GROUP_ID_PARAM];

        permissionsService.getSystemPermissions().then(p => {
            this.systemPermissions = p;
        });

        if (this.groupId) {
            this.loadGroup();
        } else {
            PageHeader.set().header("New user group").update();
        }
        this.getActiveGroupsEntities();
    }

    private loadGroup() {
        this.usersService.getGroup(this.groupId).then(g => {
            angular.copy(g, this.group);
            PageHeader.set().header(this.group.name).update();
        });
    }

    private getActiveGroupsEntities(): void {
        var promise = this.groupId
            ? this.usersService.getActiveGroupsNoChildren(this.groupId)
            : this.usersService.getActiveGroups();
        promise.then(result => {
                this.groupsEntities = result;
            });
    }

    public save() {
        this.disableSaveButton = true;
        var promise = this.groupId
            ? this.usersService.updateGroup(this.group)
            : this.usersService.createGroup(this.group);
        promise.then(g => {
            this.disableSaveButton = false;
            notifier.success("Group '" + this.group.name + "' has been saved");
            if (this.groupId == null) {
                this.systemPermissions.editGroup
                    ? this.$location.path(this.usersRouter.groupEditUrl(g.id))
                    : this.$location.path(this.usersRouter.groupsIndexUrl());
            } else {
                this.loadGroup();
            }
        }).catch(response => {
            this.disableSaveButton = false;
        });
    }

    public deactivate() {
        var promise = this.usersService.setActiveGroupWithChildren(false, this.group.id);
        promise.then(u => {
            this.$location.url(this.usersRouter.groupsIndexUrl());
        })
    }
}