import {StringBuilder} from "./../utils/StringBuilder";
import {BaseRouter} from "./BaseRouter";

export class ReferenceRouter extends BaseRouter {
    public static NAME = "urls.ReferenceRouter";
    public static REFERENCE_DEFAULT_VALUES_PARAM = "defaultValues";
    public static REFERENCE_FILTER_PARAM = "filter";
    public static REFERENCE_ASSOCIATION_FILTERS_PARAM = "associationFilters";
    public static REFERENCE_NAME_PARAM = "name";
    public static REFERENCE_ID_PARAM = "id";
    public static indexRoute = `/reference/:${ReferenceRouter.REFERENCE_NAME_PARAM}`;
    public static editRoute = `/reference/:${ReferenceRouter.REFERENCE_NAME_PARAM}/:${ReferenceRouter.REFERENCE_ID_PARAM}`;

    public static $inject = [
        "$location"
    ];

    constructor($location: ng.ILocationService) {
        super($location);
    }

    public indexUrl(referenceName: string): string {
        return this.url(StringBuilder.interpolate(ReferenceRouter.indexRoute)
            .with(":" + ReferenceRouter.REFERENCE_NAME_PARAM, referenceName)
            .build());
    }

    public editUrl(referenceName: string, id?: any): string {
        return this.url(StringBuilder.interpolate(ReferenceRouter.editRoute)
            .with(":" + ReferenceRouter.REFERENCE_NAME_PARAM, referenceName)
            .with(":" + ReferenceRouter.REFERENCE_ID_PARAM, id == null ? "new" : id)
            .build());
    }
}