import {BaseRouter} from "./BaseRouter";

export class LogsRouter extends BaseRouter {
    public static NAME = "urls.LogsRouter";

    public static securityLogsRoute = "/logs/security";
    public static systemLogsRoute = "/logs/system";

    public static $inject = [
        "$location"
    ];

    constructor($location: ng.ILocationService) {
        super($location);
    }

    public systemLogsUrl(): string {
        return this.url(LogsRouter.systemLogsRoute);
    }

    public securityLogsUrl(): string {
        return this.url(LogsRouter.securityLogsRoute);
    }
}