import {UrlService} from "./../utils/UrlService";

export class UrlServiceProvider implements angular.IServiceProvider {
    public static NAME = "urls.UrlServiceProvider";

    private serviceRoot = "/";
    private buildVersion = "/";
    private currentLocale = "en_US";

    public setServiceRoot(serviceRoot: string): UrlServiceProvider {
        this.serviceRoot = serviceRoot;
        return this;
    }

    public setBuildVersion(buildVersion: string): UrlServiceProvider {
        this.buildVersion = buildVersion;
        return this;
    }

    public $get(): UrlService {
        return new UrlService(this.serviceRoot, this.buildVersion, this.currentLocale);
    }
}