import {UrlService} from "./../utils/UrlService"
import {AppProperties} from "./../utils/AppProperties"
import {IMultimediaSource} from "./models/IMultimedia"
import {RandomUtils} from "../utils/RandomUtils";

export interface IVhsVideoAudioDirectiveScope extends angular.IScope {
    vhsId?: string;
    vhsSources: IMultimediaSource[];
    vhsWidth?: number;
    vhsHeight?: number;
    vhsPoster?: string;
    vhsErrorCallback?: (errorEvent: ErrorEvent) => angular.IPromise<any>;
}

export class VhsVideoAudioController {
    private errorCount: number = 0;
    private static GIVE_UP_AFTER_SO_MANY_ERRORS = 5;
    private static RETRY_TIME_INTERVAL = 10000; // ms

    private _playerIsPlaying: boolean;
    public get playerIsPlaying(): boolean {
        return this._playerIsPlaying;
    }

    public set playerIsPlaying(value: boolean) {
        this._playerIsPlaying = value;
        // console.log('this._playerIsPlaying = ', this._playerIsPlaying);
    }

    private _playerCurrentTime: number;
    public get playerCurrentTime(): number {
        return this._playerCurrentTime;
    }

    public set playerCurrentTime(value: number) {
        if (value > 0) {
            this._playerCurrentTime = value;
        }
    }

    public static $inject = [
        "$scope",
        "$timeout"
    ];

    constructor(private $scope: IVhsVideoAudioDirectiveScope,
                private $timeout: angular.ITimeoutService) {
        // do nothing
    }

    public init($scope: IVhsVideoAudioDirectiveScope,
                element: angular.IAugmentedJQuery,
                isVideoOrAudio: string, isYoutube: boolean) {

        isVideoOrAudio = isVideoOrAudio.toUpperCase();
        if (isVideoOrAudio !== "AUDIO" && isVideoOrAudio !== "VIDEO") {
            console.log("Error: Wrong multimedia directive type in init() - " + isVideoOrAudio);
            isVideoOrAudio = "VIDEO";
        }

        let _self: VhsVideoAudioController = this;
        _self.$scope = $scope;
        let playerVideoTag: HTMLMediaElement = <HTMLMediaElement> element.find(isVideoOrAudio).get(0);

        $(playerVideoTag).on('contextmenu', (event: JQueryEventObject) => {
            event.preventDefault(); // prevent a right-mouse click on the player area
        });
        /* try {
         $(window).on('contextmenu', (event: JQueryEventObject) => {
         event.preventDefault();
         });
         } catch (e) {
         } */
        if (_self.$scope.vhsId) {
            playerVideoTag.id = _self.$scope.vhsId;
        }
        if (!_self.$scope.vhsPoster) {
            _self.$scope.vhsPoster = "";
        }
        var videoJsPlayerId = null;
        if (!element.attr('id')) {
            element.attr('id', RandomUtils.generateRandomId('vhs_videoj_'));
        }
        videoJsPlayerId = element.attr('id');

        let videojsOptions: VideoJSOptions = <VideoJSOptions> {
            autoplay: false,
            controls: true,
            flash: {
                swf: "vendor/swf/video-js.swf"
            },
            loop: false,
            //plugins: {
            //    videoJsResolutionSwitcher: {
            //    }
            //},
            poster: _self.$scope.vhsPoster || "",
            preload: "none",
            techOrder: ['html5', 'flash']
        };
        let youtubeOptions: VideoJSOptions = <VideoJSOptions> {
            autoplay: false,
            controls: true,
            loop: false,
            //plugins: {
            //    videoJsResolutionSwitcher: {
            //    }
            //},
            //poster: _self.$scope.vhsPoster || "",
            poster: "",
            preload: "none",
            techOrder: ['youtube'],
            sources: _self.$scope.vhsSources,
            "youtube": {
                "ytControls": 2,
                "enablejsapi": 1
            }
        };
        if (isYoutube) {
            $(element).addClass('vhs-youtube-video').width(_self.$scope.vhsWidth).height(_self.$scope.vhsHeight);
        }
        if (isYoutube && $('#' + videoJsPlayerId).length == 0 || !isYoutube && $('#' + playerVideoTag.id).length == 0) {
            return;
        }

        videojs(isYoutube ? videoJsPlayerId : playerVideoTag, isYoutube  ? youtubeOptions : videojsOptions, function (): void {
            if (isYoutube && $('#' + videoJsPlayerId).length == 0 || !isYoutube && $('#' + playerVideoTag.id).length == 0) {
                return;
            }
            let player: VideoJSPlayer = this;
            try {
                let playerPosterTag: HTMLElement = element.find('.vjs-poster').get(0);
                $(element).find('.video-js')
                    .width(_self.$scope.vhsWidth)
                    .height(_self.$scope.vhsHeight)
                    .on('contextmenu', (event: JQueryEventObject) => {
                        event.preventDefault(); // prevent a right-mouse click on the big play button and other control elements
                    });

                //if (isYoutube) {
                //    let playerControlBarDivTag: HTMLElement = $(element).find('.vjs-control-bar').get(0);
                //    $(playerControlBarDivTag).hide();
                //}

                player.isAudio(isVideoOrAudio === 'AUDIO');
                _self._playerIsPlaying = false;
                _self._playerCurrentTime = 0;

                player.on("play", () => { // complementary to player.on("ended", ...)
                    // console.log("play");
                    _self._playerIsPlaying = true;
                });
                player.on("ended", () => { // complementary to player.on("play", ...)
                    // console.log("ended");
                    _self._playerIsPlaying = false;
                    player.exitFullscreen();
                    player.currentTime(0);
                    // _self.setPlayerPoster(player, playerVideoTag, playerPosterTag); // sometimes may be needed for audio
                    player.hasStarted(false); // this is needed to show a poster image again (using CSS)
                });

                player.on('pause', () => {
                    _self._playerIsPlaying = false;
                });
                //player.on('click', () => {
                //    (_self._playerIsPlaying) ? player.pause() : player.play();
                //});
                //player.on('tap', () => {
                //    (_self._playerIsPlaying) ? player.pause() : player.play();
                //});

                player.on('changeRes', () => {
                    // console.log('Resolution changed to: ', player['getCurrentRes']());
                });
                player.on('timeupdate', () => {
                    _self._playerIsPlaying = !player.paused();
                    if (player.currentTime() > 0) {
                        _self.playerCurrentTime = player.currentTime();
                        // console.log("on('timeupdate'): player.paused() = ", player.paused(), ", time = ", _self.playerCurrentTime);
                    }
                });

                player.on('loadstart', () => {
                    // console.log('loadstart');
                });
                player.on('firstplay', () => {
                    // console.log('firstplay');
                });
                player.on('waiting', () => {
                    // console.log('waiting');
                });
                player.on('canplay', () => {
                    // console.log('canplay');
                });
                player.on('canplaythrough', () => {
                    // console.log('canplaythrough');
                });
                player.on('loadedmetadata', () => {
                    // console.log('loadedmetadata');
                });
                player.on('loadeddata', () => {
                    // console.log('loadeddata');
                });
                player.on('stalled', () => {
                    console.log('stalled');
                });
                player.on('suspend', () => {
                    // console.log('suspend');
                });

                player.on('error', (event: Event): void => {
                    // console.log('_self._playerIsPlaying:', _self._playerIsPlaying, '; errorEvent: ', errorEvent, ';  player.error(): ', player.error(), ';  player.error().code: ', player.error().code, ';  _self.$scope.$id: ', _self.$scope.$id, ';  attempt: ', _self.errorCount, ';  _self._playerCurrentTime', _self._playerCurrentTime);
                    _self.errorCount++;
                    if (_self.errorCount > VhsVideoAudioController.GIVE_UP_AFTER_SO_MANY_ERRORS) {
                        player.off('error');
                        _self.errorCount--;
                        console.log("" + _self.errorCount + (_self.errorCount === 1
                                ? " attempt to restore playback after error has "
                                : " attempts to restore playback after error have ") +
                            "been made so far. Now we give up.");
                    }
                    else if (_self.errorCount === 1 || _self.errorCount === VhsVideoAudioController.GIVE_UP_AFTER_SO_MANY_ERRORS - 1) {
                        if (playerVideoTag.canPlayType && _self.$scope.vhsSources && _self.$scope.vhsSources.length > 0) {
                            console.log("" + _self.$scope.vhsSources[0].type + " support: ",
                                !!(playerVideoTag.canPlayType("" + _self.$scope.vhsSources[0].type + ";").replace(/no/, '')));
                        }
                        if (player.error().code === MediaError.MEDIA_ERR_SRC_NOT_SUPPORTED) {
                            if (player.error().message.toUpperCase().indexOf("FLASH") >= 0) {
                                player.options({techOrder: ['html5', 'flash']});
                                console.log("Some error occurred. We will try to switch to HTML5 mode...");
                            }
                            else if (player.techName.toUpperCase() === "HTML5") {
                                player.options({techOrder: ['flash', 'html5']});
                                console.log("Some error occurred. We will try to switch to Flash mode...");
                            }
                        }
                        _self.setPlayerSources(player, playerVideoTag, isYoutube);
                        _self.reloadPlayer(player, playerVideoTag);
                    }
                    else if (_self.$scope.vhsErrorCallback) {
                        console.log("Some error occurred. We are now trying to request multimedia sources again...");
                        let promise: angular.IPromise<any> = _self.$scope.vhsErrorCallback(player.error());
                        if (promise) {
                            promise.then(() => {
                                _self.$timeout(() => {
                                    _self.setPlayerPoster(player, playerVideoTag, playerPosterTag);
                                    if (playerVideoTag.getAttribute("src") != _self.$scope.vhsSources[0].src) { // TODO: revise later: always [0] or select a source by some conditions?
                                        _self.setPlayerSources(player, playerVideoTag, isYoutube);
                                        _self.reloadPlayer(player, playerVideoTag);
                                    } else {
                                        player.off('error');
                                        if (_self.$scope.vhsSources && _self.$scope.vhsSources.length > 0) {
                                            console.log("We received exactly the same multimedia URL." +
                                                " Won't request sources again.");
                                        } else {
                                            console.log("No multimedia sources were provided.");
                                        }
                                    }
                                }, VhsVideoAudioController.RETRY_TIME_INTERVAL); // end of $timeout()
                            }); // end of _self.$scope.vhsErrorCallback().then()
                        }
                    }
                }); // end of player.on('error', ...)
            } finally {
                _self.setPlayerSources(player, playerVideoTag, isYoutube);
            }
        }); // end of video.js player initialization
    } // end of public init($scope, element)

    public setPlayerSources(player: VideoJSPlayer, playerVideoTag: HTMLMediaElement, isYoutube: boolean) {
        if (isYoutube) return;
        let _self: VhsVideoAudioController = this;
        //$(playerVideoTag).find('source').remove();
        //for (let i = 0; i < _self.$scope.vhsSources.length; i++) {
        //    let item: IMultimediaSource = _self.$scope.vhsSources[i];
        //    $(playerVideoTag).append($(
        //        `<source src="${item.src}" type="${item.type}" data-res="${item.dataRes}">`
        //    ));
        //}
        player.src(_self.$scope.vhsSources);
    }

    public setPlayerPoster(player: VideoJSPlayer, playerVideoTag: HTMLMediaElement, playerPosterTag: HTMLElement) {
        let _self: VhsVideoAudioController = this;
        if (_self.$scope.vhsPoster && _self.$scope.vhsPoster.length > 0) {
            // $(playerPosterTag).css("background-image", 'url("' + (_self.$scope.vhsPoster || "") + '")');
            playerVideoTag.setAttribute('poster', _self.$scope.vhsPoster || ""); // TODO: test if this line can be deleted
            player.posterImage.setSrc(_self.$scope.vhsPoster || "");
        }
    }

    public reloadPlayer(player: VideoJSPlayer, playerVideoTag: HTMLMediaElement) {
        let _self: VhsVideoAudioController = this;
        player.one('loadedmetadata', () => {
            if (player.currentTime() < _self.playerCurrentTime) {
                player.currentTime(_self.playerCurrentTime);
                playerVideoTag['currentTime'] = _self.playerCurrentTime;
            }
            if (_self.playerIsPlaying) {
                player.one('canplay', () => {
                    player.play();
                });
            }
        });
        player.preload('auto');
        player.load();
    }
} // end of VhsVideoAudioController

export function VhsVideoDirectiveFactory(url: UrlService): angular.IDirective {
    function link($scope: IVhsVideoAudioDirectiveScope,
                  element: angular.IAugmentedJQuery,
                  attributes: angular.IAttributes,
                  controller: VhsVideoAudioController) {

        controller.init($scope, element, 'video', false);
    }

    let directive: angular.IDirective = {};
    directive.restrict = "A";
    directive.link = link;
    directive.controller = VhsVideoAudioController;
    directive.scope = {
        vhsId: "=?",
        vhsSources: "=",
        vhsWidth: "=?",
        vhsHeight: "=?",
        vhsPoster: "=?",
        vhsErrorCallback: "&?"
    };
    directive.templateUrl = url.template("components/vhs-video.html");
    return directive;
}

VhsVideoDirectiveFactory.$inject = [
    UrlService.NAME
];

