import {MaterialReportTarget} from "./models/MaterialReportTarget";
import {StatisticReportResult} from "./models/StatisticReportResult";
import {ReportGroupType} from "./models/ReportGroupType";
import {UrlService} from "./../utils/UrlService";
import {StringBuilder} from "./../utils/StringBuilder"
import {ISubject} from "./../ISubject";
import {IPair} from "./../IPair";

export class StatisticReportsService {
    public static NAME = "statistics.StatisticReportsService";

    public static $inject = [
        "$http",
        UrlService.NAME
    ];

    constructor(private $http: ng.IHttpService,
                private url: UrlService) {
    }

    public viewMaterialViewReport(reportTarget: MaterialReportTarget, groupType: ReportGroupType, ownerIds: Array<number>,
                                  materialTypeIds: Array<number>, subjectIds: Array<number>, dateFrom: number,
                                  dateTo: number): ng.IPromise<StatisticReportResult<any, Array<any>, number>> {
        return this.$http.post(this.url.base("/api/statistics/reports/materials/views/by/:reportTarget/groupBy/:groupType")
            .param(":reportTarget", MaterialReportTarget[reportTarget])
            .param(":groupType", ReportGroupType[groupType])
            .service(), {
                ownerIds: ownerIds,
                materialTypeIds: materialTypeIds,
                subjectIds: subjectIds,
                dateFrom: dateFrom,
                dateTo: dateTo})
            .then(result => this.fillMissingDate(result.data, dateFrom, dateTo, groupType));
    }

    public viewMaterialDownloadReport(reportTarget: MaterialReportTarget, groupType: ReportGroupType, ownerIds: Array<number>,
                                      materialTypeIds: Array<number>, subjectIds: Array<number>, dateFrom: number,
                                      dateTo: number): ng.IPromise<StatisticReportResult<any, Array<any>, number>> {
        return this.$http.post(this.url.base("/api/statistics/reports/materials/downloads/by/:reportTarget/groupBy/:groupType")
            .param(":reportTarget", MaterialReportTarget[reportTarget])
            .param(":groupType", ReportGroupType[groupType])
            .service(), {
                ownerIds: ownerIds,
                materialTypeIds: materialTypeIds,
                subjectIds: subjectIds,
                dateFrom: dateFrom,
                dateTo: dateTo})
            .then(result => this.fillMissingDate(result.data, dateFrom, dateTo, groupType));
    }

    public viewMaterialUploadReport(reportTarget: MaterialReportTarget, groupType: ReportGroupType, ownerIds: Array<number>,
                                    materialTypeIds: Array<number>, subjectIds: Array<number>, dateFrom: number,
                                    dateTo: number): ng.IPromise<StatisticReportResult<any, Array<any>, number>> {
        return this.$http.post(this.url.base("/api/statistics/reports/materials/uploads/by/:reportTarget/groupBy/:groupType")
            .param(":reportTarget", MaterialReportTarget[reportTarget])
            .param(":groupType", ReportGroupType[groupType])
            .service(), {
                ownerIds: ownerIds,
                materialTypeIds: materialTypeIds,
                subjectIds: subjectIds,
                dateFrom: dateFrom,
                dateTo: dateTo})
            .then(result => this.fillMissingDate(result.data, dateFrom, dateTo, groupType));
    }

    private fillMissingDate(report: StatisticReportResult<any, Array<any>, number>, dateFrom: number, dateTo: number,
                            groupType: ReportGroupType): StatisticReportResult<any, Array<any>, number> {
        dateFrom = dateFrom != null ? dateFrom : report.groupedValues[0];
        dateTo = dateTo != null ? dateTo : report.groupedValues[report.groupedValues.length - 1];

        report.data.forEach((d: IPair<any, Array<any>>) => {
            let valueMap: Map<number, number> = new Map<number, number>();
            d.value.forEach((v: Array<any>) => {
                valueMap.set(v[0], v[1]);
            });
            let newValues: Array<any> = [];
            let from = moment(dateFrom);
            let to = moment(dateTo).add(1, this.getUnitOfTime(groupType));
            while (from.isBefore(to)) {
                let unixTime = from.valueOf();
                newValues.push([unixTime, valueMap.has(unixTime) ? valueMap.get(unixTime) : 0]);
                from = from.add(1, this.getUnitOfTime(groupType));
            }
            d.value = newValues;
        });

        return report;
    }

    private getUnitOfTime(groupType: ReportGroupType): string {
        switch (groupType) {
            case ReportGroupType.DAYS: return 'd';
            case ReportGroupType.WEEKS: return 'w';
            case ReportGroupType.MONTHS: return 'M';
            case ReportGroupType.YEARS: return 'y';
        }
    }
}