import {IGroup, GroupEnum} from "./models/IGroup";
import {IUser, PredefinedUsersEnum} from "./models/IUser";
import {UserService} from "./UserService";
import {UsersRouter} from "./../urls/UsersRouter";
import {ISystemLocale} from "./../settings/models/ISystemLocale";
import {SettingsService} from "./../settings/SettingsService";
import {PermissionsService} from "./../security/PermissionsService";
import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";
import {UrlService} from "./../utils/UrlService";
import {AuthService} from "./../auth/AuthService";
import {MenuHelper} from "./../utils/MenuHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {notifier} from "./../INotifier";
import {IUserLogon} from "./models/IUserLogon";
import {ILogonProvider} from "./../auth/ILogonProvider";
import {AuthRouter} from "../urls/AuthRouter";

interface IGroupExtended extends IGroup {
    shown?: boolean;
    canBeRemoved?: boolean;
    toggle?: () => void;
    select?: () => void;
    unselect?: () => void;
}

interface IUserExtended extends IUser {
    userLogons?: Array<IUserLogon>;
}

export class UserEditController {
    public static NAME = "users.UserEditController";

    private userId: number;
    public confirmPassword: string;

    public user: IUserExtended = {
        fullName: "",
        isActive: true,
        newGroups: [],
        locale: null,
        userLogons: []
    };
    public activeGroup: boolean = false;
    public groups: Array<IGroupExtended>;
    public availableLocales: Array<ISystemLocale>;
    private logonProviders: Array<ILogonProvider> = [];
    public logonProvidersForAdd: Array<ILogonProvider> = [];

    public resetPasswordLink: string;

    public error: string;

    public disableSaveButton: boolean = false;
    public isDeactivationAllowed: boolean;
    public isEverythingDisabled: boolean;

    private systemPermissions: ISystemPermissions;

    public static $inject = [
        "$timeout",
        "$log",
        "$http",
        "$q",
        "$routeParams",
        "$location",
        UserService.NAME,
        SettingsService.NAME,
        PermissionsService.NAME,
        UsersRouter.NAME,
        UrlService.NAME,
        AuthService.NAME
    ];

    constructor(private $timeout: angular.ITimeoutService,
                private $log: angular.ILogService,
                private $http: angular.IHttpService,
                private $q: angular.IQService,
                private $routeParams: angular.route.IRouteParamsService,
                private $location: angular.ILocationService,
                private usersApi: UserService,
                private settingsApi: SettingsService,
                private permissionsApi: PermissionsService,
                public usersRouter: UsersRouter,
                private url: UrlService,
                private authApi: AuthService) {
        MenuHelper.setCurrentMenuItem(MenuHelper.ADMINISTRATION_USERS_MENU_KEY);

        this.userId = this.$routeParams["userId"];

        permissionsApi.getSystemPermissions().then(p => {
            PageHeader.set().preserveHeader()
                .addActionLinkIf(p.createUser, '+&nbsp;New', this.usersRouter.addUrl()).update();
            this.systemPermissions = p;
        });

        this.loadGroups();
        this.loadLogonProviders();
        this.loadAvailableLocales();

        if (this.userId) {
            this.loadUser();
        } else {
            PageHeader.set().header("New User").update();
        }
    }

    private loadUser() {
        this.isDeactivationAllowed = !(this.userId in PredefinedUsersEnum);
        this.isEverythingDisabled = (this.userId == PredefinedUsersEnum.GUEST);
        this.usersApi.get(this.userId).then(u => {
            u.firstName = u.fullName.split("\u00A0")[0];
            u.lastName = u.fullName.split("\u00A0").length > 1 ? u.fullName.split("\u00A0")[1] : "";
            PageHeader.set().header(u.fullName).update();
            angular.copy(u, this.user);
            this.user.newGroups = [];
            this.user.userLogons = [];
            this.loadUserGroups();
            this.loadUserLogons();
        });
    }

    private loadGroups() {
        this.usersApi.getGroups().then(groups => {
            this.groups = angular.copy(groups);
            this.loadUserGroups();
        });
    }

    private loadLogonProviders() {
        this.authApi.getProviders()
            .then(result => {
                this.logonProviders = result;
                this.recalculateAvailableLogonProviders()
            });
    }

    private loadUserGroups() {
        if (this.groups) {
            this.groups.forEach((group: IGroupExtended) => {
                group.selected = false;
                group.canBeRemoved = !(this.user.id == PredefinedUsersEnum.ADMIN && group.id == GroupEnum.SYS_ADMINS) || !this.user.id;
                if (this.user.groupsList) {
                    this.user.groupsList.forEach(userGroup => {
                        if (group.id == userGroup.id) {
                            group.selected = true;
                        }
                    });
                }
            });
        }
    }

    private loadUserLogons() {
        this.usersApi.getUserLogons(this.user.id).then(result => {
            this.user.userLogons.splice(0, this.user.userLogons.length);
            result.forEach(r => this.user.userLogons.push(r));
            this.recalculateAvailableLogonProviders();
        });
    }

    private loadAvailableLocales() {
        this.settingsApi.getAvailableLocales().then(locales => {
            if (!this.userId) {
                for (var i = 0; i < locales.length; i++) {
                    var localeInfo = locales[i];
                    if (localeInfo.defaultLocale) {
                        this.user.locale = localeInfo.locale;
                    }
                }
            }
            this.availableLocales = locales;
        });
    }

    public save() {
        this.disableSaveButton = true;
        this.confirmPassword = null;

        if (this.user.groupsList) {
            this.user.groupsList.forEach(group => {
                this.user.newGroups.push(group.id)
            });
        }
        this.user.fullName = this.user.firstName + "\u00A0" + this.user.lastName;

        var promise = this.userId
            ? this.usersApi.update(this.user)
            : this.usersApi.create(this.user);
        promise.then(u => {
            this.disableSaveButton = false;
            notifier.success("User '" + this.user.fullName + "' has been saved");
            if (this.userId == null) {
                this.systemPermissions.editUser
                    ? this.$location.path(this.usersRouter.editUrl(u.id))
                    : this.$location.path(this.usersRouter.indexUrl());
            } else {
                this.loadUser();
            }
        }).catch(response => {
            this.disableSaveButton = false;
            if (response.status == 409 && response.data.error) {
                switch (response.data.error) {
                    case "error.user.duplicate.name":
                        this.error = "Full name " + this.user.fullName + " already exists";
                        break;
                    case "error.user.duplicate.email":
                        this.error = "Email " + this.user.email + " already exists";
                        break;
                    case "error.duplicate.logon.provider":
                        this.error = "Only one logon for each type is allowed";
                        break;
                    default:
                        if (response.data.error.indexOf("error.duplicate.login.for.provider.") == 0) {
                            var providerKey = response.data.error.substring("error.duplicate.login.for.provider.".length);
                            var logonFound = null;
                            for (var j = 0; j < this.user.userLogons.length; j++) {
                                if (this.user.userLogons[j].providerKey == providerKey) {
                                    logonFound = this.user.userLogons[j].name;
                                    break;
                                }
                            }
                            if (logonFound) {
                                this.error = "Username " + logonFound + " already exists";
                            } else {
                                this.error = "Query has failed: please contact your system administrator or help desk";
                            }
                        } else {
                            this.error = "Query has failed: please contact your system administrator or help desk";
                        }
                }
            }
            notifier.error(this.error);
        });
    }

    public deactivate() {
        var promise = this.usersApi.setActive(this.user.id, false);
        promise.then(u => {
            this.$location.url(this.usersRouter.indexUrl());
        })
    }

    public requestPasswordRestore() {
        this.usersApi.requestPasswordRestoreForUser(this.user.id)
            .then(link => {
                this.resetPasswordLink = link;
                $("#resetPasswordLinkDialog").modal({
                    backdrop: 'static',
                    keyboard: false,
                    show: true
                });
            })
            .catch(response => {
                if (response.data.error === "error.password.restore.impossible") {
                    notifier.error("Password restore link can't be created. User do not have active Local logon.");
                } else {
                    notifier.error("Query has failed: please contact your system administrator or help desk");
                }
            });
    }

    public showGroups() {
        this.activeGroup = true;
    }

    public closeGroups() {
        this.activeGroup = false;
    }

    public selectGroup(index: number) {
        var group: IGroupExtended = this.groups[index];
        group.selected = true;
    }

    public unselectGroup(index: number) {
        var group: IGroupExtended = this.groups[index];
        group.selected = false;
    }

    public clearSelection() {
        this.loadUserGroups();
    }

    public addGroups() {
        this.user.groupsList = [];
        this.groups.forEach(g => {
            if (g.selected) {
                this.user.groupsList.push(g);
            }
        });
        this.closeGroups();
    }

    public deleteGroup(groupId: number) {
        this.groups.forEach((group: IGroupExtended) => {
            if (group.id == groupId) {
                group.selected = false;
            }
        });
        this.user.groupsList = [];
        this.groups.forEach(r => {
            if (r.selected) {
                this.user.groupsList.push(r);
            }
        });
    }


    private recalculateAvailableLogonProviders() {
        this.logonProvidersForAdd.splice(0, this.logonProvidersForAdd.length);
        this.logonProviders.forEach(p => {
            var found = false;
            for (var j = 0; j < this.user.userLogons.length; j++) {
                if (this.user.userLogons[j].providerKey == p.key) {
                    found = true;
                    break;
                }
            }
            if (!found) {
                this.logonProvidersForAdd.push(p);
            }
        });
    }

    public getLogonProviderName(providerKey: string) {
        for (var i = 0; i < this.logonProviders.length; i++) {
            if (providerKey == this.logonProviders[i].key) {
                return this.logonProviders[i].name;
            }
        }
        return undefined;
    }

    public addUserLogon(providerKey: string) {
        var userLogon: IUserLogon = {
            id: null,
            userId: this.user.id,
            providerKey: providerKey,
            name: null,
            password: null,
            isActive: true
        };
        this.user.userLogons.push(userLogon);
        this.recalculateAvailableLogonProviders();
    }

    public removeUserLogon(providerKey: string) {
        var logonIndex = -1;
        for (var i = 0; i < this.user.userLogons.length; i++) {
            if (this.user.userLogons[i].providerKey == providerKey) {
                logonIndex = i;
                break;
            }
        }
        if (logonIndex != -1) {
            this.user.userLogons.splice(logonIndex, 1);
            this.recalculateAvailableLogonProviders();
        }
    }
}
