import INgModelController = angular.INgModelController;
import IScope = angular.IScope;
import IAttributes = angular.IAttributes;

export function SuffixValidatorDirectiveFactory(): angular.IDirective {
    function link($scope: IScope, element: angular.IAugmentedJQuery,
                  attributes: IAttributes, ngModel: INgModelController,
                  transclude: angular.ITranscludeFunction) {

        ngModel.$validators["suffix"] = (modelValue: string, viewValue: string) => {
            let suffix = viewValue;
            if (!suffix) return true;
            suffix = suffix.trim();
            if (suffix.charAt(suffix.length - 1) === ".") {
                suffix = suffix.substr(0, suffix.length - 1);
            }
            suffix = suffix.toLowerCase();
            const prefixes = ["ms", "miss", "mr", "mrs", "fr", "rev", "dr", "atty", "hon", "prof",
                "pres", "vp", "gov", "ofc", "sir"];
            return prefixes.indexOf(suffix) < 0;
        };
    }

    return {
        restrict: "A",
        require: "?ngModel",
        link: link
    };
}