import {MenuHelper} from "./../utils/MenuHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {FinancesRouter} from "./../urls/FinancesRouter";
import {PurchaseService} from "./PurchaseService";
import {notifier} from "./../INotifier";
import {ActionLogService} from "./../statistics/ActionLogService";
import {ActionType} from "./../statistics/models/ActionType";
import {ReferencedObjectType} from "./../ReferencedObjectType";
import {AuthManager} from "./../authcore/AuthManager";
import {AuthRedirect} from "./../authcore/AuthRedirect";
import {RedirectReason} from "./../authcore/RedirectReason";
import {ContentRouter} from "./../urls/ContentRouter";
import {ContentService} from "./../content/ContentService";
import {IContentView} from "./../content/models/IContentView";
import {MainRouter} from "./../urls/MainRouter";

export class PurchaseController {
    public static NAME = "finances.PurchaseController";
    private objectType: ReferencedObjectType;
    private objectId: number;
    public content: IContentView = {};

    public static $inject = [
        "$location",
        "$window",
        "$routeParams",
        PurchaseService.NAME,
        ActionLogService.NAME,
        AuthManager.NAME,
        AuthRedirect.NAME,
        ContentRouter.NAME,
        ContentService.NAME,
        MainRouter.NAME
    ];

    constructor(private $location: angular.ILocationService,
                private $window: angular.IWindowService,
                private $routeParams: angular.route.IRouteParamsService,
                private purchaseApi: PurchaseService,
                private actionLogService: ActionLogService,
                private authManager: AuthManager,
                private authRedirect: AuthRedirect,
                private contentRouter: ContentRouter,
                private contentApi: ContentService,
                private mainRouter: MainRouter) {

        if (!authManager.isAuthenticated()) {
            authRedirect.redirectToLogOn(RedirectReason.LOGON_REQUIRED);
        }

        if (!MenuHelper.getCurrentMenuItemKey()) {
            MenuHelper.setCurrentMenuItem(MenuHelper.SEARCH_MENU_KEY);
        }
        this.objectType = ReferencedObjectType[<string> this.$routeParams[FinancesRouter.OBJECT_TYPE_PARAM].toUpperCase()];
        this.objectId = this.$routeParams[FinancesRouter.OBJECT_ID_PARAM];

        this.contentApi.getContentView(this.objectId, this.objectType).then(result => {
            if (result.permissions.view) {
                notifier.warning("You already have a permission to view this content, so you dont need to buy it. Lucky you!");
                this.$location.url(this.contentRouter.detailsUrl(this.objectId, this.objectType));
            }
            angular.copy(result, this.content);
        }).catch(response => {
            this.content = {
                typeName: '',
                title: 'Failed to load'
            };
        }).finally(() => {
            PageHeader.set().header((this.content.contentType || '') +
                ' ' + (this.content.name || '') + '' +
                ' > Request of purchase confirmation'
            ).update();
        });
    }

    public doSubmit(): void {
        this.purchaseApi.purchase(this.objectType, this.objectId).then(result => {
            this.actionLogService.create(this.objectId, this.objectType, this.getPurchaseActionType(this.objectType));
            notifier.success('Purchase has been successfully accomplished');
            this.$location.url(this.contentRouter.detailsUrl(this.objectId, this.objectType));
        });
    }

    public doCancel(): void {
        if (this.$window.history.length > 1) {
            this.$window.history.back();
        } else {
            this.$location.url(this.mainRouter.mainUrl());
        }
    }

    private getPurchaseActionType(objectType: ReferencedObjectType): ActionType {
        switch (objectType) {
            case ReferencedObjectType.MATERIAL:
                return ActionType.MATERIAL_PURCHASE;
            default:
                return null;
        }
    }
}