import {UrlService} from "./../utils/UrlService";
import {ISelectableReferenceEntity} from "./../reference/models/ISelectableReferenceEntity";
import {ReferenceService} from "./../reference/ReferenceService";
import {ReferenceRedirect} from "./../reference/ReferenceRedirect";
import {ReferenceEntitiesParams} from "./../reference/models/IReferenceEntitiesParams";
import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";
import {PermissionsService} from "./../security/PermissionsService";

export interface IReferenceDropdownSelectorDirectiveScope extends angular.IScope {
    referenceName?: string;
    referenceFilter?: string;
    referenceSorting?: string;
    model?: any;
    modelProperty?: string;
    disabled?: boolean;
    required?: boolean;
    elementName?: string;
    nullable?: boolean;
    placeholder?: string;
    editable?: boolean;
    style: any;
    onSelectorChange?: (object?: any) => void;

    vm?: ReferenceDropdownSelectorController;
}

export class ReferenceDropdownSelectorController {
    public systemPermissions: ISystemPermissions;
    public referenceEntities: Array<ISelectableReferenceEntity>;
    private referenceEntitiesMap: Map<number, ISelectableReferenceEntity>;

    public static $inject = [
        "$scope",
        ReferenceService.NAME,
        ReferenceRedirect.NAME,
        PermissionsService.NAME
    ];

    constructor(protected $scope: IReferenceDropdownSelectorDirectiveScope,
                protected referenceApi: ReferenceService,
                protected referenceRedirect: ReferenceRedirect,
                protected permissionsApi: PermissionsService) {
        $scope.vm = this;

        this.permissionsApi.getSystemPermissions().then(p => {
            this.systemPermissions = p;
        });
        this.referenceApi.getActiveReferenceEntitiesForSelector(this.$scope.referenceName, new ReferenceEntitiesParams(
                this.$scope.referenceFilter, this.$scope.referenceSorting, undefined, undefined), null).then(result => {
            this.referenceEntities = result;
            this.referenceEntitiesMap = new Map<number, ISelectableReferenceEntity>();
            this.referenceEntities.forEach((entity: ISelectableReferenceEntity) => {
                this.referenceEntitiesMap.set(entity.id, entity);
            });
        });
    }

    public changeCallback = (id: number) => {
        this.$scope.onSelectorChange(this.referenceEntitiesMap.get(id));
    };

    public redirectToNewReferenceItem(): void {
        this.referenceRedirect.redirectToEditReferenceItem(this.$scope.referenceName);
    }

    public redirectToEditReferenceItem(referenceId: number): void {
        this.referenceRedirect.redirectToEditReferenceItem(this.$scope.referenceName, referenceId);
    }
}

export function ReferenceDropdownSelectorDirectiveFactory(url: UrlService): angular.IDirective {
    var directive: angular.IDirective = {};
    directive.restrict = "A";
    directive.controller = ReferenceDropdownSelectorController;
    directive.scope = {
        referenceName: "=vhsReferenceName",
        referenceFilter: "=?vhsReferenceFilter",
        referenceSorting: "=?vhsReferenceSorting",
        model: "=vhsModel",
        modelProperty: "=vhsModelProperty",
        disabled: "=?vhsDisabled",
        required: "=?vhsRequired",
        elementName: "@?vhsElementName",
        nullable: "=?vhsNullable",
        placeholder: "=?vhsPlaceholder",
        editable: "=?vhsEditable",
        style: "=?vhsStyle",
        onSelectorChange: "&vhsOnChange"
    };
    directive.templateUrl = url.template("components/reference-dropdown-selector.html");
    return directive;
}

ReferenceDropdownSelectorDirectiveFactory.$inject = [
    UrlService.NAME
];
