import {PermissionsRepository} from "./PermissionsRepository";
import {PermissionsService} from "./PermissionsService";
import {SecurityService} from "./SecurityService";

import {UrlsModule} from "./../urls/UrlsModule";
import {AuthModule} from "./../auth/AuthModule";
import {AuthCoreModule} from "./../authcore/AuthCoreModule";

export var SecurityModule: angular.IModule = angular
    .module("security", [
        UrlsModule.name,
        AuthModule.name,
        AuthCoreModule.name
    ])
    .service(PermissionsRepository.NAME, PermissionsRepository)
    .service(PermissionsService.NAME, PermissionsService)
    .service(SecurityService.NAME, SecurityService);