import {QuizService} from "./QuizService";
import {QuizzesController} from "./QuizzesController";
import {QuizEditController} from "./QuizEditController";
import {QuizController} from "./QuizController";
import {QuizPassingController} from "./QuizPassingController";

import {AuthModule} from "./../auth/AuthModule";
import {AuthCoreModule} from "./../authcore/AuthCoreModule";
import {SecurityModule} from "./../security/SecurityModule";
import {UsersModule} from "./../users/UsersModule";
import {UrlsModule} from "./../urls/UrlsModule";

export var QuizzesModule: angular.IModule = angular
    .module("quizzes", [
        AuthModule.name,
        AuthCoreModule.name,
        SecurityModule.name,
        UsersModule.name,
        UrlsModule.name
    ])
    .service(QuizService.NAME, QuizService)
    .controller(QuizzesController.NAME, QuizzesController)
    .controller(QuizEditController.NAME, QuizEditController)
    .controller(QuizController.NAME, QuizController)
    .controller(QuizPassingController.NAME, QuizPassingController);