import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";
import {SecurityService} from "./../security/SecurityService";
import {MenuHelper} from "./../utils/MenuHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";

export class TeacherController {
    public static NAME = "broadcasts.TeacherController";

    public broadcastMode: string;

    public static $inject = [
        "$location",
        SecurityService.NAME
    ];

    constructor(private $location: ng.ILocationService,
                private securityService: SecurityService) {
        this.securityService.demandAuthentication()
            .demandSystemPermissions(systemPermissions => systemPermissions.organizationBroadcasts);

        MenuHelper.setCurrentMenuItem(MenuHelper.EDUCATION_TEACHER_BROADCASTS_MENU_KEY);
        PageHeader.set().header("Webcasts").update();

        this.broadcastMode = "video";
    }

    public setBroadcastMode(mode: string) {
        this.broadcastMode = mode;
    }
}
