export interface INotifierDisplayMethod {
    (message: string, title?: string, overrides?: ToastrOptions): JQuery;
}

export class Notifier {
    public success: INotifierDisplayMethod = this.getNotifierDisplayMethod(toastr.success);
    public error: INotifierDisplayMethod = this.getNotifierDisplayMethod(toastr.error);
    public warning: INotifierDisplayMethod = this.getNotifierDisplayMethod(toastr.warning);

    private getNotifierDisplayMethod(toastrDisplayMethod: ToastrDisplayMethod): INotifierDisplayMethod {
        return function (message: string, title: string, overrides: ToastrOptions): JQuery {
            if (message != undefined && title == undefined && overrides == undefined) {
                return toastrDisplayMethod(s.escapeHTML(message));
            }
            if (message != undefined && title != undefined && overrides == undefined) {
                return toastrDisplayMethod(s.escapeHTML(message), s.escapeHTML(title));
            }
            if (message != undefined && title != undefined && overrides != undefined) {
                return toastrDisplayMethod(s.escapeHTML(message), s.escapeHTML(title), overrides);
            }
            return undefined;
        };
    }
}

export var notifier: Notifier = new Notifier();