import {GridController} from "./GridDirective";
import {UrlService} from "./../utils/UrlService";

interface GridEmptyAttributes extends angular.IAttributes {
    vhsEmptyMessage: string;
}

export function GridEmptyDirectiveFactory(url: UrlService): angular.IDirective {

    function link($scope: angular.IScope, element: angular.IAugmentedJQuery,
                  attributes: GridEmptyAttributes, controller: GridController<any>,
                  transclude: angular.ITranscludeFunction) {
        $scope["_emptyMessage"] = attributes && attributes.vhsEmptyMessage 
            ? attributes.vhsEmptyMessage 
            : 'No records found';
    }

    var directive: any = {};
    directive.link = link;
    directive.restrict = "A";
    directive.replace = true;
    directive.templateUrl = url.template("components/grid-empty.html");
    directive.require = "^?vhsGrid"; //todo: temp solution, should be mandatory. Currently there is an error during template reprocessing for grid
    return directive;
}

GridEmptyDirectiveFactory.$inject = [
    UrlService.NAME
];
