export function AutoFocusDirectiveFactory($timeout: angular.ITimeoutService): angular.IDirective {
    function link($scope: angular.IScope, element: angular.IAugmentedJQuery) {
        $timeout(function () {
            element[0].focus();
        });
    }

    return {
        restrict: "A",
        link: link
    };
}

AutoFocusDirectiveFactory.$inject = [
    "$timeout"
];