import {PersonalService} from "./PersonalService";
import {ProfileController} from "./ProfileController";
import {FavoritesController} from "./FavoritesController";
import {UploadsController} from "./UploadsController";
import {CoursesController} from "./CoursesController";
import {QuizzesController} from "./QuizzesController";
import {PlaylistsController} from "./PlaylistsController";
import {SubscriptionsController} from "./SubscriptionsController";
import {NewCommentsController} from "./NewCommentsController";
import {SupportController} from "./SupportController";
import {CouponsController} from "./CouponsController";
import {PurchasesHistoryController} from "./PurchasesHistoryController";
import {BalanceController} from "./BalanceController";
import {SettingsController} from "./SettingsController";

import {SecurityModule} from "./../security/SecurityModule";
import {UrlsModule} from "./../urls/UrlsModule";
import {SettingsModule} from "./../settings/SettingsModule";
import {ReferenceModule} from "./../reference/ReferenceModule";
import {UsersModule} from "./../users/UsersModule";

export var PersonalModule: angular.IModule = angular
    .module("personal", [
        SecurityModule.name,
        UrlsModule.name,
        SettingsModule.name,
        ReferenceModule.name,
        UsersModule.name
    ])
    .service(PersonalService.NAME, PersonalService)
    .controller(ProfileController.NAME, ProfileController)
    .controller(FavoritesController.NAME, FavoritesController)
    .controller(UploadsController.NAME, UploadsController)
    .controller(CoursesController.NAME, CoursesController)
    .controller(QuizzesController.NAME, QuizzesController)
    .controller(PlaylistsController.NAME, PlaylistsController)
    .controller(NewCommentsController.NAME, NewCommentsController)
    .controller(SubscriptionsController.NAME, SubscriptionsController)
    .controller(SupportController.NAME, SupportController)
    .controller(CouponsController.NAME, CouponsController)
    .controller(PurchasesHistoryController.NAME, PurchasesHistoryController)
    .controller(BalanceController.NAME, BalanceController)
    .controller(SettingsController.NAME, SettingsController);