import {MenuHelper} from "./../utils/MenuHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";

export class ContactsController {
    public static NAME = "info.ContactsController";

    public static $inject = [
        "$location"
    ];

    constructor(private $location: angular.ILocationService) {
        MenuHelper.setCurrentMenuItem(MenuHelper.CONTACTS_MENU_KEY);
        PageHeader.set().header("Contacts").update();
    }
}
