import {UrlService} from "./../utils/UrlService";
import {ISearchControlListener, SearchControlHelper} from "./../utils/SearchControlHelper";
import {PageHeaderController} from "./PageHeaderDirective";
import {MaterialsRouter} from "./../urls/MaterialsRouter";
import {
    IMenuUpdateListener,
    MenuHelper
} from "./../utils/MenuHelper";

interface VhsSearchAttributes extends angular.IAttributes {
}

export interface IVhsSearchDirectiveScope extends angular.IScope {
    vm: VhsSearchController;
    controllable?: boolean;
    showFileFormat?: boolean;
}

export class VhsSearchController implements IMenuUpdateListener {
    public static FILE_FORMAT_PARAMETER = "fileFormat";
    public static SEARCH_STRING_PARAMETER = "searchString";

    public searchVisible: boolean = true;

    public static $inject = [
        "$scope",
        "$rootScope",
        "$location",
        MaterialsRouter.NAME
    ];

    constructor(private $scope: IVhsSearchDirectiveScope,
                private $rootScope: ng.IRootScopeService,
                private $location: angular.ILocationService,
                private materialsRouter: MaterialsRouter) {
        MenuHelper.addMenuUpdateListener(this);
        $scope.vm = this;
        if (!this.$rootScope["__vhsSearchInfo"]) {
            this.$rootScope["__vhsSearchInfo"] = {
                fileFormat: "",
                searchString: ""
            };
        }
    }

    public doSubmit(): void {
        this.$location.url(this.materialsRouter.searchUrl())
            .search(VhsSearchController.FILE_FORMAT_PARAMETER, this.$rootScope["__vhsSearchInfo"].fileFormat)
            .search(VhsSearchController.SEARCH_STRING_PARAMETER, this.$rootScope["__vhsSearchInfo"].searchString);
        this.$rootScope["__vhsSearchInfo"] = {
            fileFormat: "",
            searchString: ""
        };
    }

    public onMenuItemUpdate(newMenuItemKey: string, oldMenuItemKey: string) {
        if ((newMenuItemKey == MenuHelper.MAIN_MENU_KEY || newMenuItemKey == MenuHelper.SEARCH_MENU_KEY || newMenuItemKey == "") && this.$scope.controllable) {
            this.searchVisible = false;
        } else {
            this.searchVisible = true;
        }
    }
}

export function VhsSearchDirectiveFactory(url: UrlService): angular.IDirective {
    function link($scope: angular.IScope, element: angular.IAugmentedJQuery,
                  attributes: angular.IAttributes, controller: VhsSearchController,
                  transclude: angular.ITranscludeFunction) {
        $scope.$on("$destroy",
            () => MenuHelper.removeMenuUpdateListener(controller));
    }

    var directive: any = {};
    directive.restrict = "A";
    directive.link = link;
    directive.scope = {
        controllable: "=?vhsControllable",
        showFileFormat: "=?vhsShowFileFormat"
    };
    directive.controller = VhsSearchController;
    directive.templateUrl = url.template("components/vhs-search.html");
    return directive;
}

VhsSearchDirectiveFactory.$inject = [
    UrlService.NAME
];