import {ITreeElementInfo} from "./models/TreeElementInfo";
import {TreeElementState} from "./models/TreeElementState";

export class TreeElementsInfoFiller {

    public static MAX_VISIBLE_LEVEL: number = 2;
    public static FIRST_LEVEL: number = 1;

    public static fillHierarchyInfo(gridTreeElements: Array<ITreeElementInfo<any>>) {
        TreeElementsInfoFiller.fillHierarchyInfoWithMaxLevel(gridTreeElements, TreeElementsInfoFiller.MAX_VISIBLE_LEVEL);
    }

    public static fillHierarchyInfoWithMaxLevel(gridTreeElements: Array<ITreeElementInfo<any>>, maxVisibleLevel: number) {

        for (var i = 0; i < gridTreeElements.length; i++) {
            var element: ITreeElementInfo<any> = gridTreeElements[i];
            this.initHierarchyElement(element, maxVisibleLevel);
        }

        var childrenOnLevels: Map<number, Array<ITreeElementInfo<any>>> = new Map<number, Array<ITreeElementInfo<any>>>();
        var lastElementsOnLevels: Map<number, ITreeElementInfo<any>> = new Map<number, ITreeElementInfo<any>>();
        var previousLevel: number = -1;
        for (var i = 0; i < gridTreeElements.length; i++) {
            var element: ITreeElementInfo<any> = gridTreeElements[i];
            var currentLevel: number = element.level;
            if (currentLevel > this.FIRST_LEVEL) { // this element is a child for previous level
                childrenOnLevels.get(currentLevel - 1).push(element);
            }
            childrenOnLevels.set(currentLevel, element.children);
            lastElementsOnLevels.set(currentLevel, element);

            if (currentLevel < previousLevel) { //go back to root element
                for (var lvl = previousLevel; lvl > currentLevel; lvl--) {
                    lastElementsOnLevels.get(lvl).isLastChild = true;
                }
            }
            previousLevel = currentLevel;
        }
        if (previousLevel >= this.FIRST_LEVEL) {
            for (var lvl = previousLevel; lvl >= this.FIRST_LEVEL; lvl--) {
                lastElementsOnLevels.get(lvl).isLastChild = true;
            }
        }
    }

    private static initHierarchyElement(element: ITreeElementInfo<any>, maxVisibleLevel: number) {
        if (!element) return;
        if (element.level == undefined) {
            element.level = 0;
        }
        element.children = [];
        element.isLastChild = false;
        element.state = element.level < maxVisibleLevel ? TreeElementState[TreeElementState.EXPANDED] : TreeElementState[TreeElementState.COLLAPSED];
        element.visible = element.level <= maxVisibleLevel;

        element.show = () => {
            element.visible = true;
        };
        element.hide = () => {
            if (!element.visible) return;
            element.visible = false;
            angular.forEach(element.children, child => child.hide());
        };
        element.expand = () => {
            element.state = TreeElementState[TreeElementState.EXPANDED];
            angular.forEach(element.children, child => child.show());
        };
        element.expandAll = () => {
            element.state = TreeElementState[TreeElementState.EXPANDED];
            angular.forEach(element.children, child => child.expand());
            angular.forEach(element.children, child => child.show());
        };
        element.collapse = () => {
            angular.forEach(element.children, child => child.collapse());
            angular.forEach(element.children, child => child.hide());
            element.state = TreeElementState[TreeElementState.COLLAPSED];
        };
    }
}