import {UrlService} from "./../utils/UrlService";

import {INews} from "./models/INews";

export class NewsService {
    public static NAME = "news.NewsService";

    public static $inject = [
        "$http",
        UrlService.NAME
    ];

    constructor(private $http: angular.IHttpService,
                private url: UrlService) {
    }

    public getNews(id: number): angular.IPromise<INews> {
        return this.$http.get(this.url.base("/api/news/:id").param(":id", id).service())
            .then(result => result.data);
    }

    public create(news: INews): ng.IPromise<INews> {
        return this.$http.post(this.url.service("/api/news"), news)
            .then(result => result.data);
    }

    public update(news: INews): ng.IPromise<INews> {
        return this.$http.put(this.url.service("/api/news"), news)
            .then(result => result.data);
    }

    public checkNewsSubscription(newsType: string): angular.IPromise<boolean> {
        return this.$http.get(this.url.base("/api/news/subscription?newsType=:newsType")
            .param(":newsType", newsType).service())
            .then(result => result.data);
    }
}