import {MenuHelper} from "./../utils/MenuHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {UrlService} from "../utils/UrlService";

export class FAQController {
    public static NAME = "info.FAQController";

    public static $inject = [
        "$location",
        UrlService.NAME
    ];

    constructor(private $location: angular.ILocationService,
                private url: UrlService) {
        MenuHelper.setCurrentMenuItem(MenuHelper.FAQ_MENU_KEY);
        PageHeader.set().header("FAQ").update();
    }
}
