import {IUserInfo} from "./../security/IUserInfo";
import {AuthManager} from "./../authcore/AuthManager";
import {ITokenInfo} from "./../authcore/ITokenInfo";
import {ILogonProvider} from "./ILogonProvider";
import {UrlService} from "./../utils/UrlService";
import {IPromise} from "angular";

export class AuthService {
    public static NAME = "auth.AuthService";

    private restoreSessionPromisesMap: Map<string, IPromise<any>> = new Map<string, IPromise<any>>();

    private providers: Array<ILogonProvider> = [ //todo: AOPA temp only
        { "key" : "LOCAL",
          "name" : "Internal accounts",
          "type" : "INTERNAL" }
        // { "key" : "LOCAL_OR_PERSONIFY",
        //        "name" : "Internal accounts or Personify",
        //        "type" : "INTERNAL" }
    ];

    public static $inject = [
        "$timeout",
        "$http",
        "$q",
        AuthManager.NAME,
        UrlService.NAME
    ];

    constructor(private $timeout: angular.ITimeoutService,
                private $http: angular.IHttpService,
                private $q: angular.IQService,
                private authManager: AuthManager,
                private url: UrlService) {
    }


    public getProviders(): angular.IPromise<Array<ILogonProvider>> {
        if (this.providers) {
            var promise: angular.IDeferred<Array<ILogonProvider>> = this.$q.defer();
            promise.resolve(this.providers);
            return promise.promise;
        }

        return this.$http.get(this.url.service("/api/settings/common/logonproviders"))
            .then(result => {
                let theResult: Array<ILogonProvider> = <Array<ILogonProvider>> result.data;
                this.providers = theResult;
                return theResult;
            });
    }

    public getProvidersForLogon(): angular.IPromise<ILogonProvider[]> {
        return this.$q.when(
            [
                // {
                //     "key": "LOCAL_OR_PERSONIFY",
                //     "name": "Internal accounts or Personify",
                //     "type": "INTERNAL"
                // },
                {
                    "key": "LOCAL",
                    "name": "Internal accounts",
                    "type": "INTERNAL"
                }
            ]
        );
    }

    public getCurrentUser(): angular.IPromise<IUserInfo> {
        if (this.authManager.getCurrentUser()) {
            var promise: angular.IDeferred<IUserInfo> = this.$q.defer();
            promise.resolve(this.authManager.getCurrentUser());

            return promise.promise;
        }

        return this.$http.get(this.url.service("/api/security/user"))
            .then(result => {
                this.authManager.logOn(result.data);
                return result.data;
            });
    }

    public logOn(username: string, password: string): angular.IPromise<IUserInfo> {
        return this.authenticate(
            {"grant_type": "password", "username": username, "password": password});
    }

    public restoreSession(token: string): angular.IPromise<IUserInfo> {
        if (this.restoreSessionPromisesMap.has(token)) {
            return this.restoreSessionPromisesMap.get(token);
        }
        console.log("Session restore fired!");
        this.restoreSessionPromisesMap.set(token, this.authenticate(
            {"grant_type": "refresh_token", "refresh_token": token})
            .finally(() => {
                this.restoreSessionPromisesMap.delete(token);
            }));
        return this.restoreSessionPromisesMap.get(token);
    }

    private authenticate(params: any): angular.IPromise<IUserInfo> {
        return this.$http({
            method: "post",
            url: this.url.service("/api/token"),
            headers: {"Content-Type": "application/x-www-form-urlencoded"},
            data: params,
            transformRequest: function (data) {
                return $.param(data);
            }
        })
            .then(tokenResult => {
                this.authManager.logOff();
                this.authManager.saveToken({
                    accessToken: tokenResult.data["access_token"],
                    refreshToken: tokenResult.data["refresh_token"],
                    tokenType: tokenResult.data["token_type"],
                    expiresIn: tokenResult.data["expires_in"],
                    issuedAt: tokenResult.data["issued_at"],
                    locale: tokenResult.data["locale"]
                });
                this.startAutoRefresh();
                return this.getCurrentUser();
            });
    }

    public logOff(): angular.IPromise<any> {
        var refreshToken = this.authManager.getToken() ? this.authManager.getToken().refreshToken : null;
        this.authManager.disableSpnego();
        return this.$http.post(this.url.service("/api/security/logOff"), {refreshToken: refreshToken})
            .finally(() => {
                this.authManager.logOff();
            });
    }

    public restartAutoRefresh() {
        var token: ITokenInfo = this.authManager.getToken();

        var expirationDate: moment.Moment = moment.utc(token.issuedAt).add(token.expiresIn, "s");
        var now: moment.Moment = moment.utc();
        var timeInterval: number = expirationDate.diff(now, "s") - 2 * 60;
        if (timeInterval < 0) {
            timeInterval = 2;
        }

        this.$timeout(
           () => {
               const currentToken = this.authManager.getToken();
               if (currentToken && currentToken.refreshToken === token.refreshToken) {
                   this.restoreSession(token.refreshToken)
                       .catch(result => {
                           if (result.data.error == "invalid_request") {
                               this.authManager.eraseRefreshToken();
                           }
                       })
               }
           },
           timeInterval * 1000);
    }

    public restorePassword(logonName: string): angular.IPromise<any> {
        return this.$http.post(this.url.base("/api/security/restore/password")
            .service(), {logonName: logonName})
            .then(result => result.data);
    }

    public createNewPassword(token: string, code: string, password: string): angular.IPromise<any> {
        return this.$http.post(
            this.url.service("/api/security/create/new/password"), {token: token, code: code, password: password});
    }

    private startAutoRefresh() {
        var token: ITokenInfo = this.authManager.getToken();
        if (token.expiresIn < 4 * 60) {
            return;
        }
        var timeInterval = token.expiresIn - 2 * 60;
        this.$timeout(
            () => this.restoreSession(token.refreshToken),
            timeInterval * 1000);
    }

    public checkValidCodeFromEmail(code: string): angular.IPromise<any> {
        return this.$http.get(this.url.base("/api/security/check/code/:code")
            .param(":code", code)
            .service())
            .then(result => result.data);
    }
}
