import {AuthService} from "./AuthService";

import {AuthRouter} from "../urls/AuthRouter";
import {MenuHelper} from "../utils/MenuHelper";
import {notifier} from "../INotifier";
import {UrlService} from "../utils/UrlService";

export class CheckValidCodeController {
    public static NAME = "auth.CheckValidCodeController";

    public code: string = null;
    public disableButton: boolean = false;
    public logonName: string;
    public shortEmail: string = null;

    public static $inject = [
        "$routeParams",
        "$location",
        "$scope",
        AuthService.NAME,
        AuthRouter.NAME,
        UrlService.NAME
    ];

    constructor(private $routeParams: ng.route.IRouteParamsService,
                private $location: angular.ILocationService,
                private $scope: angular.IScope,
                private authService: AuthService,
                private authRouter: AuthRouter,
                private url: UrlService) {

        MenuHelper.setCurrentMenuItem("");
        this.logonName = this.$location.$logonName;
        if (!this.logonName) {
            this.$location.url(this.authRouter.restorePassword());
        } else {
            this.getShortEmail();
        }
    }

    public checkValidCodeFromEmail() {
        this.authService.checkValidCodeFromEmail(this.code)
            .then(() => {
                this.$location.url(this.authRouter.createNewPasswordByCode(this.code));
                return;
            }).catch(response => {
            switch (response.data.error) {
                case "error.password.restore.impossible": {
                    notifier.error("Incorrect code or code expired. Try to restore the password again");
                    this.code = null;
                    return;
                }
            }
        });
    }

    public restorePassword() {
        this.$location.url(this.authRouter.restorePassword());
    }

    public cancel() {
        this.$location.url(this.authRouter.logonUrl());
    }

    public getShortEmail() {
        if (this.logonName.indexOf("@") > 0) {
            let emailParts = this.logonName.split("@");
            if (emailParts[0].length > 3) {
                this.shortEmail = emailParts[0].charAt(0) + "..." + emailParts[0].charAt(emailParts[0].length-1) + "@" + emailParts[1];
            } else {
                this.shortEmail = emailParts[0] + "@" + emailParts[1];
            }
        } else {
            this.shortEmail = this.logonName;
        }
    }

    public codeValid() {
        let regexp = /^[0-9]{6}$/g;
        return regexp.test(this.code);
    }
}