import {StudentController} from "./StudentController";
import {TeacherController} from "./TeacherController";

import {SecurityModule} from "./../security/SecurityModule";

export var BroadcastsModule: angular.IModule = angular
    .module("broadcasts", [
        SecurityModule.name
    ])
    .controller(StudentController.NAME, StudentController)
    .controller(TeacherController.NAME, TeacherController);