export class EventService {
    public static NAME = "infrastructure.EventService";

    private listeners: Map<string, Array<() => void>> = new Map<string, Array<() => void>>();

    public registerListener(event: string, listener: () => void): void {
        event = event || "";
        if (!this.listeners.has(event)) {
            this.listeners.set(event, []);
        }
        this.listeners.get(event).push(listener);
    }

    public unregisterListener(event: string, listener: () => void): void {
        event = event || "";
        if (!this.listeners.has(event)) {
            return;
        }
        for (var i = 0; i < this.listeners.get(event).length; ++i) {
            var l = this.listeners.get(event)[i];
            if (l == listener) {
                this.listeners.get(event).splice(i, 1);
                break;
            }
        }
        if (this.listeners.get(event).length == 0) {
            this.listeners.delete(event);
        }
    }

    public notify(event: string): void {
        event = event || "";
        var listeners: Array<any> = this.listeners.get(event);
        angular.forEach(listeners, listener => listener());
    }
}