import {StorageWrapper, Storages} from "./../infrastructure/Storage";
import {ITokenInfo} from "./ITokenInfo";

export class TokenStorage {
    public static NAME = "authcore.TokenStorage";

    private static TOKEN_KEY = "access-token";

    private tokenInfo: ITokenInfo;

    public static $inject = [
        Storages.LOCAL_NAME
    ];

    constructor(private storage: StorageWrapper) {
        this.tokenInfo = storage.get<ITokenInfo>(TokenStorage.TOKEN_KEY);
    }

    public get(): ITokenInfo {
        return this.tokenInfo;
    }

    public put(token: ITokenInfo) {
        this.tokenInfo = token;
        this.storage.put(TokenStorage.TOKEN_KEY, token);
    }

    public remove() {
        this.tokenInfo = null;
        this.storage.remove(TokenStorage.TOKEN_KEY);
    }
}
