import {ObjectFieldResolver} from "./../utils/ObjectFieldResolver";
import {UrlService} from "./../utils/UrlService";

interface GridExternalConfigurationAttributes extends angular.IAttributes {
    gridModelPath: string;
    hidePageSizeSelector: boolean;
}

export function GridExternalConfigurationFactory(url: UrlService): angular.IDirective {

    function link($scope: angular.IScope, element: angular.IAugmentedJQuery,
                  attributes: GridExternalConfigurationAttributes, controller: any,
                  transclude: angular.ITranscludeFunction) {
        if (typeof attributes.gridModelPath != 'undefined') {
            var obj = ObjectFieldResolver.findForPath($scope, attributes.gridModelPath);
            $scope["gridVm"] = {gridModel: obj};
            $scope["_showPageSizeSelector"] = !attributes.hidePageSizeSelector;
        }
    }

    var directive: any = {};
    directive.restrict = "A";
    directive.templateUrl = url.template("components/grid-configuration.html");
    directive.link = link;
    return directive;
}

GridExternalConfigurationFactory.$inject = [
    UrlService.NAME
];
