import {UrlService} from "./../utils/UrlService"
import {UploadsService} from "./../storage/UploadsService";
import {StringBuilder} from "./../utils/StringBuilder";
import {notifier} from "./../INotifier";

export class UploadUtils {
    public static NAME = "utils.UploadUtils";

    public static MATERIAL_PREVIEW_KEY = "material_preview_";
    public static MATERIAL_FILE_KEY = "material_file_";
    public static COURSE_PREVIEW_KEY = "course_preview_";
    public static BUNDLE_KEY = "bundle_";
    public static FILE_KEY = "_file_";

    public static $inject = [
        "$http",
        UrlService.NAME,
        UploadsService.NAME
    ];

    constructor(private $http: angular.IHttpService,
                private url: UrlService,
                private uploadsService: UploadsService) {
    }

    public uploadMaterialByChunks(file: File, fileId: number, materialId: number, url: string, method: string) {
        let successFunc: (file: File) => void = (file: File) => {
            let successUrl: string = this.url.base("/api/materials/upload/completed?fileId=:fileId&materialId=:materialId")
                .param(":fileId", fileId)
                .param(":materialId", materialId)
                .service();
            this.$http.post(successUrl, null).then(() => {
                notifier.success(StringBuilder.interpolate("The file (:fileName) has been successfully uploaded")
                    .with(":fileName", file.name)
                    .build());
            });
        };
        let abortFunc: (file: File) => void = (file: File) => {
            let abortUrl: string = this.url.base("/api/materials/upload/not/completed?fileId=:fileId&materialId=:materialId")
                .param(":fileId", fileId)
                .param(":materialId", materialId)
                .service();
            this.$http.post(abortUrl, null).then(() => {
                notifier.error(StringBuilder.interpolate("Uploading of the file (:fileName) has been canceled")
                    .with(":fileName", file.name)
                    .build());
            });
        };
        let errorFunc: (file: File) => void = (file: File) => {
            let errorUrl: string = this.url.base("/api/materials/upload/not/completed?fileId=:fileId&materialId=:materialId")
                .param(":fileId", fileId)
                .param(":materialId", materialId)
                .service();
            this.$http.post(errorUrl, null).then(() => {
                notifier.error(StringBuilder.interpolate("Uploading of the file (:fileName) has failed")
                    .with(":fileName", file.name)
                    .build());
            });
        };
        this.uploadsService.upload(file, UploadUtils.MATERIAL_PREVIEW_KEY + materialId, url, method, successFunc, abortFunc, errorFunc);
    }

    public uploadCourseByChunks(file: File, fileId: number, courseId: number, url: string, method: string) {
        let successFunc: (file: File) => void = (file: File) => {
            let successUrl: string = this.url.base("/api/courses/upload/completed?fileId=:fileId&courseId=:courseId")
                .param(":fileId", fileId)
                .param(":courseId", courseId)
                .service();
            this.$http.post(successUrl, null).then(() => {
                notifier.success(StringBuilder.interpolate("The file (:fileName) has been successfully uploaded")
                    .with(":fileName", file.name)
                    .build());
            });
        };
        let abortFunc: (file: File) => void = (file: File) => {
            let abortUrl: string = this.url.base("/api/courses/upload/not/completed?fileId=:fileId&courseId=:courseId")
                .param(":fileId", fileId)
                .param(":courseId", courseId)
                .service();
            this.$http.post(abortUrl, null).then(() => {
                notifier.error(StringBuilder.interpolate("Uploading of the file (:fileName) has been canceled")
                    .with(":fileName", file.name)
                    .build());
            });
        };
        let errorFunc: (file: File) => void = (file: File) => {
            let errorUrl: string = this.url.base("/api/courses/upload/not/completed?fileId=:fileId&courseId=:courseId")
                .param(":fileId", fileId)
                .param(":courseId", courseId)
                .service();
            this.$http.post(errorUrl, null).then(() => {
                notifier.error(StringBuilder.interpolate("Uploading of the file (:fileName) has failed")
                    .with(":fileName", file.name)
                    .build());
            });
        };
        this.uploadsService.upload(file, UploadUtils.COURSE_PREVIEW_KEY + courseId, url, method, successFunc, abortFunc, errorFunc);
    }

    public uploadBundleByChunks(file: File, fileId: number, bundleId: number, url: string, method: string) {
        let successFunc: (file: File) => void = (file: File) => {
            let successUrl: string = this.url.base("/api/bundles/upload/completed?fileId=:fileId&bundleId=:bundleId")
                .param(":fileId", fileId)
                .param(":bundleId", bundleId)
                .service();
            this.$http.post(successUrl, null).then(() => {
                notifier.success(StringBuilder.interpolate("The file (:fileName) has been successfully uploaded")
                    .with(":fileName", file.name)
                    .build());
            });
        };
        let abortFunc: (file: File) => void = (file: File) => {
            let abortUrl: string = this.url.base("/api/bundles/upload/not/completed?fileId=:fileId&bundleId=:bundleId")
                .param(":fileId", fileId)
                .param(":bundleId", bundleId)
                .service();
            this.$http.post(abortUrl, null).then(() => {
                notifier.error(StringBuilder.interpolate("Uploading of the file (:fileName) has been canceled")
                    .with(":fileName", file.name)
                    .build());
            });
        };
        let errorFunc: (file: File) => void = (file: File) => {
            let errorUrl: string = this.url.base("/api/bundles/upload/not/completed?fileId=:fileId&bundleId=:bundleId")
                .param(":fileId", fileId)
                .param(":bundleId", bundleId)
                .service();
            this.$http.post(errorUrl, null).then(() => {
                notifier.error(StringBuilder.interpolate("Uploading of the file (:fileName) has failed")
                    .with(":fileName", file.name)
                    .build());
            });
        };
        this.uploadsService.upload(file, UploadUtils.BUNDLE_KEY + bundleId + UploadUtils.FILE_KEY + fileId, url, method, successFunc, abortFunc, errorFunc);
    }

    public cancelMaterialPreviews(materialId: number): void {
        this.uploadsService.cancelByKey(UploadUtils.MATERIAL_PREVIEW_KEY + materialId);
    }

    public cancelMaterialFiles(materialId: number): void {
        this.uploadsService.cancelByKey(UploadUtils.MATERIAL_FILE_KEY + materialId);
    }

    public cancelCoursePreviews(courseId: number): void {
        this.uploadsService.cancelByKey(UploadUtils.COURSE_PREVIEW_KEY + courseId);
    }
}