import {ITokenInfo} from "./ITokenInfo";
import {TokenStorage} from "./TokenStorage";
import {AuthRedirect} from "./AuthRedirect";
import {StorageWrapper, Storages} from "./../infrastructure/Storage";
import {IUserInfo} from "./../security/IUserInfo";
import {Event} from "./Event";
import {LogonProviderEnum} from "./../security/LogonProviderEnum";
import {AppProperties} from "../utils/AppProperties";

export class AuthManager {
    public static NAME = "authcore.AuthManager";
    public static GUEST_ID = 2;

    private userInfo: IUserInfo;

    public static $inject = [
        "$rootScope",
        "$location",
        AuthRedirect.NAME,
        Storages.LOCAL_NAME,
        TokenStorage.NAME
    ];

    constructor(private $rootScope: angular.IRootScopeService,
                private $location: ng.ILocationService,
                private authRedirect: AuthRedirect,
                private localStorage: StorageWrapper,
                private tokenStorage: TokenStorage) {
        this.userInfo = null;
    }

    public isAuthenticated(): boolean {
        var token = this.tokenStorage.get();
        return token != null
            && token.accessToken
            && token.issuedAt
            && moment.utc(token.issuedAt).add(token.expiresIn, "s").isAfter(moment.utc());
    }

    public hasToken(): boolean {
        var token = this.tokenStorage.get();
        return token != null
            && token.accessToken != null
            && token.accessToken != "";
    }

    public restoreAvailable(): boolean {
        var token = this.tokenStorage.get();
        return token != null
            && token.refreshToken != null
            && token.refreshToken != "";
    }

    public getCurrentUser(): IUserInfo {
        return this.userInfo;
    }

    public getToken(): ITokenInfo {
        return this.tokenStorage.get();
    }

    public saveToken(token: ITokenInfo) {
        if (token && token.issuedAt) {
            if (moment.utc(token.issuedAt).add(1, "hours") < moment.utc()) {
                // incorrect user local time! save local time as issue date
                token.issuedAt = moment.utc().subtract(2, "minutes").format();
            }
        }
        this.tokenStorage.put(token);
    }

    public eraseToken() {
        this.userInfo = null;
        var token = this.tokenStorage.get();
        if (token != null) {
            token.accessToken = null;
            this.tokenStorage.put(token);
        }
    }

    public eraseRefreshToken() {
        var token = this.tokenStorage.get();
        if (token != null) {
            token.refreshToken = null;
            this.tokenStorage.put(token);
        }
    }

    public logOn(user: IUserInfo) {
        this.userInfo = user;
        if (this.userInfo.id != AuthManager.GUEST_ID) {
            this.$rootScope.$broadcast(Event.LOGGED_ON);
        }
    }

    public logOff() {
        this.tokenStorage.remove();
        let userId: number = this.userInfo ? this.userInfo.id : null;
        this.userInfo = null;
        if (userId != null && userId != AuthManager.GUEST_ID) {
            this.$rootScope.$broadcast(Event.LOGGED_OFF);
        }
    }

    public completeSpnegoExchange(): boolean {
        var tokenInfo = AuthManager.getUrlVars();
        if (LogonProviderEnum[LogonProviderEnum.AD_SSO] !== tokenInfo["ssoProvider"]) {
            return false;
        }
        if (tokenInfo["accessDenied"]) {
            console.log("ACCESS DENIED");
            this.disableSpnego();
            this.localStorage.put("vhs.application.spnego.login.failed", true);
            this.authRedirect.forwardToLogOn(true /*native*/);
            return true;
        }
        if (!tokenInfo["token"]) {
            return false;
        }

        this.saveToken({
            accessToken: tokenInfo["token"],
            tokenType: tokenInfo["tokenType"],
            refreshToken: tokenInfo["refreshToken"],
            expiresIn: tokenInfo["lifetimeSeconds"],
            issuedAt: tokenInfo["issuedAt"],
            locale: tokenInfo["locale"]  //todo: check this var in token info
        });
        this.authRedirect.returnAfterLogOn(true /*native*/);
        return true;
    }

    public tryPersonifySsoIfAllowed(): boolean {
        if (!this.isPersonifySsoEnabled()) {
            return false;
        }
        this.authRedirect.forwardToStartPersonifySSO();
        return true;
    }

    public completePersonifySsoExchange(): boolean {
        if (!this.isPersonifySsoEnabled()) {
            return false;
        }
        var tokenInfo = AuthManager.getUrlVars();
        if (LogonProviderEnum[LogonProviderEnum.PERSONIFY_SSO] !== tokenInfo["ssoProvider"]) {
            return false;
        }
        if (tokenInfo["accessDenied"]) {
            console.log("ACCESS DENIED");
            this.localStorage.put("vhs.application.sso.login.failed", true);
            this.clearTokenInfoFromUrl();
            this.authRedirect.forwardToLogOn(false /*native*/);
            return true;
        }
        if (tokenInfo["ssoDisabled"]) {
            this.clearTokenInfoFromUrl();
            this.authRedirect.returnAfterLogOn(false /*native*/);
            return true;
        }
        if (!tokenInfo["token"]) {
            return false;
        }

        this.saveToken({
            accessToken: tokenInfo["token"],
            tokenType: tokenInfo["tokenType"],
            refreshToken: tokenInfo["refreshToken"],
            expiresIn: tokenInfo["lifetimeSeconds"],
            issuedAt: tokenInfo["issuedAt"],
            locale: tokenInfo["locale"]
        });
        this.clearTokenInfoFromUrl();
        this.authRedirect.returnAfterLogOn(false /*native*/);
        return true;
    }

    public enableSpnego(): void {
        this.localStorage.remove("vhs.application.no.spnego");
    }

    public disableSpnego(): void {
        this.localStorage.put("vhs.application.no.spnego", true);
    }

    public isPersonifySsoEnabled() {
        return AppProperties.authProvider.indexOf(LogonProviderEnum[LogonProviderEnum.PERSONIFY_SSO]) != -1;
    }

    public isPersonifyOrLocalEnabled() {
        return AppProperties.authProvider.indexOf(LogonProviderEnum[LogonProviderEnum.LOCAL_OR_PERSONIFY]) != -1;
    }

    private static getUrlVars() {
        var vars = {},
            hash,
            hashes = window.location.href.slice(window.location.href.indexOf('?') + 1).split('&');

        for(var i = 0; i < hashes.length; i++) {
            hash = hashes[i].split('=');
            vars[hash[0]] = hash[1];
        }
        return vars;
    }

    private clearTokenInfoFromUrl() {
        this.$location.search("accessDenied", null);
        this.$location.search("ssoDisabled", null);
        this.$location.search("token", null);
        this.$location.search("tokenType", null);
        this.$location.search("refreshToken", null);
        this.$location.search("lifetimeSeconds", null);
        this.$location.search("issuedAt", null);
        this.$location.search("locale", null);
        this.$location.url(this.$location.path());
    }
}