import {StringBuilder} from "./../utils/StringBuilder";
import {BaseRouter} from "./BaseRouter";

export class SystemSettingsRouter extends BaseRouter {
    public static NAME = "urls.SystemSettingsRouter";
    public static commonSettingsRoute = "/settings/common";

    public static STORAGE_PLACE_ID_PARAM = "storagePlaceId";

    public static storagePlacesIndexRoute = "/settings/storagePlaces";
    public static storagePlaceCreateRoute = "/settings/storagePlaces/create";
    public static storagePlaceEditRoute = `/settings/storagePlaces/:${SystemSettingsRouter.STORAGE_PLACE_ID_PARAM}/edit`;

    public static $inject = [
        "$location"
    ];

    constructor($location: ng.ILocationService) {
        super($location);
    }

    public commonSettingsUrl(): string {
        return this.url(SystemSettingsRouter.commonSettingsRoute);
    }

    public storagePlacesIndexUrl(): string {
        return this.url(SystemSettingsRouter.storagePlacesIndexRoute);
    }

    public storagePlaceCreateUrl(): string {
        return this.url(SystemSettingsRouter.storagePlaceCreateRoute);
    }

    public storagePlaceEditUrl(storagePlaceId: number): string {
        let result: string = StringBuilder
            .interpolate(SystemSettingsRouter.storagePlaceEditRoute)
            .with(":" + SystemSettingsRouter.STORAGE_PLACE_ID_PARAM, storagePlaceId)
            .build();
        return this.url(result);
    }
}