import {ReferenceUtils} from "./ReferenceUtils";

import {StorageWrapper, Storages} from "./../infrastructure/Storage";
import {ReferenceRouter} from "./../urls/ReferenceRouter";

export class ReferenceRedirect {
    public static NAME = "reference.ReferenceRedirect";
    private static KEY = "reference-redirect";

    public static $inject = [
        "$location",
        Storages.SESSION_NAME,
        ReferenceRouter.NAME,
        ReferenceUtils.NAME
    ];

    constructor(private $location: angular.ILocationService,
                private sessionStorage: StorageWrapper,
                private referenceRouter: ReferenceRouter,
                private referenceUtils: ReferenceUtils) {
        this.sessionStorage.remove(ReferenceRedirect.KEY);
    }

    public redirectToEditReferenceItem(referenceName: string, referenceId?: number, referenceDefaultValues?: string,
                                       referenceFilter?: string, referenceAssociationFilters?: string): void {
        let currentUrl: string = this.$location.path();
        if (!this.sessionStorage.contains(ReferenceRedirect.KEY)) {
            this.sessionStorage.put(ReferenceRedirect.KEY, currentUrl);
        }
        this.$location.url(this.referenceRouter.editUrl(referenceName, referenceId))
            .search(ReferenceRouter.REFERENCE_DEFAULT_VALUES_PARAM, this.referenceUtils.getStringInBrowserFormat(referenceDefaultValues))
            .search(ReferenceRouter.REFERENCE_FILTER_PARAM, this.referenceUtils.getStringInBrowserFormat(referenceFilter))
            .search(ReferenceRouter.REFERENCE_ASSOCIATION_FILTERS_PARAM, this.referenceUtils.getStringInBrowserFormat(referenceAssociationFilters));
    }

    public getAndRemoveStorageUrl(): string {
        let url: string = this.sessionStorage.get<string>(ReferenceRedirect.KEY);
        this.sessionStorage.remove(ReferenceRedirect.KEY);
        return url;
    }
}