export class LocationUtils {
    public static FULL_SCREEN_LOCATION_PARAM = "fullScreen";

    public static isFullScreen(location: ng.ILocationService): boolean {
        return LocationUtils.isFullScreenBySearch(location.search());
    }
    public static isFullScreenBySearch(search: any): boolean {
        if (search[LocationUtils.FULL_SCREEN_LOCATION_PARAM] != undefined) {
            let fullScreenParam = search[LocationUtils.FULL_SCREEN_LOCATION_PARAM];
            return fullScreenParam !== "false";
        } else {
            return true;
        }
    }

    public static preserveFullScreenUrl(baseUrl: string, location: ng.ILocationService): string {
        if (!baseUrl) {
            return baseUrl;
        }
        if (!LocationUtils.isFullScreen(location)) {
            return baseUrl + "?" + LocationUtils.FULL_SCREEN_LOCATION_PARAM + "=false";
        }
        return baseUrl;
    }
}