import {NewsRouter} from "./../urls/NewsRouter";
import {NewsService} from "./NewsService";

import {AuthManager} from "./../authcore/AuthManager";
import {AuthRedirect} from "./../authcore/AuthRedirect";
import {MenuHelper} from "./../utils/MenuHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {notifier} from "./../INotifier";
import {SecurityService} from "./../security/SecurityService";

import {INews} from "./models/INews";

export class NewsEditController {
    public static NAME = "news.NewsEditController";

    public newsId: number;
    public news: INews = {type: "ALL"};

    public isAuthenticated: boolean;
    public disabledSaveButton: boolean;

    public static $inject = [
        "$location",
        "$routeParams",
        AuthManager.NAME,
        AuthRedirect.NAME,
        NewsService.NAME,
        SecurityService.NAME,
        NewsRouter.NAME
    ];


    constructor(private $location: angular.ILocationService,
                private $routeParams: angular.route.IRouteParamsService,
                private authManager: AuthManager,
                public authRedirect: AuthRedirect,
                public newsService: NewsService,
                private securityService: SecurityService,
                public newsRouter: NewsRouter) {
        this.newsId = $routeParams[NewsRouter.ONE_NEWS_ID_PARAM];

        this.isAuthenticated = this.authManager.isAuthenticated();
        if (!this.isAuthenticated) {
            authRedirect.redirectToLogOn();
        }
        this.securityService.demandAuthentication()
            .demandSystemPermissionsIf(this.newsId == null, systemPermissions => systemPermissions.addNews)
            .demandNewsPermissionsIf(this.newsId != null, String(this.newsId), newsPermissions => newsPermissions.edit);

        if (this.newsId) {
            this.loadNews();
        } else {
            PageHeader.set().header("Add News").update();
        }
    }

    public loadNews() {
        this.newsService.getNews(this.newsId).then(n => {
            angular.copy(n, this.news);
            PageHeader.set().header("Edit This News").update();
        })
    }

    public save() {
        this.disabledSaveButton = true;
        var promise = this.newsId
            ? this.newsService.update(this.news)
            : this.newsService.create(this.news);
        promise.then(n => {
            this.disabledSaveButton = false;
            notifier.success("News has been saved");
            this.$location.url(this.newsRouter.oneNewsUrl(n.id));
        })
    }

    public redirectOrClearFields(): void {
        if(this.newsId) {
            this.$location.url(this.newsRouter.oneNewsUrl(this.newsId));
        } else {
            this.cleanFields();
        }
    }

    private cleanFields() : void {
        this.news = {};
        this.news.type = "ALL";
    }
}