import {CommonRouter} from "./../urls/CommonRouter";
import {notifier} from "./../INotifier";

export class CommonNavigation {
    public static NAME = "common.CommonNavigation";

    public static $inject = [
        "$location",
        "$rootScope",
        "$timeout",
        CommonRouter.NAME
    ];

    private preventNavigationFromUrl: string; // window.location.href is stored here when needed

    constructor(private $location: angular.ILocationService,
                private $rootScope: angular.IRootScopeService,
                private $timeout: angular.ITimeoutService,
                private commonRouter: CommonRouter) {
        $rootScope.$on('$locationChangeStart', (event: angular.IAngularEvent, newUrl: string, oldUrl: string) => {
            if (!event.defaultPrevented) {
                // console.log("" + oldUrl + " -> " + newUrl);
                if (!this.preventNavigationFromUrl || this.preventNavigationFromUrl !== oldUrl) {
                    // console.log("this.preventNavigationFromUrl:", this.preventNavigationFromUrl);
                    this.allowNavigation();
                } else if (this.preventNavigationFromUrl) {
                    event.preventDefault();
                    this.$timeout(() => {
                        notifier.warning("Unsaved data will be lost if you leave this page");
                    });
                } else {
                    this.allowNavigation();
                }
            }
        });
        $(window).bind("beforeunload", (event: JQueryEventObject) => {
            if (this.preventNavigationFromUrl && this.preventNavigationFromUrl === window.location.href) {
                return "Unsaved data will be lost if you leave this page";
            }
        });
    }

    public allowNavigation() {
        this.preventNavigationFromUrl = null;
    }

    public preventNavigation() {
        // console.log("window.location.href:", window.location.href);
        let _url = window.location.href;
        this.preventNavigationFromUrl = _url;
    }

    public redirectToNotFound() {
        this.allowNavigation();
        this.$location.url(this.commonRouter.notFoundUrl());
    }
}
