// Use this method for third party libraries/services configuration on module loading
// Note only providers can be injected as dependencies here.
function thirdPartyConfiguration($provide: ng.auto.IProvideService) {
    $provide.decorator('taOptions', ['taRegisterTool','$rootScope', '$delegate', function (taRegisterTool: any,
                                                                                           $rootScope: ICommonRootScope,
                                                                                           taOptions: any) {
        var callExternalTaToolFunction = function(textAngular, deferred, event, restoreSelection) {
            var eventData: ITextAngularEventData = {
                eventSourceButton: <angular.IAugmentedJQuery>$(textAngular.$element),
                callbackFunction: (html) => {
                    $(textAngular.$editor().displayElements.text[0]).trigger('focus');
                    if (restoreSelection) {
                        restoreSelection();
                    }
                    if (html) {
                        textAngular.$editor().wrapSelection('insertHTML', "" + html, true);
                        deferred.resolve();
                    }
                }
            };
            $rootScope.$broadcast(event, eventData);
            return false;
        };
        taRegisterTool(RichTextEditorController.TA_EMBEDDED_MATERIAL_BUTTON, {
            iconclass: "fa fa-file-video-o",
            tooltiptext: "Insert material",
            action: function(deferred, restoreSelection) {
                return callExternalTaToolFunction(this, deferred, ComponentsEvents.TA_SELECT_EMBEDDED_MATERIAL, restoreSelection);
            }
        });
        taRegisterTool(RichTextEditorController.TA_EMBEDDED_BUNDLE_BUTTON, {
            iconclass: "fa fa-file-zip-o",
            tooltiptext: "Insert attached file",
            action: function(deferred, restoreSelection){
                return callExternalTaToolFunction(this, deferred, ComponentsEvents.TA_SELECT_EMBEDDED_BUNDLE, restoreSelection);
            }
        });
        taRegisterTool(RichTextEditorController.TA_SANDBOX_BUTTON, {
            iconclass: "fa fa-file-code-o",
            tooltiptext: "Insert sandbox window",
            action: function(deferred, restoreSelection){
                return callExternalTaToolFunction(this, deferred, ComponentsEvents.TA_SELECT_SANDBOX, restoreSelection);
            }
        });
        taOptions.toolbar = [
            ['h1', 'h2', 'h3', 'h4', 'h5', 'h6', 'p', 'pre', 'quote'],
            ['bold', 'italics', 'underline', 'strikeThrough', 'ul', 'ol', 'redo', 'undo', 'clear'],
            ['justifyLeft', 'justifyCenter', 'justifyRight', 'justifyFull', 'indent', 'outdent'],
            ['html', 'insertImage', 'insertLink', 'insertVideo',
                RichTextEditorController.TA_EMBEDDED_MATERIAL_BUTTON, RichTextEditorController.TA_EMBEDDED_BUNDLE_BUTTON,
                RichTextEditorController.TA_SANDBOX_BUTTON]
        ];
        return taOptions;
    }]);
    $provide.decorator('taCustomRenderers', ['$delegate', '$compile', '$rootScope', '$timeout', function (taCustomRenderers: Array<any>,
                                                                                $compile: angular.ICompileService,
                                                                                $rootScope: ICommonRootScope,
                                                                                $timeout: angular.ITimeoutService) {
        taCustomRenderers.push({
                selector: VhsEmbeddedMaterialController.TA_ELEMENT_SELECTOR, customAttribute: '',
                renderLogic: function(element) {
                    VhsEmbeddedMaterialController.TA_RENDER_FUNCTION(element, $compile, $rootScope, $timeout);
                }
        });
        taCustomRenderers.push({
                selector: VhsEmbeddedBundleController.TA_ELEMENT_SELECTOR, customAttribute: '',
                renderLogic: function(element) {
                    VhsEmbeddedBundleController.TA_RENDER_FUNCTION(element, $compile, $rootScope, $timeout);
                }
        });
        taCustomRenderers.push({
                selector: VhsSandboxController.TA_ELEMENT_SELECTOR, customAttribute: '',
                renderLogic: function(element) {
                    VhsSandboxController.TA_RENDER_FUNCTION(element, $compile, $rootScope, $timeout);
                }
        });
        taCustomRenderers.push({
                selector: VhsDownloadBundleController.TA_ELEMENT_SELECTOR, customAttribute: '',
                renderLogic: function(element) {
                    VhsDownloadBundleController.TA_RENDER_FUNCTION(element, $compile, $rootScope, $timeout);
                }
        });
        return taCustomRenderers;
    }]);
}

// Use this method to register work which should be performed when the injector is done loading all modules.
function run($log: ng.ILogService, $rootScope: ICommonRootScope, authApi: AuthService, authManager: AuthManager,
             authRedirect: AuthRedirect, localStorage: StorageWrapper, urlService: UrlService) {
    $rootScope.appVersionNumber = buildVersionNumber(
        AppProperties.appVersion,
        AppProperties.svnRevision,
        AppProperties.buildDate);
    $rootScope.url = urlService;
    //var completed = authManager.completeSpnegoExchange();
    //if (completed) {
    //    console.log("SPNEGO is completed. Waiting for redirect...");
    //    return;
    //}
    var completedSso = authManager.completePersonifySsoExchange();
    if (!authManager.isAuthenticated()) {
        //console.log("Is not authenticated. Redirect to Log On.");
        //authRedirect.redirectToLogOn();
        if (authManager.restoreAvailable()) {
            authApi.restartAutoRefresh();
        } else if (!completedSso) {
            console.log("Try personify SSO!");
            authManager.tryPersonifySsoIfAllowed();
        }
    } else {
        //console.log("Is authenticated. Start refresh.");
        var locale = authManager.getToken().locale ? authManager.getToken().locale : AppProperties.currentLocale;
        if (!LogOnController.reloadApplicationForLocaleIfNeeded(locale)) {
            authApi.restartAutoRefresh();
        }
    }
    var VERSION_STORAGE_KEY = "vhs.application.version.number";
    //console.log("storage version: " + localStorage.get(VERSION_STORAGE_KEY));
    if (localStorage.contains(VERSION_STORAGE_KEY)
        && localStorage.get(VERSION_STORAGE_KEY) != AppProperties.buildVersion) {
        notifier.warning("Application version has been updated since your last visit");
    }
    localStorage.put(VERSION_STORAGE_KEY, AppProperties.buildVersion);
}

function buildVersionNumber(appVersion: any, svnRevision: any, buildDate: any) {
    var parsed = moment(buildDate, "YYYYMMDDHHmm");
    return appVersion + "." + svnRevision + "; " + parsed.format("YYYY-MM-DD HH:mm");
}