import {UserService} from "./../users/UserService";
import {ISelectable} from "./../ISelectable";
import {UrlService} from "./../utils/UrlService";
import {IMaterial} from "./../materials/models/IMaterial";
import {IPlaylist} from "./../materials/models/IPlaylist";
import {ICourse} from "./../courses/models/ICourse";
import {IQuiz} from "./../quizzes/models/IQuiz";
import {SecuredObjectType} from "./../security/permissions/SecuredObjectType";
import {IPermissionWithUsersAndGroups} from "./../security/permissions/IPermissionWithUsersAndGroups";
import {AllUsersPermissions} from "./../security/permissions/IPermission";

export interface IUsersAndGroupsSelectorDirectiveScope extends ng.IScope {
    objectModel: any;
    objectModelProperty: string;
    currentUserIdModel: any,
    currentUserIdModelProperty: string,
    currentUserNameModel: any,
    currentUserNameModelProperty: string,

    permissionsWithUsersAndGroupsModel: IPermissionWithUsersAndGroups[];
    businessUnitFilter?: number;
    disabled: boolean;
    isPermissionsDelegation: boolean;

    vm?: UsersAndGroupsSelectorController;
}

interface IExtPermissionWithUsersAndGroups extends IPermissionWithUsersAndGroups {
    isEditing?: boolean;
    isUsersShowed?: boolean;
    isGroupsShowed?: boolean;

    currentAvailableUserIds?: number[];
    currentAvailableGroupIds?: number[];
    currentAvailableUsers?: ISelectable[];
    currentAvailableGroups?: ISelectable[];
    currentSelectedUsers?: ISelectable[];
    currentSelectedGroups?: ISelectable[];
    userIds?: number[];
    groupIds?: number[];

    currentAvailableUsersChangeCallback?: (selected: boolean, id: number, name: string) => void;
    currentAvailableGroupsChangeCallback?: (selected: boolean, id: number, name: string) => void;
}

interface IExtUser extends ISelectable {
    disabled?: boolean;
}

export class UsersAndGroupsSelectorController {
    private static ALL_USERS_GROUP_ID: number = 1;

    public permissionsWithUsersAndGroups: IExtPermissionWithUsersAndGroups[] = [];
    public isLoaded = false;

    public static $inject = [
        "$scope",
        "$q",
        UserService.NAME
    ];

    constructor(public $scope: IUsersAndGroupsSelectorDirectiveScope,
                private $q: angular.IQService,
                private userService: UserService) {
        $scope.vm = this;

        this.$scope.disabled = this.$scope.disabled != null
            ? this.$scope.disabled
            : false;

        this.$scope.isPermissionsDelegation= this.$scope.isPermissionsDelegation != null
            ? this.$scope.isPermissionsDelegation
            : false;

        this.permissionsWithUsersAndGroups = this.$scope.permissionsWithUsersAndGroupsModel;
        this.permissionsWithUsersAndGroups.forEach((permissionsWithUsersAndGroup: IExtPermissionWithUsersAndGroups) => {
            permissionsWithUsersAndGroup.isEditing = false;
            permissionsWithUsersAndGroup.isUsersShowed = true;
            permissionsWithUsersAndGroup.isGroupsShowed = true;

            permissionsWithUsersAndGroup.currentAvailableUserIds = [];
            permissionsWithUsersAndGroup.currentAvailableGroupIds = [];
            permissionsWithUsersAndGroup.currentAvailableUsers = [];
            permissionsWithUsersAndGroup.currentAvailableGroups = [];
            permissionsWithUsersAndGroup.currentSelectedUsers = [];
            permissionsWithUsersAndGroup.currentSelectedGroups = [];

            const currentUser: ISelectable = this.getCurrentUser();
            if (!this.$scope.objectModel[this.$scope.objectModelProperty].id && currentUser.id && currentUser.name) {
                let isFound: boolean = false;
                permissionsWithUsersAndGroup.users.forEach(user => {
                    if (!isFound && user.id === currentUser.id) {
                        isFound = true;
                    }
                });

                if (!isFound) {
                    let currentExtUser:IExtUser = currentUser;
                    currentExtUser.disabled = true;
                    permissionsWithUsersAndGroup.users.push(currentExtUser);
                    permissionsWithUsersAndGroup.users.sort(this.usersCompareFn);
                }
            }

            permissionsWithUsersAndGroup.userIds = [];
            permissionsWithUsersAndGroup.users.forEach(user => permissionsWithUsersAndGroup.userIds.push(user.id));
            permissionsWithUsersAndGroup.groupIds = [];
            permissionsWithUsersAndGroup.groups.forEach(group => permissionsWithUsersAndGroup.groupIds.push(group.id));

            permissionsWithUsersAndGroup.currentAvailableUsersChangeCallback = (selected: boolean, id: number, name: string): void => {
                if (selected) {
                    permissionsWithUsersAndGroup.currentAvailableUsers.push({id: id, name: name});
                } else {
                    for (let i = 0; i < permissionsWithUsersAndGroup.currentAvailableUsers.length; ++i) {
                        let currentAvailableUser: ISelectable = permissionsWithUsersAndGroup.currentAvailableUsers[i];
                        if (currentAvailableUser.id === id) {
                            permissionsWithUsersAndGroup.currentAvailableUsers.splice(i, 1);
                            break;
                        }
                    }
                }
            };
            permissionsWithUsersAndGroup.currentAvailableGroupsChangeCallback = (selected: boolean, id: number, name: string): void => {
                if (selected) {
                    permissionsWithUsersAndGroup.currentAvailableGroups.push({id: id, name: name});
                } else {
                    for (let i = 0; i < permissionsWithUsersAndGroup.currentAvailableGroups.length; ++i) {
                        let currentAvailableGroup: ISelectable = permissionsWithUsersAndGroup.currentAvailableGroups[i];
                        if (currentAvailableGroup.id === id) {
                            permissionsWithUsersAndGroup.currentAvailableGroups.splice(i, 1);
                            break;
                        }
                    }
                }
            }
        });

        this.$scope.$watch(($scope: IUsersAndGroupsSelectorDirectiveScope) => this.$scope.vm.getCurrentUserId(), (newValue, oldValue) => {
            if (newValue != oldValue) {
                this.permissionsWithUsersAndGroups.forEach((p: IExtPermissionWithUsersAndGroups) => {
                    p.isUsersShowed = false;
                    const currentUser: ISelectable = this.getCurrentUser();
                    UsersAndGroupsSelectorController.moveItems<ISelectable>([currentUser], p.users);
                    UsersAndGroupsSelectorController.moveItemIds<ISelectable>([currentUser], p.userIds);
                    for (let i = 0; i < p.users.length; ++i) {
                        const u: ISelectable = p.users[i];
                        if (u.id === oldValue) {
                            p.users.splice(i, 1);
                            break;
                        }
                    }
                    p.userIds.splice(p.userIds.indexOf(oldValue), 1);
                    p.currentAvailableUserIds = [];
                    p.currentAvailableUsers = [];
                    p.currentSelectedUsers = [];
                    p.users.sort(this.usersCompareFn);
                    setTimeout(() => p.isUsersShowed = true, 0);
                });
            }
        });

        //if (this.$scope.isPermissionsDelegation) {
        //    this.userService.getManagedUsersForSelector(this.$scope.businessUnitFilter).then((users: Array<ISelectable>) => {
        //        const usersMap: Map<number, ISelectable> = new Map<number, ISelectable>();
        //        users.forEach(user => usersMap.set(user.id, user));
        //        this.permissionsWithUsersAndGroups.forEach((permissionsWithUsersAndGroup: IExtPermissionWithUsersAndGroups) => {
        //            for (let j = permissionsWithUsersAndGroup.users.length - 1; j >= 0; j--) {
        //                if (!usersMap.has(permissionsWithUsersAndGroup.users[j].id)) {
        //                    permissionsWithUsersAndGroup.users.splice(j, 1);
        //                }
        //            }
        //        });
        //    });
        //
        //    this.userService.getManagedGroupsForSelector(this.$scope.businessUnitFilter).then((groups: Array<ISelectable>) => {
        //        const groupsMap: Map<number, ISelectable> = new Map<number, ISelectable>();
        //        groups.forEach(group => groupsMap.set(group.id, group));
        //        this.permissionsWithUsersAndGroups.forEach((permissionsWithUsersAndGroup: IExtPermissionWithUsersAndGroups) => {
        //            for (let j = permissionsWithUsersAndGroup.groups.length - 1; j >= 0; j--) {
        //                if (!groupsMap.has(permissionsWithUsersAndGroup.groups[j].id)) {
        //                    permissionsWithUsersAndGroup.groups.splice(j, 1);
        //                }
        //            }
        //        });
        //    });
        //}

        this.isLoaded = true;
    }

    public editing(p: IExtPermissionWithUsersAndGroups): void {
        p.isEditing = true;
    }

    public edited(p: IExtPermissionWithUsersAndGroups): void {
        p.isEditing = false;
    }

    public selectUsers(p: IExtPermissionWithUsersAndGroups): void {
        p.isUsersShowed = false;
        UsersAndGroupsSelectorController.moveItems<ISelectable>(p.currentAvailableUsers, p.users);
        UsersAndGroupsSelectorController.moveItemIds<ISelectable>(p.currentAvailableUsers, p.userIds);
        p.currentAvailableUserIds = [];
        p.currentAvailableUsers = [];
        p.users.sort(this.usersCompareFn);
        setTimeout(() => p.isUsersShowed = true, 0);
    }

    public unselectUsers(p: IExtPermissionWithUsersAndGroups): void {
        p.isUsersShowed = false;
        p.currentSelectedUsers.forEach(user => {
            p.users.splice(p.users.indexOf(user), 1);
            p.userIds.splice(p.userIds.indexOf(user.id), 1);
        });
        p.currentSelectedUsers = [];
        setTimeout(() => p.isUsersShowed = true, 0);
    }

    public selectGroups(p: IExtPermissionWithUsersAndGroups): void {
        p.isGroupsShowed = false;
        UsersAndGroupsSelectorController.moveItems<ISelectable>(p.currentAvailableGroups, p.groups);
        UsersAndGroupsSelectorController.moveItemIds<ISelectable>(p.currentAvailableGroups, p.groupIds);
        p.currentAvailableGroupIds = [];
        p.currentAvailableGroups = [];
        p.groups.sort(this.groupsCompareFn);
        setTimeout(() => p.isGroupsShowed = true, 0);
    }

    public unselectGroups(p: IExtPermissionWithUsersAndGroups): void {
        p.isGroupsShowed = false;
        p.currentSelectedGroups.forEach(group => {
            p.groups.splice(p.groups.indexOf(group), 1);
            p.groupIds.splice(p.groupIds.indexOf(group.id), 1);
        });
        p.currentSelectedGroups = [];
        setTimeout(() => p.isGroupsShowed = true, 0);
    }

    public disableUser(user: ISelectable, p: IExtPermissionWithUsersAndGroups): boolean {
        switch (p.permission.type) {
            case SecuredObjectType.MATERIAL.name.toUpperCase():
                let material: IMaterial = this.$scope.objectModel[this.$scope.objectModelProperty];
                return material.ownerId == user.id;
            case SecuredObjectType.PLAYLIST.name.toUpperCase():
                let playlist: IPlaylist = this.$scope.objectModel[this.$scope.objectModelProperty];
                return playlist.ownerId == user.id;
            case SecuredObjectType.COURSE.name.toUpperCase():
                let course: ICourse = this.$scope.objectModel[this.$scope.objectModelProperty];
                return course.ownerIds.indexOf(user.id) != -1;
            case SecuredObjectType.QUIZ.name.toUpperCase():
                let quiz: IQuiz = this.$scope.objectModel[this.$scope.objectModelProperty];
                return quiz.ownerId == user.id;
        }
        return false;
    }

    public getAvailableUsersUrl(businessUnit: number, excludedUserIds: number[]): string {
        let url: string = this.$scope.isPermissionsDelegation
            ? `/api/userSelector/managedUsers?businessUnitId=${businessUnit}`
            : `/api/userSelector/users?businessUnitId=${businessUnit}`;
        if (excludedUserIds && excludedUserIds.length > 0) {
            excludedUserIds.forEach(id => url += `&excludedUserIds=${id}`);
        }
        return url;
    }

    public getAvailableGroupsUrl(businessUnit: number, excludedGroupIds: number[], permissionId: number): string {
        let url: string = this.$scope.isPermissionsDelegation
            ? `/api/userSelector/managedGroups?businessUnitId=${businessUnit}`
            : `/api/userSelector/groups?businessUnitId=${businessUnit}`;
        let excludedAllUsersGroup: boolean = false;
        if (excludedGroupIds && excludedGroupIds.length > 0) {
            excludedGroupIds.forEach(id => {
                url += `&excludedGroupIds=${id}`;
                if (!excludedAllUsersGroup) {
                    excludedAllUsersGroup = id === UsersAndGroupsSelectorController.ALL_USERS_GROUP_ID;
                }
            });
        }
        if (!excludedAllUsersGroup && AllUsersPermissions.PERMISSIONS_IDS.indexOf(permissionId) === -1) {
            url += `&excludedGroupIds=${UsersAndGroupsSelectorController.ALL_USERS_GROUP_ID}`;
        }
        return url;
    }

    private getCurrentUserId(): number {
        return this.$scope.currentUserIdModel && this.$scope.currentUserIdModelProperty
            ? this.$scope.currentUserIdModel[this.$scope.currentUserIdModelProperty]
            : null;
    }

    private getCurrentUserName(): string {
        return this.$scope.currentUserNameModel && this.$scope.currentUserNameModelProperty
            ? this.$scope.currentUserNameModel[this.$scope.currentUserNameModelProperty]
            : null;
    }

    private getCurrentUser(): ISelectable {
        return {
            id: this.getCurrentUserId(),
            name: this.getCurrentUserName()
        };
    }

    private usersCompareFn = (first: ISelectable, second: ISelectable): number => {
        if (first.name > second.name) {
            return 1;
        } else if (first.name < second.name) {
            return -1;
        } else {
            return 0;
        }
    };

    private groupsCompareFn = (first: ISelectable, second: ISelectable): number => {
        if (first.name > second.name) {
            return 1;
        } else if (first.name < second.name) {
            return -1;
        } else {
            return 0;
        }
    };

    private static moveItems<T extends ISelectable>(items: T[], destination: T[]): void {
        items.forEach((item: T) => {
            let destinationIds: Array<number> = destination.map(destinationItem => {
                return destinationItem.id;
            });
            let index = destinationIds.indexOf(item.id);
            if (index === -1) {
                destination.push(item);
            }
        });
    }

    private static moveItemIds<T extends ISelectable>(items: T[], destination: number[]): void {
        items.forEach((item: T) => {
            let index = destination.indexOf(item.id);
            if (index === -1) {
                destination.push(item.id);
            }
        });
    }
}

UsersAndGroupsSelectorDirectiveFactory.$inject = [
    UrlService.NAME
];

export function UsersAndGroupsSelectorDirectiveFactory(url: UrlService): ng.IDirective {
    var directive: ng.IDirective = {};
    directive.restrict = "A";
    directive.controller = UsersAndGroupsSelectorController;
    directive.scope = {
        objectModel: "=vhsObjectModel",
        objectModelProperty: "=vhsObjectModelProperty",
        currentUserIdModel: "=?vhsCurrentUserIdModel",
        currentUserIdModelProperty: "=?vhsCurrentUserIdModelProperty",
        currentUserNameModel: "=?vhsCurrentUserNameModel",
        currentUserNameModelProperty: "=?vhsCurrentUserNameModelProperty",
        permissionsWithUsersAndGroupsModel: "=vhsPermissionsWithUsersAndGroupsModel",
        businessUnitFilter: "=?vhsBusinessUnitFilter",
        disabled: "=?vhsDisabled",
        isPermissionsDelegation: "@?vhsIsPermissionsDelegation"
    };
    directive.templateUrl = url.template("components/users-and-groups-selector.html");
    directive.transclude = true;
    return directive;
}