import {AuthService} from "./AuthService";

import {AuthRouter} from "../urls/AuthRouter";
import {MenuHelper} from "../utils/MenuHelper";
import {PageHeader} from "../utils/PageHeaderHelper";
import {notifier} from "../INotifier";
import {UrlService} from "../utils/UrlService";

export class CreateNewPasswordController {
    public static NAME = "auth.CreateNewPasswordController";

    public password: string = null;
    public confirmPassword: string = null;

    private token: string = null;
    public code: string = null;

    public static $inject = [
        "$routeParams",
        "$location",
        AuthService.NAME,
        AuthRouter.NAME,
        UrlService.NAME
    ];

    constructor(private $routeParams: ng.route.IRouteParamsService,
                private $location: angular.ILocationService,
                private authService: AuthService,
                private authRouter: AuthRouter,
                private url: UrlService) {
        this.token = this.$routeParams[AuthRouter.PASSWORD_TOKEN_PARAM_NAME];
        this.code = this.$routeParams[AuthRouter.PASSWORD_CODE_PARAM_NAME];

        MenuHelper.setCurrentMenuItem("");
        PageHeader.set().header("Create New Password").update();
    }

    public createNewPassword() {
        this.authService.createNewPassword(this.token, this.code, this.password)
            .then(() => {
                notifier.success("Password successfully changed");
                this.$location.url(this.authRouter.logonUrl());
            }).catch((response) => {
                switch (response.data.error) {
                    case "error.password.restore.impossible": {
                        notifier.error("Incorrect code or code expired. Try to restore the password again");
                        this.$location.url(this.authRouter.restorePassword());
                        return;
                    }
                }
            })
    }

    public showPassword() {
        if ($('#passwordField').attr('type') == 'password') {
            $(this).addClass('view');
            $('#passwordField').attr('type', 'text');
            $('#confirmPasswordField').attr('type', 'text');
        } else {
            $(this).removeClass('view');
            $('#passwordField').attr('type', 'password');
            $('#confirmPasswordField').attr('type', 'password');
        }
        return false;
    }

    public checkHidePassword() {
        return $('#passwordField').attr('type') == 'password';
    }
}