import {AuthRouter} from "./../urls/AuthRouter";
import {AuthRedirect} from "./../authcore/AuthRedirect";
import {AuthManager} from "./../authcore/AuthManager";
import {Event} from "./../authcore/Event";
import {AuthService} from "./../auth/AuthService";
import {UrlService} from "./../utils/UrlService";
import {IUserInfo} from "./../security/IUserInfo";
import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";
import {MainRouter} from "./../urls/MainRouter";
import {PersonalRouter} from "./../urls/PersonalRouter";
import {UsersRouter} from "./../urls/UsersRouter";
import {InfoRouter} from "./../urls/InfoRouter";

export class LoginController {
    private systemPermissions: ISystemPermissions;

    public authenticated: boolean;
    public user: IUserInfo;

    public static $inject = [
        "$scope",
        AuthService.NAME,
        MainRouter.NAME,
        AuthRouter.NAME,
        UsersRouter.NAME,
        InfoRouter.NAME,
        PersonalRouter.NAME,
        AuthManager.NAME,
    ];

    constructor(private $scope,
                private authApi: AuthService,
                private mainRouter: MainRouter,
                public authRouter: AuthRouter,
                public usersRouter: UsersRouter,
                public infoRouter: InfoRouter,
                public personalRouter: PersonalRouter,
                authManager: AuthManager) {
        $scope.vm = this;
        this.authenticated = authManager.isAuthenticated();
        if (this.authenticated) {
            this.getCurrentUser();
            this.updatePermissions();
        }
        $scope.$on(Event.LOGGED_ON, () => {
            this.authenticated = true;
            this.getCurrentUser();
            this.updatePermissions();
        });
        $scope.$on(Event.LOGGED_OFF, () => {
            this.authenticated = false;
            this.systemPermissions = {};
        });
    }

    private getCurrentUser(): void {
        this.authApi.getCurrentUser().then(u => {
            this.user = u;
        });
    }

    private updatePermissions() {
        this.systemPermissions = this.authApi.getCurrentUser().then(u => {
            this.systemPermissions = u.permissions;
        });
    }
}

export function LoginDirectiveFactory(url: UrlService): angular.IDirective {
    function link($scope: angular.IScope, element: angular.IAugmentedJQuery,
                  attributes: angular.IAttributes, controller: LoginController,
                  transclude: angular.ITranscludeFunction) {
    }

    var directive: any = {};
    directive.restrict = "A";
    directive.templateUrl = url.template("components/login.html");
    directive.link = link;
    directive.scope = true;
    directive.controller = LoginController;
    return directive;
}

LoginDirectiveFactory.$inject = [
    UrlService.NAME
];
