import {StringBuilder} from "./../utils/StringBuilder";
import {BaseRouter} from "./BaseRouter";

export class MaterialsRouter extends BaseRouter {
    public static NAME = "urls.MaterialsRouter";
    public static MATERIAL_ID_PARAM = "materialId";
    public static PLAYLIST_ID_PARAM = "playlistId";

    public static searchRoute = "/materials/search";
    public static materialRoute = `/materials/:${MaterialsRouter.MATERIAL_ID_PARAM}`;
    public static myDownloadsRoute = "/materials/my/downloads";
    public static createRoute = "/materials/create";
    public static playlistsRoute = "/materials/playlists";
    public static playlistCreateRoute = `/materials/playlists/create`;
    public static playlistDetailsRoute = `/materials/playlists/:${MaterialsRouter.PLAYLIST_ID_PARAM}`;
    public static playlistEditRoute = `/materials/playlists/:${MaterialsRouter.PLAYLIST_ID_PARAM}/edit`;
    public static updateRoute = `/materials/update/:${MaterialsRouter.MATERIAL_ID_PARAM}`;

    public static $inject = [
        "$location"
    ];

    constructor($location: ng.ILocationService) {
        super($location);
    }

    public searchUrl(): string {
        return this.url(MaterialsRouter.searchRoute);
    }

    public watchUrl(fileId: number): string {
        return this.url(StringBuilder.interpolate(MaterialsRouter.materialRoute)
            .with(':' + MaterialsRouter.MATERIAL_ID_PARAM, fileId.toString())
            .build());
    }

    public myDownloadsUrl(): string {
        return this.url(MaterialsRouter.myDownloadsRoute);
    }

    public createUrl(): string {
        return this.url(MaterialsRouter.createRoute);
    }

    public playlistsUrl(): string {
        return this.url(MaterialsRouter.playlistsRoute);
    }

    public playlistDetailsUrl(playlistId: number): string {
        return this.url(StringBuilder.interpolate(MaterialsRouter.playlistDetailsRoute)
            .with(':' + MaterialsRouter.PLAYLIST_ID_PARAM, playlistId.toString())
            .build());
    }

    public playlistEditUrl(playlistId: number): string {
        return this.url(StringBuilder.interpolate(MaterialsRouter.playlistEditRoute)
            .with(':' + MaterialsRouter.PLAYLIST_ID_PARAM, playlistId.toString())
            .build());
    }

    public playlistCreateUrl(): string {
        return this.url(MaterialsRouter.playlistCreateRoute);
    }

    public updateUrl(materialId: number): string {
        return this.url(StringBuilder.interpolate(MaterialsRouter.updateRoute)
            .with(':' + MaterialsRouter.MATERIAL_ID_PARAM, materialId.toString())
            .build());
    }
}