import {ProfileMenuItemsKey} from "./ProfileMenuItemsKey";
import {MaterialType} from "./../materials/models/MaterialType";

import {PermissionsService} from "./../security/PermissionsService";
import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";
import {SecurityService} from "./../security/SecurityService";
import {PersonalService} from "./PersonalService";
import {UrlService} from "./../utils/UrlService";
import {MaterialService} from "./../materials/MaterialService";
import {UserService} from "./../users/UserService";
import {SettingsService} from "./../settings/SettingsService";
import {QuizService} from "./../quizzes/QuizService";
import {ReferenceService} from "./../reference/ReferenceService";

import {MaterialsRouter} from "./../urls/MaterialsRouter";
import {CoursesRouter} from "./../urls/CoursesRouter";
import {NewsRouter} from "./../urls/NewsRouter";
import {QuizzesRouter} from "./../urls/QuizzesRouter";

import {MenuHelper} from "./../utils/MenuHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";
import {ObjectTypeUtils} from "./../utils/ObjectTypeUtils";
import {IGridModel, GridModel, IGridLoadingListener} from "./../IGridModel";
import {HttpGridHelper} from "./../IHttpGridService";
import {notifier} from "./../INotifier";

import {IMaterial} from "./../materials/models/IMaterial";
import {IObjectWithComments} from "./models/IObjectWithComments";
import {IComment} from "./../social/models/IComment";
import {IUser, PredefinedUsersEnum} from "./../users/models/IUser";
import {IUserLogon} from "./../users/models/IUserLogon";
import {ISelectable} from "./../ISelectable";
import {ISystemLocale} from "./../settings/models/ISystemLocale";
import {ReferencedObjectType} from "./../ReferencedObjectType";
import {IPlaylist} from "./../materials/models/IPlaylist";
import {ISubject} from "./../ISubject";
import {IQuiz} from "./../quizzes/models/IQuiz";
import {IQuizResult} from "./../quizzes/models/IQuizResult";
import {ICourse} from "./../courses/models/ICourse";

export class ProfileController {
    public static NAME = "personal.ProfileController";

    public isAuthenticated: boolean;
    private systemPermissions: ISystemPermissions;

    public leftMenu: Array<any>;
    public currentMenuItem: number;
    public rowShow: Map<number, boolean> = new Map<number, boolean>();
    public viewType: number;
    public uploadsGridModel: IGridModel<IMaterial>;
    public objectsWithCommentsGridModel: IGridModel<IObjectWithComments>;
    public objectComments: Map<number, Array<IComment>> = new Map<number, Array<IComment>>();
    public disableGetCommentsButton: boolean;
    public coursesGridModel: IGridModel<ICourse>;
    public playlistsGridModel: IGridModel<IPlaylist>;
    public createdQuizzesGridModel: IGridModel<IQuiz>;
    public passedQuizzesGridModel: IGridModel<IQuizResult>;
    public quizResults: Map<number, Array<IQuizResult>> = new Map<number, Array<IQuizResult>>();
    public disableGetQuizResultsButton: boolean;

    public userProfile: IUser;
    public newUserProfile: IUser;
    public userLogon: IUserLogon;
    public confirmPassword: string;
    public groups: Array<ISelectable>;
    public availableLocales: Array<ISystemLocale>;
    public disableSaveButton: boolean = false;
    public isEverythingDisabled: boolean;
    public playlistViewType: number;
    public quizViewType: number;
    public subscriptions: any;
    public users: Array<ISelectable> = [];
    public messageToSupport: string;
    public notificationsFrequency: Array<ISelectable>;
    public hasCreatedQuizzes: boolean = false;

    public static $inject = [
        "$http",
        "$location",
        UrlService.NAME,
        PermissionsService.NAME,
        ReferenceService.NAME,
        PersonalService.NAME,
        SecurityService.NAME,
        UserService.NAME,
        SettingsService.NAME,
        QuizService.NAME,
        MaterialsRouter.NAME,
        CoursesRouter.NAME,
        NewsRouter.NAME,
        QuizzesRouter.NAME
    ];

    constructor($http: angular.IHttpService,
                private $location: angular.ILocationService,
                private url: UrlService,
                private permissionsApi: PermissionsService,
                private referenceApi: ReferenceService,
                private personalService: PersonalService,
                private securityService: SecurityService,
                private userService: UserService,
                private settingsService: SettingsService,
                private quizService: QuizService,
                public materialsRouter: MaterialsRouter,
                public coursesRouter: CoursesRouter,
                public newsRouter: NewsRouter,
                public quizzesRouter: QuizzesRouter) {
        this.securityService.demandAuthentication();
        this.isAuthenticated = true;

        this.currentMenuItem = 0;
        this.viewType = 0;
        this.disableGetCommentsButton = false;
        this.disableGetQuizResultsButton = false;
        this.rowShow.set(1, false);
        this.rowShow.set(2, false);
        this.rowShow.set(3, false);
        this.rowShow.set(4, false);

        this.permissionsApi.getSystemPermissions().then(systemPermissions => {
            this.systemPermissions = systemPermissions;
            this.loadLeftMenu();
        });

        MenuHelper.setCurrentMenuItem(MenuHelper.PROFILE_MENU_KEY);
        PageHeader.set().header("My Profile").update();

        this.newUserProfile = {
            fullName: "",
            locale: null
        };
        this.userLogon = {
            name: "",
            password: ""
        };
        this.loadUserProfile();
        this.loadUserLogon();

        this.uploadsGridModel = new GridModel(HttpGridHelper.createLoader<IMaterial>($http, this.url.service("/api/materials/uploads")));
        this.uploadsGridModel.entries.forEach(e => {
            this.rowShow.set(e.id, false);
        });

        this.objectsWithCommentsGridModel = new GridModel(HttpGridHelper.createLoader<IObjectWithComments>($http,
            this.url.service("/api/personal/comments/objects")));
        this.objectsWithCommentsGridModel.entries.forEach(e => {
            this.rowShow.set(e.objectId, false);
        });

        this.coursesGridModel = new GridModel(HttpGridHelper.createLoader<ICourse>($http, this.url.service("/api/courses/my")));
        this.coursesGridModel.entries.forEach(e => {
            this.rowShow.set(e.id, false);
        });

        this.playlistsGridModel = new GridModel(HttpGridHelper.createLoader<IPlaylist>($http, this.url.service("/api/playlists/profilePlaylists")));
        this.playlistsGridModel.entries.forEach(e => {
            this.rowShow.set(e.id, false);
        });

        this.playlistViewType = 1;

        this.createdQuizzesGridModel = new GridModel(HttpGridHelper.createLoader<IQuiz>($http, this.url.service("/api/quizzes/profileQuizzes/created")));
        this.createdQuizzesGridModel.entries.forEach(e => {
            this.rowShow.set(e.id, false);
        });

        this.passedQuizzesGridModel = new GridModel(HttpGridHelper.createLoader<IQuizResult>($http, this.url.service("/api/quizzes/profileQuizzes/passed")));
        this.passedQuizzesGridModel.entries.forEach(e => {
            this.rowShow.set(e.id, false);
        });

        this.quizViewType = 1;

        this.subscriptions = {
            authorsIds: [],
            subjectsIds: [],
            allNewsSubscription: false,
            studentsNewsSubscription: false,
            commentsSubscription: false
        };

        this.loadSubscriptions();
        this.loadNotificationsFrequency();
        this.getUserHasCreatedQuizzes();
    }

    private loadLeftMenu(): void {
        this.leftMenu = [
            {key: ProfileMenuItemsKey.MY_COURSES, value: "My courses"},
            {key: ProfileMenuItemsKey.MY_QUIZZES, value: "My quizzes"},
            {key: ProfileMenuItemsKey.MY_PLAYLISTS, value: "My playlists"},
            {key: ProfileMenuItemsKey.SUBSCRIPTIONS, value: "My subscriptions"},
            {key: ProfileMenuItemsKey.NEW_COMMENTS, value: "New comments"},
            {key: ProfileMenuItemsKey.SUPPORT, value: "Support"},
            {key: ProfileMenuItemsKey.COUPONS, value: "Coupons"},
            {key: ProfileMenuItemsKey.SETTINGS, value: "Settings"},
        ];
        if (this.systemPermissions.addMaterial) {
            this.leftMenu.push({
                key: ProfileMenuItemsKey.MY_UPLOADS,
                value: "My uploads"
            });
            /* Hidden as per VHS-95
             this.leftMenu.push({
             key: ProfileMenuItemsKey.ACCOUNT_MANAGEMENT,
             value: "Withdraw"
             });
             */
        }
        if (this.systemPermissions.rechargeAccount) {
            this.leftMenu.push({key: ProfileMenuItemsKey.BALANCE, value: "Top-up"});
        }
        if (this.systemPermissions.viewPurchaseHistory) {
            this.leftMenu.push({
                key: ProfileMenuItemsKey.HISTORY,
                value: "History of purchases"
            });
        }
        this.leftMenu.sort(function compare(a, b) {
            if (a.key < b.key) {
                return -1;
            }
            if (a.key > b.key) {
                return 1;
            }
            return 0;
        })
    }

    private loadUserProfile(): void {
        this.confirmPassword = "";
        this.userService.getCurrentUserProfile().then(u => {
            this.userProfile = u;
            angular.copy(u, this.newUserProfile);
            this.isEverythingDisabled = (u.id == PredefinedUsersEnum.GUEST);
            this.loadAvailableLocales();
        })
    }

    private loadUserLogon(): void {
        this.userService.getCurrentUserLocalLogon().then(logon => {
            if (logon) {
                angular.copy(logon, this.userLogon);
            }
        })
    }


    private loadAvailableLocales(): void {
        this.settingsService.getAvailableLocales().then(locales => {
            this.availableLocales = locales;
        });
    }

    private loadNotificationsFrequency(): void {
        this.referenceApi.getActiveReferenceEntitiesForSelectorWithoutParams("calendarPeriods", [])
        .then(frequency => {
            this.notificationsFrequency = frequency;
        })
    }

    private getUserHasCreatedQuizzes(): void {
        this.quizService.hasUserCreatedQuizzes().then(result => {
            this.hasCreatedQuizzes = result;
        })
    }

    public saveProfileSettings() {
        this.disableSaveButton = true;
        this.confirmPassword = null;

        var promise = this.userService.updateCurrentUserProfile(this.newUserProfile, this.userLogon.password);
        promise.then(u => {
            this.disableSaveButton = false;
            notifier.success("Settings are successfully saved");
            this.loadUserProfile();
        }).catch(response => {
            this.disableSaveButton = false;
            if (response.data.error == "error.email.not.unique") {
                notifier.error("This email has already been registered in the system")
            } else if (response.data.error == "error.try.update.another.user") {
                notifier.error("You can change only your own profile settings");
            }
        });
    }

    public cancelProfileSettings() {
        this.loadUserProfile();
        this.loadUserLogon();
    }

    public setLeftMenuItem(item: number): void {
        this.currentMenuItem = item;
    }

    public isLeftMenuItemActive(item: number): boolean {
        return this.currentMenuItem == item;
    }

    public showDetails(index: number): void {
        this.rowShow.set(index, !this.rowShow.get(index));
    }

    public watch(materialId: number): void {
        this.$location.url(this.materialsRouter.watchUrl(materialId));
    }

    public getObjectRoute(object: IObjectWithComments) {
        if (object.objectType == ReferencedObjectType[ReferencedObjectType.MATERIAL]) {
            if (!object.available) {
                notifier.error("Access to the material is restricted");
            } else {
                this.$location.url(this.materialsRouter.watchUrl(object.objectId));
            }
        } else if (object.objectType == ReferencedObjectType[ReferencedObjectType.NEWS]) {
            this.$location.url(this.newsRouter.oneNewsUrl(object.objectId));
        }
    }

    public getObjectRouteForComment(object:IObjectWithComments, commentId: number) {
        if (object.permissions.view) {
            if (object.objectType == ReferencedObjectType[ReferencedObjectType.MATERIAL]) {
                this.$location.url(this.materialsRouter.watchUrl(object.objectId)).hash("comment" + commentId);
            } else if (object.objectType == ReferencedObjectType[ReferencedObjectType.NEWS]) {
                this.$location.url(this.newsRouter.oneNewsUrl(object.objectId)).hash("comment" + commentId);
            }
        }
    }

    public getGlyphiconClass(objectType: string, materialTypeId?: number): string {
        switch (objectType) {
            case ReferencedObjectType[ReferencedObjectType.MATERIAL]: return ObjectTypeUtils.getMaterialGlyphiconClass(materialTypeId);
            case ReferencedObjectType[ReferencedObjectType.NEWS]: return ObjectTypeUtils.getNewsGlyphiconClass();
        }
    }

    public getTypeTitle(objectType: string, materialTypeId: number): string {
        switch (objectType) {
            case ReferencedObjectType[ReferencedObjectType.MATERIAL]: return ObjectTypeUtils.getMaterialTypeTitle(materialTypeId);
            default: return ObjectTypeUtils.getReferenceObjectTypeTitle(objectType);
        }
    }

    public showComments(objectId: number, objectType: string): void {
        if (!this.rowShow.get(objectId) && !this.disableGetCommentsButton) {
            if (!this.objectComments.has(objectId)) {
                this.disableGetCommentsButton = true;
                this.personalService.getUserCommentsForObject(objectId, ObjectTypeUtils.getReferenceObjectTypeFromString(objectType)).then(result => {
                    this.objectComments.set(objectId, result);
                    this.rowShow.set(objectId, !this.rowShow.get(objectId));
                    this.disableGetCommentsButton = false;
                });
            }
        } else {
            this.rowShow.set(objectId, !this.rowShow.get(objectId));
        }
    }

    public showQuizResults(id: number, quizId: number, sourceType: string, sectionId?: number): void {
        if (!this.rowShow.get(id) && !this.disableGetQuizResultsButton && this.quizViewType == 1 && !this.quizResults.has(id)) {
            this.disableGetQuizResultsButton = true;
            this.quizService.getUserQuizResults(quizId, sourceType, sectionId).then(result => {
                this.quizResults.set(id, result);
                this.rowShow.set(id, !this.rowShow.get(id));
                this.disableGetQuizResultsButton = false;
            });
        } else {
            this.rowShow.set(id, !this.rowShow.get(id));
        }
    }

    public getLanguages(languages: Array<string>): string {
        var languagesStr: string = "";
        languages.forEach(l => {
            languagesStr +=  l + ", ";
        });
        return languagesStr.substring(0, languagesStr.length-2);
    }

    public loadPlaylist(playlistId: number): void {
        this.$location.url(this.materialsRouter.playlistDetailsUrl(playlistId));
    }

    public loadCourse(courseId: number): void {
        this.$location.url(this.coursesRouter.detailsUrl(courseId));
    }

    public loadQuiz(quizId: number): void {
        this.$location.url(this.quizzesRouter.detailsUrl(quizId));
    }

    public addCourseToFavorites(course: ICourse) {
        this.addToFavorites(course.id, ReferencedObjectType.COURSE).then(result => {
            notifier.success("The course has been added to your favorites");
            course.favorite = true;
        }).catch(response => {
            if(response.data.error == "object.already.in.favorite") {
                notifier.error("This course is already in your favorites");
            }
        });
    }

    public removeCourseFromFavorites(course: ICourse) {
       this.removeFromFavorites(course.id, ReferencedObjectType.COURSE).then(result => {
           notifier.success("The course has been removed from your favorites");
           course.favorite = false;
       }).catch(response => {
           if(response.data.error == "object.not.in.favorite") {
               notifier.error("Failed to remove from your favorites");
           }
       });
    }

    public addPlaylistToFavorites(playlist: IPlaylist) {
        this.addToFavorites(playlist.id, ReferencedObjectType.PLAYLIST).then(result => {
            notifier.success("The playlist has been added to your favorites");
            playlist.favorite = true;
        }).catch(response => {
            if(response.data.error == "object.already.in.favorite") {
                notifier.error("This playlist is already in your favorites");
            }
        });
    }

    public removePlaylistFromFavorites(playlist: IPlaylist) {
        this.removeFromFavorites(playlist.id, ReferencedObjectType.PLAYLIST).then(result => {
            notifier.success("The playlist has been removed from your favorites");
            playlist.favorite = false;
        }).catch(response => {
            if(response.data.error == "object.not.in.favorite") {
                notifier.error("Failed to remove from your favorites");
            }
        });
    }

    private addToFavorites(id: number, type: ReferencedObjectType) {
        this.rowShow.set(id, !this.rowShow.get(id));
        return this.personalService.addToFavorites(id, type);
    }

    private removeFromFavorites(id: number, type: ReferencedObjectType) {
        this.rowShow.set(id, !this.rowShow.get(id));
        return this.personalService.removeFromFavorites(id, type);
    }

    public setPlaylistViewType(viewType: number): void {
        this.playlistViewType = viewType;
        this.searchGridModelObjects(this.playlistsGridModel, this.playlistViewType);
    }

    public setQuizViewType(viewType: number): void {
        this.quizViewType = viewType;
    }

    private searchGridModelObjects(gridModel: IGridModel<any>, viewType: number): void {
        gridModel.filter.viewType = viewType;
        gridModel.loadCurrentPage();
    }

    public loadSubscriptions(): void {
        this.personalService.loadSubscriptions().then(result => {
            angular.copy(result, this.subscriptions);
            this.loadUsers();
        });
    }

    private loadUsers(): void {
        this.userService.getUsersWithoutExcludedUsersForSelector().then(result => {
            angular.copy(result, this.users);
        });
    }

    public changeSubscriptionsSettings(): void {
        this.personalService.changeSubscriptions(this.subscriptions).then(result => {
            this.userService.changeUserNotificationFrequency(this.userProfile.notificationFrequencyId).then(result => {
                notifier.success("Subscriptions are successfully saved");
            });
        });
    }

    public cancelSubscriptionsSettings(): void {
        this.loadSubscriptions();
    }

    public sendMessageToSupport(): void {
        this.personalService.sendMessageToSupport(this.messageToSupport).then(result => {
            notifier.success("Your message has been sent to the help desk, you will hear from them soon");
            this.messageToSupport = "";
        });
    }
}