import {IUser} from "./../users/models/IUser";
import {UserService} from "./../users/UserService";
import {SecurityService} from "./../security/SecurityService";
import {MenuHelper} from "./../utils/MenuHelper";
import {QuizService} from "./../quizzes/QuizService";
import {IQuiz} from "./../quizzes/models/IQuiz";
import {IQuizResult} from "./../quizzes/models/IQuizResult";
import {UrlService} from "./../utils/UrlService";
import {QuizzesRouter} from "./../urls/QuizzesRouter";
import {IGridModel, GridModel} from "./../IGridModel";
import {HttpGridHelper} from "./../IHttpGridService";

export class QuizzesController {
    public static NAME = "personal.QuizzesController";

    public userProfile: IUser;
    public createdQuizzesGridModel: IGridModel<IQuiz>;
    public passedQuizzesGridModel: IGridModel<IQuizResult>;
    public rowShow: Map<number, boolean> = new Map<number, boolean>();
    public quizResults: Map<number, Array<IQuizResult>> = new Map<number, Array<IQuizResult>>();
    public viewType: number;
    public quizViewType: number;
    public hasCreatedQuizzes: boolean = false;
    public disableGetQuizResultsButton: boolean;

    public static $inject = [
        "$http",
        "$location",
        UrlService.NAME,
        SecurityService.NAME,
        UserService.NAME,
        QuizService.NAME,
        QuizzesRouter.NAME
    ];

    constructor($http: angular.IHttpService,
                private $location: angular.ILocationService,
                private url: UrlService,
                private securityService: SecurityService,
                private userService: UserService,
                private quizService: QuizService,
                public quizzesRouter: QuizzesRouter) {
        this.securityService.demandAuthentication();
        MenuHelper.setCurrentMenuItem(MenuHelper.PERSONAL_ACCOUNT_QUIZZES_MENU_KEY);

        this.viewType = 0;
        this.quizViewType = 1;
        this.disableGetQuizResultsButton = false;

        this.loadUserProfile();
        this.hasUserCreatedQuizzes();

        this.createdQuizzesGridModel = new GridModel(HttpGridHelper.createLoader<IQuiz>($http, this.url.service("/api/quizzes/profileQuizzes/created")));
        this.createdQuizzesGridModel.entries.forEach(e => {
            this.rowShow.set(e.id, false);
        });

        this.passedQuizzesGridModel = new GridModel(HttpGridHelper.createLoader<IQuizResult>($http, this.url.service("/api/quizzes/profileQuizzes/passed")));
        this.passedQuizzesGridModel.entries.forEach(e => {
            this.rowShow.set(e.id, false);
        });
    }

    public setQuizViewType(viewType: number): void {
        this.quizViewType = viewType;
    }

    public showDetails(index: number): void {
        this.rowShow.set(index, !this.rowShow.get(index));
    }

    public showQuizResults(id: number, quizId: number, sourceType: string, sectionId?: number): void {
        if (!this.rowShow.get(id) && !this.disableGetQuizResultsButton && this.quizViewType == 1 && !this.quizResults.has(id)) {
            this.disableGetQuizResultsButton = true;
            this.quizService.getUserQuizResults(quizId, sourceType, sectionId).then(result => {
                this.quizResults.set(id, result);
                this.rowShow.set(id, !this.rowShow.get(id));
                this.disableGetQuizResultsButton = false;
            });
        } else {
            this.rowShow.set(id, !this.rowShow.get(id));
        }
    }

    public loadQuiz(quizId: number): void {
        this.$location.url(this.quizzesRouter.detailsUrl(quizId));
    }

    private hasUserCreatedQuizzes(): void {
        this.quizService.hasUserCreatedQuizzes().then(result => {
            this.hasCreatedQuizzes = result;
        })
    }

    private loadUserProfile(): void {
        this.userService.getCurrentUserProfile().then(u => {
            this.userProfile = u;
        })
    }
}