export class Moment {
    public static DEFAULT_FORMAT: string = "M/D/YYYY";

    public static parse(text: string): moment.Moment {
        return moment(text, Moment.DEFAULT_FORMAT);
    }

    public static textToJavaDate(text: string, format?: string): number {
        if (text == null || text == "") {
            return null;
        }
        format = format || Moment.DEFAULT_FORMAT;
        return moment(text, format).unix() * 1000;
    }

    public static isValidDate(date: number): string {
        if (date == null) {
            return null;
        }
        return moment(date).format(Moment.DEFAULT_FORMAT);
    }
}