import {ISystemPermissions} from "./../security/permissions/ISystemPermissions";
import {SecurityService} from "./../security/SecurityService";
import {MaterialsRouter} from "./../urls/MaterialsRouter";
import {MenuHelper} from "./../utils/MenuHelper";
import {SearchControlHelper} from "./../utils/SearchControlHelper";
import {PageHeader} from "./../utils/PageHeaderHelper";

export class MyDownloadsController {
    public static NAME = "materials.MyDownloadsController";

    public rowShow: Map<number, boolean> = new Map<number, boolean>();
    public viewType: number;

    public static $inject = [
        "$location",
        MaterialsRouter.NAME,
        SecurityService.NAME
    ];

    constructor(private $location: angular.ILocationService,
                public materialsRouter: MaterialsRouter,
                private securityService: SecurityService) {
        this.securityService.demandAuthentication()
            .demandSystemPermissions(systemPermissions => systemPermissions.addMaterial);

        this.rowShow.set(1, false);
        this.rowShow.set(2, false);
        this.rowShow.set(3, false);
        this.rowShow.set(4, false);

        this.viewType = 0;

        // TODO: this menu item was deleted, needs updating // MenuHelper.setCurrentMenuItem(MenuHelper.PURCHASES_DOWNLOADED_FILES_MENU_KEY);
        // TODO: // PageHeader.set().header(...).update();
    }

    public showDetails(index: number) {
        this.rowShow.set(index, !this.rowShow.get(index));
    }

    public watch(materialId: number) {
        this.$location.url(this.materialsRouter.watchUrl(materialId));
    }
}
