import {MainRouter} from "./MainRouter";
import {NewsRouter} from "./NewsRouter";
import {InfoRouter} from "./InfoRouter";
import {AuthRouter} from "./AuthRouter";
import {ReferenceRouter} from "./ReferenceRouter";
import {SystemSettingsRouter} from "./SystemSettingsRouter";
import {UsersRouter} from "./UsersRouter";
import {CoursesRouter} from "./CoursesRouter";
import {BroadcastsRouter} from "./BroadcastsRouter";
import {StatisticsRouter} from "./StatisticsRouter";
import {MaterialsRouter} from "./MaterialsRouter";
import {FinancesRouter} from "./FinancesRouter";
import {PersonalRouter} from "./PersonalRouter";
import {CommonRouter} from "./CommonRouter";
import {ContentRouter} from "./ContentRouter";
import {QuizzesRouter} from "./QuizzesRouter";
import {LogsRouter} from "./LogsRouter";
import {UrlServiceProvider} from "./UrlServiceProvider";

import {UrlService} from "./../utils/UrlService";

export var UrlsModule: angular.IModule = angular
    .module("urls", [])
    .service(MainRouter.NAME, MainRouter)
    .service(NewsRouter.NAME, NewsRouter)
    .service(InfoRouter.NAME, InfoRouter)
    .service(AuthRouter.NAME, AuthRouter)
    .service(ReferenceRouter.NAME, ReferenceRouter)
    .service(SystemSettingsRouter.NAME, SystemSettingsRouter)
    .service(UsersRouter.NAME, UsersRouter)
    .service(CoursesRouter.NAME, CoursesRouter)
    .service(BroadcastsRouter.NAME, BroadcastsRouter)
    .service(StatisticsRouter.NAME, StatisticsRouter)
    .service(MaterialsRouter.NAME, MaterialsRouter)
    .service(FinancesRouter.NAME, FinancesRouter)
    .service(PersonalRouter.NAME, PersonalRouter)
    .service(CommonRouter.NAME, CommonRouter)
    .service(ContentRouter.NAME, ContentRouter)
    .service(QuizzesRouter.NAME, QuizzesRouter)
    .service(LogsRouter.NAME, LogsRouter)
    .provider(UrlService.NAME, UrlServiceProvider);