import {StringBuilder} from "./../utils/StringBuilder";
import {LocationUtils} from "./../utils/LocationUtils";
import {BaseRouter} from "./BaseRouter";

export class UsersRouter extends BaseRouter {
    public static NAME = "urls.UsersRouter";
    public static GROUP_ID_PARAM = "groupId";

    public static indexRoute = "/users/";
    public static addRoute = "/users/create";
    public static signupRoute = "/users/signup";
    public static editRoute = "/users/:userId/edit";
    public static currentProfileRoute = "/users/:userProfileId/profile";

    public static groupIndexRoute = "/users/groups";
    public static groupAddRoute = "/users/groups/create";
    public static groupEditRoute = "/users/groups/:groupId";

    public static $inject = [
        "$location"
    ];

    constructor($location: ng.ILocationService) {
        super($location);
    }

    public indexUrl(): string {
        return this.url(UsersRouter.indexRoute);
    }

    public addUrl(): string {
        return this.url(UsersRouter.addRoute);
    }

    public signupUrl(): string {
        return this.url(UsersRouter.signupRoute);
    }

    public editUrl(userId: number): string {
        return this.url(
            !userId
                ? undefined
                : UsersRouter.editRoute.replace(":userId", userId.toString())
            );
    }

    public currentProfileUrl(profileId: string): string {
        return this.url(
            !profileId
                ? undefined
                : UsersRouter.currentProfileRoute.replace(":userProfileId", profileId)
            );
    }

    public groupsIndexUrl(): string {
        return this.url(UsersRouter.groupIndexRoute);
    }

    public groupsAddUrl(): string {
        return this.url(UsersRouter.groupAddRoute);
    }

    public groupEditUrl(groupId: number): string {
        return this.url(
                StringBuilder.interpolate(UsersRouter.groupEditRoute)
                    .with(':' + UsersRouter.GROUP_ID_PARAM, groupId.toString())
                    .build()
            );
    }

    private fullScreenUrl(baseUrl: string): string {
        return this.url(baseUrl);
    }
}
